import { Button } from '@mui/material'
import React from 'react'
import { TimeTableItemData } from 'types'

import styles from './styles.module.scss'

interface Props {
  onClose?: () => void
  onDelete?: () => void
  userData: TimeTableItemData | null
  title?: string
  subtitle?: string
  buttonText?: string
}

const HolidayPopup: React.FC<Props> = ({
  onClose = () => null,
  onDelete = () => null,
  userData,
  title,
  subtitle,
  buttonText,
}) => (
  <div className={styles.popupBackground}>
    <div className={styles.popupWrap}>
      <h5>{title}</h5>
      <p className={styles.womenName}>{subtitle}</p>
      <div className={styles.rangeWrap}>
        <span className={styles.greyText}>{'Von:'}</span>
        <span className={styles.numberText}>{userData?.start_time || ''}</span>
      </div>

      <div className={styles.rangeWrap}>
        <span className={styles.greyText}>{'Bis:'}</span>
        <span className={styles.numberText}>{userData?.end_time || ''}</span>
      </div>
      <div className={styles.rangeWrap}>
        <span className={styles.greyText}>{'Urlaubsvertretung:'}</span>
        {userData?.substitutes?.map((item, index) => (
          <span
            key={`${item.employee_name}__${index}`}
            className={styles.numberText}
          >
            {item.employee_name}
          </span>
        ))}
      </div>
      <div className={styles.buttonsWrap}>
        <Button
          className={styles.buttonLater}
          variant="outlined"
          type="button"
          onClick={onDelete}
        >
          {buttonText}
        </Button>
        <Button
          onClick={onClose}
          className={styles.buttonAccept}
          variant="contained"
          type="button"
        >
          {'Abbrechen'}
        </Button>
      </div>
    </div>
  </div>
)

export default HolidayPopup
