import Layout from 'common/Layout'
import Modal from 'common/Modal'
import Routes from 'constants/routes'
import { getUnixTime } from 'date-fns'
import useToggle from 'hooks/useToggle'
import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { zonedDate } from 'services/date'
import notify from 'services/notify'
import { getSelectValue } from 'services/utils'
import { createOffer } from 'store/ives/offers/actions'
import { CreateOfferRequest } from 'types'

import Form, { FormValues } from './OffersForm'
import ContentView from './view'

// ? we need use type instead interface because of https://github.com/microsoft/TypeScript/issues/15300
export type TableData = {
  id: string
  number: string
  created: string
  title: string
  customer: string
  amount: string
  field: string
  probability: string
  responsible: string
  department: string
  index: string
  bindingUntil: string
  commentCount: string
  nan: string
}

const Offers = (): JSX.Element => {
  const newModalToggle = useToggle()
  const [globalFilter, setGlobalFilter] = useState<string>()
  const dispatch = useDispatch()
  const history = useHistory()

  const _mapFormValueToOfferObj = (values: FormValues): CreateOfferRequest => ({
    comment: values.comment,
    created: getUnixTime(zonedDate(values.created)),
    customer: { id: values.customer || '' },
    liability_damage: getSelectValue(values.liabilityDamage),
    probability: getSelectValue(values.probability),
    title: values.title,
    department: getSelectValue(values.department),
    pgl: getSelectValue(values.pgl),
    project: getSelectValue(values.project),
    project_manager: getSelectValue(values.projectManager),
  })

  const onNewOfferSubmit = (values: FormValues) => {
    dispatch(
      createOffer({ ..._mapFormValueToOfferObj(values) }, () =>
        notify('Angebote wurde erfolgreich erstellt', 'success'),
      ),
    )
    newModalToggle.off()
  }

  const goToEvaluation = () => history.push(Routes.evaluations)

  return (
    <Layout headerProps={{ withoutSearch: true }}>
      <ContentView
        openNewOfferForm={newModalToggle.on}
        goToEvaluation={goToEvaluation}
        globalFilter={globalFilter}
        setGlobalFilter={setGlobalFilter}
      />
      <Modal isOpen={newModalToggle.value} onRequestClose={newModalToggle.off}>
        <Form onCancel={newModalToggle.off} onSubmit={onNewOfferSubmit} />
      </Modal>
    </Layout>
  )
}

export default Offers
