import { ReactComponent as ArrowNextOrange } from 'assets/icons/arrowNextOrange.svg'
import cn from 'classnames'
import Loader from 'components/Loader'
import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getEmployee } from 'store/companyStructure/actions'
import { ReduxState } from 'types'

import styles from './styles.module.scss'

interface Props {
  id: number | null
}

const EmployeeInfo: React.FC<Props> = ({ id }) => {
  const { employee, isLoading } = useSelector(
    (state: ReduxState) => state.companyStructure,
  )
  const { employees } = useSelector(({ ives }: ReduxState) => ives.employees)
  const dispatch = useDispatch()
  // entry for corporate mobile phone number that has a separate implementation from 'employee'
  const employeesExtra = employees?.rows.find(
    (item) => item.nid === employee?.nid.toString(),
  )

  useEffect(() => {
    id && dispatch(getEmployee(id))
  }, [id])

  if (!employee || !id || isLoading) return <Loader className={styles.loader} />

  return (
    <section className={styles.memberInfo}>
      <header className={styles.header}>
        <h3>{employee.full_name}</h3>
        <div>{employee.company_function.join(' / ')}</div>
      </header>
      <img
        src={employee.picture || undefined}
        alt={employee.full_name}
        className={styles.photo}
      />
      {employee.location && (
        <div className={styles.section}>
          <span className={styles.label}>{'Firmen Addresse:'}</span>
          {employee.location}
        </div>
      )}
      {employee.corporate_phone && (
        <div className={styles.section}>
          <span className={styles.label}>{'Telefon (Arbeit):'}</span>
          {employee.corporate_phone}
        </div>
      )}
      {employee.corporate_phone && (
        <div className={styles.section}>
          <span className={styles.label}>{'Mobil (Arbeit):'}</span>
          {employeesExtra?.corporate_mobile}
        </div>
      )}
      {employee.corporate_email && (
        <div className={styles.section}>
          <span className={styles.label}>{'E-mail:'}</span>
          <a
            className={styles.link}
            href={`mailto:${employee.corporate_email}`}
          >
            {employee.corporate_email}
          </a>
        </div>
      )}
      {(employee.project_group ||
        employee.competence_areas ||
        employee.working_groups ||
        employee.competence_centers) && (
        <div className={styles.section}>
          <span className={styles.label}>{'Mitglied:'}</span>
          <div className={cn(styles.group, 'blue')}>
            {employee.project_group}
            <ArrowNextOrange />
          </div>
          {employee.working_groups.map((group, index) => (
            <div key={index} className={cn(styles.group, 'green')}>
              <span>{group.working_group}</span>
              <ArrowNextOrange />
            </div>
          ))}
          {employee.competence_areas.map((area, index) => (
            <div key={index} className={cn(styles.group, 'orange')}>
              <span>{area.competence_area}</span>
              <ArrowNextOrange />
            </div>
          ))}
          {employee.competence_centers.map((area, index) => (
            <div key={index} className={cn(styles.group, 'yellow')}>
              <span>{area.competence_center}</span>
              <ArrowNextOrange />
            </div>
          ))}
        </div>
      )}
    </section>
  )
}
export default EmployeeInfo
