import Modal from 'common/Modal'
import Button from 'components/Button'
import React, { useState } from 'react'
import ReactCrop, { Crop, PercentCrop } from 'react-image-crop'
import { centerAspectCrop, cropImage } from 'services/image'
import notify from 'services/notify'
import { FileWithPreview } from 'types'

import styles from './styles.module.scss'

interface Props {
  isOpen?: boolean
  originalImage: FileWithPreview
  aspect?: number
  onClose(): void
  setCroppedImage(image: FileWithPreview): void
}

const ImageCropModal: React.FC<Props> = ({
  isOpen,
  originalImage,
  aspect,
  onClose,
  setCroppedImage,
}) => {
  const [crop, setCrop] = useState<Crop>()
  const [completedCrop, setCompletedCrop] = useState<PercentCrop>()

  const onImageLoad = (e: React.SyntheticEvent<HTMLImageElement>) => {
    if (aspect) {
      const { width, height } = e.currentTarget
      setCrop(centerAspectCrop(width, height, aspect))
    }
  }

  return (
    <Modal
      isOpen={isOpen}
      onRequestClose={onClose}
      maxWidth={600}
      footerContent={
        <div className={styles.footer}>
          <Button variant="fulfilled" color="blue" onClick={onClose}>
            {'Abbrechen'}
          </Button>
          <Button
            variant="fulfilled"
            color="orange"
            onClick={async () => {
              try {
                if (completedCrop) {
                  const cropped = await cropImage(originalImage, completedCrop)
                  setCroppedImage(cropped)
                }
              } catch (error) {
                notify(error)
              }
              onClose()
            }}
          >
            {'Änderungen speichern'}
          </Button>
        </div>
      }
      withCloseButton
    >
      <div className={styles.container}>
        {originalImage.preview && (
          <ReactCrop
            crop={crop}
            onChange={(crop) => setCrop(crop)}
            onComplete={(_, c) => setCompletedCrop(c)}
            aspect={aspect}
          >
            <img
              alt="Cropped"
              src={originalImage.preview}
              onLoad={onImageLoad}
            />
          </ReactCrop>
        )}
      </div>
    </Modal>
  )
}

export default ImageCropModal
