import axios from 'axios'
import { toast, ToastOptions, TypeOptions } from 'react-toastify'
import { Error } from 'types/index'

const notify = (
  text: Error,
  type: TypeOptions = 'error',
  params?: ToastOptions,
): void => {
  const textArray: string[] = []

  if (text) {
    if (Array.isArray(text)) {
      text.forEach((element: string) => textArray.push(element))
    } else if (typeof text === 'string') {
      textArray.push(text)
    } else if (axios.isAxiosError(text) && text.response?.data.message) {
      if (Array.isArray(text.response.data.message)) {
        text.response.data.message.forEach((element: string) =>
          textArray.push(element),
        )
      } else if (typeof text.response.data.message === 'string') {
        textArray.push(text.response.data.message)
      }
    } else {
      textArray.push('Etwas ist schief gelaufen')
    }
  }

  textArray.forEach((el) => {
    toast(el, { type, ...params })
  })
}

export default notify
