import { centerCrop, makeAspectCrop, PercentCrop } from 'react-image-crop'
import { FileWithPreview } from 'types'

import api from './api'

const TOLERANCE = 0.002
const WIDE_ASPECT_RATIO = 16 / 9

export const compareFloats = (
  first: number,
  second: number,
  tolerance = TOLERANCE,
) => Math.abs(first - second) < tolerance

const helperCanvas = document.createElement('canvas')

export const uploadImage = async (file: File, url: string) => {
  const { data } = await api.post(url, file, {
    headers: {
      'Content-Type': 'application/octet-stream',
      'Content-Disposition': `file; filename="${file.name}"`,
    },
  })

  return data.fid[0].value as number
}

export const centerAspectCrop = (
  mediaWidth: number,
  mediaHeight: number,
  aspect: number,
) =>
  centerCrop(
    makeAspectCrop(
      {
        unit: '%',
        width: 100,
      },
      aspect,
      mediaWidth,
      mediaHeight,
    ),
    mediaWidth,
    mediaHeight,
  )

export const cropImage = async (
  file: FileWithPreview,
  cropParam?: PercentCrop,
  aspectRatio?: number,
): Promise<FileWithPreview> => {
  const context = helperCanvas.getContext('2d')
  const image = new Image()
  image.src = file.preview || ''

  await new Promise((res) => {
    image.onload = res
  })

  const crop =
    cropParam ||
    centerAspectCrop(
      image.width,
      image.height,
      aspectRatio || WIDE_ASPECT_RATIO,
    )

  helperCanvas.width = (crop.width * image.width) / 100
  helperCanvas.height = (crop.height * image.height) / 100

  const cropX = (crop.x * image.width) / 100
  const cropY = (crop.y * image.height) / 100

  context?.clearRect(0, 0, helperCanvas.width, helperCanvas.height)

  context?.save()

  context?.translate(-cropX, -cropY)

  context?.drawImage(
    image,
    0,
    0,
    image.naturalWidth,
    image.naturalHeight,
    0,
    0,
    image.naturalWidth,
    image.naturalHeight,
  )

  context?.restore()

  const blob = await new Promise((res: BlobCallback) =>
    helperCanvas.toBlob(res, file.type),
  )

  const preview = helperCanvas.toDataURL(file.type)

  if (!blob) {
    throw new Error()
  }

  return Object.assign(new File([blob], file.name), { preview })
}
