/* eslint-disable camelcase */
import { Accordion, AccordionDetails, AccordionSummary } from '@mui/material'
import { ReactComponent as Arrow } from 'assets/icons/arrowDown.svg'
import Form from 'common/Form'
import getSubstitutes from 'common/pipes/getSubstitutesOfEmployee'
import Button from 'components/Button'
import Checkbox from 'components/inputs/Checkbox'
import DatePicker from 'components/inputs/DatePicker'
import DateRange from 'components/inputs/DateRange'
import Select from 'components/inputs/Select'
import TextInput from 'components/inputs/TextInput'
import { UserAccess } from 'constants/index'
import { eachDayOfInterval, format, isValid, parse } from 'date-fns'
import { useFormik } from 'formik'
import { useCallback, useEffect, useMemo, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Option } from 'react-select/src/filters'
import api from 'services/api'
import { formatDate } from 'services/date'
import notify from 'services/notify'
import { createHoliday } from 'store/ives/holidays/actions'
import type {
  AllEmployee,
  Employee,
  FreeEmployee,
  ReduxState,
  SelectOption,
} from 'types'
import { HolidaySubstitutes } from 'types'
import * as Yup from 'yup'
import { AnySchema } from 'yup'

import styles from '../../styles.module.scss'

interface Substitute {
  selectOption: Option
  period: { from: string; to: string }
}
const substitutionField = {
  selectOption: {
    label: '',
    value: '',
  },
  period: { from: '', to: '' },
}

const validationSchema = Yup.object().shape({
  substitutes: Yup.array(
    Yup.object({
      selectOption: Yup.object({
        label: Yup.string().required(),
        value: Yup.string().required(),
      }),
      period: Yup.object({
        from: Yup.string().required(),
        to: Yup.string().required(),
      }),
    }),
  ).when('planned', (planned: boolean, schema: AnySchema) =>
    planned ? schema.nullable() : schema,
  ),

  planned: Yup.boolean(),
  fromDate: Yup.date().required('Urlaubsanfang ist erforderlich'),
  toDate: Yup.date()
    .required('Urlaubsende ist erforderlich')
    .min(Yup.ref('fromDate'), 'Enddatum kann nicht vor Startdatum liegen'),
})

const selectOptionAll = { label: 'Betriebsruhe', value: `all` }

interface EmployeeFormProps {
  initialData?: Employee
  title?: string
  defaultEmployeeOption?: Option | null
  disabledChooseEmployee?: boolean
  onCancel: () => void
  onSubmit: () => void
  substitutes?: HolidaySubstitutes[]
  holidaysData?: {
    from_date: string
    to_date: string
  }
}

const HolidayForm: React.FC<EmployeeFormProps> = ({
  title,
  defaultEmployeeOption = null,
  disabledChooseEmployee = false,
  substitutes,
  holidaysData,
  onCancel,
  onSubmit,
}) => {
  const { employee, access, holidays_left } =
    useSelector(({ user }: ReduxState) => user.user) || {}
  const [substituteOptions, setSubstituteOptions] = useState<SelectOption[]>([])
  const [employeeOptions, setEmployeeOptions] = useState<SelectOption[]>([])
  const [disabledCheckbox, setDisabledCheckbox] = useState(false)
  const { employees } = useSelector(({ ives }: ReduxState) => ives.holidays)
  const dispatch = useDispatch()
  const substitutesData = useMemo<Substitute[]>(
    () =>
      substitutes
        ? substitutes.map(
            (substitute) =>
              ({
                selectOption: {
                  label: substitute.employee_name,
                  value: `${substitute.employee}`,
                } as Option,
                period: substitute.period,
              } as Substitute),
          )
        : ([substitutionField] as Substitute[]),
    [substitutes],
  )
  const isLengthEqual = (values: any) => {
    if (values.planned) {
      return true
    }
    let counter = 0
    const holidayOwnerCounter = eachDayOfInterval({
      start: new Date(values.fromDate),
      end: new Date(values.toDate),
    }).length
    values.substitutes.forEach((substitute: any) => {
      counter += eachDayOfInterval({
        start: new Date(substitute.period.from),
        end: new Date(substitute.period.to),
      }).length
    })
    return holidayOwnerCounter === counter
  }
  const isValidSubstitution = (values: any) => {
    if (values.planned) {
      return true
    }
    let validity = true
    values.substitutes.forEach((substitute: any) => {
      if (new Date(substitute.period?.from) < new Date(values?.fromDate)) {
        validity = false
      }
      if (new Date(substitute.period?.to) > new Date(values?.toDate)) {
        validity = false
      }
    })
    return validity
  }

  const initialValues = {
    chosenEmployee: defaultEmployeeOption as Option | null,
    substitutes: substitutesData,
    planned: false,
    autoApproval: false,
    fromDate: holidaysData?.from_date
      ? holidaysData?.from_date
      : formatDate(new Date(), 'yyyy-MM-dd'),
    toDate: holidaysData?.to_date
      ? holidaysData?.to_date
      : formatDate(new Date(), 'yyyy-MM-dd'),
  }

  const isQ3ConditionBroken = (values: any) => {
    const isQ3 = format(new Date(), 'MM-dd') > '07-01'
    let returnStatement = false
    const currentYear = new Date().getFullYear()
    if (
      new Date(values.fromDate).getFullYear() > currentYear ||
      new Date(values.toDate).getFullYear() > currentYear
    ) {
      if (isQ3) {
        returnStatement = true
      }
    } else {
      returnStatement = true
    }
    return returnStatement
  }

  // match chosen employee value with substitute value
  // eslint-disable-next-line consistent-return
  const matchSubstitute = (
    values: any,
    chosenEmployee?: number | string | null,
  ) => {
    const subArray = getSubstitutes(employees)
    const currentInterval = eachDayOfInterval({
      start: new Date(values.fromDate),
      end: new Date(values.toDate),
    }).map((d) => format(d, 'yyyy-MM-dd'))
    const chosenSubstitutes = subArray[chosenEmployee] || []
    return chosenSubstitutes.every((substitute: any) => {
      const substituteInterval = eachDayOfInterval({
        start: new Date(substitute.from),
        end: new Date(substitute.to),
      }).map((d) => format(d, 'yyyy-MM-dd'))
      return !substituteInterval.some((element) =>
        currentInterval.includes(element),
      )
    })
  }
  const formik = useFormik({
    initialValues,
    validationSchema,
    onSubmit: async (values) => {
      console.log(
        disabledChooseEmployee,
        employee,
        values?.chosenEmployee?.value,
      )
      const currentEmployeeSelected = !access?.[
        UserAccess.createEmployeeHolidayForAny
      ]
        ? employee
        : values?.chosenEmployee?.value
      if (holidays_left > 0) {
        if (matchSubstitute(values, currentEmployeeSelected)) {
          if (isValidSubstitution(values)) {
            if (isLengthEqual(values)) {
              if (isQ3ConditionBroken(values)) {
                try {
                  dispatch(
                    createHoliday(currentEmployeeSelected, {
                      period: { from: values.fromDate, to: values.toDate },
                      substitutes: values.substitutes
                        ? values.substitutes.map((item) => ({
                            period: item.period,
                            employee: item.selectOption.value,
                          }))
                        : '',
                      state: values.planned ? 'planned' : 'requested',
                      auto_approval: values.autoApproval,
                    }),
                  )
                  onSubmit()
                } catch (error) {
                  notify(error)
                }
              } else {
                notify(
                  'Das dritte Quartal des Jahres hat noch nicht begonnen, daher können Sie noch keinen Urlaub für das nächste Jahr buchen.',
                )
              }
            } else {
              notify(
                'Die Urlaubszeiten der Vertretung sollten Ihrem Zeitraum entsprechen',
                'warning',
              )
            }
          } else {
            notify(
              'Die Vertretungstermine müssen im Bereich der geplanten Bereiche liegen',
              'warning',
            )
          }
        } else {
          notify(
            'Im aktuellen Zeitraum ist bereits eine Vertretung eingetragen',
          )
        }
      } else {
        notify('Sie haben keine Urlaubstage')
      }
    },
  })

  const { fromDate, toDate, chosenEmployee } = formik.values

  const shouldDisableDate = useCallback(
    (date: Date) => {
      const correctDate = formatDate(date, 'yyyy-MM-dd')
      return (
        correctDate < formik.values.fromDate! ||
        correctDate > formik.values.toDate!
      )
    },
    [formik],
  )

  const disabledFromDateRange = useCallback(
    (date: Date) => formatDate(date, 'yyyy-MM-dd') < formik.values.fromDate!,
    [formik],
  )

  /** Show current year, prev year and next one year from current date */
  const disabledOneYear = useCallback((date: Date) => {
    const currentDate = new Date()
    const prevYear = currentDate.getFullYear() - 1
    const nextYear = currentDate.getFullYear() + 1

    return date.getFullYear() < prevYear || date.getFullYear() > nextYear
  }, [])
  const setCorrectSubstitutes = async (fromDate: string, toDate: string) => {
    const response = await api.get<FreeEmployee[]>(
      `/employees/${
        chosenEmployee?.value || employee
      }/holidays/substitutes/${fromDate}/${toDate}?_format=json`,
    )
    setSubstituteOptions(
      response.data.map((employee) => ({
        label: employee.name,
        value: String(employee.id),
      })),
    )
  }

  /** Get free substitutions */
  useEffect(() => {
    const fetchEmployees = async () => {
      const timeoutId = setTimeout(() => {
        const fromParsed = parse(fromDate, 'yyyy-MM-dd', new Date())
        const toParsed = parse(toDate, 'yyyy-MM-dd', new Date())
        if (isValid(fromParsed) && isValid(toParsed)) {
          try {
            setCorrectSubstitutes(fromDate, toDate)
          } catch (error) {
            notify(error)
          }
        }
      }, 200)
      return () => clearTimeout(timeoutId)
    }

    fetchEmployees()
  }, [fromDate, toDate, formik.values.chosenEmployee])

  /** Get employees for order holiday */
  useEffect(() => {
    const fetchEmployees = async () => {
      try {
        const response = await api.get<AllEmployee[]>(`/pages/employee/holiday`)

        const result = response.data.reduce(
          (lastItem, item) =>
            lastItem.concat(
              item.project_group.employees.map((item) => ({
                label: item.name,
                value: `${item.id}`,
              })),
            ),
          [] as { label: string; value: string }[],
        )

        setEmployeeOptions([selectOptionAll, ...result])
      } catch (error) {
        notify(error)
      }
    }

    fetchEmployees()
  }, [fromDate, toDate])

  /** Set and disabled checkbox when selected all employee for holidays */
  useEffect(() => {
    const setCheckbox = () => {
      if (formik.values.chosenEmployee?.value === selectOptionAll.value) {
        setDisabledCheckbox(true)
        return formik?.setFieldValue('autoApproval', true)
      }
      return setDisabledCheckbox(false)
    }
    setCheckbox()
  }, [formik.values.chosenEmployee])

  /** Add field with Select and Date to Holiday form */
  const addSubstitution = () => {
    formik.setFieldValue('substitutes', [
      ...formik.values.substitutes,
      substitutionField,
    ])
  }

  /** Remove field with Select and Date from Holiday form */
  const removeSubstitution = (index: number) => {
    if (formik.values.substitutes.length > 1) {
      formik.setFieldValue('substitutes', [
        ...formik.values.substitutes.slice(0, index),
        ...formik.values.substitutes.slice(index + 1),
      ])
    }
  }

  /**  Submit form and check substitutes for showing notify with error */
  const submitFrom = useCallback(
    (event: React.FormEvent<HTMLFormElement> | undefined) => {
      event?.preventDefault()
      if (
        formik.values.substitutes &&
        (formik.errors.substitutes ||
          formik.values.substitutes[0].selectOption.label === '') &&
        !formik.values.planned
      ) {
        notify('Bitte überprüfen Sie die Vertretungen')
      }
      formik.handleSubmit()
    },
    [formik, formik.errors.substitutes],
  )

  /**  Set checkbox with planned or not */
  const setPlanned = useCallback(() => {
    const planned = !formik.values.planned
    formik.setFieldValue('planned', planned)

    if (!planned) {
      return formik.setFieldValue('substitutes', [substitutionField])
    }
    return formik.setFieldValue('substitutes', null)
  }, [formik])

  return (
    <Form
      handleSubmit={submitFrom}
      title={title}
      header={<div className={styles.formHeader} />}
      onCancel={onCancel}
    >
      <div id="holidayForm">
        {access?.[UserAccess.createEmployeeHolidayForAny] && (
          <Select
            name="chosenEmployee"
            options={employeeOptions}
            placeholder="Mitarbeiter"
            label="Mitarbeiter"
            formik={formik}
            isCreatable={false}
            isDisabled={disabledChooseEmployee}
          />
        )}
        <DatePicker
          name="fromDate"
          placeholder="Urlaubsanfang"
          formik={formik}
          shouldDisableDate={disabledOneYear}
        />
        <DatePicker
          name="toDate"
          placeholder="Urlaubsende"
          formik={formik}
          shouldDisableDate={disabledFromDateRange}
        />
        <TextInput
          name="vacation_remaining"
          label={`Urlaubstage verbleibend: ${holidays_left}`}
          formik={formik}
          disabled
        />
        {!formik.values.planned && formik.values.substitutes && (
          <section>
            {formik.values.substitutes.map((item, index) => (
              <div key={`${item}_${index}`}>
                <Accordion className={styles.accordionWrap}>
                  <AccordionSummary
                    expandIcon={<Arrow />}
                    className={styles.dropdown}
                  >
                    <h3 className={styles.fieldTitle}>
                      {'Vertretung '}
                      {index + 1}
                      {' auswählen'}
                    </h3>
                  </AccordionSummary>
                  <AccordionDetails>
                    <div className={styles.fieldWrap}>
                      <Select
                        name={`substitutes[${index}].selectOption`}
                        options={substituteOptions}
                        label="Vertretung auswählen"
                        formik={formik}
                        isCreatable={false}
                        value={formik.values.substitutes[index].selectOption}
                        isDisabled={disabledChooseEmployee}
                      />
                      <DateRange
                        name={`substitutes[${index}].period`}
                        formik={formik}
                        values={formik.values.substitutes[index].period}
                        shouldDisableDate={shouldDisableDate}
                      />
                      <Button
                        variant="text"
                        type="button"
                        className={styles.removeButton}
                        onClick={() => removeSubstitution(index)}
                      >
                        &times;
                      </Button>
                    </div>
                  </AccordionDetails>
                </Accordion>
              </div>
            ))}

            <Button
              variant="text"
              type="button"
              className={styles.button}
              onClick={addSubstitution}
            >
              {'+ Hinzufügen'}
            </Button>
          </section>
        )}

        <div className={styles.checkboxesWrap}>
          {/* Available when admin role, approve holiday short way */}
          {access?.[UserAccess.createEmployeeHolidayForAny] && (
            <Checkbox
              disabled={disabledCheckbox}
              checked={!!formik.values.autoApproval}
              theme="default"
              {...formik.getFieldProps('autoApproval')}
            >
              {'Urlaub bereits genehmigt'}
            </Checkbox>
          )}
          <Checkbox
            theme="default"
            {...formik.getFieldProps('planned')}
            onChange={setPlanned}
          >
            {'Urlaub ist nur angedacht'}
          </Checkbox>
        </div>
      </div>
    </Form>
  )
}

export default HolidayForm
