import cn from 'classnames'
import {
  ContextMenuParams,
  Item,
  ItemParams,
  Menu,
  TriggerEvent,
} from 'react-contexify'
import { Breakpoint } from 'types'

import { ContextMenuProps, MENU_ID, Offset } from '.'
import RowItem from './RowItem'
import { ItemWithMenu } from './rows'
import styles from './styles.module.scss'

interface Props {
  zoomed: boolean
  zoomOffset: Offset
  currentBreakpoint: Breakpoint
  rows: Array<Array<ItemWithMenu>>
  searchValue: string
  showContextMenu(
    event: TriggerEvent,
    params?: Pick<ContextMenuParams, 'id' | 'props' | 'position'>,
  ): void
  onContextItemClick(params: ItemParams<ContextMenuProps>): void
  zoom(offset: Offset): () => void
}

const ContentView: React.FC<Props> = ({
  zoomed,
  zoomOffset,
  currentBreakpoint: bp,
  rows,
  searchValue,
  showContextMenu,
  onContextItemClick,
  zoom,
}) => (
  <div>
    <div className={styles.title}>{'S&P Prozesslandkarte'}</div>
    <div className={styles.zoomableContainer}>
      <div
        className={cn(styles.zoomable, zoomed && styles.zoomed)}
        style={{ left: zoomOffset.x, top: zoomOffset.y }}
      >
        <section className={styles.section}>
          <div className={cn(styles.sectionTitle, styles.blue)}>
            <div>{'QMS-Dateien'}</div>
          </div>
          <div className={styles.rows}>
            <div className={styles.row}>
              <div className={cn(styles.rowTitle, styles.gray)}>
                <div className={styles.number}>{'I'}</div>
                <div>{'Übergreifend'}</div>
              </div>
              <div className={styles.rowItemsWrapper}>
                {rows[0].map((item, i) => (
                  <RowItem
                    key={item.title}
                    item={item}
                    index={i}
                    color="gray"
                    searchValue={searchValue}
                    showContextMenu={showContextMenu}
                  />
                ))}
              </div>
            </div>
            <div className={styles.row}>
              <div className={cn(styles.rowTitle, styles.blue)}>
                <div className={styles.number}>{'II'}</div>
                <div>{'MP Management- prozesse'}</div>
              </div>
              <div className={styles.rowItemsWrapper}>
                {rows[1].map((item, i) => (
                  <RowItem
                    key={item.title}
                    item={item}
                    index={i}
                    color="blue"
                    searchValue={searchValue}
                    showContextMenu={showContextMenu}
                  />
                ))}
              </div>
            </div>
            <div className={styles.row}>
              <div className={cn(styles.rowTitle, styles.blue)}>
                <div className={styles.number}>{'III'}</div>
                <div>{'KP Kernprozesse'}</div>
              </div>
              <div className={styles.rowItemsWrapper}>
                {rows[2].map((item, i) => (
                  <RowItem
                    key={item.title}
                    item={item}
                    index={i}
                    color="blue"
                    searchValue={searchValue}
                    showContextMenu={showContextMenu}
                    onClick={
                      item.offset?.[bp]?.x && item.offset?.[bp]?.y
                        ? zoom({
                            x: item.offset?.[bp]?.x || 0,
                            y: item.offset?.[bp]?.y || 0,
                          })
                        : undefined
                    }
                  />
                ))}
              </div>
            </div>
            <div className={styles.row}>
              <div className={cn(styles.rowTitle, styles.blue)}>
                <div className={styles.number}>{'IV'}</div>
                <div>{'UP Unterstützende Prozesse'}</div>
              </div>
              <div className={styles.rowItemsWrapper}>
                {rows[3].map((item, i) => (
                  <RowItem
                    key={item.title}
                    item={item}
                    index={i}
                    color="blue"
                    searchValue={searchValue}
                    showContextMenu={showContextMenu}
                    onClick={zoom({
                      x: item.offset?.[bp]?.x || 0,
                      y: item.offset?.[bp]?.y || 0,
                    })}
                  />
                ))}
              </div>
            </div>
          </div>
        </section>
        {/* <div className={styles.section}>
          <div className={cn(styles.sectionTitle, styles.orange)}>
            <div>{'Entwicklungsprozesse'}</div>
          </div>
          <div className={styles.rows}>
            <div className={cn(styles.row, styles.orange)}>
              <div className={cn(styles.rowTitle, styles.orange)}>
                <div>{'Entwicklungsprozesse'}</div>
              </div>
              <div className={styles.rowItemsWrapper}>
                {rows[4].map((item) => (
                  <RowItem
                    key={item.title}
                    item={item}
                    color="orange"
                    searchValue={searchValue}
                  />
                ))}
              </div>
            </div>
            <div className={cn(styles.row, styles.orange)}>
              <div className={cn(styles.rowTitle, styles.orange)}>
                <div>{'Software'}</div>
              </div>
              <div className={styles.rowItemsWrapper}>
                {rows[5].map((item) => (
                  <RowItem
                    key={item.title}
                    item={item}
                    color="orange"
                    searchValue={searchValue}
                  />
                ))}
              </div>
            </div>
            <div className={cn(styles.row, styles.orange)}>
              <div className={cn(styles.rowTitle, styles.orange)}>
                <div>{'Onlinedienste'}</div>
              </div>
              <div className={styles.rowItemsWrapper}>
                {rows[6].map((item) => (
                  <RowItem
                    key={item.title}
                    item={item}
                    color="orange"
                    searchValue={searchValue}
                  />
                ))}
              </div>
            </div>
            <div className={cn(styles.row, styles.orange)}>
              <div className={cn(styles.rowTitle, styles.orange)}>
                <div>{'Allgemein'}</div>
              </div>
              <div className={styles.rowItemsWrapper}>
                {rows[7].map((item) => (
                  <RowItem
                    key={item.title}
                    item={item}
                    color="orange"
                    searchValue={searchValue}
                  />
                ))}
              </div>
            </div>
          </div>
        </div> */}
      </div>
    </div>
    <Menu id={MENU_ID}>
      <Item onClick={onContextItemClick}>{'Auf Mein S&P Hinzufügen'}</Item>
    </Menu>
  </div>
)

export default ContentView
