// import cn from 'classnames'
import Modal from 'common/Modal'
import PrintFilterModal from 'common/PrintTemplates/components/PrintFilterModal'
// import Button from 'components/Button'
import useToggle from 'hooks/useToggle'
import React, { forwardRef, useRef } from 'react'
// import { useReactToPrint } from 'react-to-print'
import { Employee } from 'types'

import styles from './styles.module.scss'
import BasePrintTemplate from './Templates/BasePrintTemplate'
import SalaryPrintTemplate from './Templates/SalaryPrintTemplate'
import TimePrintTemplate from './Templates/TimePrintTemplate'

interface PrintProps {
  currentRecord?: Employee
  employeeTitle: Employee | undefined
}

// eslint-disable-next-line react/display-name
const PrintTemplate = forwardRef<any, PrintProps>((props, ref) => (
  <div ref={ref} className={styles.printContainer}>
    <BasePrintTemplate {...props} />
    <SalaryPrintTemplate {...props} />
    <TimePrintTemplate {...props} />
  </div>
))

const ComponentToPrint = ({ currentRecord, employeeTitle }: PrintProps) => {
  const componentRef = useRef<any>()
  const printModalToggle = useToggle()
  // const printPreview = () => {}
  // const handlePrint = useReactToPrint({
  //   content: () => componentRef.current,
  // })
  return (
    <div>
      <PrintTemplate
        employeeTitle={employeeTitle}
        currentRecord={currentRecord}
        ref={componentRef}
      />
      {/* <Button
        className={cn(styles.historyBtn, styles.base, styles.headerBtn)}
        variant="fulfilled"
        onClick={handlePrint}
      >
        {'Ausdrucken1'}
      </Button> */}
      <Modal
        isOpen={printModalToggle.value}
        onRequestClose={printModalToggle.off}
        withCloseButton
        maxWidth={600}
      >
        <PrintFilterModal />
      </Modal>
    </div>
  )
}

export default ComponentToPrint
