import { isDev } from 'constants/index'
import { applyMiddleware, compose, createStore, Middleware, Store } from 'redux'
import { composeWithDevTools } from 'redux-devtools-extension'
import logger from 'redux-logger'
import {
  PersistConfig,
  Persistor,
  persistReducer,
  persistStore,
} from 'redux-persist'
import thunk from 'redux-thunk'
import { AllAction } from 'store/action'
import { rootReducer } from 'store/reducers'
import { ReduxState } from 'types/index'

import storage from './storage'

const persistConfig: PersistConfig<ReduxState> = {
  key: 'root',
  storage,
}

const middleware: Middleware[] = []

middleware.push(thunk)
if (isDev) {
  middleware.push(logger)
}

const persistedReducer = persistReducer(persistConfig, rootReducer)
const composeEnhancers = compose(applyMiddleware(...middleware))

export interface ConfigureStoreProps {
  store: Store<ReduxState, AllAction>
  persistor: Persistor
}

const ConfigureStore = (): ConfigureStoreProps => {
  const store: Store<ReduxState, AllAction> = createStore(
    persistedReducer,
    isDev ? composeWithDevTools(composeEnhancers) : composeEnhancers,
  )

  const persistor = persistStore(store)

  return {
    store,
    persistor,
  }
}

export default ConfigureStore
