// import Modal from 'common/Modal'
import FullscreenLoader from 'common/FullscreenLoader'
import employeesFilterTransform from 'common/pipes/employeeFilterPipe'
import { UserAccess } from 'constants/index'
import Routes from 'constants/routes'
import useDebounce from 'hooks/useDebounce'
import useUpdateEffect from 'hooks/useUpdateEffect'
import { useMemo } from 'react'
import { Item, Menu, useContextMenu } from 'react-contexify'
import { CSVLink } from 'react-csv'
import { useDispatch, useSelector } from 'react-redux'
import { Column, Row } from 'react-table'
import { getEmployees } from 'store/ives/employees/actions'
import { ReduxState, UserRole } from 'types'

import ContentView from './view'

const MENU_ID = 'menu-id'

// ? we need use type instead interface because of https://github.com/microsoft/TypeScript/issues/15300
export type TableData = {
  id: string
  firstName: string
  lastName: string
  fullName: string
  projectGroup: string
  address: string
  location: string
  email: string
  contactPhone: string
  contactMobile: string
  functionInCompany: string
  salary: string
}

const EmployeesTable = ({
  globalFilter,
}: {
  globalFilter?: string
}): JSX.Element => {
  const dispatch = useDispatch()
  const access = useSelector(({ user }: ReduxState) => user.user?.access)
  const { isLoading, employees } = useSelector(
    ({ ives }: ReduxState) => ives.employees,
  )
  const currentUser = useSelector(({ user }: ReduxState) => user.user)
  const hasNextPage = useMemo(
    () =>
      !!(
        employees &&
        (employees.pager.current_page || 0) + 1 < employees.pager.total_pages
      ),
    [employees?.pager],
  )
  const tableData = useMemo<TableData[]>(
    () =>
      employeesFilterTransform(currentUser, employees, hasNextPage)?.rows?.map(
        (row, i) => ({
          id: `${row.nid || i}`,
          firstName: row.first_name || '',
          lastName: row.last_name || '',
          fullName: row.full_name || '',
          address: row.address_address || '',
          projectGroup: row.project_group || '',
          location: row.location || '',
          email: row.corporate_email || '',
          contactPhone: row.corporate_phone || '',
          contactMobile: row.corporate_mobile || '',
          functionInCompany:
            typeof row.company_function === 'string'
              ? row.company_function
              : '',
          salary: row.salary || '',
        }),
      ) || [],
    [employees],
  )
  const columns = [
    {
      Header: 'Vorname',
      accessor: 'firstName',
    },
    {
      Header: 'Nachname',
      accessor: 'lastName',
    },
    {
      Header: 'Projektgruppe',
      accessor: 'projectGroup',
    },
    {
      Header: 'Standort',
      accessor: 'location',
    },
    {
      Header: 'Adresse',
      accessor: 'address',
    },
    {
      Header: 'E-Mail(Arbeit)',
      accessor: 'email',
    },
    {
      Header: 'Telefon(Arbeit)',
      accessor: 'contactPhone',
    },
    {
      Header: 'Handy(Arbeit)',
      accessor: 'contactMobile',
    },
    {
      Header: 'Funktion im Unternehmen',
      accessor: 'functionInCompany',
    },
  ]
  const salariedColumns = [
    ...columns,
    {
      Header: 'Gehalt',
      accessor: 'salary',
    },
  ]
  const columnsData = useMemo<Column<TableData>[]>(
    () =>
      currentUser?.roles.includes(UserRole.HumanResource)
        ? salariedColumns
        : columns,
    [currentUser],
  )
  const debouncedQuery = useDebounce(globalFilter, 500)

  useUpdateEffect(() => {
    dispatch(getEmployees({ query: globalFilter }))
  }, [debouncedQuery])

  const headers = columnsData.map((el) => ({
    label: el.Header,
    key: el.accessor,
  }))

  const onEditClick = (row?: Row<TableData>) => {
    if (access && access[UserAccess.editEmployeeEntities]) {
      if (row?.original) {
        window.open(
          `${Routes.employeeDetails}/user?id=${row?.original.id}`,
          '_self',
        )
      }
    }
  }

  const { show } = useContextMenu({
    id: MENU_ID,
  })

  const displayMenu = (row: Row<TableData>, e: React.MouseEvent) =>
    show(e, { props: { row } })

  const loadNextPage = (startIndex: number, stopIndex: number) => {
    if (currentUser) {
      dispatch(
        getEmployees({
          page: stopIndex / (employees?.pager.items_per_page || 50),
          query: globalFilter,
        }),
      )
    }

    return null
  }

  return (
    <>
      <FullscreenLoader
        isLoading={
          isLoading &&
          employees?.pager?.current_page + 1 < employees?.pager?.total_pages
        }
      />
      <ContentView
        columnsData={columnsData}
        tableData={tableData}
        onRowDbClick={onEditClick}
        hasNextPage={hasNextPage}
        loadNextPage={loadNextPage}
        isNextPageLoading={isLoading}
        displayMenu={displayMenu}
        // appendedColumn={appendedColumn}
      />

      <Menu id={MENU_ID}>
        <Item>
          <CSVLink
            filename="Mitarbeiter_Export.csv"
            data={tableData}
            headers={[...headers] as any}
          >
            {'Als excel Tabelle exportieren'}
          </CSVLink>
        </Item>
      </Menu>
    </>
  )
}

export default EmployeesTable
