import PrivateRoute from 'components/PrivateProps'
import Routes from 'constants/routes'
import ComingSoon from 'containers/ComingSoon'
import CompanyStructure from 'containers/CompanyStructure'
import DashboardNew from 'containers/Dashboard'
import Dashboard from 'containers/DashboardMy'
import Billing from 'containers/Ives/Billing'
import Employees from 'containers/Ives/Employees'
import EmployeeDetails from 'containers/Ives/Employees/EmployeeDetails'
import Evaluations from 'containers/Ives/Evaluations'
import Holidays from 'containers/Ives/Holidays'
import OfferDetails from 'containers/Ives/OfferDetails'
import Offers from 'containers/Ives/Offers'
import ProjectsInAcquisition from 'containers/Ives/ProjectsInAcquisition'
import Login from 'containers/Login'
import ProcessLandscape from 'containers/ProcessLandscape'
import ProjectGroupContainer from 'containers/ProjectGroup'
import Wiki from 'containers/Wiki'
import WikiLive from 'containers/WikiLive'
import { BrowserRouter, Redirect, Route, Switch } from 'react-router-dom'
import { AcquisitionState } from 'types'

const Navigation = () => (
  <BrowserRouter>
    <Switch>
      <Route path={Routes.login} exact component={Login} />
      <PrivateRoute path={Routes.dashboard} exact component={DashboardNew} />
      <PrivateRoute path={Routes.mySP} exact component={Dashboard} />
      <PrivateRoute
        path={Routes.companyStructure}
        exact
        component={CompanyStructure}
      />
      <Redirect from={Routes.newsBlog} to={`${Routes.newsBlog}${Routes.all}`} />
      <PrivateRoute
        path={Routes.processesLandscape}
        exact
        component={ProcessLandscape}
      />
      <PrivateRoute path={Routes.offers} exact component={Offers} />
      <PrivateRoute path={Routes.offerDetails} exact component={OfferDetails} />
      <PrivateRoute path={Routes.evaluations} exact component={Evaluations} />
      <PrivateRoute
        path={`${Routes.acquisition}/:acquisitionState`}
        component={ProjectsInAcquisition}
      />
      <Redirect
        from={Routes.acquisition}
        to={`${Routes.acquisition}/${'all' as AcquisitionState}`}
      />
      <PrivateRoute path={Routes.employees} exact component={Employees} />
      <PrivateRoute
        path={`${Routes.employeeDetails}/:employeeId`}
        exact
        component={EmployeeDetails}
      />
      <PrivateRoute path={Routes.holidays} exact component={Holidays} />
      <PrivateRoute path={Routes.billing} exact component={Billing} />
      <Redirect from={`${Routes.ives}/*`} to={Routes.comingSoon} />
      <PrivateRoute path={Routes.comingSoon} exact component={ComingSoon} />

      <PrivateRoute path={Routes.wiki} exact component={Wiki} />
      <PrivateRoute path={Routes.wikiLive} exact component={WikiLive} />
      <PrivateRoute
        path={`${Routes.projectGroups}/:groupId`}
        component={ProjectGroupContainer}
      />
      <Redirect to={Routes.dashboard} />
    </Switch>
  </BrowserRouter>
)

export default Navigation
