import cn from 'classnames'
import Delete from 'common/Delete'
import Layout from 'common/Layout'
import Modal from 'common/Modal'
import LinkItem from 'components/LinkItem'
import { UserAccess } from 'constants/index'
import Form, {
  ArticleTab,
  CompanyInfoFormValues,
  EditArticle,
} from 'containers/Dashboard/ArticleForm'
import HolidayInfo, {
  HolidayInfoClickMethod,
} from 'containers/Dashboard/HolidayInfo'
import HolidayForm from 'containers/Ives/Holidays/HolidayForm/ApplicationForm'
import useToggle from 'hooks/useToggle'
import { useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import api from 'services/api'
import notify from 'services/notify'
import {
  createArticle,
  deleteBlogItem,
  getBlog,
  getCategories,
  updateArticle,
} from 'store/blog/actions'
import {
  createCompanyInformation,
  getCompanyInformation,
  getDashboardMenu,
} from 'store/dashboard/actions'
import { getEmployeeRecord } from 'store/ives/employees/actions'
import { getHolidayEmployeeView } from 'store/ives/holidays/actions'
import {
  BlogCategory,
  BlogItem,
  Employee,
  EmployeeHolidaysInner,
  ReduxState,
  TimeTableItemData,
} from 'types'

import CompanyInfo from './CompanyInfo'
import GridLayout from './GridLayout'
import styles from './styles.module.scss'

const Dashboard = () => {
  const newModalToggle = useToggle()
  const companyInfoModalToggle = useToggle()
  const dashboardItemToggle = useToggle()
  const holidaysItemToggle = useToggle()
  const holidaysItemEditToggle = useToggle()
  const [currentPopup, setCurrentPopup] = useState<string | null>(null)
  const [currentPost, setCurrentPost] = useState<BlogItem | null>(null)
  const { categories } = useSelector((state: ReduxState) => state.blog)
  const [isLoading, setIsLoading] = useState(false)

  const { user } = useSelector((state: ReduxState) => state.user)
  const { companyInfo, menu } = useSelector(
    (state: ReduxState) => state.dashboard,
  )
  const [selectedItem, setSelectedItem] = useState<BlogCategory>(menu?.[0])
  const [selectedSubItem, setSelectedSubItem] = useState<BlogCategory | null>(
    null,
  )
  const [page, setPage] = useState(0)
  const [employeeRecord, setEmployeeRecord] = useState<Employee>()
  const [
    currentEmployee,
    setCurrentEmployee,
  ] = useState<TimeTableItemData | null>(null)
  const [
    nextThoughtHoliday,
    setNextThoughtHoliday,
  ] = useState<EmployeeHolidaysInner>({} as EmployeeHolidaysInner)
  const dispatch = useDispatch()
  const { holidays } = useSelector((state: ReduxState) => state.ives)
  const scrollableRef = useRef<HTMLDivElement>(null)
  const readEverything = companyInfo.every((item) => item.read !== '0')

  const checkIfUserHasCloseHolidays = () => {
    holidays.employees?.forEach((employee) => {
      if (employee.id === user?.employee) {
        employee?.holidays
          ?.sort(
            (a, b) =>
              new Date(a.from_date).getTime() - new Date(b.from_date).getTime(),
          )
          .forEach((holiday) => {
            const difference =
              (new Date(holiday.from_date).getTime() -
                new Date(new Date().setHours(0, 0, 0, 0)).getTime()) /
              (1000 * 3600 * 24)
            if (holiday.state === 'planned') {
              if (difference > 0 && difference <= 7) {
                setNextThoughtHoliday(holiday)
                setCurrentEmployee(employee)
                holidaysItemToggle.on()
                return true
              }
            }
            return false
          })
      }
    })
  }

  useEffect(() => {
    dispatch(getCategories())
    dispatch(getCompanyInformation())
    dispatch(getDashboardMenu())
    dispatch(getHolidayEmployeeView(user?.project_group.id))
    dispatch(
      getEmployeeRecord(user?.employee as number, (record: Employee) => {
        setEmployeeRecord(record)
      }),
    )
    checkIfUserHasCloseHolidays()
  }, [])

  useEffect(() => {
    companyInfo.some((item) => !item.read || item.read === '0') &&
      companyInfoModalToggle.on()
    readEverything ? checkIfUserHasCloseHolidays() : holidaysItemToggle.off()
  }, [companyInfo])

  const onArticleSubmit = (values: EditArticle) => {
    switch (currentPopup) {
      case 'update':
        values.images.unshift(values.mainImage)
        dispatch(
          updateArticle(values, () =>
            notify('Artikel wurde erfolgreich erstellt', 'success'),
          ),
        )
        dashboardItemToggle.off()
        dispatch(getBlog(page, selectedSubItem?.tid))
        return newModalToggle.off()
      default:
        dispatch(
          createArticle(values, () =>
            notify('Artikel wurde erfolgreich erstellt', 'success'),
          ),
        )
        return newModalToggle.off()
    }
  }

  const onDelete = () => {
    currentPost?.nid &&
      dispatch(
        deleteBlogItem(
          currentPost?.nid,
          () => dispatch(getBlog(page, selectedSubItem?.tid)),
          () => notify('Dieser Artikel wurde gelöscht', 'success'),
        ),
      )
    dashboardItemToggle.off()
    return newModalToggle.off()
  }
  const onCompanyInfoSubmit = (values: CompanyInfoFormValues) => {
    dispatch(createCompanyInformation(values))
    newModalToggle.off()
  }

  const createNewsPopup = () => {
    switch (currentPopup) {
      case 'update':
        return (
          <Modal
            maxWidth={750}
            isOpen={newModalToggle.value}
            onRequestClose={newModalToggle.off}
          >
            <Form
              onCancel={newModalToggle.off}
              onArticleSubmit={onArticleSubmit}
              onCompanyInfoSubmit={onCompanyInfoSubmit}
              categories={categories?.rows}
              tabs={[ArticleTab.ARTICLE]}
              post={currentPost}
            />
          </Modal>
        )
      case 'delete':
        return (
          <Modal
            maxWidth={400}
            isOpen={newModalToggle.value}
            onRequestClose={newModalToggle.off}
          >
            <Delete
              title=""
              onCancel={newModalToggle.off}
              onSubmit={onDelete}
            />
          </Modal>
        )
      default:
        return (
          <Modal
            maxWidth={750}
            isOpen={newModalToggle.value}
            onRequestClose={newModalToggle.off}
          >
            <Form
              onCancel={newModalToggle.off}
              onArticleSubmit={onArticleSubmit}
              onCompanyInfoSubmit={onCompanyInfoSubmit}
              categories={categories?.rows}
              tabs={
                user?.access?.[
                  UserAccess.createCompanyInfoContent &&
                    UserAccess.createBlogPostContent
                ]
                  ? Object.values(ArticleTab)
                  : [ArticleTab.ARTICLE]
              }
            />
          </Modal>
        )
    }
  }
  const handleHolidayInfoClick = (arg: HolidayInfoClickMethod) => {
    setIsLoading(true)
    switch (arg) {
      case 'delete':
        api
          .delete(`employees/holidays/${nextThoughtHoliday?.id}?_format=json`)
          .then(() => {
            notify('Dieser Urlaub wurde erfolgreich gelöscht')
            holidaysItemToggle.off()
          })
          .finally(() => setIsLoading(false))
        return
      case 'edit':
        holidaysItemEditToggle.on()
        api.delete(`employees/holidays/${nextThoughtHoliday?.id}?_format=json`)
        holidaysItemToggle.off()
        setIsLoading(false)
        return
      case 'mutate':
        if (nextThoughtHoliday.substitutes.length > 0) {
          api
            .patch(
              `employees/holidays/${nextThoughtHoliday?.id}?_format=json`,
              {
                ...nextThoughtHoliday,
                state: 'requested',
              },
            )
            .then(() => {
              notify('Dieser Urlaub wurde erfolgreich beantragt')
              holidaysItemToggle.off()
            })
            .finally(() => setIsLoading(false))
        } else {
          notify(
            'Dieser Urlaub hat keine Vertretung, um den Urlaubsantrag zu starten, fügen Sie über “Angedachter Urlaub verschieben“ eine Vertretung hinzu.',
            'warning',
            { autoClose: 10000 },
          )
          setIsLoading(false)
        }
        return
      default:
        holidaysItemToggle.off()
    }
  }
  const onEditEmployeeSend = () => {
    holidaysItemEditToggle.off()
    dispatch(getHolidayEmployeeView(user?.project_group.id))
    notify('Urlaubsantrag wurde versandt', 'success')
  }
  const checkIfConditionIsComplete = () => {
    checkIfUserHasCloseHolidays()
    if (!readEverything) {
      holidaysItemToggle.on()
    }
    holidaysItemEditToggle.off()
  }
  return (
    <Layout scrollableRef={scrollableRef} headerProps={{ withoutSearch: true }}>
      <h1 className={styles.title}>{'S&P Leipzig Plattform'}</h1>
      <div className={cn(styles.navigation, styles.mainNavigation)}>
        <LinkItem
          onClick={() => {
            setCurrentPopup(null)
            newModalToggle.on()
          }}
        >
          {'Artikel erstellen'}
        </LinkItem>
      </div>
      <GridLayout
        selectedSubItem={selectedSubItem}
        setSelectedSubItem={setSelectedSubItem}
        setSelectedItem={setSelectedItem}
        dashboardItemToggle={dashboardItemToggle}
        setPage={setPage}
        menu={menu}
        selectedItem={selectedItem}
        page={page}
        scrollableRef={scrollableRef}
        openCompanyInfo={companyInfoModalToggle.on}
        onEditPost={(post: BlogItem) => {
          setCurrentPopup('update')
          newModalToggle.on()
          setCurrentPost(post)
        }}
        onDeletePost={
          user?.access[UserAccess.editAnyBlogPostContent]
            ? (post: BlogItem) => {
                setCurrentPopup('delete')
                newModalToggle.on()
                setCurrentPost(post)
              }
            : null
        }
      />
      {createNewsPopup()}
      <Modal
        isOpen={holidaysItemToggle.value}
        onRequestClose={holidaysItemToggle.off}
        className={styles.holidaysInfoModal}
        maxWidth={630}
      >
        <HolidayInfo
          holiday={nextThoughtHoliday}
          onButtonClick={handleHolidayInfoClick}
          isLoading={isLoading}
        />
      </Modal>
      <Modal
        isOpen={holidaysItemEditToggle.value}
        onRequestClose={checkIfConditionIsComplete}
        className={styles.holidaysInfoModal}
        maxWidth={430}
      >
        <HolidayForm
          title="Urlaubsantrag Verschieben"
          onSubmit={onEditEmployeeSend}
          onCancel={checkIfConditionIsComplete}
          initialData={employeeRecord}
          holidaysData={nextThoughtHoliday}
          substitutes={nextThoughtHoliday?.substitutes}
          defaultEmployeeOption={{
            value: currentEmployee?.group || '',
            label: currentEmployee?.title || '',
            data: currentEmployee,
          }}
        />
      </Modal>
      <Modal
        isOpen={companyInfoModalToggle.value}
        onRequestClose={companyInfoModalToggle.off}
        maxWidth={927}
        className={styles.companyInfoModal}
      >
        <CompanyInfo
          items={companyInfo}
          onOkClick={companyInfoModalToggle.off}
        />
      </Modal>
    </Layout>
  )
}

export default Dashboard
