import Button from 'components/Button'
import { useState } from 'react'
import Lightbox from 'react-image-lightbox'

interface ImageWithSrc {
  element: JSX.Element
  src: string
}

interface Props {
  images: ImageWithSrc[]
}

const ImageContainer: React.FC<Props> = ({ images }) => {
  const [openedImageIndex, setOpenedImageIndex] = useState<number | null>(null)

  return (
    <>
      {images.map((image, index) => (
        // ToDo: find out why react is whining about not unique or missing key prop
        <Button
          key={`${image.src}_${index}`}
          onClick={() => setOpenedImageIndex(index)}
        >
          {image.element}
        </Button>
      ))}
      {openedImageIndex !== null && (
        <Lightbox
          mainSrc={images[openedImageIndex]?.src}
          nextSrc={images[openedImageIndex + 1]?.src}
          prevSrc={images[openedImageIndex - 1]?.src}
          onMoveNextRequest={() => setOpenedImageIndex(openedImageIndex + 1)}
          onMovePrevRequest={() => setOpenedImageIndex(openedImageIndex - 1)}
          onCloseRequest={() => setOpenedImageIndex(null)}
          imagePadding={125}
        />
      )}
    </>
  )
}

export default ImageContainer
