import cn from 'classnames'
import ComponentToPrint from 'common/PrintTemplates'
import Button from 'components/Button'
import { useFormik } from 'formik'
import { useState } from 'react'
import api from 'services/api'
import notify from 'services/notify'
import { Employee, EmployeeHolidays, ProjectGroup } from 'types'

import { FormSchema } from '.'
import BaseDataView from './Slides/baseData'
// import SalaryDataView from './Slides/salaryData'
import TimeManagement from './Slides/timeManagement'
import styles from './styles.module.scss'

interface EmployeeData {
  currentRecord: Employee
  employeeTitle: Employee | undefined
  groupMember: Record<
    | 'verwaltung'
    | 'projektgruppen'
    | 'arbeitsgruppen'
    | 'kompetenzbereiche'
    | 'standorte',
    ProjectGroup[]
  >
  holidayData: EmployeeHolidays[] | undefined
}

const ContentView = ({
  currentRecord,
  employeeTitle,
  groupMember,
  holidayData,
}: EmployeeData) => {
  const IDquery = new URLSearchParams(window.location.search)
  const [allAccordions, setAllAccordions] = useState(false)

  const ID = IDquery.get('id')
  const getCurrentData = (values: any) => {
    const object: any = {}
    if (values.notes_main) {
      object.notes_main = [{ attachments: [], note: values.notes_main }]
    }
    if (values.notes_contract) {
      object.notes_contract = [{ attachments: [], note: values.notes_contract }]
    }
    if (values.notes_absence) {
      object.notes_absence = [{ attachments: [], note: values.notes_absence }]
    }
    return object
  }

  const Notes = useFormik({
    initialValues: {
      notes_main: '',
      notes_contract: '',
      notes_absence: '',
    },
    validationSchema: FormSchema,
    onSubmit: async (values) => {
      try {
        await api
          .patch(`/employees/${ID}?_format=json`, getCurrentData(values))
          .then(() => {
            Notes.resetForm()
          })
      } catch (error) {
        notify(error)
      }
    },
  })
  const handleClick = () => {
    setAllAccordions(!allAccordions)
  }

  return (
    <div className={styles.content}>
      <div className={styles.headerBtnContainer}>
        {currentRecord && (
          <ComponentToPrint
            currentRecord={currentRecord}
            employeeTitle={employeeTitle}
          />
        )}
        <Button
          className={cn(styles.historyBtn, styles.base, styles.headerBtn)}
          variant="fulfilled"
          onClick={handleClick}
        >
          {!allAccordions ? 'Alle Felder anzeigen' : 'Alle Felder ausblenden'}
        </Button>
      </div>
      <div className={styles.detailContent}>
        <BaseDataView
          currentRecord={currentRecord}
          employeeTitle={employeeTitle}
          groupMember={groupMember}
          allAccordions={allAccordions}
        />
        {/* <SalaryDataView
          currentRecord={currentRecord}
          allAccordions={allAccordions}
        /> */}
        <TimeManagement
          holidayData={holidayData}
          allAccordions={allAccordions}
          currentRecord={currentRecord}
        />
      </div>
    </div>
  )
}

export default ContentView
