import { AxiosResponse } from 'axios'
import { apiUrl, isDev } from 'constants/index'
import api from 'services/api'
import notify from 'services/notify'
import { action } from 'store/action'
import { AppThunkAsync, Employee, Employees, Error } from 'types/index'

import ActionType from './types'

export const updateEmployee = (
  data: Employee | { nid: string | number },
  onSuccess?: (employee: Employee) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.UPDATE_EMPLOYEES_RECORD_BEGIN))
  try {
    const employee: AxiosResponse<Employee[]> = await api.patch(
      `employees/${data.nid}?_format=json`,
      data,
    )
    dispatch(action(ActionType.UPDATE_EMPLOYEES_RECORD_SUCCESS, employee.data))
    onSuccess?.(employee.data[0])
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.UPDATE_EMPLOYEES_RECORD_ERROR, error))
    onError?.(error)
  }
}

export const getEmployeeRecord = (
  id: string | number,
  onSuccess?: (employee: Employee) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_EMPLOYEES_RECORD_BEGIN))
  try {
    const employee: AxiosResponse<Employee[]> = await api.get(
      `employees/${id}?_format=json`,
    )
    // const bonusesData: AxiosResponse<Bonuses[]> = await api.get(
    //   `employees/${id}/bonuses?_format=json`,
    // )
    dispatch(action(ActionType.GET_EMPLOYEES_RECORD_SUCCESS, employee.data))
    onSuccess?.({ ...employee.data[0] })
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_EMPLOYEES_RECORD_ERROR, error))
    onError?.(error)
  }
}

export const getEmployees = (
  { page = 0, query }: { page?: number; query?: string },
  onSuccess?: (employees: Employees) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_EMPLOYEES_BEGIN))
  const queryParam = query ? `/${query}` : ''
  try {
    const employees: AxiosResponse<Employees> = await api.get(
      `pages/employees${queryParam}?_format=json&page=${page}`,
    )
    if (page) {
      dispatch(action(ActionType.EMPLOYEES_PAGE_LOADED, employees.data))
    } else {
      dispatch(action(ActionType.GET_EMPLOYEES_SUCCESS, employees.data))
    }
    onSuccess?.(employees.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_EMPLOYEES_ERROR, error))
    onError?.(error)
  }
}

export const editComment = (
  ID: string | number,
  values: any,
  onSuccess?: () => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.EDIT_COMMENT_BEGIN))
  try {
    const edit = await api.patch(`/employees/${ID}?_format=json`, values)
    dispatch(action(ActionType.EDIT_COMMENT_SUCCESS, edit.data))
    onSuccess?.()
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.EDIT_COMMENT_ERROR, error))
    onError?.(error)
  }
}
export const deleteNote = (ID: string | number): AppThunkAsync => async (
  dispatch,
) => {
  dispatch(action(ActionType.EDIT_COMMENT_BEGIN))
  try {
    api.defaults.baseURL = isDev ? 'http://localhost:8888/' : '/backend/'
    await api.delete(`/node/${ID}?_format=json`).then((deleteNote) => {
      api.defaults.baseURL = apiUrl
      dispatch(action(ActionType.DELETE_NOTE_SUCCESS, deleteNote.data))
    })
  } catch (error) {
    notify(error)
  }
}
