import { Checkbox, Fade, FormControl, Menu, MenuItem } from '@mui/material'
import cn from 'classnames'
import { clickedMessagesTypes } from 'common/RightMenu'
import React from 'react'
import { CheckBoxCallback, Message } from 'types'

import styles from './styles.module.scss'

interface Props {
  item: Message
  checkBoxCallback?: CheckBoxCallback
  onRightClickMessage?: (messageId?: number) => void
  onClickMessage?: (item?: Message) => void
}

const MessageCheckbox: React.FC<Props> = ({
  item,
  checkBoxCallback = () => null,
  onRightClickMessage = () => null,
  onClickMessage = () => null,
}) => {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null)
  const { mid, body, read, data } = item

  const open = Boolean(anchorEl)
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  return (
    <div className={styles.messageWrap}>
      <FormControl className={styles.checkboxWrap}>
        <div className={styles.checkbox}>
          <Checkbox
            checked={read !== 0}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
              checkBoxCallback({
                messageId: mid,
                read: Number(event.target.checked),
              })
            }
          />
        </div>

        <button
          className={cn(
            styles.label,
            read === 1 ? styles.crossOut : '',
            clickedMessagesTypes.includes(data.type) && styles.underline,
          )}
          onContextMenu={(event) => {
            event.preventDefault()
            handleClick(event)
          }}
          type="button"
          onClick={() =>
            read === 0 &&
            clickedMessagesTypes.includes(data.type) &&
            onClickMessage(item)
          }
          id="fade-button"
          aria-controls={open ? 'fade-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}
        >
          {body}
        </button>
        <Menu
          id="fade-menu"
          MenuListProps={{
            'aria-labelledby': 'fade-button',
          }}
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          TransitionComponent={Fade}
        >
          <MenuItem
            onClick={() => {
              onRightClickMessage(mid)
              handleClose()
            }}
          >
            {'Nachricht löschen'}
          </MenuItem>
        </Menu>
      </FormControl>
    </div>
  )
}

export default MessageCheckbox
