import { ReactComponent as ComingSoonImg } from 'assets/images/comingSoon.svg'
import Layout from 'common/Layout'

import styles from './styles.module.scss'

const ComingSoon = () => (
  <Layout>
    <div className={styles.container}>
      <ComingSoonImg />
      <h1 className={styles.title}>{'Hier entsteht was Neues'}</h1>
    </div>
  </Layout>
)

export default ComingSoon
