import { Button } from '@mui/material'
import Loader from 'components/Loader'
import { useCallback, useRef } from 'react'
import { CheckBoxCallback, Message } from 'types'

import MessageCheckbox from './MessageCheckbox'
import styles from './styles.module.scss'

interface Props {
  messages: Message[]
  isLoad: boolean
  onButton: (messagesCount?: number) => void
  fetchItems: () => void
  checkBoxCallback: CheckBoxCallback
  onRightClickMessage?: (messageId?: number) => void
  onClickMessage?: (item?: Message) => void
}

const TaskMenu: React.FC<Props> = ({
  messages,
  isLoad,
  onButton,
  fetchItems,
  checkBoxCallback,
  onRightClickMessage,
  onClickMessage,
}) => {
  const listInnerRef = useRef(null)

  const onScrollBottom = () => {
    if (listInnerRef.current) {
      const { scrollTop, scrollHeight, clientHeight } = listInnerRef.current
      if (
        Math.ceil(scrollTop) + Math.ceil(clientHeight) === scrollHeight ||
        Math.ceil(scrollTop) + Math.ceil(clientHeight) === scrollHeight + 1
      ) {
        fetchItems()
      }
    }
  }

  const onClick = useCallback(() => {
    if (messages && messages.length <= 10) {
      return onButton()
    }
    return null
  }, [messages])

  return (
    <div className={styles.taskListWrap}>
      <div className={styles.titleWrap}>
        <h3>{'Benachrichtigungen'}</h3>
        <Button onClick={() => onClick()} variant="text">
          {'Alle'}
        </Button>
      </div>
      <ul
        className={styles.taskList}
        onScroll={() => onScrollBottom()}
        ref={listInnerRef}
      >
        {isLoad ? (
          <>
            {messages && messages.length > 0 ? (
              messages.map((item, index) => (
                <MessageCheckbox
                  item={item}
                  key={`${item.body.slice(5)}__${index}__${item.mid}`}
                  checkBoxCallback={checkBoxCallback}
                  onRightClickMessage={onRightClickMessage}
                  onClickMessage={onClickMessage}
                />
              ))
            ) : (
              <span>{'Keine Benachrichtigungen'}</span>
            )}
          </>
        ) : (
          <Loader className={styles.loader} />
        )}
      </ul>
    </div>
  )
}

export default TaskMenu
