import Comments from 'common/Comments'
import Customers from 'common/Customers'
import Layout from 'common/Layout'
import Modal from 'common/Modal'
import dateTransform from 'common/pipes/dateTime.pipe'
import CommentCell from 'components/tableComponents/CommentCell'
import { DEFAULT_TABLE_WIDTH } from 'constants/index'
import Routes from 'constants/routes'
import { fromUnixTime, getUnixTime, parse } from 'date-fns'
import useDebounce from 'hooks/useDebounce'
import useToggle from 'hooks/useToggle'
import useWindowSize from 'hooks/useWindowSize'
import { createRef, useEffect, useMemo, useState } from 'react'
import {
  Item,
  ItemParams,
  Menu,
  Separator,
  useContextMenu,
} from 'react-contexify'
import { CSVLink } from 'react-csv'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory, useParams } from 'react-router-dom'
import { Column, ColumnInstance, Row } from 'react-table'
import { useReactToPrint } from 'react-to-print'
import { formatDate, zonedDate } from 'services/date'
import notify from 'services/notify'
import { percent } from 'services/utils'
import {
  createAcquisition,
  getAcquisitionComments,
  getAcquisitionRecord,
  getAcquisitions,
  updateAcquisition,
} from 'store/ives/acquisitions/actions'
import { Acquisition, Acquisitions, AcquisitionState, ReduxState } from 'types'

import AcquisitionForm, {
  FormValues as AcquisitionFormValues,
  InitialData,
} from './AcquisitionForm'
import styles from './styles.module.scss'
import ContentView from './view'

const MENU_ID = 'menu-id'

export type TableData = {
  id: string
  state: AcquisitionState
  title: string
  created: string
  responsible: string
  customer: string
  customerResponsible: string
  commentCount: string
}

interface ParamTypes {
  acquisitionState: AcquisitionState
}

const Billing = ({ tableOnly }: { tableOnly?: boolean }) => {
  const dispatch = useDispatch()
  const { isLoading } = useSelector(({ ives }: ReduxState) => ives.acquisitions)
  const { acquisitionState } = useParams<ParamTypes>()
  const customers = useToggle()
  const newAcquisition = useToggle()
  const editAcquisition = useToggle()
  const commentsToggle = useToggle()
  const [acquisitions, setAcquisitions] = useState<Acquisitions>()
  const [selectedRow, setSelectedRow] = useState<Row<TableData>>()
  const [acquisitionRecord, setAcquisitionRecord] = useState<Acquisition>()
  const [globalFilter, setGlobalFilter] = useState<string>()
  const debouncedQuery = useDebounce(globalFilter, 500)
  const { width = DEFAULT_TABLE_WIDTH, height = 300 } = useWindowSize()
  const history = useHistory()
  const componentRef = createRef<HTMLDivElement>()
  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
  })

  useEffect(() => {
    dispatch(
      getAcquisitions(
        {
          state: acquisitionState,
        },
        setAcquisitions,
      ),
    )
  }, [])

  useEffect(() => {
    editAcquisition.value &&
      selectedRow?.original &&
      dispatch(
        getAcquisitionRecord(selectedRow.original.id, (records) =>
          setAcquisitionRecord(records[0]),
        ),
      )
  }, [editAcquisition.value])

  useEffect(() => {
    dispatch(getAcquisitions({ state: acquisitionState }, setAcquisitions))
  }, [acquisitionState])

  useEffect(() => {
    dispatch(
      getAcquisitions(
        { state: acquisitionState, query: globalFilter },
        setAcquisitions,
      ),
    )
  }, [debouncedQuery])

  const hasNextPage = useMemo(
    () =>
      !!(
        acquisitions &&
        (acquisitions.pager.current_page || 0) + 1 <
          acquisitions.pager.total_pages
      ),
    [acquisitions?.pager],
  )

  const loadNextPage = (startIndex: number, stopIndex: number) => {
    dispatch(
      getAcquisitions({
        page: stopIndex / (acquisitions?.pager.items_per_page || 50),
        state: acquisitionState,
      }),
    )

    return null
  }

  const tableData = useMemo<TableData[]>(
    () =>
      acquisitions?.rows.map(
        ({
          nid,
          title,
          created,
          customer,
          customer_responsible: customerResponsible,
          responsible,
          state,
          comment_count: commentCount,
        }) => ({
          id: `${nid}`,
          state,
          title,
          created: formatDate(fromUnixTime(created)),
          customer: typeof customer === 'string' ? customer : '',
          customerResponsible: Array.isArray(customerResponsible)
            ? customerResponsible?.join(', ')
            : customerResponsible || '',
          responsible: Array.isArray(responsible)
            ? responsible?.join(', ')
            : responsible || '',
          commentCount,
        }),
      ) || [],
    [acquisitions],
  )

  const columnsData = useMemo<Column<TableData>[]>(
    () =>
      tableOnly
        ? [
            {
              Header: 'Bezeichnung',
              accessor: 'title', // accessor is the "key" in the data
            },
            {
              Header: 'Erstellungsdatum',
              accessor: 'created',
              sortType: (a, b) =>
                dateTransform(a.values.created, b.values.created),
            },
            {
              Header: 'Verantwortliche',
              accessor: 'responsible',
            },
            {
              Header: 'Kunde',
              accessor: 'customer',
            },
          ]
        : [
            {
              Header: 'Bezeichnung',
              accessor: 'title', // accessor is the "key" in the data
            },
            {
              Header: 'Erstellungsdatum',
              accessor: 'created',
              isSortedDesc: true,
              sortType: (a, b) =>
                parse(b.values.from, 'dd.MM.yyyy', new Date()).valueOf() -
                parse(a.values.from, 'dd.MM.yyyy', new Date()).valueOf(),
            },
            {
              Header: 'Verantwortliche',
              accessor: 'responsible',
            },
            {
              Header: 'Kunde',
              accessor: 'customer',
            },
            {
              Header: 'Verantwortliche beim Kunde',
              accessor: 'customerResponsible',
            },
          ],
    [],
  )

  const headers = columnsData.map((el) => ({
    label: el.Header,
    key: el.accessor,
  }))

  const onCommentClick = (row: Row<TableData>) => {
    setSelectedRow(row)
    commentsToggle.on()
  }

  const appendedColumn = useMemo<Partial<ColumnInstance<TableData>>>(
    () => ({
      id: 'comments',
      Cell: CommentCell(onCommentClick),
      width: 50,
      canSort: false,
    }),
    [],
  )

  const toggleModal = (modalName: string) => {
    switch (modalName) {
      case Routes.new:
        newAcquisition.toggle()
        break
      case Routes.customers:
        customers.toggle()
        break
      case Routes.edit:
        editAcquisition.toggle()
        break
      default:
        break
    }
  }

  const onEditSubmit = (values: AcquisitionFormValues) => {
    dispatch(
      updateAcquisition(
        {
          created: getUnixTime(zonedDate(values.created)),
          customer: { id: values.customer },
          customer_responsible: [values.customerResponsible],
          nid: selectedRow?.original.id || '',
          responsible: values.responsible.map((item) => ({
            id: item.value,
            title: item.label,
          })),
          state: selectedRow?.original.state || 'new',
          title: values.title,
          comment: values.comment,
        },
        () =>
          notify('Akquisitionsantrag wurde erfolgreich bearbeitet', 'success'),
      ),
    )
    editAcquisition.off()
  }

  const onNewSubmit = (values: AcquisitionFormValues) => {
    dispatch(
      createAcquisition(
        {
          created: getUnixTime(zonedDate(values.created)),
          customer: { id: values.customer },
          customer_responsible: [values.customerResponsible],
          responsible: values.responsible.map((item) => ({
            id: item.value,
            title: item.label,
          })),
          state: 'new',
          title: values.title,
          comment: values.comment,
        },
        () => {
          dispatch(
            getAcquisitions({ state: acquisitionState }, setAcquisitions),
          )
          notify('Akquise Antrag wurde erfolgreich erstellt', 'success')
        },
      ),
    )
    newAcquisition.off()
  }

  const mapAcquisitionToForm = (): InitialData => ({
    title: acquisitionRecord?.title || '',
    created: acquisitionRecord?.created
      ? formatDate(fromUnixTime(acquisitionRecord?.created))
      : '',
    customer: acquisitionRecord?.customer
      ? {
          id: acquisitionRecord.customer.id.toString(),
          title: acquisitionRecord.customer.title || '',
        }
      : undefined,
    customerResponsible:
      acquisitionRecord?.customer_responsible.join(', ') || '',
    // customerResponsible: Array.isArray(acquisitionRecord?.customer_responsible)
    //   ? acquisitionRecord?.customer_responsible?.join(', ')
    //   : acquisitionRecord?.customer_responsible || '',
    responsible:
      acquisitionRecord?.responsible.map((item) => ({
        label: item.title,
        value: item.id,
      })) || [],
  })

  const { show } = useContextMenu({
    id: MENU_ID,
  })

  const displayMenu = (row: Row<TableData>, e: React.MouseEvent) =>
    show(e, { props: { row } })

  const onEditClick = (row?: Row<TableData>) => {
    setSelectedRow(row)
    toggleModal(Routes.edit)
  }

  const onEditClose = () => {
    editAcquisition.off()
    setAcquisitionRecord(undefined)
  }

  const onContextEditClick = ({ props }: ItemParams<{ row: Row<TableData> }>) =>
    onEditClick(props?.row)

  const onContextArchive = ({ props }: ItemParams<{ row: Row<TableData> }>) => {
    props?.row &&
      dispatch(
        updateAcquisition(
          { nid: props.row.original.id, state: 'archived' },
          () => notify('Akquisition wurde erfolgreich archiviert', 'success'),
        ),
      )
  }

  const onContextHold = ({ props }: ItemParams<{ row: Row<TableData> }>) => {
    props?.row &&
      dispatch(
        updateAcquisition({ nid: props.row.original.id, state: 'hold' }, () =>
          notify('Akquisition wurde erfolgreich auf Eis gelegt', 'success'),
        ),
      )
  }

  const onMutateToOfferClick = () => history.push(Routes.offers)

  const content = (
    <>
      <ContentView
        ref={componentRef}
        selectedCategory={acquisitionState}
        toggleModal={toggleModal}
        columnsData={columnsData}
        tableData={tableData}
        appendedColumn={appendedColumn}
        onRowDbClick={onEditClick}
        globalFilter={globalFilter}
        setGlobalFilter={setGlobalFilter}
        displayMenu={displayMenu}
        hasNextPage={hasNextPage}
        isNextPageLoading={isLoading}
        loadNextPage={loadNextPage}
        tableOnly={tableOnly}
      />
      <Modal
        isOpen={newAcquisition.value}
        onRequestClose={newAcquisition.off}
        className={styles.modal}
      >
        <AcquisitionForm onCancel={newAcquisition.off} onSubmit={onNewSubmit} />
      </Modal>
      <Modal
        isOpen={!!(editAcquisition.value && acquisitionRecord)}
        onRequestClose={onEditClose}
        className={styles.modal}
      >
        <AcquisitionForm
          onCancel={onEditClose}
          onSubmit={onEditSubmit}
          initialData={mapAcquisitionToForm()}
        />
      </Modal>
      <Modal
        isOpen={customers.value}
        onRequestClose={customers.off}
        maxWidth={percent(46.2, width)}
        className={styles.modal}
      >
        <Customers
          tableWidth={percent(45.8, width)}
          tableHeight={percent(50, height)}
          onCancel={customers.off}
          onSubmit={customers.off}
        />
      </Modal>
      <Modal
        isOpen={commentsToggle.value}
        onRequestClose={commentsToggle.off}
        maxWidth={percent(46, width)}
        className={styles.modal}
        withCloseButton
      >
        {selectedRow?.original && (
          <Comments
            acquisitionName={selectedRow?.original.title}
            acquisitionId={selectedRow?.original.id}
            loadComments={getAcquisitionComments}
          />
        )}
      </Modal>
      <Menu id={MENU_ID}>
        <Item onClick={onContextEditClick}>{'Projekt Bearbeiten'}</Item>
        <Item onClick={onContextArchive}>{'Projekt Archivieren'}</Item>
        <Item onClick={onContextHold}>
          {'Projekt auf Wiedervorlage setzen'}
        </Item>
        <Item onClick={onMutateToOfferClick}>{'A.A zu Angebot Mutieren'}</Item>
        <Separator />
        <Item>
          <CSVLink
            filename={`Akquise_${acquisitionState}.csv`}
            data={tableData}
            headers={[...headers] as any}
          >
            {'Als excel Tabelle exportieren'}
          </CSVLink>
        </Item>
        <Separator />
        <Item onClick={handlePrint}>{'Ausdrucken'}</Item>
      </Menu>
    </>
  )

  return tableOnly ? (
    content
  ) : (
    <Layout headerProps={{ withoutSearch: true }}>{content}</Layout>
  )
}

export default Billing
