import { Accordion, AccordionDetails, AccordionSummary } from '@mui/material'
import { ReactComponent as Arrow } from 'assets/icons/arrowDown.svg'
import { ReactComponent as Remove } from 'assets/icons/trashbin.svg'
import classNames from 'classnames'
import Modal from 'common/Modal'
import Button from 'components/Button'
import useToggle from 'hooks/useToggle'
import React, { useEffect } from 'react'

import styles from './styles.module.scss'

interface Props {
  name: string
  disable?: boolean
  isGroup?: boolean
  initialEmptyAllowed?: boolean
  keepLastEntry?: boolean
  stopAddingMoreFields?: boolean
  withoutPlusButton?: boolean
  formik?: any // TODO: FIX THIS!
  label?: string
  renderField: (value: any, index: number) => void
}

const FieldArrayComponent = ({
  name,
  formik,
  disable,
  isGroup,
  withoutPlusButton,
  label,
  initialEmptyAllowed,
  keepLastEntry,
  stopAddingMoreFields,
  renderField,
}: Props) => {
  useEffect(() => {
    if (!initialEmptyAllowed && formik.values[name]?.length === 0) {
      formik.setFieldValue(`${name}`, [''])
    }
  }, [])

  const handleAddEntry = () => {
    if (!stopAddingMoreFields) {
      const values = formik.values[name]?.length ? [...formik.values[name]] : []
      // const emptyObject = { ...formik.values[name][0] } // Copy the structure of the first object
      const originalObject = formik.values[name][0]
      const emptyObject: any = Object.keys(originalObject).reduce(
        (acc, key) => {
          if (key !== 'id') {
            if (Array.isArray(originalObject[key])) {
              acc[key] = Array.from(originalObject[key], () => '').slice(0, 1)
            } else {
              acc[key] = ''
            }
          }
          return acc
        },
        {},
      )
      values.push(emptyObject)
      formik.setFieldValue(`${name}`, values)
    }
  }
  const onToggle = useToggle()
  const [deleteIndex, setDeleteIndex] = React.useState<number>(0)
  const [deleteValue, setDeleteValue] = React.useState<any>(null)
  const deleteEntry = (index: number) => {
    const values = formik.values[name]?.length ? [...formik.values[name]] : []
    values.splice(index, 1)
    formik.setFieldValue(`${name}`, values)
    onToggle.off()
  }
  const checkName = (index: number) => {
    if (name === 'notes_absence') {
      if (formik.values[name][index].creation) {
        return formik.values[name][index].creation.value
      }
    }
    return ''
  }
  return (
    <>
      <div>
        {formik.values[name]?.map((_: any, i: number, arr: any) => {
          const index = arr.length - i - 1
          const value = arr[index]

          return (
            <React.Fragment key={value.id || value.localId || `field-${index}`}>
              <Accordion>
                <AccordionSummary
                  expandIcon={<Arrow />}
                  className={styles.dropdown}
                >
                  <h3 className={styles.fieldTitle}>
                    {label}
                    {checkName(index)}
                    {arr.length > 1 && name !== 'notes_absence'
                      ? ` ${index + 1}`
                      : ''}
                  </h3>
                </AccordionSummary>
                <AccordionDetails className={styles.dropdown}>
                  <div
                    className={classNames(styles.fieldWrap, {
                      [styles.group]: isGroup,
                    })}
                  >
                    {renderField(value, index)}
                  </div>
                </AccordionDetails>
                {(arr.length > 1 || !keepLastEntry) && !disable && (
                  <Button
                    variant="text"
                    type="button"
                    className={styles.removeButton}
                    onClick={() => {
                      setDeleteIndex(index)
                      setDeleteValue(value)
                      onToggle.on()
                    }}
                  >
                    <Remove />
                  </Button>
                )}
              </Accordion>
            </React.Fragment>
          )
        })}
        {(!disable || withoutPlusButton) &&
          (formik.values[name]?.length || !initialEmptyAllowed ? (
            <Button
              variant="text"
              type="button"
              className={styles.button}
              onClick={handleAddEntry}
            >
              {'+ Hinzufügen'}
            </Button>
          ) : (
            <Button
              color="orange"
              variant="fulfilled"
              type="button"
              className={styles.addFirstEntry}
              onClick={handleAddEntry}
            >
              {`${label} einfügen`}
            </Button>
          ))}
      </div>
      <Modal
        className={styles.warningModal}
        isOpen={onToggle.value}
        onRequestClose={onToggle.off}
      >
        <div className={styles.warning}>
          <h3 className={styles.modalHeader}>{'Eintrag löschen?'}</h3>
          <p className={styles.warningText}>{`Möchten Sie ${label} ${
            deleteIndex + 1
          } wirklich löschen?`}</p>
          {deleteValue?.period?.from || deleteValue?.from ? (
            <div className={styles.values}>
              <p className={styles.warningTitle}>{'Datum'}</p>
              <p className={styles.warningText}>{`${
                deleteValue?.period?.from || deleteValue?.from || ''
              } - ${deleteValue?.period?.to || deleteValue?.to || ''}`}</p>
            </div>
          ) : null}
          {deleteValue?.family_vacation_type ? (
            <div className={styles.values}>
              <p className={styles.warningTitle}>{'Art'}</p>
              <p className={styles.warningText}>
                {deleteValue?.family_vacation_type?.label || ''}
              </p>
            </div>
          ) : null}
          {deleteValue?.value ? (
            <div className={styles.values}>
              <p className={styles.warningTitle}>{'Wert'}</p>
              <p className={styles.warningText}>{`${
                deleteValue?.period?.from || deleteValue?.from || ''
              } - ${deleteValue?.period?.to || deleteValue?.to || ''}`}</p>
            </div>
          ) : null}
          <div className={styles.buttons}>
            <Button
              variant="fulfilled"
              type="button"
              onClick={() => {
                deleteEntry(deleteIndex)
              }}
            >
              {'Eintrag löschen'}
            </Button>
            <Button
              color="orange"
              variant="fulfilled"
              type="button"
              onClick={onToggle.off}
            >
              {'Abbrechen'}
            </Button>
          </div>
        </div>
      </Modal>
    </>
  )
}

export default FieldArrayComponent
