  description: string | null
  enabled: boolean
  existing: boolean
  /**
   * @param expanded item is selected
   */
  expanded: boolean
  external: boolean
  key: string
  options?: {
    attributes?: {
      /**
       * @param class always array with 1 item - string of item classes separated with spaces
       */
      class?: [string]
    }
    menu_icon?: {
      fid: string
      uri?: string
      url?: string
    }
    tid?: number
  }
  relative: string
  title: string
  uri: string
  /**
   * @param weight items are sorted by this field (order)
   */
