export interface Offer {
  /**
   * id
   */
  nid: number | string
  /**
   * Angebots Nr, (or Angebotsnummer)
   */
  number: number | string
  /**
   * Angebotsinhalt
   */
  title: string
  /**
   * Datum
   */
  created: number | string
  /**
   * Ersteller
   */
  responsible: string[]
  /**
   * Auftraggeber
   */
  customer:
    | {
        id: string
        title?: string
        uid?: string
        created?: string
      }
    | null
    | string
  /**
   * ??? (have no usage yet)
   */
  customer_responsible: string[]
  /**
   * Bemerkung
   */
  comment_count: number | string
  /**
   * Angebotssumme
   */
  amount: number | string
  /**
   * ??? (have no usage yet)
   */
  amount_estimated: boolean
  /**
   * NAN Summe
   */
  nan: number
  /**
   * Bindefrist bis zum
   */
  binding_until: number | string | null
  /**
   * Warscheilichkeit der Beauftragung
   */
  probability: string | null
  /**
   * Haftung Personen-/Sachschaden
   */
  liability_damage: number | string | null
  /**
   * Comment
   */
  comment: string
  /**
   * Projekt
   */
  project: string
  /**
   * Angedachter PGL
   */
  pgl: string | null
  /**
   * Zuordnung Fachbereich
   */
  department: string
  /**
   * Index
   */
  index: string
  /**
   * Projektleiter
   */
  project_manager: string
  /**
   * Angebotsersteller
   */
  author: string
}

