import { ReactComponent as Upload } from 'assets/icons/upload.svg'
import cn from 'classnames'
import { useState } from 'react'
import { useDropzone } from 'react-dropzone'
import api from 'services/api'
import { FileWithPreview } from 'types'

import styles from '../styles.module.scss'

interface Props {
  onUpload: (id: string, name: string) => void
}

const DocUploader = ({ onUpload }: Props) => {
  const [addedDocuments, setAddedDocuments] = useState<FileWithPreview>()

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    accept: 'application/msword, text/plain, application/pdf',
    maxSize: 6000000,
    maxFiles: 1,
    onDrop: async (acceptedFiles) => {
      const file = acceptedFiles[0]
      const { data } = await api.post(
        '/file/upload/node/employee_appraisal/field_appraisal_files?_format=json',
        file,
        {
          headers: {
            'Content-Type': 'application/octet-stream',
            'Content-Disposition': `file; filename="${file.name}"`,
          },
        },
      )

      setAddedDocuments(
        Object.assign(file, {
          id: data.fid[0].value,
          preview: URL.createObjectURL(file),
        }),
      )
      onUpload(data.fid[0].value, file.name)
    },
  })

  return (
    <div
      {...getRootProps({
        className: cn(styles.dndContainer, {
          [styles.active]: isDragActive,
        }),
      })}
    >
      <input {...getInputProps()} />
      {'Dokument hochladen'} <br />
      {'oder'} <br />
      <button type="button" className={styles.upload}>
        {'Hochladen'}
        <Upload />
      </button>
      {addedDocuments && <span>{addedDocuments.name}</span>}
      <div className={styles.hint}>{'*.pdf, *.doc, *docx, max. 6Mb'}</div>
    </div>
  )
}

export default DocUploader
