import cn from 'classnames'
import Button from 'components/Button'
import addDays from 'date-fns/addDays'
import useToggle from 'hooks/useToggle'
import { useState } from 'react'
import { DayModifiers } from 'react-day-picker'

import styles from './styles.module.scss'
import Calendar from './tabs/Calendar'
import Modules from './tabs/Modules'

const BigMenu = ({ hasTabs = false }) => {
  const tabsToggle = useToggle(true)
  const [selectedDay, setSelectedDay] = useState<Date | undefined>(
    addDays(new Date(), 1),
  )

  const onDayClick = (date: Date, { selected }: DayModifiers) =>
    setSelectedDay(selected ? undefined : date)

  return (
    <div className={styles.bigMenu}>
      {hasTabs && (
        <div className={styles.tabs}>
          <Button
            variant="custom"
            onClick={tabsToggle.toggle}
            className={cn(styles.tab, tabsToggle.value && styles.selected)}
          >
            {'Kalendar'}
          </Button>
          <Button
            variant="custom"
            onClick={tabsToggle.toggle}
            className={cn(styles.tab, !tabsToggle.value && styles.selected)}
          >
            {'Alle Module'}
          </Button>
        </div>
      )}
      {tabsToggle.value ? (
        <Calendar selectedDay={selectedDay} onDayClick={onDayClick} />
      ) : (
        <Modules />
      )}
    </div>
  )
}

export default BigMenu
