import {
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  SelectChangeEvent,
} from '@mui/material'
import { ReactComponent as ArrowDown } from 'assets/icons/arrowDown.svg'
import { DropDownItems } from 'types'

import styles from './styles.module.scss'

interface DropdownProps {
  menuItems?: DropDownItems[]
  dropdownValue: string
  onItemChange: (event: SelectChangeEvent) => void
  label: string
}

const Dropdown = ({
  menuItems,
  dropdownValue,
  onItemChange,
  label,
}: DropdownProps) => (
  <FormControl
    className={styles.navigation}
    sx={{
      '& .MuiInputLabel-shrink': {
        zIndex: 0,
      },
      '& .MuiInputLabel-animated': {
        transform: 'none',
      },
    }}
  >
    <InputLabel className={styles.dropdownLabel}>{label}</InputLabel>
    <Select
      className={styles.dropdownContainer}
      value={dropdownValue}
      onChange={onItemChange}
      IconComponent={ArrowDown}
      sx={{
        '& .MuiSelect-select': { fontFamily: 'Open Sans, sans-serif' },
        '& .MuiSelect-icon': { top: 14, right: 14 },
        '& .MuiOutlinedInput-notchedOutline': { border: 'none' },
      }}
    >
      {menuItems?.map((subItem) => (
        <MenuItem
          key={Number(subItem.id) * Math.random()}
          value={subItem.id}
          className={styles.dropdownItem}
        >
          {subItem.name}
        </MenuItem>
      ))}
    </Select>
  </FormControl>
)

export default Dropdown
