// import { ReactComponent as Export } from 'assets/icons/download.svg'
import { ReactComponent as Calendar } from 'assets/icons/employeeDataCalendarWhite.svg'
import cn from 'classnames'
import AccordionComponent from 'common/Accordion'
import HistoryModal from 'common/History'
import Modal from 'common/Modal'
import Button from 'components/Button'
import { eachDayOfInterval, format, isWeekend } from 'date-fns'
import { useFormik } from 'formik'
import useToggle from 'hooks/useToggle'
import { useEffect, useState } from 'react'
// import { CSVLink } from 'react-csv'
import ReactHtmlParser from 'react-html-parser'
import { useDispatch } from 'react-redux'
import api from 'services/api'
import notify from 'services/notify'
import { getEmployeeRecord } from 'store/ives/employees/actions'
import { Employee, EmployeeHolidays, Sick } from 'types'

import { FormOptions, getFormSelectOptions } from '../../EmployeeForm/formData'
import { FormSchema } from '..'
import styles from '../styles.module.scss'

interface EmployeeData {
  currentRecord: Employee
  allAccordions: boolean
  holidayData: EmployeeHolidays[] | undefined
}
const TimeManagement = ({
  currentRecord,
  holidayData,
  allAccordions,
}: EmployeeData) => {
  const [slideAbsence, setSlideAbsence] = useState(false)
  const dispatch = useDispatch()
  const toggleHolidayModal = useToggle()
  const toggleSickModal = useToggle()
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [currentEmployee, setCurrentEmployee] = useState<Employee>(
    currentRecord,
  )
  const IDquery = new URLSearchParams(window.location.search)
  const ID = IDquery.get('id')
  const [options, setOptions] = useState<FormOptions>({
    projectGroups: [],
    familyOptions: [],
    leaveOptions: [],
    companyFunctionOptions: [],
    locationOptions: [],
    academicOptions: [],
    hourlyModelOptions: [],
    competenceFunctionOptions: [],
    caOptions: [],
    crossCompetenceOptions: [],
    crossGroupOptions: [],
    competenceCenterOptions: [],
    functionInCompetenceOptions: [],
    workingGroupOptions: [],
    workingFunctionOptions: [],
    workingDaysOptions: [],
  })
  const setEmployeeRecordsFromDispatch = () => {
    dispatch(
      getEmployeeRecord(ID as string, (record: Employee) =>
        setCurrentEmployee(record),
      ),
    )
  }
  useEffect(() => {
    getFormSelectOptions().then((res: FormOptions) => {
      setOptions(res)
    })
  }, [])
  const translateFamilyVacation = currentRecord?.family_vacation?.map(
    (item) => {
      const type = options.familyOptions.find(
        (option) => option.value === item.family_vacation_type,
      )
      return {
        ...item,
        family_vacation_type: type?.label,
      }
    },
  )
  const calculateSickDays = () => {
    let counter = 0
    currentRecord?.employee_sick?.forEach((item) => {
      const intervalArray = eachDayOfInterval({
        start: new Date(item.period.from),
        end: new Date(item.period.to),
      })
      intervalArray.forEach((day) => {
        if (!isWeekend(day)) {
          counter += 1
        }
      })
    })
    return counter
  }

  const calculateSickDaysPerEntry = (sick: Sick) => {
    let counter = 0
    const intervalArray = eachDayOfInterval({
      start: new Date(sick.period.from),
      end: new Date(sick.period.to),
    })
    intervalArray.forEach((day) => {
      if (!isWeekend(day)) {
        counter += 1
      }
    })
    return counter
  }
  // eslint-disable-next-line consistent-return
  const removePTags = (text?: string): string | undefined => {
    if (text) {
      return text?.replace(/<(?:"[^"]*"['"]*|'[^']*'['"]*|[^'">])+>/g, '')
    }
  }
  const getCurrentData = (values: any) => {
    const object: any = {}
    if (values.notes_main) {
      const tempNotes: any = currentRecord.notes_main
        ? currentRecord.notes_main
        : []
      tempNotes?.push({ attachments: [], note: values.notes_main })
      object.notes_main = tempNotes
    }
    if (values.notes_contract) {
      const tempNotes: any = currentRecord.notes_contract
        ? currentRecord.notes_contract
        : []
      tempNotes?.push({ attachments: [], note: values.notes_contract })
      object.notes_contract = tempNotes
    }
    if (values.notes_absence) {
      const tempNotes: any = currentRecord.notes_absence
        ? currentRecord.notes_absence
        : []
      tempNotes?.push({ attachments: [], note: values.notes_absence })
      object.notes_absence = tempNotes
    }
    return object
  }

  const Notes = useFormik({
    initialValues: {
      notes_main: '',
      notes_contract: '',
      notes_absence: '',
    },
    validationSchema: FormSchema,
    onSubmit: async (values) => {
      try {
        await api
          .patch(`/employees/${ID}?_format=json`, getCurrentData(values))
          .then(() => {
            Notes.resetForm()
            setEmployeeRecordsFromDispatch()
          })
      } catch (error) {
        notify(error)
      }
    },
  })
  const filterLeaveType = (type: string) =>
    currentRecord?.leave?.filter((item) => item.type === type)
  // const projectGroup = options.projectGroups.find(
  //   (option) => option.value === currentRecord.project_group,
  // )
  // const csvData = [
  //   ['Mitarbeiter, Krankheitstage, Projektgruppe, Krankheit, Vertretung'],
  //   [
  //     currentEmployee.last_name,
  //     calculateSickDays(),
  //     projectGroup?.label || 'Keine',
  //     '',
  //     '',
  //   ],
  // ]
  return (
    <>
      <div className={styles.detailMainWrap}>
        <div
          className={cn(
            styles.detailMain,
            styles.absence,
            slideAbsence && styles.open,
          )}
        >
          <div className={cn(styles.detailHeader, styles.absence)}>
            <h2>{'Zeitmanagement'}</h2>
          </div>
          <div className={cn(styles.detailMainInnerOverview, styles.absence)}>
            {/* <CSVLink */}
            {/*  className={styles.exportBtn} */}
            {/*  data={csvData || []} */}
            {/*  target="_blank" */}
            {/*  filename={`${currentEmployee.first_name} ${currentEmployee.last_name} - Zeitmanagement.csv`} */}
            {/* > */}
            {/*  <Export /> */}
            {/* </CSVLink> */}
            <div className={styles.detailBody}>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Letzte Weiterbildung'}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {currentEmployee.vacation_year || 0}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Anzahl der Krankheitstage'}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {calculateSickDays() || 0}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'Soziale Maßnahmen'}</span>
                <br />
                <span className={styles.dataValue}>
                  {currentEmployee.remaining_previous_year || 0}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Dauer zusätzlicher Ausfall'}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {currentEmployee.remaining_previous_year || 0}
                </span>
              </div>
            </div>
          </div>
          <div className={styles.detailMainInner}>
            <AccordionComponent
              expanded={allAccordions}
              title="Urlaub"
              currentTitleStyle={styles.absence}
            >
              <div className={styles.accordionData}>
                <span className={styles.dataLabel}>{'Gesamtanspruch'}</span>
                <br />
                <span className={styles.dataValue}>
                  {`${
                    holidayData && holidayData?.length > 0
                      ? holidayData?.map(
                          (item) =>
                            item.holidays_left + item.holidays_left_last_year,
                        )
                      : 0
                  } Tage`}
                </span>
              </div>
              <div className={styles.accordionData}>
                <span className={styles.dataLabel}>
                  {'Urlaubsanspruch Kalendarjahr'}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {`${
                    holidayData?.map((item) => item.holidays_left) || 0
                  } Tage`}
                </span>
              </div>
              <div className={styles.accordionData}>
                <span className={styles.dataLabel}>
                  {'Verbleibend aus dem Vorjahr'}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {`${
                    holidayData?.map((item) => item.holidays_left_last_year) ||
                    0
                  } Tage`}
                </span>
              </div>
              <div className={styles.accordionData}>
                <span className={styles.dataLabel}>{'Bereits genehmigt'}</span>
                <br />
                <span className={styles.dataValue}>
                  {`${
                    holidayData?.map((item) => item.holidays_count?.planned) ||
                    0
                  } Tage`}
                </span>
              </div>
              <div className={styles.accordionData}>
                <span className={styles.dataLabel}>{'Bereits beantragt'}</span>
                <br />
                <span className={styles.dataValue}>
                  {`${
                    holidayData?.map(
                      (item) => item.holidays_count?.requested,
                    ) || 0
                  } Tage`}
                </span>
              </div>
              <div className={styles.accordionData}>
                <span className={styles.dataLabel}>
                  {'Urlaubsnotizen aktuelles Jahr'}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {currentEmployee.notes_absence
                    ?.map((item) => removePTags(item.note))
                    .slice(-1)}
                </span>
              </div>
            </AccordionComponent>
            <div className={styles.accordionContainer}>
              <AccordionComponent
                expanded={allAccordions}
                title="Tod/Hochzeit/Geburt"
                currentTitleStyle={styles.absence}
              >
                {translateFamilyVacation?.map((vacation, index) => (
                  <div className={styles.accordionData} key={index}>
                    <span className={styles.dataLabel}>
                      {vacation.family_vacation_type}
                    </span>
                    <div className={styles.dataWrap}>
                      {vacation.period?.from && vacation.period?.to ? (
                        <span className={styles.dataValue}>
                          {`${format(
                            new Date(vacation.period?.from),
                            'dd.MM.yyyy',
                          )} - ${format(
                            new Date(vacation.period?.to),
                            'dd.MM.yyyy',
                          )}`}
                        </span>
                      ) : null}
                    </div>
                  </div>
                ))}
                {/* <Button
                  className={cn(styles.historyBtn, styles.absence)}
                  variant="fulfilled"
                  onClick={() => undefined}
                >
                  {'Historienansicht'}
                </Button> */}
              </AccordionComponent>
              <AccordionComponent
                expanded={allAccordions}
                title="Betrieblicher Umzug"
                currentTitleStyle={styles.absence}
              >
                {currentRecord?.corporate_relocation?.from ? (
                  <div className={styles.accordionData}>
                    <span className={styles.dataLabel}>{'Umzug'}</span>
                    <br />
                    <span className={styles.dataValue}>
                      {` ${format(
                        new Date(
                          currentRecord?.corporate_relocation?.from as string,
                        ),
                        'dd.MM.yyyy',
                      )} - ${format(
                        new Date(
                          currentRecord?.corporate_relocation?.to as string,
                        ),
                        'dd.MM.yyyy',
                      )}`}
                    </span>
                  </div>
                ) : undefined}
              </AccordionComponent>
              <AccordionComponent
                expanded={allAccordions}
                title="Krankheitstage"
                currentTitleStyle={styles.absence}
              >
                {currentEmployee.employee_sick?.map((sick, index) => (
                  <div className={styles.accordionData} key={index}>
                    <span className={styles.dataLabel}>{sick.type}</span>
                    <div>
                      {sick.period.to && (
                        <>
                          <div style={{ flexDirection: 'column' }}>
                            <span className={styles.dataValue}>
                              {`${format(
                                new Date(sick?.period.from),
                                'dd.MM.yyyy',
                              )} - ${format(
                                new Date(sick?.period.to),
                                'dd.MM.yyyy',
                              )}`}
                            </span>
                          </div>
                          <span className={styles.dataValue}>
                            {calculateSickDaysPerEntry(sick)}{' '}
                            {calculateSickDaysPerEntry(sick) < 2
                              ? 'Tag'
                              : 'Tage'}
                          </span>
                        </>
                      )}
                    </div>
                  </div>
                ))}
                {currentEmployee.employee_sick &&
                  currentEmployee.employee_sick?.length > 3 && (
                    <Button
                      className={cn(styles.history, styles.absence)}
                      variant="fulfilled"
                      onClick={toggleSickModal.on}
                    >
                      {'Historienansicht'}
                    </Button>
                  )}
              </AccordionComponent>

              <AccordionComponent
                expanded={allAccordions}
                title="Mutterschutz"
                currentTitleStyle={styles.absence}
              >
                {filterLeaveType('Mutterschutz')?.map((protection, index) => (
                  <div className={styles.accordionData} key={index}>
                    <span className={styles.dataLabel}>{protection.child}</span>
                    <div className={styles.dataWrap}>
                      {protection.period && (
                        <span className={styles.dataValue}>
                          {`${format(
                            new Date(protection.period.from),
                            'dd.MM.yyyy',
                          )} - ${format(
                            new Date(protection.period.to),
                            'dd.MM.yyyy',
                          )}`}
                        </span>
                      )}
                    </div>
                  </div>
                ))}
                {/* <Button
                  className={cn(styles.historyBtn, styles.absence)}
                  variant="fulfilled"
                  onClick={() => undefined}
                >
                  {'Historienansicht'}
                </Button> */}
              </AccordionComponent>
              <AccordionComponent
                expanded={allAccordions}
                title="Elternzeit"
                currentTitleStyle={styles.absence}
              >
                {filterLeaveType('Elternzeit')?.map((protection, index) => (
                  <div className={styles.accordionData} key={index}>
                    <span className={styles.dataLabel}>{protection.child}</span>
                    <div className={styles.dataWrap}>
                      {protection.period && (
                        <span className={styles.dataValue}>
                          {`${format(
                            new Date(protection.period.from),
                            'dd.MM.yyyy',
                          )} - ${format(
                            new Date(protection.period.to),
                            'dd.MM.yyyy',
                          )}`}
                        </span>
                      )}
                    </div>
                  </div>
                ))}
                {/* <Button
                  className={cn(styles.historyBtn, styles.absence)}
                  variant="fulfilled"
                  onClick={() => undefined}
                >
                  {'Historienansicht'}
                </Button> */}
              </AccordionComponent>
              <AccordionComponent
                expanded={allAccordions}
                currentTitleStyle={styles.absence}
                title="Andere Soziale Leistungen"
              >
                <div className={styles.detailBody}>
                  <div className={styles.dataWrap}>
                    <span className={styles.dataLabel}>
                      {'Andere soziale Leistungen'}
                    </span>
                    <br />
                    {currentEmployee?.sb_etc ? (
                      <span className={styles.dataValue}>
                        {ReactHtmlParser(currentEmployee?.sb_etc) ||
                          'Keine Angabe'}
                      </span>
                    ) : null}
                  </div>
                </div>
              </AccordionComponent>
            </div>
            <AccordionComponent
              expanded={allAccordions}
              currentTitleStyle={styles.absence}
              title="Medizinische Leistungen"
            >
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'Leistungen'}</span>
                <br />
                {currentEmployee?.medical_services ? (
                  <span className={styles.dataValue}>
                    {ReactHtmlParser(currentEmployee?.medical_services) ||
                      'Keine Angabe'}
                  </span>
                ) : null}
              </div>
            </AccordionComponent>
            <AccordionComponent
              expanded={allAccordions}
              currentTitleStyle={styles.absence}
              title="Qualifizierungen/Weiterbildungen"
            >
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Qualifizierungen/Weiterbildungen'}
                </span>
                <br />
                {currentEmployee?.qualifications ? (
                  <span className={styles.dataValue}>
                    {ReactHtmlParser(currentEmployee?.qualifications) ||
                      'Keine Angabe'}
                  </span>
                ) : null}
              </div>
            </AccordionComponent>
          </div>
        </div>
        <Button
          className={cn(
            styles.detailBtn,
            styles.absenceBtn,
            slideAbsence && styles.toggleAbsence,
          )}
          onClick={() => setSlideAbsence(!slideAbsence)}
        >
          {(!slideAbsence && <Calendar className={styles.absenceIcon} />) || (
            <Calendar />
          )}
          <h4 className={styles.sideTitle}>{'Zeitmanagement'}</h4>
        </Button>
      </div>
      <Modal
        isOpen={toggleHolidayModal.value}
        onRequestClose={toggleHolidayModal.off}
        withCloseButton
        maxWidth={1000}
      >
        <HistoryModal
          modalType="holiday"
          holidayData={holidayData}
          workingDays={currentEmployee.working_days}
        />
      </Modal>
      <Modal
        isOpen={toggleSickModal.value}
        onRequestClose={toggleSickModal.off}
        withCloseButton
        maxWidth={1000}
      >
        <HistoryModal
          modalType="sick"
          sickData={currentEmployee.employee_sick}
        />
      </Modal>
    </>
  )
}
export default TimeManagement
