import {
  EmployeeHolidays,
  Error,
  NationalHolidays,
  ProjectGroupHolidays,
} from 'types/index'

enum ActionType {
  GET_HOLIDAYS_BEGIN = '[ives] GET_HOLIDAYS_BEGIN',
  GET_HOLIDAYS_SUCCESS = '[ives] GET_HOLIDAYS_SUCCESS',
  GET_HOLIDAYS_ERROR = '[ives] GET_HOLIDAYS_ERROR',
  GET_HOLIDAY_EMPLOYEES_BEGIN = '[ives] GET_HOLIDAY_EMPLOYEES_BEGIN',
  GET_HOLIDAY_EMPLOYEES_SUCCESS = '[ives] GET_HOLIDAY_EMPLOYEES_SUCCESS',
  GET_HOLIDAY_EMPLOYEES_ERROR = '[ives] GET_HOLIDAY_EMPLOYEES_ERROR',
  GET_HOLIDAY_EMPLOYEE_VIEW_BEGIN = '[ives] GET_HOLIDAY_EMPLOYEE_VIEW_BEGIN',
  GET_HOLIDAY_EMPLOYEE_VIEW_SUCCESS = '[ives] GET_HOLIDAY_EMPLOYEE_VIEW_SUCCESS',
  GET_HOLIDAY_EMPLOYEE_VIEW_ERROR = '[ives] GET_HOLIDAY_EMPLOYEE_VIEW_ERROR',
  UPDATE_HOLIDAYS_BEGIN = '[ives] UPDATE_HOLIDAYS_BEGIN',
  UPDATE_HOLIDAYS_SUCCESS = '[ives] UPDATE_HOLIDAYS_SUCCESS',
  UPDATE_HOLIDAYS_ERROR = '[ives] UPDATE_HOLIDAYS_ERROR',
  UPDATE_HOLIDAY_EMPLOYEES_BEGIN = '[ives] UPDATE_HOLIDAY_EMPLOYEES_BEGIN',
  UPDATE_HOLIDAY_EMPLOYEES_SUCCESS = '[ives] UPDATE_HOLIDAY_EMPLOYEES_SUCCESS',
  UPDATE_HOLIDAY_EMPLOYEES_ERROR = '[ives] UPDATE_HOLIDAY_EMPLOYEES_ERROR',
  SET_EMPLOYEE_VIEW = '[ives] SET_EMPLOYEE_VIEW',
  GET_NATIONAL_HOLIDAYS_BEGIN = '[ives] GET_NATIONAL_HOLIDAYS_BEGIN',
  GET_NATIONAL_HOLIDAYS_SUCCESS = '[ives] GET_NATIONAL_HOLIDAYS_SUCCESS',
  GET_NATIONAL_HOLIDAYS_ERROR = '[ives] GET_NATIONAL_HOLIDAYS_ERROR',
  CREATE_HOLIDAY_BEGIN = '[ives] CREATE_HOLIDAY_BEGIN',
  CREATE_HOLIDAY_SUCCESS = '[ives] CREATE_HOLIDAY_SUCCESS',
  CREATE_HOLIDAY_ERROR = '[ives] CREATE_HOLIDAY_ERROR',
}

export interface GetHolidaysBeginAction {
  type: ActionType.GET_HOLIDAYS_BEGIN
}

export interface GetHolidaysSuccessAction {
  type: ActionType.GET_HOLIDAYS_SUCCESS
  payload: ProjectGroupHolidays[]
}

export interface GetHolidaysErrorAction {
  type: ActionType.GET_HOLIDAYS_ERROR
  payload: Error
}
export interface GetHolidayEmployeesBeginAction {
  type: ActionType.GET_HOLIDAY_EMPLOYEES_BEGIN
}

export interface GetHolidayEmployeesSuccessAction {
  type: ActionType.GET_HOLIDAY_EMPLOYEES_SUCCESS
  payload: EmployeeHolidays[]
}

export interface GetHolidayEmployeesErrorAction {
  type: ActionType.GET_HOLIDAY_EMPLOYEES_ERROR
  payload: Error
}
export interface GetHolidayEmployeeViewBeginAction {
  type: ActionType.GET_HOLIDAY_EMPLOYEE_VIEW_BEGIN
}

export interface GetHolidayEmployeeViewSuccessAction {
  type: ActionType.GET_HOLIDAY_EMPLOYEE_VIEW_SUCCESS
  payload: EmployeeHolidays[]
}

export interface GetHolidayEmployeeViewErrorAction {
  type: ActionType.GET_HOLIDAY_EMPLOYEE_VIEW_ERROR
  payload: Error
}

export interface GetEmployeeView {
  type: ActionType.SET_EMPLOYEE_VIEW
  payload: boolean
}

export interface GetNationalHolidaysBeginAction {
  type: ActionType.GET_NATIONAL_HOLIDAYS_BEGIN
}

export interface GetNationalHolidaysSuccessAction {
  type: ActionType.GET_NATIONAL_HOLIDAYS_SUCCESS
  payload: NationalHolidays[]
}

export interface GetNationalHolidaysErrorAction {
  type: ActionType.GET_NATIONAL_HOLIDAYS_ERROR
  payload: Error
}

export interface CreateHolidayBeginAction {
  type: ActionType.CREATE_HOLIDAY_BEGIN
}

export interface CreateHolidaySuccessAction {
  type: ActionType.CREATE_HOLIDAY_SUCCESS
}

export interface CreateHolidayErrorAction {
  type: ActionType.CREATE_HOLIDAY_ERROR
  payload: Error
}

export type Action =
  | GetHolidaysBeginAction
  | GetHolidaysSuccessAction
  | GetHolidaysErrorAction
  | GetHolidayEmployeesBeginAction
  | GetHolidayEmployeesSuccessAction
  | GetHolidayEmployeesErrorAction
  | GetHolidayEmployeeViewBeginAction
  | GetHolidayEmployeeViewSuccessAction
  | GetHolidayEmployeeViewErrorAction
  | GetEmployeeView
  | GetNationalHolidaysBeginAction
  | GetNationalHolidaysSuccessAction
  | GetNationalHolidaysErrorAction
  | CreateHolidayBeginAction
  | CreateHolidaySuccessAction
  | CreateHolidayErrorAction

export default ActionType
