import './styles.scss'

import FormHelperText from '@mui/material/FormHelperText'
import cn from 'classnames'
import { FormikErrors, FormikProps } from 'formik'
import { useCallback, useMemo, useState } from 'react'
import ReactSelect, {
  ActionMeta,
  NamedProps,
  OptionsType,
  ValueType,
} from 'react-select'
import CreatableReactSelect from 'react-select/creatable'
import { GroupSelectOption, SelectOption as Option } from 'types'

import styles from './styles.module.scss'

interface Props<F extends Record<string, unknown>, T extends boolean>
  extends NamedProps<Option, T> {
  formik?: FormikProps<F>
  name: string
  // name: keyof F & string
  className?: string
  containerClassName?: string
  value?: ValueType<Option, T>
  defaultVal?: string
  options: OptionsType<Option> | OptionsType<GroupSelectOption>
  isMulti?: T
  error?: boolean
  helperText?: FormikErrors<F>[keyof F]
  isCreatable?: boolean
  label?: string
  onChange?: (option: ValueType<Option, T>, action: ActionMeta<Option>) => void
}

const Select = <
  F extends Record<string, unknown> = Record<string, never>,
  T extends boolean = false
>({
  formik,
  className,
  containerClassName,
  isMulti,
  name,
  value,
  options,
  onChange,
  isSearchable = true,
  isClearable = true,
  isCreatable = true,
  error = !!(formik?.touched[name] && formik.errors[name]),
  helperText = formik?.touched[name] && formik.errors[name],
  placeholder,
  label,
  ...props
}: Props<F, T>): JSX.Element => {
  const [focused, setFocused] = useState(false)
  const [selected, setSelected] = useState(false)

  const SelectComponent = useCallback(
    (_props: NamedProps<Option, T>) =>
      isCreatable ? (
        <CreatableReactSelect {..._props} />
      ) : (
        <ReactSelect {..._props} />
      ),
    [isCreatable],
  )

  const formikOnChange = useCallback((val: ValueType<Option, T>) => {
    val ? setSelected(true) : setSelected(false)
    formik?.setFieldValue(name, val)
  }, [])

  const onChangeFunc = useMemo(() => (formik ? formikOnChange : onChange), [
    formik,
  ])

  return (
    <div
      className={cn('react-select-container', containerClassName, { error })}
    >
      <SelectComponent
        inputId={`${name}-id`}
        name={name}
        className={cn(className, {
          filled:
            formik?.values[name] ||
            (Array.isArray(value) ? value.length : value),
          withLabel: label,
        })}
        classNamePrefix="react-select"
        options={options}
        formatGroupLabel={(data) => <span>{data.label}</span>}
        isMulti={isMulti}
        isSearchable={isSearchable}
        isClearable={isClearable}
        value={(formik?.values[name] || value) as ValueType<Option, T>}
        onChange={onChangeFunc}
        placeholder={label ? placeholder : undefined}
        onFocus={() => setFocused(true)}
        onBlur={() => setFocused(false)}
        {...props}
      />
      {label && (
        <div
          className={cn(styles.labelContainer, {
            [styles.filled]:
              formik?.values[name] ||
              (Array.isArray(value) ? value.length : value),
            [styles.focused]: focused,
            [styles.disabled]: props.isDisabled,
            [styles.selected]: selected,
          })}
        >
          <fieldset>
            <legend>{label}</legend>
          </fieldset>
          <label htmlFor={`${name}-id`}>{label}</label>
        </div>
      )}
      {error && (
        <FormHelperText error variant="outlined">
          {helperText}
        </FormHelperText>
      )}
    </div>
  )
}

export default Select
