import CompanyClusterWidget from 'common/widgets/CompanyCluster'
import { CompanyCluster } from 'constants/companyStructure'
import {
  ColorSchema,
  CompanyStructureWidgets,
  layouts,
} from 'constants/companyStructureLayout'
import Widgets from 'constants/widgets'
import { nanoid } from 'nanoid'
import { useState } from 'react'
import { Item, ItemParams, Menu, useContextMenu } from 'react-contexify'
import { Responsive, WidthProvider } from 'react-grid-layout'
import { useDispatch } from 'react-redux'
import { addWidget } from 'store/user/actions'
import { ProjectGroup } from 'types'

const MENU_ID = 'company-cluster-context-menu'

export interface ContextMenuProps {
  group: ProjectGroup
}

const ResponsiveGridLayout = WidthProvider(Responsive)

interface Props {
  selectedCompany: number
  companyStructure: Record<CompanyCluster, ProjectGroup[]>
  openEmployeeInfo(id: number | null): void
}

const GridLayout: React.FC<Props> = ({
  companyStructure,
  openEmployeeInfo,
}) => {
  const [active, setActive] = useState<ProjectGroup>({} as ProjectGroup)
  const { show } = useContextMenu({ id: MENU_ID })
  const dispatch = useDispatch()

  return (
    <>
      <ResponsiveGridLayout
        className="layout"
        layouts={layouts}
        isResizable
        isBounded
        isDroppable
        compactType="horizontal"
        autoSize
        useCSSTransforms
        draggableHandle=".draggable"
        breakpoints={{ lg: 1280, md: 992, sm: 767, xs: 480, xxs: 0 }}
        cols={{ lg: 16, md: 14, sm: 10, xs: 8, xxs: 4 }}
        rowHeight={40}
        margin={[32, 32]}
        containerPadding={[28, 200]}
        measureBeforeMount
      >
        <div key={CompanyStructureWidgets.verwaltung}>
          <CompanyClusterWidget
            groups={companyStructure.verwaltung}
            openEmployeeInfo={openEmployeeInfo}
            title="Geschäftsführung / Verwaltung"
            color={ColorSchema.gray}
            active={active}
            setActive={setActive}
            onContextMenu={show}
            withoutMenu
          />
        </div>
        <div key={CompanyStructureWidgets.projektgruppen}>
          <CompanyClusterWidget
            groups={companyStructure.projektgruppen}
            withoutMenu
            openEmployeeInfo={openEmployeeInfo}
            title="Projektgruppen"
            color={ColorSchema.blue}
            active={active}
            setActive={setActive}
            onContextMenu={show}
          />
        </div>
        <div key={CompanyStructureWidgets.arbeitsgruppen}>
          <CompanyClusterWidget
            groups={companyStructure.arbeitsgruppen}
            openEmployeeInfo={openEmployeeInfo}
            title="Arbeitsgruppen"
            color={ColorSchema.green}
            active={active}
            withoutMenu
            setActive={setActive}
            onContextMenu={show}
          />
        </div>
        <div key={CompanyStructureWidgets.kompetenzbereiche}>
          <CompanyClusterWidget
            groups={companyStructure.kompetenzbereiche}
            openEmployeeInfo={openEmployeeInfo}
            title="Kompetenzbereiche"
            color={ColorSchema.orange}
            active={active}
            setActive={setActive}
            onContextMenu={show}
            withoutMenu
          />
        </div>
        <div key={CompanyStructureWidgets.standorte}>
          <CompanyClusterWidget
            groups={companyStructure.standorte}
            openEmployeeInfo={openEmployeeInfo}
            title="Standorte"
            color={ColorSchema.gray}
            active={active}
            setActive={setActive}
            onContextMenu={show}
            withoutMenu
          />
        </div>
        <div key={CompanyStructureWidgets.kompetenzzentrum}>
          <CompanyClusterWidget
            groups={companyStructure.kompetenzzentrum}
            openEmployeeInfo={openEmployeeInfo}
            title="Kompetenzzentrum"
            color={ColorSchema.yellow}
            active={active}
            setActive={setActive}
            onContextMenu={show}
            withoutMenu
          />
        </div>
      </ResponsiveGridLayout>
      <Menu id={MENU_ID}>
        <Item
          onClick={({ props }: ItemParams<ContextMenuProps>) => {
            props &&
              dispatch(
                addWidget({
                  data: {
                    groupId: props.group.tid,
                  },
                  title: props.group.name,
                  type: Widgets.groupPreview,
                  layout: {
                    i: nanoid(),
                    x: 0,
                    y: Infinity,
                    w: 6,
                    h: 4,
                    minW: 5,
                    minH: 4,
                  },
                }),
              )
          }}
        >
          {'Auf Mein S&P Hinzufügen'}
        </Item>
      </Menu>
    </>
  )
}

export default GridLayout
