import { EmployeeHolidays } from 'types'

import styles from './styles.module.scss'

interface CounterProps {
  userData: EmployeeHolidays[] | null
  calculateHolidays: (employee: EmployeeHolidays[] | null) => number
}

const HolidayCounter: React.FC<CounterProps> = ({
  userData,
  calculateHolidays,
}) => (
  <div className={styles.counterWrap}>
    {console.log('this is the calculate holidays', calculateHolidays)}
    <div className={styles.counter}>
      <span className={styles.counterLabel}>{'Gesamtanspruch'}</span>
      <span className={styles.counterValue}>
        {userData ? calculateHolidays(userData) : '0'}
        {'Tage'}
      </span>
    </div>
    <div className={styles.counter}>
      <span className={styles.counterLabel}>
        {'Urlaubsanspruch Kalenderjahr'}
      </span>
      <span className={styles.counterValue}>
        {userData ? userData[0]?.holidays_left : '0'} {`Tage`}
      </span>
    </div>
    <div className={styles.counter}>
      <span className={styles.counterLabel}>
        {'Verbleibend aus dem Vorjahr'}
      </span>
      <span className={styles.counterValue}>
        {userData ? userData[0]?.holidays_left_last_year : '0'} {`Tage`}
      </span>
    </div>
    <div className={styles.counter}>
      <span className={styles.counterLabel}>{'Bereits genehmigt'}</span>
      <span className={styles.counterValue}>
        {userData ? userData[0]?.holidays_count?.approved : '0'} {`Tage`}
      </span>
    </div>
    <div className={styles.counter}>
      <span className={styles.counterLabel}>{'Bereits beantragt'}</span>
      <span className={styles.counterValue}>
        {userData ? userData[0]?.holidays_count?.requested : '0'} {`Tage`}
      </span>
    </div>
  </div>
)

export default HolidayCounter
