import Comments from 'common/Comments'
import Modal from 'common/Modal'
import dateTransform from 'common/pipes/dateTime.pipe'
import CommentCell from 'components/tableComponents/CommentCell'
import { DEFAULT_TABLE_WIDTH } from 'constants/index'
import Routes from 'constants/routes'
import { fromUnixTime, getUnixTime } from 'date-fns'
import useDebounce from 'hooks/useDebounce'
import useToggle from 'hooks/useToggle'
import useWindowSize from 'hooks/useWindowSize'
import { useEffect, useMemo, useState } from 'react'
import {
  Item,
  ItemParams,
  Menu,
  Separator,
  useContextMenu,
} from 'react-contexify'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { Column, ColumnInstance, Row } from 'react-table'
import { formatDate, zonedDate } from 'services/date'
import notify from 'services/notify'
import { getSelectValue, percent } from 'services/utils'
import {
  getOfferComments,
  getOfferRecord,
  getOffers,
  updateOffer,
} from 'store/ives/offers/actions'
import { CreateOfferRequest, Offer, Offers as IOffers, ReduxState } from 'types'

import Form, { FormValues, InitialData } from '../OffersForm'
import styles from './styles.module.scss'
import ContentView from './view'

const MENU_ID = 'offer-menu-id'

// ? we need use type instead interface because of https://github.com/microsoft/TypeScript/issues/15300
export type TableData = {
  id: string
  number: string
  created: string
  title: string
  customer: string
  amount: string
  field: string
  probability: string
  responsible: string
  department: string
  index: string
  bindingUntil: string
  commentCount: string
  nan: string
}

const Offers = ({ query }: { query?: string }): JSX.Element => {
  const editModalToggle = useToggle()
  const commentsToggle = useToggle()
  const [selectedRow, setSelectedRow] = useState<Row<TableData>>()
  const [offers, setOffers] = useState<IOffers>()
  const [offerRecord, setOfferRecord] = useState<Offer>()
  const dispatch = useDispatch()
  const { isLoading } = useSelector(({ ives }: ReduxState) => ives.offers)
  const { width = DEFAULT_TABLE_WIDTH } = useWindowSize()
  const history = useHistory()
  const debouncedQuery = useDebounce(query, 500)

  const { show } = useContextMenu({
    id: MENU_ID,
  })

  useEffect(() => {
    dispatch(getOffers({ page: 0, query }, setOffers))
  }, [debouncedQuery])

  useEffect(() => {
    editModalToggle.value &&
      selectedRow?.original &&
      dispatch(
        getOfferRecord(selectedRow.original.id, (records) =>
          setOfferRecord(records[0]),
        ),
      )
  }, [editModalToggle.value])

  const tableData = useMemo<TableData[]>(
    () =>
      offers?.rows.map((row) => ({
        id: `${row.nid}`,
        amount: `${row.amount}`,
        field: row.pgl || '',
        bindingUntil: row.binding_until
          ? formatDate(fromUnixTime(+row.binding_until))
          : '',
        commentCount: `${row.comment_count}`,
        created: formatDate(fromUnixTime(+row.created)),
        customer:
          typeof row.customer === 'string'
            ? row.customer
            : row.customer?.title || '',
        department: row.department,
        index: row.index,
        nan: `${row.nan}`,
        number: `${row.number}`,
        probability: row.probability || '',
        responsible: Array.isArray(row.responsible)
          ? row.responsible?.join(', ')
          : row.responsible || '',
        title: row.title,
      })) || [],
    [offers],
  )

  const columnsData = useMemo<Column<TableData>[]>(
    () => [
      {
        Header: 'Angebotsnummer',
        accessor: 'number', // accessor is the "key" in the data
      },
      {
        Header: 'Datum',
        accessor: 'created',
        isSortedDesc: true,
        sortType: (a, b) => dateTransform(a.values.created, b.values.created),
      },
      {
        Header: 'Angebotsinhalt',
        accessor: 'title',
      },
      {
        Header: 'Auftraggeber',
        accessor: 'customer',
      },
      {
        Header: 'Angebotssumme',
        accessor: 'amount',
      },
      {
        Header: 'Angedachter PGL',
        accessor: 'field',
      },
      {
        Header: 'Warscheilichkeit der Beauftragung',
        accessor: 'probability',
      },
      {
        Header: 'Ersteller',
        accessor: 'responsible',
      },
      {
        Header: 'Zuordnung Fachbereich',
        accessor: 'department',
      },
      {
        Header: 'Index',
        accessor: 'index',
      },
      {
        Header: 'Bindefrist',
        accessor: 'bindingUntil',
      },
      {
        Header: 'Bemerkung',
        accessor: 'commentCount',
      },
      {
        Header: 'NAN Summe',
        accessor: 'nan',
      },
    ],
    [],
  )

  const onCommentClick = (row: Row<TableData>) => {
    setSelectedRow(row)
    commentsToggle.on()
  }

  const appendedColumn = useMemo<Partial<ColumnInstance<TableData>>>(
    () => ({
      id: 'comments',
      Cell: CommentCell(onCommentClick),
      width: 50,
      canSort: false,
    }),
    [],
  )

  const displayMenu = (row: Row<TableData>, e: React.MouseEvent) =>
    show(e, { props: { row } })

  const onEditClick = (row?: Row<TableData>) => {
    setSelectedRow(row)
    editModalToggle.on()
  }

  const onContextEditClick = ({ props }: ItemParams<{ row: Row<TableData> }>) =>
    history.push({
      pathname: Routes.offerDetails,
      hash: props?.row.original.id,
    })

  const onContextPrintClick = () => window.print()

  const hasNextPage = useMemo(
    () =>
      !!(
        offers &&
        (offers.pager.current_page || 0) + 1 < offers.pager.total_pages
      ),
    [offers?.pager],
  )

  const loadNextPage = (startIndex: number, stopIndex: number) => {
    dispatch(
      getOffers(
        { page: stopIndex / (offers?.pager.items_per_page || 50) },
        setOffers,
      ),
    )
    return null
  }

  const _mapFormValueToOfferObj = (values: FormValues): CreateOfferRequest => ({
    comment: values.comment,
    created: getUnixTime(zonedDate(values.created)),
    customer: { id: values.customer || '' },
    liability_damage: getSelectValue(values.liabilityDamage),
    probability: getSelectValue(values.probability),
    title: values.title,
    department: getSelectValue(values.department),
    pgl: getSelectValue(values.pgl),
    project: getSelectValue(values.project),
    project_manager: getSelectValue(values.projectManager),
  })

  const _mapOfferToFormValues = (offer: Offer): InitialData => ({
    comment: offer.comment,
    created: offer.created
      ? formatDate(fromUnixTime(Number(offer.created)))
      : '',
    customer:
      typeof offer.customer === 'string'
        ? offer.customer
        : offer.customer?.title || '',
    department: offer.department || '',
    liabilityDamage: `${offer.liability_damage}` || '',
    pgl: offer.pgl || '',
    probability: `${offer.probability}`,
    number: `${offer.number}`,
    project: offer.project,
    projectManager: offer.project_manager,
    title: offer.title,
  })

  const onEditOfferSubmit = (values: FormValues) => {
    dispatch(
      updateOffer(
        {
          nid: selectedRow?.original.id,
          ..._mapFormValueToOfferObj(values),
        },
        () => notify('Angebote wurde erfolgreich bearbeitet', 'success'),
      ),
    )
    editModalToggle.off()
  }

  const onEditOfferClose = () => {
    editModalToggle.off()
    setOfferRecord(undefined)
  }

  return (
    <>
      <ContentView
        columnsData={columnsData}
        tableData={tableData}
        displayMenu={displayMenu}
        onRowDbClick={onEditClick}
        hasNextPage={hasNextPage}
        loadNextPage={loadNextPage}
        isNextPageLoading={isLoading}
        appendedColumn={appendedColumn}
      />
      <Modal
        isOpen={!!(editModalToggle.value && offerRecord)}
        onRequestClose={onEditOfferClose}
      >
        <Form
          onCancel={onEditOfferClose}
          onSubmit={onEditOfferSubmit}
          initialData={offerRecord && _mapOfferToFormValues(offerRecord)}
        />
      </Modal>
      <Modal
        isOpen={commentsToggle.value}
        onRequestClose={commentsToggle.off}
        maxWidth={percent(46, width)}
        className={styles.modal}
        withCloseButton
      >
        {selectedRow?.original && (
          <Comments
            acquisitionName={selectedRow?.original.title}
            acquisitionId={selectedRow?.original.id}
            loadComments={getOfferComments}
          />
        )}
      </Modal>
      <Menu id={MENU_ID}>
        <Item onClick={onContextEditClick}>{'Angebot Bearbeiten'}</Item>
        <Separator />
        <Item onClick={onContextPrintClick}>{'Ausdrucken'}</Item>
      </Menu>
    </>
  )
}

export default Offers
