import { BackendMenuItem, Blog, CompanyInfoItem, Error } from 'types/index'

enum ActionType {
  GET_MENU_BEGIN = '[dashboard] GET_MENU_BEGIN',
  GET_MENU_SUCCESS = '[dashboard] GET_MENU_SUCCESS',
  GET_MENU_ERROR = '[dashboard] GET_MENU_ERROR',
  GET_BLOG_WIDGET_BEGIN = '[dashboard] GET_BLOG_BEGIN',
  GET_BLOG_WIDGET_SUCCESS = '[dashboard] GET_BLOG_SUCCESS',
  GET_BLOG_WIDGET_ERROR = '[dashboard] GET_BLOG_ERROR',
  GET_COMPANY_INFORMATION_BEGIN = '[dashboard] GET_COMPANY_INFORMATION_BEGIN',
  GET_COMPANY_INFORMATION_SUCCESS = '[dashboard] GET_COMPANY_INFORMATION_SUCCESS',
  GET_COMPANY_INFORMATION_ERROR = '[dashboard] GET_COMPANY_INFORMATION_ERROR',
  COMPANY_INFORMATION_READ = '[dashboard] COMPANY_INFORMATION_READ',
  COMPANY_INFORMATION_CREATED = '[dashboard] COMPANY_INFORMATION_CREATED',
  BLOG_RECORD_DELETED = '[dashboard] BLOG_RECORD_DELETED',
}

export interface GetBlogBeginAction {
  type: ActionType.GET_BLOG_WIDGET_BEGIN
}

export interface GetBlogSuccessAction {
  type: ActionType.GET_BLOG_WIDGET_SUCCESS
  payload: Blog
}

export interface GetBlogErrorAction {
  type: ActionType.GET_BLOG_WIDGET_ERROR
  payload: Error
}

export interface GetCompanyInformationBeginAction {
  type: ActionType.GET_COMPANY_INFORMATION_BEGIN
}

export interface GetCompanyInformationSuccessAction {
  type: ActionType.GET_COMPANY_INFORMATION_SUCCESS
  payload: CompanyInfoItem[]
}

export interface GetCompanyInformationErrorAction {
  type: ActionType.GET_COMPANY_INFORMATION_ERROR
  payload: Error
}

export interface CompanyInformationReadAction {
  type: ActionType.COMPANY_INFORMATION_READ
  payload: {
    nid: number
    userId: number
  }
}

export interface CompanyInformationCreatedAction {
  type: ActionType.COMPANY_INFORMATION_CREATED
  payload: CompanyInfoItem
}

export interface GetMenuBeginAction {
  type: ActionType.GET_MENU_BEGIN
}

export interface GetMenuSuccessAction {
  type: ActionType.GET_MENU_SUCCESS
  payload: BackendMenuItem[]
}

export interface GetMenuErrorAction {
  type: ActionType.GET_MENU_ERROR
  payload: Error
}

export type Action =
  | GetBlogBeginAction
  | GetBlogSuccessAction
  | GetBlogErrorAction
  | GetCompanyInformationBeginAction
  | GetCompanyInformationSuccessAction
  | GetCompanyInformationErrorAction
  | CompanyInformationReadAction
  | CompanyInformationCreatedAction
  | GetMenuBeginAction
  | GetMenuSuccessAction
  | GetMenuErrorAction

export default ActionType
