import { mapLeftMenu } from 'services/utils'
import { BackendSettings, Error, MenuItem } from 'types/index'

import ActionType, { Action } from './types'

export interface IState {
  isLoading: boolean
  error: Error | null
  backendSettings?: BackendSettings
  menu: MenuItem[]
}

const initialState: IState = {
  isLoading: false,
  error: null,
  backendSettings: undefined,
  menu: [],
}

const reducer = (state = initialState, action: Action): IState => {
  switch (action.type) {
    case ActionType.GET_BACKEND_SETTINGS_BEGIN:
    case ActionType.GET_LEFT_MENU_ITEMS_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.GET_BACKEND_SETTINGS_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: null,
        backendSettings: action.payload,
      }
    }
    case ActionType.GET_LEFT_MENU_ITEMS_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: null,
        menu: mapLeftMenu(action.payload),
      }
    }
    case ActionType.SELECT_LEFT_MENU_ITEM: {
      return {
        ...state,
        isLoading: false,
        error: null,
        menu: action.payload,
      }
    }
    case ActionType.GET_BACKEND_SETTINGS_ERROR:
    case ActionType.GET_LEFT_MENU_ITEMS_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    default:
      return state
  }
}

export default reducer
