import { ReactComponent as Zoom } from 'assets/icons/zoom.svg'
import process from 'assets/images/process.png'
import Button from 'components/Button'
import Widget, { WidgetProps } from 'components/Widget'
import Routes from 'constants/routes'
import { useHistory } from 'react-router-dom'
import strings from 'strings'

import styles from './styles.module.scss'

type Props = WidgetProps

const ProcessWidget: React.FC<Props> = (props) => {
  const history = useHistory()

  const onZoomClick = () => {
    history.push(Routes.processesLandscape)
  }

  return (
    <Widget {...props} title={strings.processLandscape}>
      <div className={styles.process}>
        <img src={process} alt="processes" />
        <Button id="zoom" className={styles.zoom} onClick={onZoomClick}>
          <Zoom />
        </Button>
      </div>
    </Widget>
  )
}

export default ProcessWidget
