import Button from 'components/Button'
import { Document, Page, pdfjs } from 'react-pdf'
import { onLoadSuccessType } from 'types'

import shared from '../shared.module.scss'
import styles from './styles.module.scss'

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`

interface OfferDocumentProps {
  fileDocument: string | ArrayBuffer
  onDocumentLoadSuccess: onLoadSuccessType
  pageNumbers: number
}
const OfferDocument = ({
  fileDocument,
  onDocumentLoadSuccess,
  pageNumbers,
}: OfferDocumentProps) => (
  <div>
    <div className={shared.subtitle}>{'Angebotsdokument'}</div>
    <div className={styles.container}>
      <div className={styles.documentContainer}>
        {fileDocument && (
          <Document
            file={fileDocument}
            className={styles.content}
            onLoadSuccess={onDocumentLoadSuccess}
            options={{ workerSrc: '/pdf.worker.js' }}
          >
            {Array.from(new Array(pageNumbers), (el, index) => (
              <Page
                key={`page_${index + 1}`}
                className={styles.page}
                pageNumber={index + 1}
              />
            ))}
          </Document>
        )}
      </div>
      <div className={styles.buttonsContainer}>
        <Button className={styles.button}>{'Angebotsdokument löschen'}</Button>
        <div>
          <Button className={styles.button}>{'Abbrechen'}</Button>
          <Button className={styles.buttonPrimary}>
            {'Änderungen Speichern'}
          </Button>
        </div>
      </div>
    </div>
  </div>
)

export default OfferDocument
