import cn from 'classnames'
import Comments from 'common/Comments'
import Button from 'components/Button'
import LinkItem from 'components/LinkItem'
import { getOfferComments } from 'store/ives/offers/actions'
import { Offer, onLoadSuccessType } from 'types'

import styles from './styles.module.scss'
import MoreOffers from './tabs/MoreOffers'
import OfferDocument from './tabs/OfferDocument'
import OfferProcessing from './tabs/OfferProcessing'

interface ViewProps {
  selectedMenuItem: string
  navigation: string[]
  onNavClick: (navItem: string) => void
  openNewOfferForm: () => void
  offer: Offer | undefined
  offerId: string
  onDocumentLoadSuccess: onLoadSuccessType
  pageNumbers: number
  fileDocument: string | ArrayBuffer
}

export interface OfferProcessingField {
  name: string
  value: keyof Offer
}

const View = ({
  selectedMenuItem,
  navigation,
  onNavClick,
  openNewOfferForm,
  offer,
  offerId,
  onDocumentLoadSuccess,
  pageNumbers,
  fileDocument,
}: ViewProps) => (
  <div>
    <div className={styles.title}>{'Angebote'}</div>
    <div className={styles.controlsWrapper}>
      <div className={styles.controls}>
        <LinkItem>{'Einem Projekt zuordnen'}</LinkItem>
        <LinkItem onClick={openNewOfferForm}>{'Neues Angebot'}</LinkItem>
        <LinkItem>{'Angebot Indexierien'}</LinkItem>
      </div>
    </div>
    <div className={styles.navigation}>
      {navigation.map((item) => (
        <Button
          key={item}
          className={cn(
            styles.navItem,
            selectedMenuItem === item && styles.selected,
          )}
          onClick={() => onNavClick(item)}
        >
          {item}
        </Button>
      ))}
    </div>
    {selectedMenuItem === 'Angebotsbearbeitung' && offer && (
      <OfferProcessing offer={offer} />
    )}
    {selectedMenuItem === 'Angebotsdiskussion' && (
      <div>
        <div className={styles.subtitle}>{'Angebotsbearbeitung'}</div>
        <Comments
          acquisitionId={offerId}
          acquisitionName={offer?.title || ''}
          loadComments={getOfferComments}
          withoutHeader
        />
      </div>
    )}
    {selectedMenuItem === 'Weitere Angebote' && <MoreOffers />}
    {selectedMenuItem === 'Angebotsdokument' && (
      <OfferDocument
        onDocumentLoadSuccess={onDocumentLoadSuccess}
        pageNumbers={pageNumbers}
        fileDocument={fileDocument}
      />
    )}
  </div>
)

export default View
