import { ReactComponent as ArrowNext } from 'assets/icons/arrowNextOrange.svg'
import Form from 'common/Form'
import Button from 'components/Button'
import DatePicker from 'components/inputs/DatePicker'
import Select from 'components/inputs/Select'
import TextInput from 'components/inputs/TextInput'
import { FormikProps } from 'formik'
import { OptionsType } from 'react-select'
import { SelectOption } from 'types'

import { FormValues } from '.'
import styles from './styles.module.scss'

interface FormProps {
  formik: FormikProps<FormValues>
  probabilityOptions: OptionsType<SelectOption>
  departmentOptions: OptionsType<SelectOption>
  liabilityOptions: OptionsType<SelectOption>
  pglOptions: OptionsType<SelectOption>
  projectManagerOptions: OptionsType<SelectOption>
  projectOptions: OptionsType<SelectOption>
  onCancel: () => void
  onUserSelectClick: () => void
}

const FormView = ({
  formik,
  probabilityOptions,
  departmentOptions,
  liabilityOptions,
  pglOptions,
  projectManagerOptions,
  projectOptions,
  onCancel,
  onUserSelectClick,
}: FormProps) => (
  <Form
    handleSubmit={formik.handleSubmit}
    title="Angebotserstellung"
    header={
      <div className={styles.buttonContainer}>
        <Button className={styles.button}>
          {'Angebotsdaten aus Projekt übernehmen'}
        </Button>
      </div>
    }
    onCancel={onCancel}
  >
    <TextInput
      name="number"
      label="Angebots-Nr."
      variant="outlined"
      disabled
      formik={formik}
    />
    <Select
      name="project"
      label="Projekt"
      isSearchable
      options={projectOptions}
      formik={formik}
    />
    <TextInput
      name="title"
      label="Angebotsinhalt"
      variant="outlined"
      formik={formik}
    />
    <div className={styles.userInputContainer}>
      <TextInput
        name="customer"
        label="Auftraggeber"
        formik={formik}
        autoComplete="off"
      />
      <Button className={styles.userInputArrow} onClick={onUserSelectClick}>
        <ArrowNext />
      </Button>
    </div>
    <DatePicker name="created" formik={formik} placeholder="Datum" />
    <Select
      name="probability"
      label="Wahrscheinlichkeit der Beauftragung"
      isSearchable={false}
      options={probabilityOptions}
      formik={formik}
    />
    <Select
      name="liabilityDamage"
      label="Haftung Personen-/Sachschaden"
      isSearchable={false}
      options={liabilityOptions}
      formik={formik}
    />
    <Select
      name="pgl"
      label="Angedachter PGL"
      isSearchable={false}
      options={pglOptions}
      formik={formik}
    />
    <Select
      name="department"
      label="Zuordnung Fachbereich"
      isSearchable={false}
      options={departmentOptions}
      formik={formik}
    />
    <Select
      name="projectManager"
      label="Projektleiter"
      isSearchable
      options={projectManagerOptions}
      formik={formik}
    />
    <TextInput
      name="comment"
      label="Kommentar erstellen"
      multiline
      rows={15}
      maxRows={25}
      formik={formik}
    />
  </Form>
)

export default FormView
