import dateTransform from 'common/pipes/dateTime.pipe'
import Table from 'common/Table'
import { format, parse } from 'date-fns'
import { useMemo } from 'react'
import { Item, Menu, useContextMenu } from 'react-contexify'
import { CSVLink } from 'react-csv'
import { Column, Row } from 'react-table'
import { FinancialDetails, WeeklyHours } from 'types'

interface NotesModalProps {
  salaryData?: FinancialDetails[]
}
const MENU_ID = 'salary-menu-id'

type TableData = {
  // eslint-disable-next-line camelcase
  id: string
  // eslint-disable-next-line camelcase
  contract_date: string
  // eslint-disable-next-line camelcase
  christmas_bonus: string
  bonus: string
  // eslint-disable-next-line camelcase
  other_payments: string
  // eslint-disable-next-line camelcase
  hourly_model?: string
  // eslint-disable-next-line camelcase
  salary_part_type?: string
  // eslint-disable-next-line camelcase
  salary_part?: string
  salary: string
  vl?: string
  vbe: string
}

const SalaryModal = ({ salaryData }: NotesModalProps) => {
  const getVBWeeklyhours = (weekly: WeeklyHours) => {
    let sum = 0
    sum =
      (Number(weekly.monday) +
        Number(weekly.tuesday) +
        Number(weekly.wednesday) +
        Number(weekly.thursday) +
        Number(weekly.friday)) /
      40
    return sum.toString()
  }
  const tableData = useMemo<TableData[]>(
    () =>
      salaryData?.map((salary) => ({
        contract_date: format(new Date(salary.contract_date), 'dd.MM.yyyy'),
        id: salary.source_id,
        christmas_bonus: salary.christmas_bonus
          ? `${salary.christmas_bonus} €`
          : '',
        bonus: salary.bonus ? `${salary.bonus} €` : '',
        other_payments: salary.other_payments
          ? `${salary.other_payments} €`
          : '',
        salary: salary.salary ? `${salary.salary} €` : '',
        vl: salary.vl ? `${salary.vl} €` : 'Kein Daten',
        vbe: salary.weekly_hours
          ? getVBWeeklyhours(salary.weekly_hours[0])
          : 'Kein Daten',
      })) || [],
    [salaryData],
  )
  const columnsData = useMemo<Column<TableData>[]>(
    () => [
      {
        Header: 'Monat',
        accessor: 'contract_date',
        sortType: (a, b) =>
          dateTransform(a.values.contract_date, b.values.contract_date),
      },
      {
        Header: 'Gehalt',
        accessor: 'salary',
      },
      {
        Header: 'Bonuszahlungen',
        accessor: 'bonus',
      },
      {
        Header: 'Weihnachtsgeld',
        accessor: 'christmas_bonus',
      },
      {
        Header: 'Sonstiges',
        accessor: 'other_payments',
      },
      {
        Header: 'Verm.Leistungen',
        accessor: 'vl',
      },
      {
        Header: 'VBE',
        accessor: 'vbe',
      },
    ],
    [],
  )
  const headers = columnsData.map((el) => ({
    label: el.Header,
    key: el.accessor,
  }))
  const { show } = useContextMenu({
    id: MENU_ID,
  })
  const displayMenu = (row: Row<any>, e: React.MouseEvent) =>
    show(e, { props: { row } })
  return (
    <>
      <Table
        columnsData={columnsData}
        onContextMenu={displayMenu}
        tableData={
          tableData.sort(
            (a, b) =>
              parse(b.contract_date, 'dd.MM.yyyy', new Date()).valueOf() -
              parse(a.contract_date, 'dd.MM.yyyy', new Date()).valueOf(),
          ) as any
        }
      />
      <Menu id={MENU_ID}>
        <Item>
          <CSVLink
            filename={`GehatltsHistorien${new Date()}.csv`}
            data={tableData}
            headers={[...headers] as any}
          >
            {'Als excel Tabelle exportieren'}
          </CSVLink>
        </Item>
      </Menu>
    </>
  )
}

export default SalaryModal
