import { ReactComponent as ArrowOrange } from 'assets/icons/arrowNextOrange.svg'
import { ReactComponent as Cancel } from 'assets/icons/cancelThin.svg'
import { ReactComponent as Filter } from 'assets/icons/filter.svg'
import classNames from 'classnames'
import BlogItemBig from 'components/BlogItemBig'
import Button from 'components/Button'
import Loader from 'components/Loader'
import { MenuItem } from 'constants/dashboard'
import Widgets from 'constants/widgets'
import { UseToggle } from 'hooks/useToggle'
import { nanoid } from 'nanoid'
import { useEffect, useMemo, useState } from 'react'
import { Item, ItemParams, Menu, useContextMenu } from 'react-contexify'
import InfiniteScroll from 'react-infinite-scroller'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { getBlog, getFullItem } from 'store/blog/actions'
import { addWidget } from 'store/user/actions'
import { BlogCategory, BlogItem, ReduxState } from 'types'

import styles from '../styles.module.scss'
import Card from './card'

const MENU_ID = 'dashboard-context-menu'

interface ContextMenuProps {
  subCategory: BlogCategory
}

interface Props {
  page: number
  selectedItem: BlogCategory
  selectedSubItem: BlogCategory | null
  menu: BlogCategory[]
  scrollableRef: React.RefObject<HTMLDivElement>
  dashboardItemToggle: UseToggle
  openCompanyInfo(): void
  onEditPost?: ((post: BlogItem) => void) | null
  onDeletePost?: ((post: BlogItem) => void) | null
  setPage: React.Dispatch<React.SetStateAction<number>>
  setSelectedItem: React.Dispatch<React.SetStateAction<BlogCategory>>
  setSelectedSubItem: React.Dispatch<React.SetStateAction<BlogCategory | null>>
}

const GridLayout: React.FC<Props> = ({
  page,
  selectedSubItem,
  selectedItem,
  menu,
  scrollableRef,
  dashboardItemToggle,
  onEditPost,
  onDeletePost,
  openCompanyInfo,
  setPage,
  setSelectedItem,
  setSelectedSubItem,
}) => {
  const [showFilter, setFilter] = useState(true)
  const [highlighted, setHighlighted] = useState<BlogCategory[] | null>(null)

  const categoryId = Number(
    new URLSearchParams(window.location.search).get('categoryId'),
  )

  const {
    selectedItem: selectedArticle,
    isLoading,
    selectedItemLoading,
    blog,
  } = useSelector((state: ReduxState) => state.blog)

  const history = useHistory()

  const { show } = useContextMenu({ id: MENU_ID })

  const dispatch = useDispatch()

  useEffect(() => {
    if (!menu || !categoryId) return

    menu.find((item) =>
      item.below?.some((subItem) => {
        if (subItem.tid === categoryId) {
          setSelectedItem(item)
          setSelectedSubItem(subItem)
          history.push({ search: '' })
          return true
        }

        return false
      }),
    )
  }, [menu])

  useEffect(() => {
    selectedSubItem && dispatch(getBlog(0, selectedSubItem?.tid))
  }, [selectedSubItem])

  useEffect(() => {
    !categoryId && dispatch(getBlog(page, selectedSubItem?.tid))
  }, [page])

  const data = useMemo(
    () =>
      blog?.rows.map((item) => (
        <Card
          item={item}
          key={item.nid}
          categories={menu}
          selectedItem={selectedItem}
          onClickDetails={() => {
            dashboardItemToggle.on()
            dispatch(getFullItem(item.nid))
          }}
          setHighlighted={setHighlighted}
        />
      )),
    [blog],
  )

  const hasMore = !blog || page + 1 < blog.pager.total_pages

  return (
    <>
      <nav className={styles.sticky}>
        <div className={styles.navigation}>
          {showFilter ? (
            <div className={styles.wrapNav}>
              {menu?.map((item) =>
                item.link ? (
                  <a
                    key={item.title}
                    href={item.link}
                    target="_blank"
                    rel="noopener noreferrer"
                    className={styles.navigationItem}
                  >
                    {item.title}
                  </a>
                ) : (
                  <button
                    key={item.title}
                    type="button"
                    onClick={() => {
                      setSelectedItem(item)
                      setSelectedSubItem(item.below?.[0] || null)
                      dashboardItemToggle.off()
                      item.title === MenuItem.companyInfo && openCompanyInfo()
                    }}
                    className={classNames(styles.navigationItem, {
                      [styles.selected]: selectedItem?.title === item.title,
                      [styles.active]: highlighted?.includes(item),
                      [styles.orange]: item.title === MenuItem.competenceAreas,
                      [styles.green]: item.title === MenuItem.workingGroups,
                      [styles.blue]:
                        item.title === MenuItem.leipzigProjectGroups,
                      [styles.gray]: ([
                        MenuItem.companyInfo,
                        MenuItem.spGroupContent,
                        MenuItem.leipzigContent,
                      ] as string[]).includes(item.title),
                    })}
                  >
                    {item.title}
                  </button>
                ),
              )}
            </div>
          ) : null}
          <div className={styles.filterBtn}>
            <Filter />
            <button
              type="button"
              className={showFilter ? styles.filterActive : styles.filter}
              onClick={() => setFilter((s) => !s)}
            >
              {'Filter\r'}
            </button>
          </div>
        </div>
        {selectedItem?.below && showFilter ? (
          <div className={styles.navigation}>
            {selectedItem.below.map((item) => (
              <button
                key={item.title}
                type="button"
                onClick={() => {
                  setSelectedSubItem(item)
                  dashboardItemToggle.off()
                }}
                onContextMenu={(event) =>
                  show(event, {
                    props: {
                      subCategory: item,
                    },
                  })
                }
                className={classNames(
                  styles.navigationItem,
                  selectedSubItem?.title === item.title && styles.selected,
                )}
              >
                {item.title}
              </button>
            ))}
          </div>
        ) : null}
      </nav>
      <div
        className={classNames(styles.wrap, {
          [styles.withArticle]: dashboardItemToggle.value,
        })}
        role="grid"
      >
        <InfiniteScroll
          className={classNames(styles.dashboard)}
          hasMore={hasMore}
          getScrollParent={() => scrollableRef.current}
          loader={<Loader key="loader" />}
          loadMore={() => !isLoading && hasMore && setPage(page + 1)}
          pageStart={0}
          useWindow={false}
          initialLoad={false}
        >
          {([MenuItem.all, MenuItem.companyInfo] as string[]).includes(
            selectedItem?.title,
          ) && (
            <div
              className={classNames(
                styles.news,
                styles.mainNews,
                styles.withBg,
              )}
            >
              <button
                type="button"
                className={styles.more}
                onClick={openCompanyInfo}
              >
                {'Unternehmensinformationen lesen'}
                <ArrowOrange />
              </button>
            </div>
          )}
          {selectedItem?.title !== MenuItem.companyInfo && data}
        </InfiniteScroll>
        {dashboardItemToggle.value && (
          <div
            style={{
              // 95 is height of navbar with 2 lines, 55 is height of one with 1 line
              top: selectedItem?.below ? 95 : 55,
              // 74 is height of header and I also added 10px for bottom margin
              maxHeight: `calc(100vh - ${
                84 + (selectedItem?.below ? 95 : 55)
              }px)`,
            }}
            className={classNames('blogItemBig', styles.bigItemContainer)}
          >
            <Button className={styles.cancel} onClick={dashboardItemToggle.off}>
              <Cancel />
            </Button>

            {selectedItemLoading || !selectedArticle ? (
              <Loader className={styles.loader} />
            ) : (
              <BlogItemBig
                item={selectedArticle}
                onEditPost={onEditPost}
                onDeletePost={onDeletePost}
              />
            )}
          </div>
        )}
      </div>
      <Menu id={MENU_ID}>
        <Item
          onClick={({ props }: ItemParams<ContextMenuProps>) => {
            props &&
              dispatch(
                addWidget({
                  data: {
                    categoryId: props.subCategory.tid,
                  },
                  title: props.subCategory.title,
                  type: Widgets.newsPreview,
                  layout: {
                    i: nanoid(),
                    x: 0,
                    y: Infinity,
                    w: 6,
                    h: 4,
                    minW: 4,
                    minH: 4,
                  },
                }),
              )
          }}
        >
          {'Auf Mein S&P Hinzufügen'}
        </Item>
      </Menu>
    </>
  )
}

export default GridLayout
