import { AxiosResponse } from 'axios'
import api from 'services/api'
import notify from 'services/notify'
import { action } from 'store/action'
import {
  Acquisition,
  Acquisitions,
  AcquisitionState,
  AppThunkAsync,
  Comments,
  Error,
} from 'types/index'

import ActionType from './types'

export const getAcquisitionRecord = (
  id: string | number,
  onSuccess?: (acquisitions: Acquisition[]) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_ACQUISITIONS_BEGIN))
  try {
    const acquisition: AxiosResponse<Acquisition[]> = await api.get(
      `processes/acquisition/${id}?_format=json`,
    )
    dispatch(action(ActionType.GET_ACQUISITIONS_SUCCESS, acquisition.data))
    onSuccess?.(acquisition.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_ACQUISITIONS_ERROR, error))
    onError?.(error)
  }
}

export const getAcquisitions = (
  {
    page = 0,
    state,
    query,
  }: { state?: AcquisitionState; page?: number; query?: string },
  onSuccess?: (acquisitions: Acquisitions) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_ACQUISITIONS_BEGIN))
  const stateParam = state ? `/${state}` : ''
  const queryParam = query ? `/${query}` : ''

  try {
    const acquisition: AxiosResponse<Acquisitions> = await api.get(
      `processes/acquisitions${stateParam}${queryParam}?_format=json&page=${page}`,
    )
    dispatch(action(ActionType.GET_ACQUISITIONS_SUCCESS, acquisition.data))
    onSuccess?.(acquisition.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_ACQUISITIONS_ERROR, error))
    onError?.(error)
  }
}

export const updateAcquisition = (
  acquisition: Partial<
    Omit<Acquisition, 'comment_count'> & { comment: string }
  >,
  onSuccess?: () => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.UPDATE_ACQUISITION_BEGIN))
  try {
    await api.patch(`processes/acquisitions/${acquisition.nid}?_format=json`, {
      title: acquisition.title,
      created: acquisition.created,
      customer: acquisition.customer?.id,
      state: acquisition.state,
      responsible: acquisition.responsible?.map((item) => item.id),
      customer_responsible: acquisition.customer_responsible,
      comment: acquisition.comment,
    })
    dispatch(action(ActionType.UPDATE_ACQUISITION_SUCCESS))
    onSuccess?.()
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.UPDATE_ACQUISITION_ERROR, error))
    onError?.(error)
  }
}

export const createAcquisition = (
  acquisition: Omit<Acquisition, 'nid' | 'comment_count'> & { comment: string },
  onSuccess?: () => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.CREATE_ACQUISITION_BEGIN))
  try {
    await api.post('processes/acquisitions?_format=json', {
      title: acquisition.title,
      created: acquisition.created,
      customer: acquisition.customer?.id,
      state: acquisition.state,
      responsible: acquisition.responsible?.map((item) => item.id),
      customer_responsible: acquisition.customer_responsible,
      comment: acquisition.comment,
    })
    dispatch(action(ActionType.CREATE_ACQUISITION_SUCCESS, acquisition))
    onSuccess?.()
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.CREATE_ACQUISITION_ERROR, error))
    onError?.(error)
  }
}

export const deleteAcquisitionRecord = (
  acquisitionId: string,
): AppThunkAsync => async (dispatch) => {
  try {
    await api.delete(`processes/acquisitions/${acquisitionId}?_format=json`)
    dispatch(action(ActionType.ACQUISITION_RECORD_DELETED, acquisitionId))
  } catch (error) {
    notify(error)
  }
}

export const getAcquisitionComments = (
  acquisitionId: string | number,
  commentId: string | number | null = null,
  onSuccess?: (comments: Comments) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_COMMENTS_BEGIN))
  try {
    const comments: AxiosResponse<Comments> = await api.get(
      `processes/acquisitions/${acquisitionId}/comments/${commentId}?_format=json`,
    )
    dispatch(action(ActionType.GET_COMMENTS_SUCCESS, comments.data))
    onSuccess?.(comments.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_COMMENTS_ERROR, error))
    onError?.(error)
  }
}

export const createComment = (
  data: {
    acquisitionId: string | number
    commentRootId: string | number | null
    comment: string
  },
  onSuccess?: () => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  const { acquisitionId, commentRootId, comment } = data
  dispatch(action(ActionType.CREATE_COMMENT_BEGIN))
  try {
    await api.post(
      `processes/${acquisitionId}/comments/${commentRootId}?_format=json`,
      {
        comment,
      },
    )
    dispatch(action(ActionType.CREATE_COMMENT_SUCCESS))
    onSuccess?.()
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.CREATE_COMMENT_ERROR, error))
    onError?.(error)
  }
}

