import { UserAccess } from 'constants/index'
import { fromUnixTime } from 'date-fns'
import { useSelector } from 'react-redux'
import { formatDate } from 'services/date'
import { EmployeeHolidays, ReduxState } from 'types'

import { Absentees } from '..'
import styles from './styles.module.scss'

interface Props {
  onCancel?: () => void
  compareDate: (holidayArray: EmployeeHolidays[] | null) => void
  absentees: Absentees[] | null
}

const AbsenceView: React.FC<Props> = ({ absentees }) => {
  const { user } = useSelector((state: ReduxState) => state.user)
  const seeReasons = user?.access?.[UserAccess.createEmployeeHolidayForAny]
  const TableRow = () => (
    <div>
      {absentees?.map((absentee) => (
        <div key={absentee.id}>
          <div className={styles.absenceList}>
            <div className={styles.content}>
              <span className={styles.dataLabel}>{'Name'}</span>
              <span className={styles.dataValue}>{absentee.name}</span>
            </div>
            <div className={styles.content}>
              <span className={styles.dataLabel}>{'Urlaubsende'}</span>
              <span className={styles.dataValue}>
                {formatDate(fromUnixTime(absentee.to))}
              </span>
            </div>
            <div className={styles.content}>
              <span className={styles.dataLabel}>{'Vertretung'}</span>
              <span className={styles.dataValue}>
                {absentee.substitutes || 'Keine Vertretung'}
              </span>
            </div>
            <div className={styles.content}>
              <span className={styles.dataLabel}>{'Krankheitstage'}</span>
              <span className={styles.dataValue}>
                {absentee.sickDays || '0'}
              </span>
            </div>
            <div className={styles.content}>
              <span className={styles.dataLabel}>{'Grund'}</span>
              <span className={styles.dataValue}>
                {seeReasons ? absentee.reason : 'Abwesend'}
              </span>
            </div>
          </div>
        </div>
      ))}
    </div>
  )
  const EmptyTableResults = () => (
    <div className={styles.noAbsence}>{'Keine derzeitigen Abwesenheiten'}</div>
  )

  return (
    <>
      <h2 className={styles.absenceHeading}>
        {'Heute abwesend '}
        {formatDate(new Date(), 'dd.MM.yyyy')}
      </h2>
      <div className={styles.absenceWrap}>
        <div className={styles.absenceView}>
          {
            // check if there are any absentees else show empty table
            absentees && absentees.length > 0 ? (
              <TableRow />
            ) : (
              <EmptyTableResults />
            )
          }
        </div>
      </div>
    </>
  )
}

export default AbsenceView
