import { Button } from '@mui/material'
import { ReactComponent as Cancel } from 'assets/icons/cancelThin.svg'
import classNames from 'classnames'
import BlogItemBig from 'components/BlogItemBig'
import Loader from 'components/Loader'
import Widget from 'components/Widget'
import NewsWidgetItem from 'components/widgetItems/NewsItem'
import useToggle from 'hooks/useToggle'
import { FC, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getBlog, getFullItem } from 'store/blog/actions'
import strings from 'strings'
import { BlogCategory, ReduxState } from 'types'

import styles from './styles.module.scss'

interface NewsWidgetProps {
  menu: BlogCategory[]
  groupId?: string
}
const NewsWidget: FC<NewsWidgetProps> = ({ groupId, menu }) => {
  const dispatch = useDispatch()
  const { blog, isLoading } = useSelector((state: ReduxState) => state.blog)
  const dashboardItemToggle = useToggle()
  const [, setHighlighted] = useState<BlogCategory[] | null>(null)

  const { selectedItem: selectedArticle, selectedItemLoading } = useSelector(
    (state: ReduxState) => state.blog,
  )

  useEffect(() => {
    dispatch(getBlog(0, groupId))
  }, [])

  return (
    <Widget title={strings.newsblog} containerClassName={styles.newsWidget}>
      <div
        className={classNames(styles.wrap, {
          [styles.withArticle]: dashboardItemToggle.value,
        })}
        role="grid"
      >
        <div className={styles.itemsContainer}>
          {!isLoading ? (
            blog?.rows.map((item) => (
              <NewsWidgetItem
                item={item}
                key={item.nid}
                categories={menu}
                onClickDetails={() => {
                  dashboardItemToggle.on()
                  dispatch(getFullItem(item.nid))
                }}
                setHighlighted={setHighlighted}
              />
            ))
          ) : (
            <Loader />
          )}
        </div>
        {dashboardItemToggle.value && (
          <div className={classNames('blogItemBig', styles.bigItemContainer)}>
            <Button className={styles.cancel} onClick={dashboardItemToggle.off}>
              <Cancel />
            </Button>

            {selectedItemLoading || !selectedArticle ? (
              <Loader />
            ) : (
              <BlogItemBig item={selectedArticle} />
            )}
          </div>
        )}
      </div>
    </Widget>
  )
}
export default NewsWidget
