import { TextField } from '@mui/material'
import Form from 'common/Form'
import { useFormik } from 'formik'
import React from 'react'
import { Message, RejectProps } from 'types'
import * as Yup from 'yup'

import styles from './styles.module.scss'

interface Props {
  userData: Message | null
  onCancel?: () => void
  onReject?: ({ values, userData }: RejectProps) => void
}

const CancelForm: React.FC<Props> = ({
  userData,
  onCancel = () => null,
  onReject = () => null,
}) => {
  const validationSchema = Yup.object().shape({
    reason: Yup.string()
      .min(10, 'Too Short!')
      .max(100, 'Too Long!')
      .required('Required'),
  })

  const formik = useFormik({
    initialValues: {
      reason: '',
    },
    validationSchema,
    onSubmit: (values, { resetForm }) => {
      onReject({ values, userData })
      resetForm()
    },
  })

  return (
    <Form
      handleSubmit={formik.handleSubmit}
      title="Grund der ablehnung"
      onCancel={onCancel}
    >
      <span className={styles.info}>
        {'Die Begründung muss mindestens 10 Charakter lang sein.'}
      </span>
      <TextField
        className={styles.textField}
        error={formik.touched.reason && Boolean(formik.errors.reason)}
        id="reason"
        name="reason"
        label="Vertretung"
        multiline
        rows={7}
        value={formik.values.reason}
        onChange={formik.handleChange}
      />
    </Form>
  )
}

export default CancelForm
