import { Error } from 'types/index'

import ActionType, { Action } from './types'

export interface IState {
  isLoading: boolean
  error: Error | null
}

const initialState: IState = {
  isLoading: false,
  error: null,
}

const reducer = (state = initialState, action: Action): IState => {
  switch (action.type) {
    case ActionType.CREATE_COMMENT_BEGIN:
    case ActionType.UPDATE_COMMENT_BEGIN:
    case ActionType.GET_COMMENTS_BEGIN:
    case ActionType.CREATE_ACQUISITION_BEGIN:
    case ActionType.UPDATE_ACQUISITION_BEGIN:
    case ActionType.GET_ACQUISITION_RECORD_BEGIN:
    case ActionType.GET_ACQUISITIONS_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.CREATE_COMMENT_SUCCESS:
    case ActionType.UPDATE_COMMENT_SUCCESS:
    case ActionType.GET_COMMENTS_SUCCESS:
    case ActionType.GET_ACQUISITIONS_SUCCESS:
    case ActionType.GET_ACQUISITION_RECORD_SUCCESS:
    case ActionType.CREATE_ACQUISITION_SUCCESS:
    case ActionType.ACQUISITION_RECORD_DELETED: {
      return {
        ...state,
        isLoading: false,
        error: '',
      }
    }
    case ActionType.UPDATE_ACQUISITION_SUCCESS: {
      return {
        ...state,
        isLoading: true,
        error: '',
      }
    }
    case ActionType.CREATE_COMMENT_ERROR:
    case ActionType.UPDATE_COMMENT_ERROR:
    case ActionType.GET_COMMENTS_ERROR:
    case ActionType.GET_ACQUISITION_RECORD_ERROR:
    case ActionType.CREATE_ACQUISITION_ERROR:
    case ActionType.UPDATE_ACQUISITION_ERROR:
    case ActionType.GET_ACQUISITIONS_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    default:
      return state
  }
}

export default reducer
