import cn from 'classnames'
import Button from 'components/Button'
import useToggle from 'hooks/useToggle'
import { Link } from 'react-router-dom'
import { MenuItem } from 'types'

import styles from './styles.module.scss'

interface MenuSubItemProps {
  item: MenuItem
  onClick: () => void
}

const MenuSubItem = ({ item, onClick }: MenuSubItemProps) => {
  const { title, below, selected } = item
  const expanded = useToggle()

  return (
    <div className={styles.subItem}>
      {below ? (
        <Button
          onClick={expanded.toggle}
          className={cn(styles.title, expanded.value && styles.expanded)}
        >
          {item.title}
        </Button>
      ) : (
        <Link
          to={item.link}
          id={title}
          className={cn(styles.title, selected && styles.selected)}
        >
          {title}
        </Link>
      )}

      {expanded.value &&
        below?.map((subItem) => (
          <MenuSubItem key={subItem.title} item={subItem} onClick={onClick} />
        ))}
    </div>
  )
}
export default MenuSubItem
