import { useCallback } from 'react'
import { FixedSizeList as List } from 'react-window'
import InfiniteLoader from 'react-window-infinite-loader'

interface InfinityScrollProps {
  hasNextPage: boolean
  isNextPageLoading: boolean
  itemsLength: number
  loadNextPage: (
    startIndex: number,
    stopIndex: number,
  ) => Promise<unknown> | null
  renderItem: ({ index, style }: { index: number; style: any }) => JSX.Element
  height?: number
}

const InfinityScroll = ({
  hasNextPage,
  isNextPageLoading,
  // Array of items loaded so far.
  itemsLength,
  loadNextPage,
  renderItem,
  height,
}: InfinityScrollProps) => {
  // If there are more items to be loaded then add an extra row to hold a loading indicator.
  const itemCount = hasNextPage ? itemsLength + 1 : itemsLength

  // Only load 1 page of items at a time.
  // Pass an empty callback to InfiniteLoader in case it asks us to load more than once.
  const loadMoreItems: (
    startIndex: number,
    stopIndex: number,
  ) => Promise<unknown> | null = isNextPageLoading ? () => null : loadNextPage

  // Every row is loaded except for our loading indicator row.
  const isItemLoaded = (index: number) => !hasNextPage || index < itemsLength

  const Item = useCallback(
    ({ index, style }: { index: number; style: any }) => {
      let content
      if (!isItemLoaded(index)) {
        content = <div style={style}>{'Loading...'}</div>
      } else {
        content = renderItem({ index, style })
      }
      return content
    },
    [renderItem],
  )

  return (
    <InfiniteLoader
      isItemLoaded={isItemLoaded}
      itemCount={itemCount}
      loadMoreItems={loadMoreItems}
      minimumBatchSize={1}
    >
      {({ onItemsRendered, ref }) => (
        <List
          className="List"
          itemCount={itemCount}
          onItemsRendered={onItemsRendered}
          ref={ref}
          height={height || itemsLength * 32}
          itemSize={32}
          width="100%"
        >
          {Item}
        </List>
      )}
    </InfiniteLoader>
  )
}

export default InfinityScroll
