import cn from 'classnames'
import dateTransform from 'common/pipes/dateTime.pipe'
import { format, parse } from 'date-fns'
import { useMemo } from 'react'
import { useSelector } from 'react-redux'
import { Column } from 'react-table'
// import { format } from 'date-fns'
import { Employee, ReduxState, UserRole, WeeklyHours } from 'types'

import styles from '../styles.module.scss'

interface EmployeeData {
  currentRecord?: Employee
}
type TableData = {
  // eslint-disable-next-line camelcase
  id: string
  // eslint-disable-next-line camelcase
  contract_date: string
  // eslint-disable-next-line camelcase
  christmas_bonus: string
  bonus: string
  // eslint-disable-next-line camelcase
  other_payments: string
  // eslint-disable-next-line camelcase
  hourly_model?: string
  // eslint-disable-next-line camelcase
  salary_part_type?: string
  // eslint-disable-next-line camelcase
  salary_part?: string
  salary: string
  vl?: string
  vbe: string
}

type BonusTableData = {
  date: string
  type: string
  amount: string
}
const SalaryPrintTemplate = ({ currentRecord }: EmployeeData) => {
  const currentProjectGroup = useSelector(({ user }: ReduxState) => user.user)
    ?.roles
  const getVBWeeklyhours = (weekly: WeeklyHours) => {
    let sum = 0
    sum =
      (Number(weekly.monday) +
        Number(weekly.tuesday) +
        Number(weekly.wednesday) +
        Number(weekly.thursday) +
        Number(weekly.friday)) /
      40
    return sum.toString()
  }
  const tableData = useMemo<TableData[]>(
    () =>
      currentRecord?.financial_details?.map((salary) => ({
        contract_date: format(new Date(salary.contract_date), 'dd.MM.yyyy'),
        id: salary.source_id,
        christmas_bonus: salary.christmas_bonus
          ? `${salary.christmas_bonus} €`
          : '',
        bonus: salary.bonus ? `${salary.bonus} €` : '',
        other_payments: salary.other_payments
          ? `${salary.other_payments} €`
          : '',
        salary: salary.salary ? `${salary.salary} €` : '',
        vl: salary.vl ? `${salary.vl} €` : 'Kein Daten',
        vbe: salary.weekly_hours
          ? getVBWeeklyhours(salary.weekly_hours[0])
          : 'Kein Daten',
      })) || [],
    [currentRecord?.financial_details],
  )
  const columnsData = useMemo<Column<TableData>[]>(
    () => [
      {
        Header: 'Datum',
        accessor: 'contract_date',
        sortType: (a, b) =>
          dateTransform(a.values.contract_date, b.values.contract_date),
      },
      {
        Header: 'Gehalt',
        accessor: 'salary',
      },
      {
        Header: 'Bonuszahlungen',
        accessor: 'bonus',
      },
      {
        Header: 'Weihnachtsgeld',
        accessor: 'christmas_bonus',
      },
      {
        Header: 'Sonstiges',
        accessor: 'other_payments',
      },
      {
        Header: 'Verm.Leistungen',
        accessor: 'vl',
      },
      {
        Header: 'VBE',
        accessor: 'vbe',
      },
    ],
    [],
  )
  const tableBonusData = useMemo<BonusTableData[]>(
    () =>
      currentRecord?.allowences?.map((salary) => ({
        type: salary.type.toString(),
        amount: `${salary.amount} €`,
        date: salary.date.toString(),
      })) || [],
    [currentRecord?.allowences],
  )
  const columnsBonusData = useMemo<Column<BonusTableData>[]>(
    () => [
      {
        Header: 'Datum',
        accessor: 'date',
        sortType: (a, b) =>
          dateTransform(a.values.contract_date, b.values.contract_date),
      },
      {
        Header: 'Art ',
        accessor: 'type',
      },
      {
        Header: 'Wert',
        accessor: 'amount',
      },
    ],
    [],
  )
  return (
    <div className={styles.detailMainWrap}>
      <div className={cn(styles.detailMain, styles.contract)}>
        <div className={cn(styles.detailHeader, styles.contract)}>
          <h2>{'Gehaltsdaten'}</h2>
        </div>
      </div>
      <div className={styles.detailMainInner}>
        <div className={styles.dataBody}>
          <h4 className={styles.fieldTitle}>{'Detail Bonuszahlungen'}</h4>
          <div className={styles.dataGrid}>
            <table className={styles.custom_bonus_table}>
              <thead>
                <tr className={styles.table_row}>
                  {columnsBonusData.map((column, index) => (
                    <th className={styles.table_head} key={index}>
                      {column.Header}
                    </th>
                  ))}
                </tr>
              </thead>
              <tbody className={styles.table_body}>
                {tableBonusData.map((data, index) => (
                  <tr key={index} className={styles.table_row}>
                    {columnsBonusData.map((column, index) => (
                      <td className={styles.table_data} key={index}>
                        {data[column.accessor]}
                      </td>
                    ))}
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
        <div className={styles.dataBody}>
          <h4 className={styles.fieldTitle}>{'Gehalts Entwicklungen'}</h4>
          <div className={styles.dataGrid}>
            <div className={styles.salaryTable}>
              <table className={styles.custom_table}>
                <thead>
                  <tr className={styles.table_row}>
                    {columnsData.map((column, index) => (
                      <th className={styles.table_head} key={index}>
                        {column.Header}
                      </th>
                    ))}
                  </tr>
                </thead>
                <tbody className={styles.table_body}>
                  {tableData
                    .sort(
                      (a, b) =>
                        parse(
                          b.contract_date,
                          'dd.MM.yyyy',
                          new Date(),
                        ).valueOf() -
                        parse(
                          a.contract_date,
                          'dd.MM.yyyy',
                          new Date(),
                        ).valueOf(),
                    )
                    .map((data, index) => (
                      <tr key={index} className={styles.table_row}>
                        {columnsData.map((column, index) => (
                          <td className={styles.table_data} key={index}>
                            {data[column.accessor]}
                          </td>
                        ))}
                      </tr>
                    ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>

        <div className={styles.dataBody}>
          <h4 className={styles.fieldTitle}>{'Letzte Vertragsnotizen'}</h4>
          <div className={styles.dataGrid}>
            <span className={styles.dataValue}>
              {currentProjectGroup?.includes(UserRole.HumanResource) &&
                currentRecord?.notes_contract?.flatMap((note, index) => (
                  <div className={styles.dateIncludedField} key={index}>
                    <span className={styles.dateLabel}>
                      {note.created &&
                        note.created.substr(
                          note.created.indexOf(', ') + 1,
                          note.created.indexOf(' - ') - 3,
                        )}
                    </span>
                    <br />
                    <span className={styles.dataValue}>{note.note}</span>
                  </div>
                ))}
            </span>
          </div>
        </div>
      </div>
    </div>
  )
}
export default SalaryPrintTemplate
