import { ReactComponent as Profile } from 'assets/icons/profile.svg'
import cn from 'classnames'
import Button from 'components/Button'
import TextInput from 'components/inputs/TextInput'
import { ChangeEvent, useState } from 'react'
import { Comment } from 'types/index'

import styles from './styles.module.scss'

interface CommentEditorProps {
  editedComment: Comment | undefined
  className?: string
  onSubmit: (comment: string) => void
  onCancel: () => void
}

const CommentEditor = ({
  editedComment,
  className,
  onSubmit,
  onCancel,
}: CommentEditorProps) => {
  const [value, setValue] = useState('')

  const onChange = (e: ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
    setValue(e.target.value)

  const onSubmitClick = () => onSubmit(value)

  return (
    <div className={cn(styles.commentEditor, className)}>
      <div className={styles.avatar}>
        {editedComment?.author_picture ? (
          <img src={editedComment.author_picture} alt="" />
        ) : (
          <Profile />
        )}
      </div>
      <div className={styles.textareaWrapper}>
        <TextInput
          name="comment"
          label="Kommentar hinzufügen"
          multiline
          rows={5}
          maxRows={5}
          className={styles.textarea}
          value={value}
          onChange={onChange}
        />
      </div>
      <div className={styles.buttonsWrapper}>
        <Button className={styles.blueButton} onClick={onCancel}>
          {'Abbrechen'}
        </Button>
        <Button className={styles.orangeButton} onClick={onSubmitClick}>
          {'OK'}
        </Button>
      </div>
    </div>
  )
}

export default CommentEditor
