import { ReactComponent as ArrowNext } from 'assets/icons/arrowNext.svg'
import { ReactComponent as СancelThin } from 'assets/icons/cancelThin.svg'
import cn from 'classnames'
import Button from 'components/Button'
import { useEffect, useRef, useState } from 'react'
import { Link } from 'react-router-dom'
import { MenuItem } from 'types'

import styles from './styles.module.scss'

interface Props {
  item: MenuItem
  activeMenuItem?: string
  setActiveMenuItem: (val: string) => void
}

const SmallMenuSubItem: React.FC<Props> = ({
  item: { key, below, title, link, attributes, external },
  activeMenuItem,
  setActiveMenuItem,
}) => {
  const isActive = key === activeMenuItem
  const subItemsContainer = useRef<HTMLDivElement>(null)
  const [activeMenuSubItem, setActiveMenuSubItem] = useState<string>('')

  useEffect(
    () => () => {
      setActiveMenuSubItem('')
    },
    [activeMenuItem],
  )

  const getButtonElement = () => {
    if (below) {
      return (
        <Button
          onClick={() => setActiveMenuItem(isActive ? '' : key)}
          className={cn(
            styles.button,
            isActive && styles.selected,
            attributes?.class?.includes('highlight') && styles.highlighted,
          )}
        >
          {title}
          <ArrowNext />
        </Button>
      )
    }

    if (external) {
      return (
        <a
          href={link}
          target="_blank"
          rel="noopener noreferrer"
          className={cn(
            styles.button,
            attributes?.class?.includes('highlight') && styles.highlighted,
          )}
        >
          {title}
        </a>
      )
    }

    return (
      <Link
        to={link}
        className={cn(
          styles.button,
          attributes?.class?.includes('highlight') && styles.highlighted,
        )}
      >
        {title}
      </Link>
    )
  }

  return (
    <div ref={subItemsContainer}>
      {getButtonElement()}
      {isActive && (
        <div className={styles.subItemsContainer}>
          <button
            type="button"
            className={styles.closeButton}
            onClick={() => setActiveMenuItem('')}
          >
            <СancelThin />
          </button>
          {below?.map((subItem) => (
            <SmallMenuSubItem
              key={subItem.title}
              item={subItem}
              activeMenuItem={activeMenuSubItem}
              setActiveMenuItem={setActiveMenuSubItem}
            />
          ))}
        </div>
      )}
    </div>
  )
}

export default SmallMenuSubItem
