import LinkItem from 'components/LinkItem'
import GlobalSearch from 'components/tableComponents/GlobalSearch'

import OfferTable from './OfferTable'
import styles from './styles.module.scss'

interface ContentViewProps {
  openNewOfferForm: () => void
  goToEvaluation: () => void
  globalFilter: string | undefined
  setGlobalFilter: (filterValue: string | undefined) => void
}

const ContentView = ({
  openNewOfferForm,
  goToEvaluation,
  globalFilter,
  setGlobalFilter,
}: ContentViewProps) => (
  <div>
    <div className={styles.title}>{'Angebote'}</div>
    <div className={styles.controls}>
      <div className={styles.navigation}>
        <LinkItem onClick={openNewOfferForm}>{'Neues Angebot'}</LinkItem>
        <LinkItem onClick={goToEvaluation}>{'Auswertung'}</LinkItem>
      </div>
    </div>
    <div className={styles.searchAndTableWrapper}>
      <GlobalSearch
        globalFilter={globalFilter}
        setGlobalFilter={setGlobalFilter}
        containerClassName={styles.search}
      />
      <OfferTable query={globalFilter} />
    </div>
  </div>
)

export default ContentView
