import Avatar from '@material-ui/core/Avatar'
import Button from '@material-ui/core/Button'
import CssBaseline from '@material-ui/core/CssBaseline'
import Link from '@material-ui/core/Link'
import { makeStyles } from '@material-ui/core/styles'
import Box from '@mui/material/Box'
import Container from '@mui/material/Container'
import Grid from '@mui/material/Grid'
import Typography from '@mui/material/Typography'
import FullscreenLoader from 'common/FullscreenLoader'
import TextInput from 'components/inputs/TextInput'
import Routes from 'constants/routes'
import { useFormik } from 'formik'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import notify from 'services/notify'
import { getUser, login } from 'store/user/actions'
import { ReduxState } from 'types'
import * as Yup from 'yup'

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  sso: {
    margin: theme.spacing(1, 0, 1),
  },
}))

const FormSchema: Yup.SchemaOf<FormValues> = Yup.object().shape({
  name: Yup.string().required('Erforderlich'),
  pass: Yup.string().required('Erforderlich'),
})

export const initialValues = {
  name: '',
  pass: '',
}

export type FormValues = typeof initialValues

export default function SignIn() {
  const classes = useStyles()
  const dispatch = useDispatch()
  const history = useHistory()
  const { isLoading } = useSelector(({ user }: ReduxState) => user)

  const onLogin = ({ name, pass }: FormValues) => {
    dispatch(
      login({ name, pass }, () => {
        localStorage.setItem('username', name)
        localStorage.setItem('password', pass)
        history.push(Routes.dashboard)
        dispatch(getUser())
      }),
    )
  }

  const ssoLoginSync = () => {
    const name = localStorage.getItem('username')
    const pass = localStorage.getItem('password')
    if (name && pass) {
      try {
        onLogin({ name, pass })
      } catch (error) {
        notify(
          'Bei der SSO-Synchronisierung ist ein Fehler aufgetreten. Bitte melden Sie sich mit Ihren Anmeldeinformationen an.',
        )
      }
    } else {
      notify(
        'Bei der SSO-Synchronisierung ist ein Fehler aufgetreten. Bitte melden Sie sich mit Ihren Anmeldeinformationen an.',
      )
    }
  }

  const formik = useFormik({
    initialValues: { ...initialValues },
    onSubmit: (values, helpers) => {
      try {
        helpers.resetForm()
        onLogin(values)
      } catch (error) {
        notify(error)
      }
    },
    validationSchema: FormSchema,
  })

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar} />
        <Typography component="h1" variant="h5">
          {'Einloggen'}
        </Typography>
        <form
          onSubmit={formik.handleSubmit}
          className={classes.form}
          noValidate
        >
          <TextInput
            name="name"
            label="User Name"
            autoFocus
            autoComplete="username"
            fullWidth
            formik={formik}
          />
          <TextInput
            name="pass"
            label="Passwort"
            type="password"
            autoComplete="current-password"
            fullWidth
            formik={formik}
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            {'Login'}
          </Button>
          <Button
            type="button"
            fullWidth
            variant="contained"
            color="secondary"
            onClick={ssoLoginSync}
            className={classes.sso}
          >
            {'SSO Login Sync'}
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="/" variant="body2">
                {'Password vergessen ?'}
              </Link>
            </Grid>
            <Grid item>
              <Link href="/" variant="body2">
                {'Registrierung anfragen'}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={8}>
        <Typography variant="body2" color="textSecondary" align="center">
          {'Copyright © '}
          {'S&P Gruppe '}
          {new Date().getFullYear()}
        </Typography>
      </Box>
      <FullscreenLoader isLoading={isLoading} />
    </Container>
  )
}
