import DeleteIcon from '@mui/icons-material/Delete'
import EditIcon from '@mui/icons-material/Edit'
import { IconButton } from '@mui/material'
import cn from 'classnames'
import ImageContainer from 'common/ImageContainer'
import Button from 'components/Button'
import DOMPurify from 'dompurify'
import ReactHtmlParser from 'react-html-parser'
import { stringToFullDate } from 'services/date'
import { BlogItem } from 'types'

import styles from './styles.module.scss'

interface Props {
  item: BlogItem
  className?: string
  onRead?(): void
  onEditPost?: ((post: BlogItem) => void) | null
  onDeletePost?: ((post: BlogItem) => void) | null
}

const BlogItemBig: React.FC<Props> = ({
  item,
  className,
  onEditPost,
  onDeletePost,
  onRead,
}) => {
  const { body, created, images, title, author } = item

  const htmlFrom = (htmlString: string) =>
    ReactHtmlParser(DOMPurify.sanitize(ReactHtmlParser(htmlString)))
  return (
    <div className={cn(styles.item, className)}>
      <div className={styles.mainImageWrapper}>
        <ImageContainer
          images={[
            {
              element: (
                <img
                  className={styles.blogImage}
                  src={
                    images[0]?.url || ((images[0] as unknown) as string) || '/'
                  }
                  alt="article main"
                />
              ),
              src: images[0]?.url || ((images[0] as unknown) as string) || '/',
            },
          ]}
        />
      </div>
      <div className={styles.content}>
        <div className={styles.titleWrap}>
          <h3 className={styles.title}>{htmlFrom(title)}</h3>
          {(onEditPost || onDeletePost) && (
            <>
              <div className={styles.tools}>
                {onEditPost && (
                  <IconButton onClick={() => onEditPost(item)}>
                    <EditIcon color="orange" />
                  </IconButton>
                )}
                {onDeletePost && (
                  <IconButton onClick={() => onDeletePost(item)}>
                    <DeleteIcon color="orange" />
                  </IconButton>
                )}
              </div>
            </>
          )}
        </div>
        <time dateTime={created.toString()} className={styles.date}>
          {stringToFullDate(created)}
        </time>
        <div className={styles.author}>{`Autor: ${author}`}</div>
        <div>{ReactHtmlParser(body)}</div>
        <div className={styles.images}>
          <ImageContainer
            images={images.slice(1).map((item) => ({
              element: (
                <img
                  src={item.url || (item as any)}
                  alt="article gallery item"
                />
              ),
              src: item.url || (item as any),
            }))}
          />
        </div>
        {onRead && (
          <Button
            type="button"
            variant="fulfilled"
            color="orange"
            onClick={onRead}
            className={styles.readButton}
          >
            {'Gelesen'}
          </Button>
        )}
      </div>
    </div>
  )
}

export default BlogItemBig
