import Layout from 'common/Layout'
import { wikiURL } from 'constants/index'
import styles from 'containers/Wiki/styles.module.scss'

const Wiki = () => (
  <Layout headerProps={{ withoutSearch: true }}>
    <iframe
      src={`${wikiURL}/dokuwiki/doku.php?id=unternehmensstruktur`}
      title="Intranet Wiki"
      className={styles.iframeWrap}
    />
  </Layout>
)
export default Wiki
