import axios, { AxiosResponse } from 'axios'
import api from 'services/api'
import notify from 'services/notify'
import { findMenuItem } from 'services/utils'
import { action } from 'store/action'
import {
  AppThunkAsync,
  BackendMenuItem,
  BackendSettings,
  Error,
  MenuItem,
  VersioningContent,
} from 'types/index'

import ActionType from './types'

export const getBackendSettings = (
  onSuccess?: (menuItems: BackendSettings) => void,
  onError?: (e: Error) => void,
): AppThunkAsync<BackendSettings | undefined> => async (dispatch) => {
  dispatch(action(ActionType.GET_BACKEND_SETTINGS_BEGIN))
  try {
    const menuItems: AxiosResponse<BackendSettings> = await api.get(
      'settings?_format=json',
    )
    dispatch(action(ActionType.GET_BACKEND_SETTINGS_SUCCESS, menuItems.data))
    onSuccess?.(menuItems.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_BACKEND_SETTINGS_ERROR, error))
    onError?.(error)
  }
}

export const getLeftMenuItems = (
  onSuccess?: (menuItems: BackendMenuItem[]) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_LEFT_MENU_ITEMS_BEGIN))
  try {
    const menuItems: AxiosResponse<BackendMenuItem[]> = await api.get(
      'menu_items/left-bar-menu?_format=json',
    )
    dispatch(action(ActionType.GET_LEFT_MENU_ITEMS_SUCCESS, menuItems.data))
    onSuccess?.(menuItems.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_LEFT_MENU_ITEMS_ERROR, error))
    onError?.(error)
  }
}

export const getVersionContent = (
  onSuccess?: (data: VersioningContent) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_LEFT_MENU_ITEMS_BEGIN))
  try {
    const versioningContent: AxiosResponse<VersioningContent> = await axios.get(
      'https://cdn.contentful.com/spaces/9ehgr8o0wwxo/entries?access_token=j0p-HpFN91kiU193nYnYpKg6fPa4Mei7HDjRy5s--w4',
    )
    onSuccess?.(versioningContent.data)
  } catch (error) {
    notify(error)
    onError?.(error)
  }
}

export const selectMenuItem = (
  menuItem: MenuItem,
  collapseOthers = true,
): AppThunkAsync => async (dispatch, getState) => {
  const state = getState()
  const menuItems = state.app.menu?.map((item) =>
    findMenuItem(item, menuItem, collapseOthers),
  )
  dispatch(action(ActionType.SELECT_LEFT_MENU_ITEM, menuItems))
}

export default {
  getBackendSettings,
}
