import cn from 'classnames'

import styles from './styles.module.scss'

export interface Props extends React.InputHTMLAttributes<HTMLInputElement> {
  children: React.ReactNode
  className?: string
  boxClassName?: string
  name: string
  id?: string
  variant?: 'square' | 'round'
  theme?: 'orange' | 'default'
}

const Checkbox: React.FC<Props> = ({
  children,
  className,
  name,
  id,
  variant = 'square',
  theme = 'default',
  boxClassName,
  disabled,
  ...props
}) => (
  <div className={cn(styles.checkboxWrapper, styles[variant], className)}>
    <label
      htmlFor={id}
      className={cn(styles.checkboxView, boxClassName, {
        [styles.orange]: theme === 'orange',
        [styles.disabled]: disabled,
      })}
    >
      <input
        id={id}
        name={name}
        disabled={disabled}
        {...props}
        type="checkbox"
      />
      <div className={styles.box}>{variant === 'round' && <div />}</div>
      {children}
    </label>
  </div>
)

export default Checkbox
