import { FC } from 'react'

import Checkbox, { Props as CheckboxProps } from '../Checkbox'

interface Props extends Omit<CheckboxProps, 'onChange'> {
  value: string | undefined
  name: string
  onChange: (value: string) => void
}

const RadioBox: FC<Props> = ({ value, name, onChange, children, ...rest }) => {
  const onChangeFactory = (val: string) => () => onChange(val)

  return (
    <Checkbox
      name={name}
      checked={name === value}
      onChange={onChangeFactory(name)}
      variant="round"
      {...rest}
    >
      {children}
    </Checkbox>
  )
}

export default RadioBox
