import Button from 'components/Button'

import styles from './styles.module.scss'

interface FormProps {
  title?: string
  onCancel?: () => void
  onSubmit: () => void
}

const Delete: React.FC<FormProps> = ({ title, onCancel, onSubmit }) => (
  <div className={styles.delContainer}>
    <div className={styles.title}>{'Eintrag löschen'}</div>
    <span className={styles.warning}>{'Möchten Sie '}</span>
    <span className={styles.warningTitle}>{title}</span>
    <span className={styles.warning}>{' endgültig löschen?'}</span>

    <div className={styles.buttons}>
      <Button
        variant="fulfilled"
        className={styles.buttonPrimary}
        type="submit"
        onClick={onSubmit}
      >
        {'Löschen'}
      </Button>
      <Button
        variant="fulfilled"
        color="orange"
        className={styles.button}
        onClick={onCancel}
        type="reset"
      >
        {'Abbrechen'}
      </Button>
    </div>
  </div>
)

export default Delete
