import ZoomButton from 'components/Button/zoomButton'
import Widget, { WidgetProps } from 'components/Widget'
import Routes from 'constants/routes'
import { Link } from 'react-router-dom'
import { DetailsPageData } from 'types/settings'

import styles from './styles.module.scss'

interface Props extends DetailsPageData, WidgetProps {
  title: string
}

const DetailsPageWidget: React.FC<Props> = ({
  groupId,
  pageId,
  pageTitle,
  title,
  ...props
}) => (
  <Widget title={title} withoutMenu={false} {...props}>
    <div className={styles.title}>{pageTitle}</div>
    <Link to={`${Routes.projectGroups}/${groupId}?pageId=${pageId}`}>
      <ZoomButton />
    </Link>
  </Widget>
)

export default DetailsPageWidget
