import { ReactComponent as Exit } from 'assets/icons/exit.svg'
import ExampleUser from 'assets/icons/user.png'
import cn from 'classnames'
import Modal from 'common/Modal'
import Button from 'components/Button'
import Versioning from 'components/Versioning'
import useToggle from 'hooks/useToggle'
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  getLeftMenuItems,
  getVersionContent,
  selectMenuItem,
} from 'store/app/actions'
import { logout } from 'store/user/actions'
import strings from 'strings'
import { MenuItem, ReduxState, VersioningContent } from 'types'

import BigMenu from './BigMenu'
import SmallMenu from './SmallMenu'
import styles from './styles.module.scss'

interface LeftMenuProps {
  expanded: boolean
  toggleMenu: () => void
}

const LeftMenu = ({ expanded, toggleMenu }: LeftMenuProps) => {
  const dispatch = useDispatch()
  const { menu } = useSelector(({ app }: ReduxState) => app)
  const { user } = useSelector(({ user }: ReduxState) => user)
  const onMenuItemClick = (item: MenuItem) => dispatch(selectMenuItem(item))
  const [versions, setVersions] = useState<VersioningContent>(
    {} as VersioningContent,
  )
  const versionModalToggle = useToggle()
  function matchIdsAndAddAssets(content: VersioningContent) {
    const { items } = content
    const assets = content.includes.Asset

    items.forEach((item) => {
      item.fields.preview.forEach((preview) => {
        const previewId = preview.sys.id

        assets.forEach((asset) => {
          const assetId = asset.sys.id

          if (previewId === assetId) {
            // eslint-disable-next-line no-param-reassign
            preview.asset = asset
          }
        })
      })
    })

    return content
  }

  const onLogOutClick = () => dispatch(logout())
  useEffect(() => {
    dispatch(
      getVersionContent((data) => {
        setVersions(matchIdsAndAddAssets(data))
      }),
    )
    dispatch(getLeftMenuItems())
    return () => {
      // onMenuItemClick()
    }
  }, [])

  return (
    <div className={cn(styles.leftMenu, expanded && styles.expanded)}>
      {expanded ? (
        <BigMenu menuItems={menu} toggleMenu={toggleMenu} />
      ) : (
        <SmallMenu
          toggleMenu={toggleMenu}
          menuItems={menu}
          onMenuItemClick={onMenuItemClick}
        />
      )}
      <div>
        <div className={cn(styles.avatarWrapper, expanded && styles.expanded)}>
          <Button id="avatar">
            <div className={styles.avatar}>
              <img src={user?.user_picture || ExampleUser} alt="" />
            </div>
          </Button>
          {expanded && (
            <Button
              id="exit"
              className={styles.logoutContainer}
              onClick={onLogOutClick}
            >
              <div>
                <Exit />
              </div>
              <div className={styles.logoutText}>{strings.logout}</div>
            </Button>
          )}
        </div>
        <div
          className={cn(styles.versionContainer, expanded && styles.expanded)}
        >
          {expanded && (
            <Button
              id="exit"
              className={styles.logoutContainer}
              onClick={versionModalToggle.on}
            >
              <p className={styles.versionValue}>{'v.1.1.0'}</p>
            </Button>
          )}
        </div>
      </div>
      <Modal
        isOpen={versionModalToggle.value}
        onRequestClose={versionModalToggle.off}
        withCloseButton
        maxWidth={1100}
      >
        <Versioning versions={versions} />
      </Modal>
    </div>
  )
}
export default LeftMenu
