import { Employees, User, UserRole } from 'types'

const employeesFilterTransform = (
  currentUser?: User | null,
  employees?: Employees | null,
  shouldLoad?: boolean,
) => {
  const shouldNotBeFiltered =
    currentUser?.roles.includes(UserRole.ProjectAssistance) ||
    currentUser?.roles.includes(UserRole.Prokura) ||
    currentUser?.roles.includes(UserRole.Administrator)
  if (!shouldNotBeFiltered && !shouldLoad) {
    const rows = employees?.rows.filter(
      (employee) => employee.project_group === currentUser?.project_group?.name,
    )
    const pager = employees?.pager
    return { rows, pager }
  }
  return employees
}
export default employeesFilterTransform
