import axios from 'axios'
import Layout from 'common/Layout'
import Modal from 'common/Modal'
import Form, { FormValues } from 'containers/Ives/Offers/OffersForm'
import { getUnixTime } from 'date-fns'
import useToggle from 'hooks/useToggle'
import { useEffect, useMemo, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useLocation } from 'react-router-dom'
import { zonedDate } from 'services/date'
import notify from 'services/notify'
import { getSelectValue } from 'services/utils'
import {
  convertFileToPDF,
  createOffer,
  getOfferRecord,
} from 'store/ives/offers/actions'
import { Offer, ReduxState } from 'types'

import ContentView from './view'

// TODO refactor to enum
const NAVIGATION = [
  'Angebotsbearbeitung',
  'Angebotsdiskussion',
  'Weitere Angebote',
  'Angebotsdokument',
]

const OfferDetails = () => {
  const [selectedNavItem, setSelectedNavItem] = useState(NAVIGATION[0])
  const [offer, setOffer] = useState<Offer>()
  const [fileDocument, setFileDocument] = useState('')
  const [pageNumbers, setPageNumbers] = useState(0)
  const { offerDocumentUrl } = useSelector(
    ({ ives }: ReduxState) => ives.offers,
  )
  const { hash } = useLocation()
  const dispatch = useDispatch()
  const offerId = useMemo(() => hash.replace('#', ''), [hash])
  const newModalToggle = useToggle()

  useEffect(() => {
    dispatch(convertFileToPDF('sp_project.docx'))
  }, [])

  useEffect(() => {
    if (offerDocumentUrl) {
      axios
        .get(offerDocumentUrl, {
          responseType: 'arraybuffer',
        })
        .then((res) => {
          const uIntArray = new Uint8Array(res.data)
          const callback = (p: string, c: number) => p + String.fromCharCode(c)
          const b64encoded: string = btoa(uIntArray.reduce(callback, ''))
          setFileDocument(`data:application/pdf;base64,${b64encoded}`)
        })
    }
  }, [offerDocumentUrl])

  useEffect(() => {
    dispatch(getOfferRecord(offerId, (records) => setOffer(records?.[0])))
  }, [offerId])

  const onDocumentLoadSuccess = (arg: { numPages: number }): void => {
    setPageNumbers(arg.numPages)
  }
  const onNewOfferSubmit = (values: FormValues) => {
    dispatch(
      createOffer(
        {
          comment: values.comment,
          created: getUnixTime(zonedDate(values.created)),
          customer: { id: values.customer || '' },
          liability_damage: getSelectValue(values.liabilityDamage),
          probability: getSelectValue(values.probability),
          title: values.title,
          department: getSelectValue(values.department),
          pgl: getSelectValue(values.pgl),
          project: getSelectValue(values.project),
          project_manager: getSelectValue(values.projectManager),
        },
        () => notify('Angebote wurde erfolgreich erstellt', 'success'),
      ),
    )
    newModalToggle.off()
  }

  return (
    <Layout headerProps={{ withoutSearch: true }}>
      <ContentView
        openNewOfferForm={newModalToggle.on}
        navigation={NAVIGATION}
        selectedMenuItem={selectedNavItem}
        onNavClick={setSelectedNavItem}
        offer={offer}
        offerId={offerId}
        onDocumentLoadSuccess={onDocumentLoadSuccess}
        pageNumbers={pageNumbers}
        fileDocument={fileDocument}
      />
      <Modal isOpen={newModalToggle.value} onRequestClose={newModalToggle.off}>
        <Form onCancel={newModalToggle.off} onSubmit={onNewOfferSubmit} />
      </Modal>
    </Layout>
  )
}

export default OfferDetails
