import { useEffect, useRef } from 'react'

// import { useSelector } from 'react-redux'
// import { ReduxState } from 'types'
import { webSocketUrl } from '../constants'

const useWebSocket = (
  socketPath: string,
  onMessage: (event: MessageEvent) => void = () => null,
  onOpen: (event: Event) => void = () => null,
) => {
  const socketRef = useRef<WebSocket | null>(null)
  // const websocket = useSelector(
  //   (state: ReduxState) => state.app.backendSettings?.websocket,
  // )
  useEffect(() => {
    socketRef.current = new WebSocket(
      `${webSocketUrl}${socketPath}`,
      // isDev
      // : `${websocket?.protocol}://${websocket?.host}${websocket?.path_notify}`,
    )
  }, [socketPath])
  useEffect(() => {
    socketRef.current?.addEventListener('message', onMessage)
    onOpen && socketRef.current?.addEventListener('open', onOpen)

    return () => {
      socketRef.current?.removeEventListener('message', onMessage)
      onOpen && socketRef.current?.removeEventListener('open', onOpen)
    }
  }, [onMessage, onOpen])

  return socketRef.current
}

export default useWebSocket
