import {
  FormOptions,
  getFormSelectOptions,
} from 'containers/Ives/Employees/EmployeeForm/formData'
import { format } from 'date-fns'
import { useEffect, useState } from 'react'
import ReactHtmlParser from 'react-html-parser'
import { Employee } from 'types'

import styles from '../styles.module.scss'

interface EmployeeData {
  currentRecord?: Employee
}

const TimePrintTemplate = ({ currentRecord }: EmployeeData) => {
  const [options, setOptions] = useState<FormOptions>({
    childOptions: [],
    projectGroups: [],
    familyOptions: [],
    companyFunctionOptions: [],
    locationOptions: [],
    academicOptions: [],
    hourlyModelOptions: [],
    competenceFunctionOptions: [],
    caOptions: [],
    crossCompetenceOptions: [],
    crossGroupOptions: [],
    workingGroupOptions: [],
    workingFunctionOptions: [],
    workingDaysOptions: [],
    leaveOptions: [],
  })

  useEffect(() => {
    getFormSelectOptions().then((res: FormOptions) => {
      setOptions(res)
    })
  }, [])

  const translateFamilyVacation = currentRecord?.family_vacation?.map(
    (item) => {
      const type = options.familyOptions.find(
        (option: any) => option.value === item.family_vacation_type,
      )
      return {
        ...item,
        family_vacation_type: type?.label,
      }
    },
  )
  const calculateSickDays = () => {
    const sickDays = currentRecord?.employee_sick?.map((item) => {
      const from = Date.parse(item.from)
      const to = Date.parse(item.to)
      const diff = to - from
      return diff / (1000 * 60 * 60 * 24)
    })
    return sickDays?.reduce((a, b) => a + b, 0)
  }

  return (
    <div className={styles.detailMainWrap}>
      <div className={styles.detailMain}>
        <div className={styles.detailHeader}>
          <h2>{'Zeitmanagement'}</h2>
        </div>
        <div className={styles.detailBody}>
          <div className={styles.dataBody}>
            <div className={styles.dataGrid}>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Letzte Weiterbildung'}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {currentRecord?.vacation_year || 0}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Anzahl der Krankheitstage'}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {calculateSickDays() || 0}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'Soziale Maßnahmen'}</span>
                <br />
                <span className={styles.dataValue}>
                  {currentRecord?.remaining_previous_year || 0}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Dauer zusätzlicher Ausfall'}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {currentRecord?.remaining_previous_year || 0}
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className={styles.detailMainInner}>
        <div className={styles.dataBody}>
          <h3 className={styles.fieldTitle}>{'Tod/Hochzeit/Geburt'}</h3>
          <div className={styles.dataGrid}>
            {translateFamilyVacation?.map((vacation, index) => (
              <div className={styles.accordionData} key={index}>
                <span className={styles.dataLabel}>
                  {vacation.family_vacation_type}
                </span>
                <div className={styles.dataWrap}>
                  {vacation.period?.from && vacation.period?.to ? (
                    <span className={styles.dataValue}>
                      {`${format(
                        new Date(vacation.period?.from),
                        'dd.MM.yyyy',
                      )} - ${format(
                        new Date(vacation.period?.to),
                        'dd.MM.yyyy',
                      )}`}
                    </span>
                  ) : null}
                </div>
              </div>
            ))}
          </div>
        </div>
        <div className={styles.dataBody}>
          <h3 className={styles.fieldTitle}>{'Krankheitstage'}</h3>
          <div className={styles.dataGrid}>
            {currentRecord?.employee_sick?.map((sick, index) => (
              <div className={styles.accordionData} key={index}>
                <span className={styles.dataLabel}>{'Krank'}</span>
                <div className={styles.dataWrap}>
                  {sick.to && (
                    <span className={styles.dataValue}>
                      {`${format(
                        new Date(sick?.from),
                        'dd.MM.yyyy',
                      )} - ${format(new Date(sick?.to), 'dd.MM.yyyy')}`}
                    </span>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
        <div className={styles.dataBody}>
          <h3 className={styles.fieldTitle}>{'Krankheitstage Kinder'}</h3>
          <div className={styles.dataGrid}>
            {currentRecord?.child_sick?.map((sick, index) => (
              <div className={styles.accordionData} key={index}>
                <span className={styles.dataLabel}>{'Kindkrank'}</span>
                <div className={styles.dataWrap} key={index}>
                  {sick.to && (
                    <span className={styles.dataValue}>
                      {`${format(
                        new Date(sick?.from),
                        'dd.MM.yyyy',
                      )} - ${format(new Date(sick?.to), 'dd.MM.yyyy')}`}
                    </span>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
        <div className={styles.dataBody}>
          <h3 className={styles.fieldTitle}>{'Mutterschutz'}</h3>
          <div className={styles.dataGrid}>
            {currentRecord?.maternity_protection?.map((protection, index) => (
              <div className={styles.accordionData} key={index}>
                <span className={styles.dataLabel}>{'Mutterschutz'}</span>
                <div className={styles.dataWrap}>
                  {protection.to && protection.from && (
                    <span className={styles.dataValue}>
                      {`${format(
                        new Date(protection.from),
                        'dd.MM.yyyy',
                      )} - ${format(new Date(protection.to), 'dd.MM.yyyy')}`}
                    </span>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
        <div className={styles.dataBody}>
          <h3 className={styles.fieldTitle}>{'Elternzeit'}</h3>
          <div className={styles.dataGrid}>
            {currentRecord?.parental_leave?.map((leave, index) => (
              <div className={styles.accordionData} key={index}>
                <span className={styles.dataLabel}>{'Elternzeit'}</span>
                <div className={styles.dataWrap}>
                  {leave.from && leave.to && (
                    <span className={styles.dataValue}>
                      {`${format(
                        new Date(leave.from),
                        'dd.MM.yyyy',
                      )} - ${format(new Date(leave.to), 'dd.MM.yyyy')}`}
                    </span>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
        <div className={styles.dataBody}>
          <h3 className={styles.fieldTitle}>{'Andere Soziale Leistungen'}</h3>
          <div className={styles.dataGrid}>
            <div className={styles.detailBody}>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Andere soziale Leistungen'}
                </span>
                <br />
                {currentRecord?.sb_etc ? (
                  <span className={styles.dataValue}>
                    {ReactHtmlParser(currentRecord?.sb_etc) || 'Keine Angabe'}
                  </span>
                ) : null}
              </div>
            </div>
          </div>

          <div className={styles.dataWrap}>
            <span className={styles.dataLabel}>{'Leistungen'}</span>
            <br />
            {currentRecord?.medical_services ? (
              <span className={styles.dataValue}>
                {ReactHtmlParser(currentRecord?.medical_services) ||
                  'Keine Angabe'}
              </span>
            ) : null}
          </div>
        </div>
      </div>
      <div className={styles.dataBody}>
        <h3 className={styles.fieldTitle}>
          {'Qualifizierungen/Weiterbildungen'}
        </h3>
        <div className={styles.dataGrid}>
          <div className={styles.dataWrap}>
            <span className={styles.dataLabel}>
              {'Qualifizierungen/Weiterbildungen'}
            </span>
            <br />
            {currentRecord?.qualifications ? (
              <span className={styles.dataValue}>
                {ReactHtmlParser(currentRecord?.qualifications) ||
                  'Keine Angabe'}
              </span>
            ) : null}
          </div>
        </div>
      </div>
      <div className={styles.dataBody}>
        <h3 className={styles.fieldTitle}>{'Letzte Abwesenheitsnotizen'}</h3>
        <div className={styles.dataGrid}>
          <span className={styles.dataValue}>
            {currentRecord?.notes_absence?.flatMap((note, index) => (
              <div className={styles.dateIncludedField} key={index}>
                <span className={styles.dateLabel}>
                  {note.created &&
                    note.created.substr(
                      note.created.indexOf(', ') + 1,
                      note.created.indexOf(' - ') - 3,
                    )}
                </span>
                <br />
                <span className={styles.dataValue}>{note.note}</span>
              </div>
            ))}
          </span>
        </div>
      </div>
    </div>
  )
}
export default TimePrintTemplate
