import BlogItemBig from 'components/BlogItemBig'
import Button from 'components/Button'
import WidgetCarouselItem from 'components/widgetItems/WidgetCarouselItem'
import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { readCompanyInformation } from 'store/dashboard/actions'
import { CompanyInfoItem } from 'types'

import styles from './styles.module.scss'

interface Props {
  items: CompanyInfoItem[]
  onOkClick(): void
}

const CompanyInfo: React.FC<Props> = ({ items, onOkClick }) => {
  const [activeArticleId, setActiveArticleId] = useState(items[0]?.nid)

  const dispatch = useDispatch()

  const activeArticle =
    items.find((item) => item.nid === activeArticleId) || items[0]

  const readEverything = items.every((item) => item.read !== '0')
  return (
    <div className={styles.modal}>
      <div className={styles.articles}>
        <h2>{'Unternehmensinformationen'}</h2>
        <div className={styles.items}>
          {items.map(
            (item) =>
              item && (
                <WidgetCarouselItem
                  key={item.nid}
                  title={item.title}
                  body={item.summary}
                  date={item.created}
                  read={item.read}
                  onClick={() => setActiveArticleId(item.nid)}
                  className={styles.articleBlock}
                />
              ),
          )}
        </div>
        <Button
          variant="fulfilled"
          color="orange"
          className={styles.okButton}
          disabled={!readEverything}
          onClick={onOkClick}
        >
          {'OK'}
        </Button>
      </div>
      <div className={styles.openedArticleWrapper}>
        {items.length !== 0 && (
          <BlogItemBig
            item={activeArticle}
            className={styles.openedArticle}
            onRead={
              activeArticle.read === '0'
                ? () => dispatch(readCompanyInformation(activeArticle))
                : undefined
            }
          />
        )}
      </div>
    </div>
  )
}

export default CompanyInfo
