import { Action as ReduxAction } from 'redux'

import AppActionType, { Action as AppAction } from './app/types'
import BlogActionType, { Action as BlogAction } from './blog/types'
import CompanyStructureActionType, {
  Action as CompanyStructureAction,
} from './companyStructure/types'
import DashboardActionType, {
  Action as DashboardAction,
} from './dashboard/types'
import IvesActionType, { IvesAction } from './ives/types'
import UserActionType, { Action as UserAction } from './user/types'

export interface Action<T = string, P = unknown> extends ReduxAction<T> {
  payload?: P
}

export const action = <T = string, P = unknown>(
  type: T,
  payload?: P,
): Action<T, P> => ({
  type,
  payload,
})

export type AllActionType =
  | UserActionType
  | DashboardActionType
  | AppActionType
  | BlogActionType
  | IvesActionType
  | CompanyStructureActionType

export type AllAction =
  | UserAction
  | DashboardAction
  | AppAction
  | BlogAction
  | IvesAction
  | CompanyStructureAction

export default action
