/* eslint-disable guard-for-in */
import { UserAccess } from 'constants/index'
import { FormikProps, useFormik } from 'formik'
import { useEffect, useState } from 'react'
import { useDropzone } from 'react-dropzone'
import { useDispatch, useSelector } from 'react-redux'
import { Option } from 'react-select/src/filters'
import api from 'services/api'
import { formatDate } from 'services/date'
import notify from 'services/notify'
import { getUser } from 'store/user/actions'
import {
  Appraisal,
  DateRange,
  Employee,
  FileWithPreview,
  ReduxState,
  SelectOption,
  Sick,
  SingleSelectOption,
  SubstituteOption,
  UserRole,
} from 'types'

import Form from './form'
import {
  FormOptions,
  FormValues,
  initialValuesStep1,
  initialValuesStep2,
  initialValuesStep3,
  initialValuesStep4,
  initialValuesStep5,
  Step1,
  Step2,
  Step3,
  Step4,
  Step5,
} from './formData'
import FormStep1 from './Steps/step1'
import FormStep2 from './Steps/step2'
import FormStep3 from './Steps/step3'
import FormStep4 from './Steps/step4'
import FormStep5 from './Steps/step5'

interface Step {
  formik: FormikProps<FormValues>
  step: JSX.Element
}

interface EmployeeFormProps {
  initialData?: Employee
  options: FormOptions
  onCancel: () => void
  onSubmit: (values: FormValues, finished: boolean) => void
}

const EmployeeForm = ({
  initialData,
  options,
  onSubmit,
  onCancel,
}: EmployeeFormProps) => {
  const { access, roles } =
    useSelector(({ user }: ReduxState) => user.user) || {}
  const [step, setStep] = useState<number>(1)
  const stepCount =
    2 +
    Number(access?.[UserAccess.viewEmployeeContractData]) +
    Number(access?.[UserAccess.viewEmployeeAppraisals]) +
    Number(access?.[UserAccess.viewEmployeeCorporateStructure])

  const finished = step > stepCount
  const dispatch = useDispatch()
  const [photo, setPhoto] = useState<FileWithPreview>()

  useEffect(() => {
    if (!access) {
      dispatch(getUser())
    }
  }, [access])

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    accept: 'image/png, image/jpg, image/jpeg',
    maxSize: 6000000,
    maxFiles: 1,
    onDrop: async (acceptedFiles) => {
      const file = acceptedFiles[0]
      const { data } = await api.post(
        '/file/upload/employee/employee/field_picture?_format=json',
        file,
        {
          headers: {
            'Content-Type': 'application/octet-stream',
            'Content-Disposition': `file; filename="${file.name}"`,
          },
        },
      )
      setPhoto(
        Object.assign(file, {
          id: data.fid[0].value,
          preview: URL.createObjectURL(file),
        }),
      )
    },
  })

  const _mappedValues = (obj: FormValues) => {
    const initial: any = { ...initialData }
    const newObj: any = { ...obj }

    // eslint-disable-next-line no-restricted-syntax
    for (const key in newObj) {
      newObj[key] = initial[key]
    }
    return newObj
  }

  const _valuesToSave = (values: any) => {
    const initial: any = { ...initialData }
    const toSave: any = {}

    Object.keys(values).forEach((key: string) => {
      if (initial[key] !== values[key]) {
        toSave[key] = values[key]
        if (Array.isArray(toSave[key])) {
          toSave[key] = toSave[key].map((entry: Record<string, unknown>) => ({
            ...entry,
          }))
        }
      }
    })

    return toSave
  }

  const initialData1 = _mappedValues(initialValuesStep1)
  const initialData2 = _mappedValues(initialValuesStep2)
  const initialData3 = _mappedValues(initialValuesStep3)
  const initialData4 = _mappedValues(initialValuesStep4)
  const initialData5 = _mappedValues(initialValuesStep5)

  const getGroupOption = (val: string | number) =>
    options.companyFunctionOptions
      .reduce((acc: SelectOption[], val) => acc.concat(val.options), [])
      .find((item) => `${item.value}` === `${val}`)

  const getAcademicDegreeOption = (val: string | number) =>
    options.academicOptions.find((item) => `${item.value}` === `${val}`)

  const formik1 = useFormik({
    initialValues: {
      ...initialData1,
      project_group: initialData1.project_group
        ? ({
            value: initialData1.project_group,
            label: options.projectGroups.find(
              (item) => `${item.value}` === `${initialData1.project_group}`,
            )?.label,
          } as SingleSelectOption)
        : undefined,
      location: initialData1.location
        ? ({
            value: initialData1.location,
            label: options.locationOptions.find(
              (item) => `${item.value}` === `${initialData1.location}`,
            )?.label,
          } as SingleSelectOption)
        : undefined,
      academic_degrees:
        initialData1.academic_degrees?.map((val: string | number) => ({
          label: getAcademicDegreeOption(val)?.label || '',
          value: val,
        })) || [],
      company_function:
        initialData1.company_function?.map((val: string | number) => ({
          label: getGroupOption(val)?.label || '',
          value: val,
        })) || [],
    } as Step1,
    onSubmit: (values) => {
      try {
        const valuesToSave = _valuesToSave(values)
        onSubmit(
          {
            ..._valuesToSave(values),
            picture_id: photo?.id,
            project_group:
              typeof valuesToSave.project_group === 'object'
                ? valuesToSave.project_group.value
                : valuesToSave.project_group,
            company_function: valuesToSave.company_function.map(
              (item: Record<string, unknown> | number) =>
                typeof item === 'object' ? item.value : item,
            ),
            location:
              typeof valuesToSave.location === 'object'
                ? valuesToSave.location.value
                : valuesToSave.location,
            academic_degrees: valuesToSave.academic_degrees.map(
              (item: Record<string, unknown>) => item.value,
            ),
          },
          finished,
        )
      } catch (error) {
        notify(error)
      }
    },
  })

  const formik2 = useFormik({
    initialValues: {
      ...initialData2,
      hourly_model: initialData2.hourly_model
        ? ({
            value: initialData2.hourly_model,
            label: options.hourlyModelOptions.find(
              (item) => item.value === initialData2.hourly_model,
            )?.label,
          } as SingleSelectOption)
        : undefined,
      working_days: initialData2.working_days?.map(
        (item: number, index: number) => {
          if (!item) {
            return null
          }

          return options.workingDaysOptions[index]
        },
      ),
      allowences: (initialData2.allowences || []).map((el: any) => ({
        ...el,
        type: el.type
          ? ({
              value: el.type,
              label: el.type,
            } as SingleSelectOption)
          : undefined,
        date: el.date,
      })),
    } as Step2,
    onSubmit: (values) => {
      try {
        const valuesToSave = _valuesToSave(values)
        const prepareWorkDate = options.workingDaysOptions.map((option) => {
          if (
            (valuesToSave.working_days as SelectOption[]).find(
              (item) => item.value === option.value,
            )
          ) {
            return 1
          }
          return 0
        })
        onSubmit(
          {
            ...valuesToSave,
            hourly_model:
              typeof valuesToSave.hourly_model === 'object'
                ? valuesToSave.hourly_model.value
                : valuesToSave.hourly_model,
            working_days: prepareWorkDate,
            allowences: valuesToSave.allowences.map(
              (item: Record<string, any>) => ({
                ...item,
                type: item.type.value,
                date: formatDate(new Date(item.date), 'yyyy-MM-dd'),
                amount: item.amount,
              }),
            ),
          },
          finished,
        )
      } catch (error) {
        notify(error)
      }
    },
  })
  const substitutionField = {
    employee: {
      label: '',
      value: '',
    },
    period: { from: '', to: '' },
  }
  const formik3 = useFormik({
    initialValues: {
      ...initialData3,
      family_vacation: (initialData3.family_vacation || []).map((el: any) => ({
        ...el,
        family_vacation_type: el.family_vacation_type
          ? ({
              value: el.family_vacation_type,
              label: options.familyOptions.find(
                (item) => `${item.value}` === `${el.family_vacation_type}`,
              )?.label,
            } as SingleSelectOption)
          : undefined,
      })),
      leave: (initialData3.leave || []).map((el: any) => ({
        ...el,
        type: el.type
          ? ({
              value: el.type,
              label: el.type,
            } as SingleSelectOption)
          : undefined,
        child: el.child
          ? ({
              value: el.child,
              label: el.child,
            } as SingleSelectOption)
          : undefined,
        duration: el.period,
      })),
      notes_absence: (initialData3.notes_absence || []).map((el: any) => ({
        ...el,
        note: el.note,
        creation: el.creation
          ? ({
              value: el.creation,
              label: el.creation,
            } as SingleSelectOption)
          : undefined,
      })),
      employee_sick:
        initialData3.employee_sick.length > 0
          ? initialData3.employee_sick.map((el: Sick) => ({
              ...el,
              type: el.type ? { value: el.type, label: el.type } : null,
              medical_certificate: el.medical_certificate,
              substitutes: el.substitutes
                ? el.substitutes.map(
                    (substitute: any) =>
                      ({
                        employee: {
                          label: substitute.employee_name,
                          value: substitute.employee,
                        } as Option,
                        period: substitute.period,
                      } as SubstituteOption),
                  )
                : (([substitutionField] as unknown) as SubstituteOption[]),
            }))
          : [
              {
                period: {} as DateRange,
                substitutes: ([
                  substitutionField,
                ] as unknown) as SubstituteOption[],
              },
            ],
    } as Step3,
    onSubmit: (values) => {
      try {
        const valuesToSave = _valuesToSave(values)
        console.log({
          ...valuesToSave,
          family_vacation: (valuesToSave.family_vacation || []).map(
            (el: any) => ({
              ...el,
              family_vacation_type: el.family_vacation_type?.value || '',
            }),
          ),
          leave:
            valuesToSave.leave.length > 0 &&
            'child' in valuesToSave.leave[0] &&
            'type' in valuesToSave.leave[0]
              ? (valuesToSave.leave || []).map((el: any) => ({
                  ...el,
                  child: el.child ? el.child.value : null,
                  type: el.type.value ? el.type.value : null,
                  period: el.period ? el.period : null,
                }))
              : [],
          notes_absence: (valuesToSave.notes_absence || []).map((el: any) => ({
            ...el,
            note: el.note,
            creation: el.creation ? el.creation.value : null,
          })),
          employee_sick:
            valuesToSave.employee_sick.length > 0 &&
            'type' in valuesToSave.employee_sick[0] &&
            'period' in valuesToSave.employee_sick[0]
              ? (valuesToSave.employee_sick || []).map((el: any) => ({
                  ...el,
                  type: el.type.value,
                  medical_certificate: el.medical_certificate,
                  period: el.period,
                  substitutes: el.substitutes
                    ? (el.substitutes || []).map((sub: any) => ({
                        ...sub,
                        period: sub.period,
                        employee: sub.employee.value,
                        employee_name: sub.employee.label,
                      }))
                    : [],
                }))
              : [],
        })
        onSubmit(
          {
            ...valuesToSave,
            family_vacation: (valuesToSave.family_vacation || []).map(
              (el: any) => ({
                ...el,
                family_vacation_type: el.family_vacation_type?.value || '',
              }),
            ),
            leave:
              valuesToSave.leave.length > 0 &&
              'child' in valuesToSave.leave[0] &&
              'type' in valuesToSave.leave[0]
                ? (valuesToSave.leave || []).map((el: any) => ({
                    ...el,
                    child: el.child ? el.child.value : null,
                    type: el.type.value ? el.type.value : null,
                    period: el.period ? el.period : null,
                  }))
                : [],
            notes_absence: (valuesToSave.notes_absence || []).map(
              (el: any) => ({
                ...el,
                note: el.note,
                creation: el.creation ? el.creation.value : null,
              }),
            ),
            employee_sick:
              valuesToSave.employee_sick.length > 0 &&
              'type' in valuesToSave.employee_sick[0] &&
              'period' in valuesToSave.employee_sick[0]
                ? (valuesToSave.employee_sick || []).map((el: any) => ({
                    ...el,
                    type: el.type.value,
                    medical_certificate: el.medical_certificate,
                    period: el.period,
                    substitutes: el.substitutes
                      ? (el.substitutes || []).map((sub: any) => ({
                          ...sub,
                          period: sub.period,
                          employee: sub.employee.value,
                          employee_name: sub.employee.label,
                        }))
                      : [],
                  }))
                : [],
          },
          finished,
        )
      } catch (error) {
        notify(error)
      }
    },
  })

  const formik4 = useFormik({
    initialValues: initialData4 as Step4,
    onSubmit: (values) => {
      try {
        const valuesToSave = _valuesToSave(values).appraisal_interviews
        if (valuesToSave?.length && valuesToSave[0] !== '') {
          onSubmit(
            {
              appraisal_interviews: valuesToSave.map((item: Appraisal) => ({
                ...item,
                appraisal_files:
                  item.appraisal_files?.map((f: any) => f.fid) || [],
              })),
            },
            finished,
          )
        }
      } catch (error) {
        notify(error)
      }
    },
    // validationSchema: FormSchemaStep4,
  })

  const formik5 = useFormik({
    initialValues: {
      ...initialData5,
      working_groups: (initialData5.working_groups || []).map((el: any) => ({
        ...el,
        working_group: el.working_group
          ? ({
              value: el.working_group,
              label: options.workingGroupOptions.find(
                (item) => `${item.value}` === `${el.working_group}`,
              )?.label,
            } as SingleSelectOption)
          : undefined,
        working_group_function: el.working_group_function
          ? ({
              value: el.working_group_function,
              label: options.workingFunctionOptions.find(
                (item) => `${item.value}` === `${el.working_group_function}`,
              )?.label,
            } as SingleSelectOption)
          : undefined,
        cross_group_function: el.cross_group_function
          ? ({
              value: el.cross_group_function,
              label: options.crossGroupOptions.find(
                (item) => `${item.value}` === `${el.cross_group_function}`,
              )?.label,
            } as SingleSelectOption)
          : undefined,
      })),
      competence_areas: (initialData5.competence_areas || []).map(
        (el: any) => ({
          ...el,
          competence_area: el.competence_area
            ? ({
                value: el.competence_area,
                label: options.caOptions.find(
                  (item) => `${item.value}` === `${el.competence_area}`,
                )?.label,
              } as SingleSelectOption)
            : undefined,
          ca_function: el.ca_function
            ? ({
                value: el.ca_function,
                label: options.competenceFunctionOptions.find(
                  (item) => `${item.value}` === `${el.ca_function}`,
                )?.label,
              } as SingleSelectOption)
            : undefined,
          cross_ca_function: el.cross_ca_function
            ? ({
                value: el.cross_ca_function,
                label: options.crossCompetenceOptions.find(
                  (item) => `${item.value}` === `${el.cross_ca_function}`,
                )?.label,
              } as SingleSelectOption)
            : undefined,
        }),
      ),
      competence_centers: (initialData5.competence_centers || []).map(
        (el: any) => ({
          competence_center: el.competence_center
            ? ({
                value: el.competence_center,
                label: options.competenceCenterOptions.find(
                  (item) => `${item.value}` === `${el.competence_center}`,
                )?.label,
              } as SingleSelectOption)
            : undefined,
          function_in_compete: el.function_in_the_competence
            ? ({
                value: el.function_in_the_competence,
                label: options.functionInCompetenceOptions.find(
                  (item) =>
                    `${item.value}` === `${el.function_in_the_competence}`,
                )?.label,
              } as SingleSelectOption)
            : undefined,
        }),
      ),
    } as Step5,
    onSubmit: (values) => {
      try {
        const valuesToSave = _valuesToSave(values)

        onSubmit(
          {
            ...valuesToSave,
            working_groups: (valuesToSave.working_groups || []).map(
              (el: any) => ({
                ...el,
                working_group:
                  typeof el.working_group === 'object'
                    ? el.working_group.value
                    : el.working_group,
                working_group_function:
                  typeof el.working_group_function === 'object'
                    ? el.working_group_function.value
                    : el.working_group_function,
                cross_group_function:
                  typeof el.cross_group_function === 'object'
                    ? el.cross_group_function.value
                    : el.cross_group_function,
              }),
            ),
            competence_areas: (valuesToSave.competence_areas || []).map(
              (el: any) => ({
                ...el,
                competence_area:
                  typeof el.competence_area === 'object'
                    ? el.competence_area.value
                    : el.competence_area,
                ca_function:
                  typeof el.ca_function === 'object'
                    ? el.ca_function.value
                    : el.ca_function,
                cross_ca_function:
                  typeof el.cross_ca_function === 'object'
                    ? el.cross_ca_function.value
                    : el.cross_ca_function,
              }),
            ),
            competence_centers: (valuesToSave.competence_centers || []).map(
              (el: any) => ({
                competence_center:
                  typeof el.competence_center === 'object'
                    ? el.competence_center.value
                    : el.competence_center,
                function_in_compete:
                  typeof el.function_in_compete === 'object'
                    ? el.function_in_compete.value
                    : el.function_in_compete,
              }),
            ),
          },
          finished,
        )
      } catch (error) {
        notify(error)
      }
    },
    // validationSchema: FormSchemaStep5,
  })

  const steps = [
    {
      formik: formik1,
      step: (
        <FormStep1
          formik={formik1}
          photo={photo}
          pictureUrl={initialData?.picture_url}
          getRootProps={getRootProps}
          getInputProps={getInputProps}
          isDragActive={isDragActive}
          options={options}
        />
      ),
    },
    access?.[UserAccess.viewEmployeeContractData] && {
      formik: formik2,
      step: <FormStep2 options={options} formik={formik2} />,
    },
    {
      formik: formik3,
      step: (
        <FormStep3
          formik={formik3}
          employeeId={initialData?.nid}
          familyOptions={options.familyOptions}
          leaveOptions={options.leaveOptions}
          childrenOptions={
            initialData?.children?.map((item) => ({
              value: item.name,
              label: item.name,
            })) || []
          }
          showVacationInputs={roles?.includes(UserRole.HumanResource)}
        />
      ),
    },
    access?.[UserAccess.viewEmployeeAppraisals] && {
      formik: formik4,
      step: <FormStep4 formik={formik4} />,
    },
    access?.[UserAccess.viewEmployeeCorporateStructure] && {
      formik: formik5,
      step: <FormStep5 formik={formik5} options={options} />,
    },
  ].filter(Boolean) as Step[]

  const onBack = () => {
    setStep(step - 1)
    steps[step - 1].formik.handleSubmit()
  }

  return (
    <>
      <Form
        currentStep={steps[finished ? stepCount - 1 : step - 1].step}
        step={step}
        stepCount={stepCount}
        back={onBack}
        setStep={setStep}
        onCancel={onCancel}
        handleSubmit={(event) => {
          event?.preventDefault()
          setStep(step + 1)
          steps[step - 1].formik.handleSubmit()
        }}
      />
    </>
  )
}

export default EmployeeForm
