/* eslint-disable react/jsx-no-undef */

import DateRange from 'components/inputs/DateRange'
import FieldArray from 'components/inputs/FieldArray'
import HtmlEditor from 'components/inputs/HtmlEditor'
import NumberInput from 'components/inputs/NumberInput'
import Select from 'components/inputs/Select'
import EmployeeSickForm from 'containers/Ives/Employees/EmployeeForm/Steps/components/EmployeeSickForm'
import { getYear } from 'date-fns'
import { FormikProps } from 'formik'
import { useState } from 'react'
import { OptionsType } from 'react-select'
import { Option } from 'react-select/src/filters'
import { SelectOption } from 'types'

import { Step3 as Props } from '../formData'
import styles from '../styles.module.scss'

interface FormProps {
  formik: FormikProps<Props>
  familyOptions: OptionsType<SelectOption>
  leaveOptions: OptionsType<SelectOption>
  childrenOptions: OptionsType<SelectOption>
  showVacationInputs?: boolean
  employeeId: string | number
}
const checkForCurrentAndNextYearInArray = (notesAbsence: any) => {
  let counter = 0
  if (notesAbsence && notesAbsence.length > 0) {
    notesAbsence.forEach((note: any) => {
      if (
        note.creation === getYear(new Date()) ||
        note.creation === getYear(new Date()) + 1
      ) {
        counter += 1
      }
    })
  }

  return counter === 2
}

const Step3 = ({
  formik,
  familyOptions,
  showVacationInputs,
  leaveOptions,
  employeeId,
  childrenOptions,
}: FormProps) => {
  const [childrenOpts, setChildrenOpts] = useState<Option[]>(
    childrenOptions as Option[],
  )

  return (
    <>
      {showVacationInputs && (
        <>
          <section>
            <h3 className={styles.stepTitle}>{'Abwesenheit'}</h3>
            <NumberInput
              name="vacation_year"
              label="Urlaubsanspruch akt."
              suffix=" Tage"
              formik={formik}
            />
            <NumberInput
              name="vacation_next_year"
              label="Urlaubsanspruch nächst."
              suffix=" Tage"
              formik={formik}
              // disabled
            />
            <NumberInput
              name="remaining_previous_year"
              label="Resturlaub Vorjahr"
              suffix=" Tage"
              formik={formik}
            />
          </section>
          <section>
            <h3 className={styles.stepTitle}>{'Urlaubsnotizen'}</h3>
            <FieldArray
              name="notes_absence"
              formik={formik}
              isGroup
              stopAddingMoreFields={checkForCurrentAndNextYearInArray(
                formik.values.notes_absence,
              )}
              // disable if there are already entries for the current year and the next year
              withoutPlusButton={checkForCurrentAndNextYearInArray(
                formik.values.notes_absence,
              )}
              renderField={(defaultValue, index) => (
                <div>
                  <Select
                    name={`notes_absence[${index}].creation`}
                    placeholder="Jahr auswählen"
                    isSearchable={false}
                    options={[
                      {
                        label: `${getYear(new Date())}`,
                        value: `${getYear(new Date())}`,
                      },
                      {
                        label: `${getYear(new Date()) + 1}`,
                        value: `${getYear(new Date()) + 1}`,
                      },
                    ]}
                    value={defaultValue.creation}
                    onChange={() => {
                      formik.setFieldValue(
                        `notes_absence[${index}].creation`,
                        getYear(new Date()) + index,
                      )
                    }}
                    formik={formik}
                  />
                  <HtmlEditor
                    name={`notes_absence[${index}].note`}
                    defaultValue={defaultValue.note}
                    label=""
                    formik={formik}
                    onChange={formik.handleChange}
                  />
                </div>
              )}
            />
          </section>

          <section>
            <FieldArray
              isGroup
              name="family_vacation"
              label="Tod / Geburt Familienangehörige / Hochzeit"
              formik={formik}
              renderField={(defaultValue, index) => (
                <div>
                  <Select
                    name={`family_vacation[${index}].family_vacation_type`}
                    placeholder="Grund Auswählen"
                    isSearchable={false}
                    options={familyOptions}
                    value={defaultValue.family_vacation_type}
                    onChange={formik.handleChange}
                    formik={formik}
                  />
                  <DateRange
                    name={`family_vacation[${index}].period`}
                    formik={formik}
                    values={defaultValue.period}
                  />
                </div>
              )}
            />
          </section>
        </>
      )}
      <section>
        <h3 className={styles.stepTitle}>{'betrieblicher Umzug'}</h3>
        <DateRange
          name="corporate_relocation"
          formik={formik}
          values={formik.values.corporate_relocation}
        />
      </section>
      <section>
        <FieldArray
          isGroup
          name="employee_sick"
          label="Krankheit"
          formik={formik}
          renderField={(val, index) => (
            <div>
              <EmployeeSickForm
                formik={formik}
                employeeId={employeeId}
                index={index}
              />
            </div>
          )}
        />
      </section>
      <section>
        <FieldArray
          isGroup
          name="leave"
          label="Elterliche Freistellung"
          formik={formik}
          renderField={(val, index) => (
            <div>
              <Select
                name={`leave[${index}].type`}
                placeholder="Art"
                label="Art"
                isSearchable={false}
                options={leaveOptions}
                value={val.type}
                // formik={formik}
                onChange={(val) => {
                  if (val?.value === 'Mutterschutz') {
                    const found = childrenOptions.find(
                      (option) => option.value === 'Schwangerschaft',
                    )
                    if (!found) {
                      setChildrenOpts([
                        ...childrenOpts,
                        {
                          label: 'Schwangerschaft',
                          value: 'Schwangerschaft',
                        } as Option,
                      ])
                    }
                  } else {
                    const nativeOptions = childrenOptions as Option[]
                    const found = nativeOptions.find(
                      (option) => option.value === 'Schwangerschaft',
                    )
                    if (found) {
                      const index = childrenOptions.findIndex(
                        (object) => object.label === 'Schwangerschaft',
                      )
                      setChildrenOpts(
                        nativeOptions.splice(index, 1) as Option[],
                      )
                    } else {
                      setChildrenOpts(nativeOptions as Option[])
                    }
                  }
                  formik.setFieldValue(`leave[${index}].type`, val)
                }}
              />
              <Select
                name={`leave[${index}].child`}
                placeholder="Kind Auswählen"
                label="Kind Auswählen"
                isCreatable
                options={childrenOpts}
                value={val.child}
                formik={formik}
              />
              <DateRange
                key={`leave-${index}.period`}
                name={`leave[${index}].period`}
                formik={formik}
                values={val.period}
              />
            </div>
          )}
        />
      </section>
      <section>
        <HtmlEditor
          name="sb_etc"
          label="Andere Soziale Leistungen"
          formik={formik}
          defaultValue={formik.values.sb_etc}
        />
        <HtmlEditor
          name="medical_services"
          label="Medizinische Leistungen"
          placeholder="G27, Arbeitsschutz, Betriebssportverein"
          formik={formik}
          defaultValue={formik.values.medical_services}
        />
        <HtmlEditor
          name="monetary_benefits"
          label="Geldwerte Vorteile"
          formik={formik}
          defaultValue={formik.values.monetary_benefits}
        />

        <HtmlEditor
          name="qualifications"
          label="Qualifizierungen/Weiterbildungen"
          formik={formik}
          defaultValue={formik.values.qualifications}
        />
      </section>
    </>
  )
}

export default Step3
