import {
  FormOptions,
  getFormSelectOptions,
} from 'containers/Ives/Employees/EmployeeForm/formData'
import { format } from 'date-fns'
import { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { Employee, ReduxState, UserRole } from 'types'

import styles from '../styles.module.scss'

interface EmployeeData {
  employeeTitle: Employee | undefined
  currentRecord?: Employee
}

const BasePrintTemplate = ({ currentRecord, employeeTitle }: EmployeeData) => {
  const [options, setOptions] = useState<FormOptions>({
    projectGroups: [],
    familyOptions: [],
    companyFunctionOptions: [],
    locationOptions: [],
    academicOptions: [],
    hourlyModelOptions: [],
    competenceFunctionOptions: [],
    caOptions: [],
    crossCompetenceOptions: [],
    crossGroupOptions: [],
    workingGroupOptions: [],
    workingFunctionOptions: [],
    workingDaysOptions: [],
  })

  useEffect(() => {
    getFormSelectOptions().then((res: FormOptions) => {
      setOptions(res)
    })
  }, [])

  const filterData = (id: string | number | undefined, object: any) => {
    const data = object.filter((option: any) => option.value === id)[0]
    return data ? data.label : ''
  }
  const currentProjectGroup = useSelector(({ user }: ReduxState) => user.user)
    ?.roles
  const academicDegree = currentRecord?.academic_degrees?.map((item) => {
    const academicDegree = options?.academicOptions.find(
      (option) => option.value === item,
    )
    return [academicDegree?.label]
  })
  return (
    <div className={styles.detailMainWrap}>
      <div className={styles.detailHeader}>
        <h2>{'Stammdaten'}</h2>
      </div>
      {/* Basic data overview */}
      <div className={styles.dataBody}>
        <h4 className={styles.fieldTitle}>{'Aktuelle Daten'}</h4>
        <div className={styles.dataGrid}>
          <div className={styles.dataWrap}>
            <span className={styles.dataLabel}>{'Titel, Name'}</span>
            <br />
            <span className={styles.dataValue}>
              {`${currentRecord?.first_name} ${currentRecord?.last_name}` ||
                'Keine Angabe'}
            </span>
          </div>
          <div className={styles.dataWrap}>
            <span className={styles.dataLabel}>{'Einstiegsdatum'}</span>
            <br />
            {(currentRecord?.entry_date && (
              <span className={styles.dataValue}>
                {`${format(
                  new Date(currentRecord?.entry_date as string),
                  'dd.MM.yyyy',
                )}` || 'Keine Angabe'}
              </span>
            )) ||
              'Keine Angabe'}
          </div>
          <div className={styles.dataWrap}>
            <span className={styles.dataLabel}>{'Position/Projektgruppe'}</span>
            <br />
            <span className={styles.dataValue}>
              {`${employeeTitle?.company_function} bei ${employeeTitle?.project_group}` ||
                'Keine Angabe'}
            </span>
          </div>
          <div className={styles.dataWrap}>
            <span className={styles.dataLabel}>{'AG/KB/Rolle in der AG'}</span>
            <br />
            <span className={styles.dataArray}>
              {currentRecord?.working_groups?.length > 0
                ? currentRecord.working_groups?.map((item, index) => (
                    <p key={index}>
                      {`${filterData(
                        item.working_group,
                        options.workingGroupOptions,
                      )} - ${filterData(
                        item.working_group_function,
                        options.workingFunctionOptions,
                      )}`}
                    </p>
                  ))
                : 'Keine Angabe'}
            </span>
          </div>
          <div className={styles.dataWrap}>
            <span className={styles.dataLabel}>
              {'KPZ und Rolle in der KPZ'}
            </span>
            <br />
            <span className={styles.dataArray}>
              {currentRecord?.competence_centers?.length > 0
                ? currentRecord.competence_centers?.map((item, index) => (
                    <p key={index}>
                      {`${filterData(
                        item.competence_center,
                        options.competenceCenterOptions,
                      )} - ${filterData(
                        item.function_in_compete as any,
                        options.competenceFunctionOptions,
                      )}\`}`}
                    </p>
                  ))
                : 'Keine Angabe'}

              {(currentProjectGroup?.includes(UserRole.HumanResource) &&
                currentRecord?.working_groups?.map((item, index) => (
                  <p key={index}>
                    {`${filterData(
                      item.working_group,
                      options.workingGroupOptions,
                    )} - ${filterData(
                      item.working_group_function,
                      options.workingFunctionOptions,
                    )}`}
                  </p>
                ))) ||
                'Keine Angabe'}
            </span>
          </div>
        </div>
      </div>
      {/* Basic data detail */}
      <div className={styles.detailMainInner}>
        <div className={styles.detailBody}>
          <div className={styles.dataBody}>
            <h4 className={styles.fieldTitle}>{'Weitere Stammdaten'}</h4>
            <div className={styles.dataGrid}>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Geburtstag, Anschrift'}
                </span>
                <br />
                {(currentRecord?.birth_date && (
                  <span className={styles.dataValue}>
                    {`${format(
                      new Date(currentRecord?.birth_date as string),
                      'dd.MM.yyyy',
                    )}` || 0}
                  </span>
                )) ||
                  'Keine Angabe'}
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'Urlaubsanspruch'}</span>
                <br />
                <span className={styles.dataValue}>
                  {`${currentRecord?.vacation_year || 0} Tage`}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Zusätzlicher Urlaubsanspruch'}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {currentRecord?.exit_date || ' - '}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Kammer, bes. Abschlüsse'}
                </span>
                <br />
                {academicDegree?.length ? (
                  <span className={styles.dataValue}>
                    {academicDegree?.map(
                      (item, index) =>
                        `${item} ${
                          academicDegree?.length - 1 === index ? '' : ', '
                        }`,
                    )}
                  </span>
                ) : (
                  <span className={styles.dataValue}>{'Keine Angabe'}</span>
                )}
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'Austrittsdatum'}</span>
                <br />
                <span className={styles.dataValue}>{' - '}</span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'Telefon (Privat) '}</span>
                <br />
                <span className={styles.dataValue}>
                  {currentRecord?.contact_phone || 'Keine Angabe'}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'Handy (Privat) '}</span>
                <br />
                <span className={styles.dataValue}>
                  {currentRecord?.contact_mobile || 'Keine Angabe'}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'Standort'}</span>
                <br />
                <span className={styles.dataValue}>
                  {filterData(
                    currentRecord?.location,
                    options.locationOptions,
                  ) || 'Keine Angabe'}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'E-Mail (Arbeit)'}</span>
                <br />
                <span className={styles.dataValue}>
                  {currentRecord?.corporate_email || 'Keine Angabe'}
                </span>
              </div>
            </div>
          </div>
          <div className={styles.dataBody}>
            <h4 className={styles.fieldTitle}>{'Kinder'}</h4>
            <div className={styles.dataGrid}>
              {currentRecord?.children?.map((child, index) => (
                <div key={index}>
                  <span className={styles.dataValue}>
                    <p>{child.name || 'Keine Angabe'}</p>
                    {child.date ? (
                      <p>{format(new Date(child.date), 'dd.MM.yyyy')}</p>
                    ) : null}
                  </span>
                  <br />
                </div>
              )) || 'Keine Angabe'}
            </div>
          </div>
          <div className={styles.dataBody}>
            <h4 className={styles.fieldTitle}>{'Verlauf Position PG'}</h4>
            <div className={styles.dataGrid}>
              <div className={styles.dateIncludedField}>
                <span className={styles.dateLabel}>
                  {new Date().getFullYear()}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {`${employeeTitle?.company_function}
                   bei ${employeeTitle?.project_group}` || 'Keine Angabe'}
                </span>
              </div>
            </div>
          </div>
          <div className={styles.dataBody}>
            <h4 className={styles.fieldTitle}>{'Verlauf Position AG/KB'}</h4>
            <div className={styles.dataGrid}>
              <div className={styles.dateIncludedField}>
                <span className={styles.dateLabel}>
                  {new Date().getFullYear()}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {currentRecord?.working_groups?.map((item, index) => (
                    <p key={index}>
                      {`${filterData(
                        item.working_group_function,
                        options.workingFunctionOptions,
                      )}
                        in
                        ${filterData(
                          item.working_group,
                          options.workingGroupOptions,
                        )}
                        `}
                    </p>
                  )) || 'Keine Angabe'}
                </span>
              </div>
            </div>
          </div>
          <div className={styles.dataBody}>
            <h4 className={styles.fieldTitle}>{'Letzte Gesprächsnotizen'}</h4>
            <div className={styles.dataGrid}>
              <span className={styles.dataValue}>
                {currentProjectGroup?.includes(UserRole.HumanResource) &&
                  currentRecord?.notes_main?.flatMap((note, index) => (
                    <div className={styles.dateIncludedField} key={index}>
                      <span className={styles.dateLabel}>
                        {note.created &&
                          note.created.substr(
                            note.created.indexOf(', ') + 1,
                            note.created.indexOf(' - ') - 3,
                          )}
                      </span>
                      <br />
                      <span className={styles.dataValue}>{note.note}</span>
                    </div>
                  ))}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default BasePrintTemplate
