import { ReactComponent as ArrowNext } from 'assets/icons/arrowNextOrange.svg'
import classNames from 'classnames'
import Form from 'common/Form'
import Button from 'components/Button'
import { FormEvent } from 'react'

import styles from './styles.module.scss'

interface FormProps {
  step: number
  currentStep: React.ReactNode
  stepCount: number
  onCancel(): void
  back(): void
  setStep(step: number): void
  handleSubmit(event?: FormEvent<HTMLFormElement>): void
}

const FormView = ({
  step,
  currentStep,
  stepCount,
  setStep,
  back,
  onCancel,
  handleSubmit,
}: FormProps) => (
  <Form
    handleSubmit={handleSubmit}
    title="Stammdaten"
    header={
      <div className={styles.formHeader}>
        <div className={styles.progress}>
          {new Array(stepCount).fill('').map((s, i) => (
            <button
              key={`step-${i}`}
              type="button"
              className={classNames({ [styles.active]: step >= i + 1 })}
              onClick={() => setStep(i + 1)}
            >
              {i + 1}
            </button>
          ))}
        </div>
      </div>
    }
    prevButton={
      step > 1 ? (
        <Button
          variant="text"
          className={styles.buttonPrimary}
          type="button"
          onClick={back}
        >
          <ArrowNext /> {'Zurück'}
        </Button>
      ) : (
        <></>
      )
    }
    onCancel={onCancel}
    isNextStep={step < stepCount}
  >
    <div id="employeeForm">{currentStep}</div>
  </Form>
)

export default FormView
