import { SelectChangeEvent } from '@mui/material'
import cn from 'classnames'
import FullscreenLoader from 'common/FullscreenLoader'
import Layout from 'common/Layout'
import Modal from 'common/Modal'
import Button from 'components/Button'
import LinkItem from 'components/LinkItem'
import { PopupTypes } from 'constants/holidaysPage'
import { UserAccess } from 'constants/index'
import {
  eachDayOfInterval,
  fromUnixTime,
  getUnixTime,
  isWeekend,
} from 'date-fns'
import useToggle from 'hooks/useToggle'
import { useCallback, useEffect, useMemo, useState } from 'react'
import { CSVLink } from 'react-csv'
import { useDispatch, useSelector } from 'react-redux'
import api from 'services/api'
import { formatDate } from 'services/date'
import notify from 'services/notify'
import { getEmployeeRecord, getEmployees } from 'store/ives/employees/actions'
import {
  getHolidayEmployeeView,
  getHolidays,
  getNationalHolidays,
} from 'store/ives/holidays/actions'
import ActionType from 'store/ives/holidays/types'
import {
  Employee,
  EmployeeHolidays,
  EmployeeHolidaysInner,
  Holiday,
  HolidaySubstitutes,
  MaternityLeave,
  ReduxState,
  TimeTableItemData,
  UserRole,
} from 'types'

import AbsenceView from './AbsenceView'
import HolidayForm from './HolidayForm/ApplicationForm'
import ChangeSubstitutionForm from './HolidayForm/ChangeSubstitutionForm'
import PersonalView, { translateState } from './HolidayPersonalView'
import styles from './styles.module.scss'
import ContentView from './view'

export interface Absentees {
  id: number
  name: string
  to: number
  substitutes?: string
  reason?: string
  sickDays?: number
}

const Holidays: React.FC = () => {
  const [searchValue, setSearchValue] = useState<string>()
  const [employeeRecord, setEmployeeRecord] = useState<Employee>()
  const [isLoading, setIsLoading] = useState(false)
  const applicationToggle = useToggle()
  const absenceToggle = useToggle()
  const [projectGroup, setProjectGroup] = useState('')
  const [csvData, setCsvData] = useState([])
  const [csvHeader, setCsvHeader] = useState([])
  // const [csvHeader, setCsvHeader] = useState([])
  const [dateFormat, setDateFormat] = useState({
    from_date: '',
    to_date: '',
  })
  const [popupType, setPopupType] = useState<PopupTypes>(PopupTypes.default)
  const [currentUser, setCurrentUser] = useState<TimeTableItemData | null>(null)
  const [absentEmployees, setAbsentEmployees] = useState<Absentees[] | null>([])
  const { user } = useSelector((state: ReduxState) => state.user)
  const { employees, showEmployeeView: oldShowEmployeeView } = useSelector(
    ({ ives }: ReduxState) => ives.holidays,
  )
  const [showEmployeeView, setShowEmployeeView] = useState(oldShowEmployeeView)
  const [employeesData, setEmployeesData] = useState<EmployeeHolidays[] | null>(
    employees,
  )
  const leader = user?.project_group.leader
  const allOption =
    user?.access?.[UserAccess.createEmployeeHolidayForAny] || leader
      ? { name: 'Alle', id: 'Alle' }
      : {}

  const { employees: allEmployess } = useSelector(
    ({ ives }: ReduxState) => ives.employees,
  )
  const { holidays } = useSelector((state: ReduxState) => state.ives)
  const dispatch = useDispatch()
  const handleSynchronizeData = async () => {
    try {
      await api.post(
        '/employees/import?_format=json',
        {},
        {
          headers: {
            'Content-Type': 'application/json',
            withCredentials: true,
          },
        },
      )
      dispatch(getEmployees({ query: '' }))
    } catch (error: unknown) {
      notify(error)
      setIsLoading(false)
    }
  }
  useEffect(() => {
    if (allEmployess?.rows.length < 1) {
      handleSynchronizeData().then()
    }
    dispatch(getNationalHolidays())

    dispatch(
      getEmployeeRecord(user?.employee as number, (record: Employee) =>
        setEmployeeRecord(record),
      ),
    )
    dispatch(getHolidayEmployeeView(user?.project_group.id))
  }, [user])
  /** Projects groups in select options */
  const projectGroupsData = useMemo(() => {
    const verifiedGroups = holidays.projectGroups
    const groups = verifiedGroups?.flatMap((item) => ({
      name: item.project_group.name,
      id: item.project_group.id,
    }))
    if (groups) {
      return [allOption, ...groups]
    }
    return []
  }, [holidays.projectGroups])

  const onEditEmployeeSend = () => {
    applicationToggle.off()
    notify('Urlaubsantrag wurde versandt', 'success')
    setEmployeeRecord(undefined)
  }

  const onEditEmployeeClose = () => {
    applicationToggle.off()
    setEmployeeRecord(undefined)
  }

  const onSubmitChangeSubstitution = useCallback(() => {
    applicationToggle.off()
    setCurrentUser(null)
    // dispatch(getHolidays())
    notify('Vertretung wurde geändert', 'success')
  }, [applicationToggle, currentUser])

  /** Select project group */
  const onSelectChange = (event: SelectChangeEvent) => {
    if (event) {
      setProjectGroup(event.target.value)
    }
  }
  const filterCurrentYear = (state: boolean) => {
    if (state && employees) {
      const data = employees.map((user) => ({
        ...user,
        holidays: user?.holidays?.filter(
          ({ from }) =>
            fromUnixTime(from).getFullYear() === new Date().getFullYear(),
        ),
        maternity: user.maternity.filter(
          ({ from }) =>
            fromUnixTime(from).getFullYear() === new Date().getFullYear(),
        ),
        parental: user.parental.filter(
          ({ from }) =>
            fromUnixTime(from).getFullYear() === new Date().getFullYear(),
        ),
      }))
      setEmployeesData(data)
    } else {
      console.log('all holidays', employees)
      setEmployeesData(employees)
    }
  }

  const getEmployeeView = async () => {
    setShowEmployeeView(!showEmployeeView)
    dispatch({
      type: ActionType.SET_EMPLOYEE_VIEW,
      payload: !showEmployeeView,
    })
    setIsLoading(true)
    if (!showEmployeeView) {
      try {
        dispatch(
          // Placeholder for testing
          getHolidayEmployeeView(user?.project_group.id, () => {
            setIsLoading(false)
          }),
        )
      } catch (error: unknown) {
        notify(error)
        setIsLoading(false)
      }
    }
    if (showEmployeeView) {
      try {
        dispatch(
          // Placeholder for testing
          getHolidayEmployeeView(user?.employee, () => {
            setIsLoading(false)
          }),
        )
      } catch (error: unknown) {
        notify(error)
        setIsLoading(false)
      }
    }
  }
  // filters each employee and returns only those who have a holiday today
  const comparisonFunction = (holidayArray: EmployeeHolidays[] | null) => {
    dispatch(
      getHolidays(undefined, (success) => {
        console.log(success)
        const newTempObject: Absentees[] = []
        holidayArray?.forEach((holiday: EmployeeHolidays) => {
          holiday?.holidays?.forEach((innerHoliday: Holiday) => {
            if (
              // loops through all holidays and checks if the current date is between the from and to date with the state approved
              innerHoliday.from <= getUnixTime(new Date()) &&
              innerHoliday.to >= getUnixTime(new Date()) &&
              innerHoliday.state === 'approved'
            ) {
              innerHoliday?.substitutes?.forEach(
                (substitute: HolidaySubstitutes) => {
                  if (
                    // loops through all substitutes and checks if the current date is between the from and to date
                    substitute.period.from <=
                      formatDate(new Date(), 'yyyy-MM-dd') &&
                    substitute.period.to >= formatDate(new Date(), 'yyyy-MM-dd')
                  ) {
                    // pushes the filtered fields into the temporary object
                    newTempObject.push({
                      id: holiday.id,
                      name: holiday.name,
                      to: innerHoliday.to,
                      substitutes: substitute.employee_name,
                      reason: 'Urlaub',
                    })
                    // sets the state of the absent employees to the temporary object
                    setAbsentEmployees(newTempObject)
                  }
                },
              )
            }
          })
          // loops through the maternityleave array and checks if the current date is between the from and to date with the state approved
          holiday.maternity.forEach((innerHoliday: MaternityLeave) => {
            if (
              innerHoliday.from <= getUnixTime(new Date()) &&
              innerHoliday.to >= getUnixTime(new Date())
            ) {
              newTempObject.push({
                id: holiday.id,
                name: holiday.name,
                to: innerHoliday.to,
                reason: 'Mutterschutz',
              })
              setAbsentEmployees(newTempObject)
            }
          })
          // loops through the parentalleave array and checks if the current date is between the from and to date with the state approved
          holiday.parental.forEach((innerHoliday: MaternityLeave) => {
            if (
              innerHoliday.from <= getUnixTime(new Date()) &&
              innerHoliday.to >= getUnixTime(new Date())
            ) {
              newTempObject.push({
                id: holiday.id,
                name: holiday.name,
                to: innerHoliday.to,
                reason: 'Elternzeit',
              })
              setAbsentEmployees(newTempObject)
            }
          })
        })
      }),
    )
    // temporary object to store the filtered fields
  }
  useEffect(() => {
    if (currentUser?.start_time) {
      setDateFormat({
        from_date: formatDate(new Date(currentUser?.start_time), 'yyyy-MM-dd'),
        to_date: formatDate(new Date(currentUser?.end_time), 'yyyy-MM-dd'),
      })
    }
  }, [currentUser, employees])

  const minMaxHolidays = () => {
    const minMaxArray = []
    const allDays = ['Vollname']
    employees?.forEach((item) => {
      item.holidays?.forEach((holiday) => {
        minMaxArray.push(new Date(holiday?.from_date).getTime() - 86400000)
        minMaxArray.push(new Date(holiday?.to_date).getTime() + 86400000)
      })
    })
    const minArray = Math.min(...minMaxArray)
    const maxArray = Math.max(...minMaxArray)
    for (let i = minArray; i <= maxArray; i += 86400000) {
      allDays.push(new Date(i).toLocaleDateString('en-GB', { timeZone: 'UTC' }))
    }
    return allDays
  }

  const currentHolidays = (
    holidays: EmployeeHolidaysInner[] | MaternityLeave[],
  ) => {
    const allHolidays: string[] = []
    const allstates: string[] = []
    if (holidays && holidays.length > 0) {
      holidays?.forEach((item: EmployeeHolidaysInner | MaternityLeave) => {
        const intervalArray = eachDayOfInterval({
          start: new Date(item.from_date),
          end: new Date(item.to_date),
        })
        intervalArray.forEach((day) => {
          if (!isWeekend(day)) {
            allHolidays.push(
              new Date(day).toLocaleDateString('en-GB', { timeZone: 'UTC' }),
            )
          }
          if ('state' in item) {
            allstates.push(item.state)
          }
        })
      })
    }
    return { holidays: allHolidays, states: allstates }
  }

  useEffect(() => {
    const days = minMaxHolidays()
    const csv = []
    const csvHead = []
    days.forEach((head) => {
      csvHead.push({ label: head, key: head })
    })
    employees?.forEach((item) => {
      const holidaysArray = currentHolidays(item.holidays)
      const leaveArray = currentHolidays(item.maternity)
      const newData = [item.name]
      days.forEach((person) => {
        if (leaveArray.holidays.includes(person)) {
          newData.push('Elternzeit/Mutterschutz')
        }
        if (holidaysArray.holidays.includes(person)) {
          const personIndex = holidaysArray.holidays.indexOf(person)
          const state = holidaysArray.states[personIndex]
          newData.push(translateState(state))
        } else {
          newData.push('')
        }
      })
      csv.push([newData])
    })
    const convertedArray = csv.map((nestedItem) => {
      const [...values] = nestedItem[0]
      const obj = {}
      days.forEach((key, index) => {
        obj[key] = values[index] || ''
      })

      return obj
    })
    setCsvData(convertedArray)
    setCsvHeader(csvHead)
  }, [])
  /** Forms */
  const getForm = useCallback(() => {
    switch (popupType) {
      case PopupTypes.default:
        return (
          <HolidayForm
            title={
              !showEmployeeView
                ? 'Mein Urlaubsantrag'
                : 'Mitarbeiter Urlaubsantrag'
            }
            onSubmit={onEditEmployeeSend}
            onCancel={onEditEmployeeClose}
            initialData={employeeRecord}
          />
        )
      case PopupTypes.update:
        return (
          <HolidayForm
            title="Urlaubsantrag Verschieben"
            onSubmit={onEditEmployeeSend}
            onCancel={onEditEmployeeClose}
            initialData={employeeRecord}
            disabledChooseEmployee
            holidaysData={dateFormat}
            substitutes={currentUser?.substitutes}
            defaultEmployeeOption={{
              value: currentUser?.group || '',
              label: currentUser?.title || '',
              data: currentUser,
            }}
          />
        )
      case PopupTypes.change:
        return (
          <ChangeSubstitutionForm
            onCancel={onEditEmployeeClose}
            title="Vertretung bearbeiten"
            onSubmit={onSubmitChangeSubstitution}
            currentUser={currentUser}
            inputTextLabel={`Urlaubstage verbleibend: ${user?.holidays_left}`}
          />
        )
      default:
        return null
    }
  }, [popupType, currentUser, employeeRecord])

  return (
    <Layout headerProps={{ withoutSearch: true }}>
      <div className={styles.header}>
        <h1 className={styles.title}>
          {showEmployeeView ? 'Meine' : 'Mitarbeiter'}
          {' Urlaubsübersicht'}
        </h1>
        <div className={`${styles.linkWrap} csv-link`}>
          <div>
            <LinkItem onClick={getEmployeeView}>
              {!showEmployeeView ? 'Meine Übersicht' : 'Mitarbeiterübersicht'}
            </LinkItem>
            {(!showEmployeeView &&
              user?.access?.[UserAccess.createEmployeeHolidayForAny]) ||
            showEmployeeView ? (
              <LinkItem
                onClick={() => {
                  applicationToggle.on()
                  setPopupType(PopupTypes.default)
                }}
              >
                {'Urlaub beantragen'}
              </LinkItem>
            ) : null}
            {!showEmployeeView ? (
              <LinkItem
                onClick={() => {
                  comparisonFunction(employees)
                  absenceToggle.on()
                }}
              >
                {'Abwesenheitsübersicht'}
              </LinkItem>
            ) : null}
          </div>
          {user?.roles.includes(UserRole.Administrator) && (
            <Button
              className={cn(styles.historyBtn, styles.base, styles.headerBtn)}
              variant="fulfilled"
            >
              <CSVLink
                data={csvData}
                headers={csvHeader}
                filename={`UrlaubsHistorien ${new Date()}.csv`}
              >
                {'Vollständige Historie exportieren'}
              </CSVLink>
            </Button>
          )}
        </div>
      </div>
      <Modal
        isOpen={applicationToggle.value}
        onRequestClose={onEditEmployeeClose}
        maxWidth={430}
      >
        {getForm()}
      </Modal>
      <Modal
        isOpen={absenceToggle.value}
        onRequestClose={() => {
          absenceToggle.off()
          dispatch(getHolidayEmployeeView(user?.project_group.id))
        }}
        maxWidth={620}
        withCloseButton
      >
        {absenceToggle.value && (
          <AbsenceView
            compareDate={comparisonFunction}
            absentees={absentEmployees}
          />
        )}
      </Modal>
      {!showEmployeeView ? (
        <ContentView
          dropdownValue={projectGroup}
          handleChange={onSelectChange}
          globalFilter={searchValue}
          setGlobalFilter={setSearchValue}
          menuItems={projectGroupsData}
          applicationToggle={applicationToggle}
          popupType={popupType}
          setPopupType={setPopupType}
          currentUser={currentUser}
          setCurrentUser={setCurrentUser}
          csvData={csvData}
          csvHeader={csvHeader}
        />
      ) : null}
      {showEmployeeView ? (
        <PersonalView
          employeeData={employeesData}
          handleChange={filterCurrentYear}
          workingDays={employeeRecord?.working_days}
        />
      ) : null}
      {!showEmployeeView ? (
        <footer className={styles.legendWrap}>
          <h4 className={styles.legendHeading}>{'Legende: '}</h4>
          <div className={styles.legend}>
            <div className={cn(styles.legendIcons, styles.plannedVacation)}>
              {''}
            </div>
            <p>{'Angedachter Urlaub'}</p>
          </div>
          <div className={styles.legend}>
            <div className={cn(styles.legendIcons, styles.appliedVacation)}>
              {''}
            </div>
            <p>{'Beantragter Urlaub'}</p>
          </div>
          <div className={styles.legend}>
            <div className={cn(styles.legendIcons, styles.confirmedVacation)}>
              {''}
            </div>
            <p>{'Bestätigter Urlaub'}</p>
          </div>
          <div className={styles.legend}>
            <div className={cn(styles.legendIcons, styles.parentalLeave)}>
              {''}
            </div>
            <p>{'Elternzeit/Mutterschutz'}</p>
          </div>
        </footer>
      ) : null}
      <FullscreenLoader isLoading={isLoading} />
    </Layout>
  )
}

export default Holidays
