import Form from 'common/Form'
import TextInput from 'components/inputs/TextInput'
import { FormikProps } from 'formik'

import { FormValues } from '.'

interface FormProps {
  title: string
  formik: FormikProps<FormValues>
  onCancel: () => void
}

const FormView = ({ title, formik, onCancel }: FormProps) => (
  <Form handleSubmit={formik.handleSubmit} title={title} onCancel={onCancel}>
    <TextInput name="name" label="Name des Auftraggebers" formik={formik} />
    <TextInput name="street" label="Straße" formik={formik} />
    <TextInput name="postcode" label="PLZ" formik={formik} />
    <TextInput name="city" label="Ort" formik={formik} />
    <TextInput name="homepage" label="Homepage" formik={formik} />
  </Form>
)

export default FormView
