import { Store } from 'redux'
import { AllAction } from 'store/action'
import { ReduxState } from 'types/index'

let _store: Store<ReduxState, AllAction> | undefined

export const setStore = (store: Store) => {
  _store = store
}
export const getState = () => (_store && _store.getState()) || undefined
export const dispatch = (action: AllAction) => _store && _store.dispatch(action)

export default {
  setStore,
  getState,
  dispatch,
}
