import { SelectChangeEvent } from '@mui/material'
import Dropdown from 'components/inputs/Dropdown'
import GlobalSearch from 'components/tableComponents/GlobalSearch'
import { PopupTypes } from 'constants/holidaysPage'
import { UseToggle } from 'hooks/useToggle'
import { DropDownItems, TimeTableItemData } from 'types'

import HolidayTable from './HolidayTable'
import styles from './styles.module.scss'

interface ContentViewProps {
  handleChange: (event: SelectChangeEvent) => void
  dropdownValue: string
  menuItems: DropDownItems[]
  globalFilter: string | undefined
  setGlobalFilter: (filterValue: string | undefined) => void
  containerClassName?: string
  disabledSearch?: boolean
  applicationToggle: UseToggle
  popupType: PopupTypes
  setPopupType: React.Dispatch<React.SetStateAction<PopupTypes>>
  currentUser: TimeTableItemData | null
  setCurrentUser: React.Dispatch<React.SetStateAction<TimeTableItemData | null>>
  csvData?: any
  csvHeader?: any
}

const ContentView: React.FC<ContentViewProps> = ({
  globalFilter,
  setGlobalFilter,
  dropdownValue,
  handleChange,
  menuItems,
  disabledSearch,
  applicationToggle,
  popupType,
  setPopupType,
  currentUser,
  setCurrentUser,
  csvData,
  csvHeader,
}) => (
  <div className={styles.holidayTable}>
    <div className={styles.tableWidgets}>
      <Dropdown
        onItemChange={handleChange}
        dropdownValue={dropdownValue}
        label="Projektgruppen"
        menuItems={menuItems}
      />
      <GlobalSearch
        globalFilter={globalFilter}
        setGlobalFilter={setGlobalFilter}
        containerClassName={styles.search}
        disabledSearch={disabledSearch}
      />
    </div>
    <HolidayTable
      globalFilter={globalFilter}
      selectedGroup={dropdownValue}
      applicationToggle={applicationToggle}
      popupType={popupType}
      setPopupType={setPopupType}
      currentUser={currentUser}
      setCurrentUser={setCurrentUser}
      csvHeader={csvHeader}
      csvData={csvData}
    />
  </div>
)

export default ContentView
