import Modal from 'common/Modal'
import useToggle from 'hooks/useToggle'
import { useEffect, useMemo, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Column, Row } from 'react-table'
import notify from 'services/notify'
import {
  createCustomer,
  getCustomerRecord,
  getCustomers,
  updateCustomer,
} from 'store/ives/customers/actions'
import { Customer, ReduxState } from 'types'

import CustomerForm, { FormValues } from './CustomerForm'
import ContentView from './view'

export type TableData = {
  id: string
  name: string
  street: string
  postcode: string
  city: string
}

interface CustomersProps {
  tableWidth: number
  tableHeight: number
  onSubmit: (selectedRow?: Row<TableData>) => void
  onCancel: () => void
}

const Customers = ({
  tableWidth,
  tableHeight,
  onSubmit,
  onCancel,
}: CustomersProps) => {
  const newCustomerModal = useToggle()
  const editCustomerModal = useToggle()
  const dispatch = useDispatch()
  const { customers, isLoading } = useSelector(
    ({ ives }: ReduxState) => ives.customers,
  )
  const [selectedRow, setSelectedRow] = useState<Row<TableData>>()
  const [editedCustomer, setEditedCustomer] = useState<Customer>()
  // TODO connect to table when will be endpoint for search
  const [globalFilter, setGlobalFilter] = useState<string>()
  const [tableData, setTableData] = useState<TableData[]>([])

  const hasNextPage = useMemo(
    () =>
      !!(
        customers &&
        (customers.pager.current_page || 0) + 1 < customers.pager.total_pages
      ),
    [customers?.pager],
  )

  const loadNextPage = (startIndex: number, stopIndex: number) => {
    dispatch(getCustomers(stopIndex / 50))
    return null
  }

  useEffect(() => {
    dispatch(getCustomers())
  }, [])

  const _mapCustomerObjToFormValues = (
    customer: Customer,
  ): Omit<FormValues, 'homepage'> => ({
    name: customer.title,
    city: customer.address_city,
    postcode: customer.address_postalcode,
    street: customer.address_address,
  })

  const mappedCustomerToForm = useMemo<FormValues | undefined>(
    () =>
      editedCustomer
        ? {
            ..._mapCustomerObjToFormValues(editedCustomer),
            homepage: editedCustomer.homepage?.text || '',
          }
        : undefined,
    [editedCustomer],
  )

  const mappedCustomersToTable = useMemo(
    () =>
      customers?.rows.map((item) => ({
        id: `${item.nid}`,
        ..._mapCustomerObjToFormValues(item),
      })) || [],
    [customers],
  )

  useEffect(() => {
    setTableData([...tableData, ...mappedCustomersToTable])
  }, [mappedCustomersToTable])

  const columnsData = useMemo<Column<TableData>[]>(
    () => [
      {
        Header: 'Name',
        accessor: 'name', // accessor is the "key" in the data
      },
      {
        Header: 'Straße',
        accessor: 'street',
      },
      {
        Header: 'PLZ',
        accessor: 'postcode',
      },
      {
        Header: 'Ort',
        accessor: 'city',
      },
    ],
    [],
  )

  const _onEdit = (id: string) => {
    editCustomerModal.on()
    dispatch(
      getCustomerRecord(id, (customers) => setEditedCustomer(customers?.[0])),
    )
  }

  const onEditCustomerClick = () => {
    selectedRow
      ? _onEdit(selectedRow.original.id)
      : notify('Bitte wählen Sie einen Kunde aus', 'warning')
  }

  const onRowDbClick = (row?: Row<TableData>) => {
    setSelectedRow(row)
    row && _onEdit(row.original.id)
  }

  const onRowSelect = (row?: Row<TableData>) => {
    setSelectedRow(row)
  }

  const onSubmitClick = () => {
    onSubmit(selectedRow)
  }

  const _mapFormValueToCustomerObj = (
    values: FormValues,
  ): Omit<Customer, 'nid'> => ({
    title: values.name,
    address_address: values.street,
    address_city: values.city,
    address_postalcode: values.postcode,
    homepage: { text: values.homepage, url: values.homepage },
  })

  const onNewCustomerSubmitClick = (values: FormValues) => {
    dispatch(
      createCustomer(
        {
          customer: _mapFormValueToCustomerObj(values),
        },
        () =>
          notify(
            'Neuer Geschäftspartner wurde erfolgreich angelegt',
            'success',
          ),
      ),
    )
    newCustomerModal.off()
  }

  const onEditCustomerSubmitClick = (values: FormValues) => {
    dispatch(
      updateCustomer(
        {
          customer: {
            ..._mapFormValueToCustomerObj(values),
            nid: selectedRow?.original.id || '',
          },
        },
        () =>
          notify('Geschäftspartner wurde erfolgreich bearbeitet', 'success'),
      ),
    )
    editCustomerModal.off()
  }

  const onEditCustomerCancel = () => {
    setEditedCustomer(undefined)
    editCustomerModal.off()
  }

  return (
    <>
      <ContentView
        onNewCustomerClick={newCustomerModal.on}
        onEditCustomerClick={onEditCustomerClick}
        onSubmit={onSubmitClick}
        onCancel={onCancel}
        onRowSelect={onRowSelect}
        onRowDbClick={onRowDbClick}
        tableData={tableData}
        columnsData={columnsData}
        tableWidth={tableWidth}
        tableHeight={tableHeight}
        globalFilter={globalFilter}
        setGlobalFilter={setGlobalFilter}
        hasNextPage={hasNextPage}
        isNextPageLoading={isLoading}
        loadNextPage={loadNextPage}
      />
      <Modal
        isOpen={newCustomerModal.value}
        onRequestClose={newCustomerModal.off}
      >
        <CustomerForm
          title="Erstellung von Geschäftspartnern"
          onCancel={newCustomerModal.off}
          onSubmit={onNewCustomerSubmitClick}
        />
      </Modal>
      <Modal
        isOpen={!!(editCustomerModal.value && editedCustomer)}
        onRequestClose={onEditCustomerCancel}
      >
        <CustomerForm
          title="Bearbeitung von Geschäftspartnern"
          onCancel={onEditCustomerCancel}
          onSubmit={onEditCustomerSubmitClick}
          initialData={mappedCustomerToForm}
        />
      </Modal>
    </>
  )
}

export default Customers
