/* eslint-disable no-param-reassign */
import DetailsPageWidget from 'common/widgets/DetailsPage'
import GroupPreviewWidget from 'common/widgets/GroupPreview'
import NewsPreviewWidget from 'common/widgets/NewsPreview'
import ProcessLinkWidget from 'common/widgets/ProcessLink'
import ProcessWidget from 'common/widgets/ProcessWidget'
import TestWidget from 'common/widgets/TestWidget'
// import initialLayouts from 'constants/dashboardLayout'
import Widgets from 'constants/widgets'
import { useCallback, useEffect, useMemo, useState } from 'react'
import { Item, ItemParams, Menu, useContextMenu } from 'react-contexify'
import { Layout, Responsive, WidthProvider } from 'react-grid-layout'
import { useDispatch, useSelector } from 'react-redux'
import { removeWidget, updateWidgets } from 'store/user/actions'
import Swiper from 'swiper'
import { ReduxState } from 'types'
import { AnyWidget } from 'types/settings'

const MENU_ID = 'my-s&p-context-menu'

interface ContextMenuProps {
  id: string
}

const ResponsiveGridLayout = WidthProvider(Responsive)

/**
 * @returns > 0 - growing; < 0 - shrinking
 */
const verticalResize = (newItem: Layout, oldItem: Layout) =>
  newItem.h - oldItem.h

const horizontalResize = (newItem: Layout, oldItem: Layout) =>
  newItem.w - oldItem.w

const onProcessWidgetResize = (newItem: Layout, oldItem: Layout) => {
  const verticalCoef = verticalResize(newItem, oldItem)
  const horizontalCoef = horizontalResize(newItem, oldItem)

  if (verticalCoef !== 0 && horizontalCoef !== 0) {
    verticalCoef < horizontalCoef
      ? (newItem.w = newItem.h)
      : (newItem.h = newItem.w)
  }

  if (verticalCoef !== 0) {
    newItem.w = newItem.h
  }

  if (horizontalCoef !== 0) {
    newItem.h = newItem.w
  }

  if (newItem.h < 4.1) {
    newItem.h = 4.1
  }
}

const GridLayout = () => {
  const [contentSwiper, setContentSwiper] = useState<Swiper>()
  const [layouts, setLayouts] = useState<ReactGridLayout.Layouts>({})
  const [currentBreakpoint, setCurrentBreakpoint] = useState<string>('lg')
  const widgets = useSelector(
    ({ user }: ReduxState) => user.settings?.mySuP.widgets || [],
  )
  const dispatch = useDispatch()

  const { show } = useContextMenu({ id: MENU_ID })

  useEffect(() => {
    const settingsLayouts = widgets.map(({ layout }) => layout)
    setLayouts({ lg: settingsLayouts })
  }, [widgets])

  // TODO implement widgets deleting
  // const onPutItem = (item: any) => {
  //   setLayouts({
  //     ...layouts,
  //     [currentBreakpoint]: layouts[currentBreakpoint].filter(
  //       ({ i }) => i !== item.i,
  //     ),
  //   })
  // }

  const onTakeItem = (item: Layout) => {
    setLayouts({
      ...layouts,
      [currentBreakpoint]: [...layouts[currentBreakpoint], item],
    })
  }

  const onDrop = (layout: Layout[], layoutItem: Layout) => {
    onTakeItem({ ...layoutItem, i: 'test', w: 2, h: 2 })
  }

  const getWidget = (widget: AnyWidget) => {
    switch (widget.type) {
      case Widgets.process: {
        return (
          <ProcessWidget title="" showContextMenu={show} id={widget.layout.i} />
        )
      }
      case Widgets.newsPreview: {
        return (
          <NewsPreviewWidget
            id={widget.layout.i}
            title={widget.title}
            categoryId={widget.data.categoryId}
            onInit={setContentSwiper}
            showContextMenu={show}
          />
        )
      }
      case Widgets.groupPreview: {
        return (
          <GroupPreviewWidget
            id={widget.layout.i}
            title={widget.title}
            groupId={widget.data.groupId}
            showContextMenu={show}
          />
        )
      }
      case Widgets.detailsPage: {
        return (
          <DetailsPageWidget
            id={widget.layout.i}
            title={widget.title}
            showContextMenu={show}
            {...widget.data}
          />
        )
      }
      case Widgets.processLink: {
        return (
          <ProcessLinkWidget
            id={widget.layout.i}
            title={widget.title}
            showContextMenu={show}
            {...widget.data}
          />
        )
      }
      default: {
        return null
      }
    }
  }

  const mappedWidgets = useMemo(
    () =>
      widgets.map((widget) => ({
        key: widget.layout.i,
        component: getWidget(widget),
      })),
    [widgets],
  )

  const onLayoutChange = useCallback(
    (layoutsParam) => {
      dispatch(updateWidgets(layoutsParam))
    },
    [layouts],
  )

  return (
    <>
      <ResponsiveGridLayout
        // width={1500}
        // preventCollision
        className="layout"
        layouts={layouts}
        isDraggable
        isResizable
        isBounded
        isDroppable
        compactType="vertical"
        onBreakpointChange={setCurrentBreakpoint}
        autoSize
        useCSSTransforms
        draggableHandle=".draggable"
        onLayoutChange={onLayoutChange}
        breakpoints={{ lg: 1280, md: 992, sm: 767, xs: 480, xxs: 0 }}
        cols={{ lg: 16, md: 14, sm: 10, xs: 8, xxs: 4 }}
        rowHeight={40}
        margin={[32, 32]}
        containerPadding={[30, 200]}
        onResize={(_layout, oldItem, newItem) => {
          if (newItem.i === Widgets.process) {
            onProcessWidgetResize(newItem, oldItem)
          }

          if (newItem.i === Widgets.content) {
            contentSwiper && contentSwiper.update()
          }
        }}
        onResizeStop={(_layout, _oldItem, newItem) => {
          if (newItem.i === Widgets.content) {
            contentSwiper && contentSwiper.update()
          }
        }}
        onDrop={onDrop}
        measureBeforeMount
      >
        {mappedWidgets.map((w) => (
          <div key={w.key}>{w.component}</div>
        ))}
        <div
          key="test"
          style={{
            display: layouts[currentBreakpoint]?.find(
              (item) => item.i === 'test',
            )
              ? 'block'
              : 'none',
          }}
        >
          <TestWidget />
        </div>
      </ResponsiveGridLayout>

      <Menu id={MENU_ID}>
        <Item
          onClick={({ props }: ItemParams<ContextMenuProps>) => {
            props && dispatch(removeWidget(props.id))
          }}
        >
          {'Widget entfernen'}
        </Item>
      </Menu>
    </>
  )
}
export default GridLayout
