import { ReactComponent as Search } from 'assets/icons/search.svg'
import cn from 'classnames'
import Button from 'components/Button'
import Select from 'components/inputs/Select'
import { ChangeEvent, useEffect, useState } from 'react'
import { OptionsType, ValueType } from 'react-select'
import strings from 'strings'
import { SelectOption } from 'types'

import styles from './styles.module.scss'

interface SearchInputProps
  extends Omit<React.InputHTMLAttributes<HTMLInputElement>, 'onChange'> {
  containerClassName?: string
  className?: string
  defaultValue?: string
  ariaLabel?: string
  searchDropdownOptions?: OptionsType<SelectOption>
  onClick?: () => void
  onChange?: (value: string) => void
  disabledSearch?: boolean
}

const SearchInput = ({
  containerClassName,
  className,
  defaultValue,
  placeholder,
  ariaLabel,
  onClick,
  value,
  searchDropdownOptions,
  onChange,
  disabledSearch = false,
}: SearchInputProps): JSX.Element => {
  const [searchVal, setSearchVal] = useState(defaultValue)
  const [selectVal, setSelectVal] = useState<ValueType<SelectOption, false>>()

  const onSearchChange = (e: ChangeEvent<HTMLInputElement>) => {
    onChange ? onChange(e.target.value) : setSearchVal(e.target.value)
  }

  useEffect(() => {
    onChange?.(selectVal?.value || '')
  }, [selectVal])

  useEffect(() => {
    searchDropdownOptions &&
      setSelectVal(searchDropdownOptions.find((item) => item.value === value))
  }, [value])

  return (
    <form
      role="search"
      className={cn(styles.form, containerClassName)}
      onSubmit={(event) => event.preventDefault()}
    >
      <Button
        type="button"
        id="search"
        onClick={onClick}
        className={styles.searchButton}
      >
        <Search />
      </Button>
      {searchDropdownOptions ? (
        <Select
          name="search"
          value={selectVal}
          onChange={setSelectVal}
          options={searchDropdownOptions}
          containerClassName="search"
          placeholder="Suche"
          isCreatable={false}
          isDisabled={disabledSearch}
        />
      ) : (
        <input
          type="search"
          className={cn(
            styles.input,
            className,
            disabledSearch && styles.inputDisabled,
          )}
          value={value || searchVal}
          onChange={onSearchChange}
          placeholder={placeholder || strings.search}
          aria-label={ariaLabel || strings.search}
          disabled={disabledSearch}
        />
      )}
    </form>
  )
}

export default SearchInput
