import dateTransform from 'common/pipes/dateTime.pipe'
import returnNumberOfWorkDays from 'common/pipes/returnCorrectNumberOfDays.pipe'
import Table from 'common/Table'
import { TableData } from 'containers/Ives/Holidays/HolidayPersonalView'
import { format, fromUnixTime, parse } from 'date-fns'
import { useMemo } from 'react'
import { Item, Menu, useContextMenu } from 'react-contexify'
import { CSVLink } from 'react-csv'
import { useSelector } from 'react-redux'
import { Column, Row } from 'react-table'
import { EmployeeHolidays, ReduxState } from 'types'

import styles from '../styles.module.scss'

interface HolidayModalProps {
  holidayData: EmployeeHolidays[] | undefined
  workingDays?: number[]
  shouldHaveMenu?: boolean
}

const HolidayModal = ({
  holidayData,
  workingDays,
  shouldHaveMenu,
}: HolidayModalProps) => {
  const { nationalHolidays } = useSelector(
    ({ ives }: ReduxState) => ives.holidays,
  )
  const MENU_ID = 'holidays-menu-id'

  const translateState = (state: string) => {
    switch (state) {
      case 'approved':
        return 'Urlaub Genehmigt'
      case 'rejected':
        return 'Urlaub Abgelehnt'
      case 'pending':
        return 'Urlaub Ausstehend'
      case 'planned':
        return 'Urlaub Angedacht'
      case 'requested':
        return 'Urlaub Beantragt'
      default:
        return 'Urlaub Ausstehend'
    }
  }

  const tableData = (): TableData[] =>
    holidayData?.flatMap((holiday) => [
      ...(holiday.holidays?.flatMap((holiday) => ({
        id: holiday.id || 0,
        from: format(fromUnixTime(holiday.from), 'dd.MM.yyyy') || '',
        to: format(fromUnixTime(holiday.to), 'dd.MM.yyyy') || '',
        state: translateState(holiday.state) || '',
        days:
          returnNumberOfWorkDays(
            holiday.from,
            holiday.to,
            nationalHolidays,
            workingDays,
          ).toString() || '0',
        substitute:
          holiday.substitutes.length > 0
            ? holiday.substitutes
                .map((sub) => `${sub.employee_name}`)
                .join('\n')
            : 'Keine Vertretung',
      })) || []),
      ...(holiday.maternity?.flatMap((maternity) => ({
        id: maternity.id || 0,
        from: format(fromUnixTime(maternity.from), 'dd.MM.yyyy') || '',
        to: format(fromUnixTime(maternity.to), 'dd.MM.yyyy') || '',
        state: 'Mutterschaftsurlaub',
        days:
          returnNumberOfWorkDays(
            maternity.from,
            maternity.to,
            nationalHolidays,
            workingDays,
          ).toString() || '0',
        substitute: 'Mutterschaftsurlaub' || '',
      })) || []),
      ...(holiday.parental?.flatMap((parental) => ({
        id: parental.id || 0,
        from: format(fromUnixTime(parental.from), 'dd.MM.yyyy') || '',
        to: format(fromUnixTime(parental.to), 'dd.MM.yyyy') || '',
        state: 'Elternzeit',
        days:
          returnNumberOfWorkDays(
            parental.from,
            parental.to,
            nationalHolidays,
            workingDays,
          ).toString() || '0',
        substitute: 'Elternzeit',
      })) || []),
    ]) || []

  const columnsData = useMemo<Column<TableData>[]>(
    () => [
      {
        Header: 'Von',
        accessor: 'from',
        isSortedDesc: true,
        sortType: (a, b) => dateTransform(a.values.from, b.values.from),
      },
      {
        Header: 'Bis',
        accessor: 'to',
        sortType: (a, b) => dateTransform(a.values.to, b.values.to),
      },
      {
        Header: 'Status',
        accessor: 'state',
      },
      {
        Header: 'Tage',
        accessor: 'days',
      },
      {
        Header: 'Vertretung',
        accessor: 'substitute',
      },
    ],
    [],
  )
  const headers = columnsData.map((el) => ({
    label: el.Header,
    key: el.accessor,
  }))
  const { show } = useContextMenu({
    id: MENU_ID,
  })
  const displayMenu = (row: Row<any>, e: React.MouseEvent) =>
    show(e, { props: { row } })
  return (
    <div className={styles.modalWrapper}>
      <Table
        columnsData={columnsData as any}
        onContextMenu={displayMenu}
        tableData={
          tableData().sort(
            (a, b) =>
              parse(b.from, 'dd.MM.yyyy', new Date()).valueOf() -
              parse(a.from, 'dd.MM.yyyy', new Date()).valueOf(),
          ) as any
        }
      />
      {shouldHaveMenu && (
        <Menu id={MENU_ID}>
          <Item>
            <CSVLink
              filename={`UrlaubsHistorien${new Date()}.csv`}
              data={tableData}
              headers={[...headers] as any}
            >
              {'Als excel Tabelle exportieren'}
            </CSVLink>
          </Item>
        </Menu>
      )}
    </div>
  )
}

export default HolidayModal
