import { FormikProps } from 'formik'
import NumberFormat, {
  NumberFormatValues,
  SourceInfo,
} from 'react-number-format'

import TextInput from '../TextInput'

interface CustomInputProps {
  name: string
  id?: string
  label: string
  disabled?: boolean
  className?: string
  formik?: FormikProps<any>
  thousandSeparator?: boolean
  format?: string
  prefix?: string
  suffix?: string
  value?: string | number
  onChange?: (values: NumberFormatValues, sourceInfo: SourceInfo) => void
}

const NumberInput = ({
  id,
  name,
  label,
  disabled,
  className,
  value,
  thousandSeparator,
  prefix,
  suffix,
  format,
  formik,
  onChange,
}: CustomInputProps) => (
  <NumberFormat
    name={name}
    id={id}
    label={label}
    className={className}
    value={value || formik?.values[name]}
    onValueChange={(values, sourceInfo) => {
      formik?.setFieldValue(`${name}`, values.value)
      if (onChange) {
        onChange(values, sourceInfo)
      }
    }}
    customInput={TextInput}
    prefix={prefix}
    suffix={suffix}
    format={format}
    thousandSeparator={thousandSeparator}
    disabled={disabled}
  />
)

export default NumberInput
