import { Accordion, AccordionDetails, AccordionSummary } from '@mui/material'
import { ReactComponent as PlusIcon } from 'assets/icons/add.svg'
import ImageContainer from 'common/ImageContainer'
import ButtonLink from 'components/ButtonLink'
import { UserAccess } from 'constants/index'
import { DomElement } from 'htmlparser2'
import ReactHtmlParser, { convertNodeToElement } from 'react-html-parser'
import { DetailsPage, User } from 'types'

import sharedStyles from '../styles.module.scss'

const groupAccordions = (parentNode: DomElement) =>
  parentNode.children
    ?.filter(({ name }) => name && ['dt', 'dd'].includes(name))
    ?.reduce((prev, curr) => {
      if (curr.name === 'dt') return [...prev, [...(curr.children || [])]]

      return [
        ...prev.slice(0, -1),
        [prev[prev.length - 1][0], ...(curr.children || [])],
      ]
    }, [] as DomElement[][])

const transform = (node: DomElement) => {
  if (node.data?.includes('@sup-sahlmann.com')) {
    return null
  }
  const convertImage = (image: DomElement) => ({
    src: image.attribs?.src || '',
    element: convertNodeToElement(image, 0, transform),
  })

  if (node.attribs?.class === 'cke-ig') {
    return (
      <div className="cke-ig">
        <ImageContainer images={node.children?.map(convertImage) || []} />
      </div>
    )
  }

  if (node.name === 'img' && node.parent?.attribs?.class !== 'cke-ig') {
    return <ImageContainer images={[convertImage(node)]} />
  }

  if (node.name !== 'dl') {
    return undefined
  }

  const groupedChildren = groupAccordions(node)

  return (
    <>
      {groupedChildren?.map(([heading, ...content], index) => (
        <Accordion
          key={index}
          disableGutters
          className={sharedStyles.accordion}
          square
        >
          <AccordionSummary
            expandIcon={<PlusIcon className={sharedStyles.plusIcon} />}
            className={sharedStyles.summary}
          >
            {convertNodeToElement(heading, 0, transform)}
          </AccordionSummary>
          <AccordionDetails className={sharedStyles.content}>
            {content.map((details, index) =>
              convertNodeToElement(details, index, transform),
            )}
          </AccordionDetails>
        </Accordion>
      ))}
    </>
  )
}

interface Props {
  page: DetailsPage
  editing: boolean
  editingLink: string
  user: User
}

const DetailsPageComponent: React.FC<Props> = ({
  page,
  editing,
  editingLink,
  user,
}) => (
  <>
    {page?.edit_link &&
      user?.access[UserAccess.editDetailPageContent] &&
      !editing && (
        <div className={sharedStyles.editButtonRow}>
          <ButtonLink
            href={editingLink}
            target="_blank"
            rel="noreferrer"
            variant="fulfilled"
            color="orange"
          >
            {'Seite Bearbeiten'}
          </ButtonLink>
        </div>
      )}
    {page?.email && (
      <div className={sharedStyles.editButtonRow}>
        <ButtonLink
          href={`mailto:${page.email}`}
          target="_blank"
          rel="noreferrer"
          variant="fulfilled"
          color="orange"
        >
          {'Per Email kontaktieren'}
        </ButtonLink>
      </div>
    )}
    {editing && page?.edit_link ? (
      <iframe
        className={sharedStyles.editor}
        title="page-edit"
        src={page.edit_link}
      />
    ) : (
      <div className={sharedStyles.detailsPage}>
        {ReactHtmlParser(page?.body || '', {
          transform,
        })}
      </div>
    )}
  </>
)

export default DetailsPageComponent
