import { BackendUserMeta, Error, User, UserWithMeta } from 'types/index'
import { Settings } from 'types/settings'

enum ActionType {
  LOG_IN_BEGIN = '[auth] LOG_IN_BEGIN',
  LOG_IN_SUCCESS = '[auth] LOG_IN_SUCCESS',
  LOG_IN_ERROR = '[auth] LOG_IN_ERROR',
  LOG_OUT_BEGIN = '[auth] LOG_OUT_BEGIN',
  LOG_OUT_SUCCESS = '[auth] LOG_OUT_SUCCESS',
  LOG_OUT_ERROR = '[auth] LOG_OUT_ERROR',
  GET_USER_BEGIN = '[auth] GET_USER_BEGIN',
  GET_USER_SUCCESS = '[auth] GET_USER_SUCCESS',
  GET_USER_ERROR = '[auth] GET_USER_ERROR',
  UPDATE_USER = '[auth] UPDATE_USER',
  REFRESH_TOKEN = '[auth] REFRESH_TOKEN',
  GET_USER_SETTINGS = '[settings] GET_USER_SETTINGS',
  SET_USER_SETTINGS = '[settings] SET_USER_SETTINGS',
}

export interface LogInBeginAction {
  type: ActionType.LOG_IN_BEGIN
}

export interface LogInSuccessAction {
  type: ActionType.LOG_IN_SUCCESS
  payload: UserWithMeta
}

export interface RefreshToken {
  type: ActionType.REFRESH_TOKEN
  payload: BackendUserMeta
}

export interface LogInErrorAction {
  type: ActionType.LOG_IN_ERROR
  payload: Error
}

export interface GetUserBeginAction {
  type: ActionType.GET_USER_BEGIN
}

export interface GetUserSuccessAction {
  type: ActionType.GET_USER_SUCCESS
  payload: UserWithMeta
}

export interface GetUserErrorAction {
  type: ActionType.GET_USER_ERROR
  payload: Error
}

export interface LogOutBeginAction {
  type: ActionType.LOG_OUT_BEGIN
}

export interface LogOutSuccessAction {
  type: ActionType.LOG_OUT_SUCCESS
}

export interface LogOutErrorAction {
  type: ActionType.LOG_OUT_ERROR
  payload: Error
}

export interface UpdateUser {
  type: ActionType.UPDATE_USER
  payload: Partial<User>
}

export interface GetUserSettings {
  type: ActionType.GET_USER_SETTINGS
  payload: Settings
}

export interface SetUserSettings {
  type: ActionType.SET_USER_SETTINGS
  payload: Settings
}

export type Action =
  | LogInBeginAction
  | LogInSuccessAction
  | LogInErrorAction
  | LogOutBeginAction
  | LogOutSuccessAction
  | LogOutErrorAction
  | UpdateUser
  | GetUserBeginAction
  | GetUserSuccessAction
  | GetUserErrorAction
  | GetUserSettings
  | SetUserSettings
  | RefreshToken

export default ActionType
