import FullscreenLoader from 'common/FullscreenLoader'
import Layout from 'common/Layout'
import { UserAccess } from 'constants/index'
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import api from 'services/api'
import notify from 'services/notify'
import { getEmployees } from 'store/ives/employees/actions'
import { ReduxState } from 'types'

import ContentView from './view'

const Employees = (): JSX.Element => {
  const [globalFilter, setGlobalFilter] = useState<string>()
  const [isLoading, setIsLoading] = useState(false)
  const { employees } = useSelector(({ ives }: ReduxState) => ives.employees)
  const access = useSelector(({ user }: ReduxState) => user.user?.access)
  const isActiveButton = access?.[UserAccess.importEmployeeDataOnDemand]
  const dispatch = useDispatch()

  const handleSynchronizeData = async () => {
    setIsLoading(true)
    try {
      await api.post(
        '/employees/import?_format=json',
        {},
        {
          headers: {
            'Content-Type': 'application/json',
            withCredentials: true,
          },
        },
      )
      dispatch(getEmployees({ query: globalFilter }))
    } catch (error: unknown) {
      notify(error)
      setIsLoading(false)
    }
  }
  useEffect(() => {
    if (employees) {
      setIsLoading(
        employees?.pager?.current_page + 1 !== employees?.pager?.total_pages,
      )
    }
  }, [employees])

  useEffect(() => {
    handleSynchronizeData().then()
  }, [])
  return (
    <Layout headerProps={{ withoutSearch: true }}>
      <ContentView
        globalFilter={globalFilter}
        setGlobalFilter={setGlobalFilter}
        handleSynchronizeData={handleSynchronizeData}
        isActiveButton={isActiveButton}
      />
      <FullscreenLoader isLoading={isLoading} />
    </Layout>
  )
}

export default Employees
