import cn from 'classnames'

import styles from './styles.module.scss'

interface Props<T> {
  items: T[]
  selectedItem: T
  className?: string
  setSelectedItem(selected: T): void
}

const NavigationBar = <T extends string>({
  items,
  selectedItem,
  className,
  setSelectedItem,
}: Props<T>) => (
  <nav className={cn(styles.navigation, className)}>
    {items.map((item) => (
      <button
        className={cn(styles.navigationItem, {
          [styles.selected]: selectedItem === item,
        })}
        type="button"
        key={item}
        onClick={() => setSelectedItem(item)}
      >
        {item}
      </button>
    ))}
  </nav>
)

export default NavigationBar
