  GET_FIELD_DEPARTMENTS_ERROR = '[ives] GET_FIELD_DEPARTMENTS_ERROR',
  GET_FIELD_PROBABILITY_BEGIN = '[ives] GET_FIELD_PROBABILITY_BEGIN',
  GET_FIELD_PROBABILITY_SUCCESS = '[ives] GET_FIELD_PROBABILITY_SUCCESS',
