import axios, { AxiosResponse } from 'axios'
import { isDev } from 'constants/index'
import { serialize } from 'object-to-formdata'
import { Layout } from 'react-grid-layout'
import api from 'services/api'
import notify from 'services/notify'
import { action } from 'store/action'
import {
  AppThunkAsync,
  BackendUserMeta,
  Error,
  User,
  UserWithMeta,
} from 'types/index'
import { AnyWidget, Settings } from 'types/settings'

import ActionType from './types'

export const login = (
  authData: {
    name: string
    pass: string
  },
  onSuccess?: (user: UserWithMeta) => void,
  onError?: (e: Error) => void,
): AppThunkAsync<UserWithMeta | undefined> => async (dispatch) => {
  dispatch(action(ActionType.LOG_IN_BEGIN))
  const formdata = serialize({
    grant_type: 'password',
    client_id: '9b300a0d-5d88-480b-a51c-4aa6d4e71e54',
    client_secret: 'k3JYKua2pjGZVyRF',
    username: authData.name,
    password: authData.pass,
  })

  try {
    const userInfo: AxiosResponse<UserWithMeta> = await api.post(
      'oauth/token',
      formdata,
      {
        baseURL: isDev
          ? 'https://supplatform.clinet.club/backend/'
          : '/backend/',
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      },
    )
    dispatch(
      action(ActionType.LOG_IN_SUCCESS, {
        ...userInfo.data,
      }),
    )
    onSuccess?.(userInfo.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.LOG_IN_ERROR, error))
    onError?.(error)
  }
}

export const refresh = (
  onSuccess?: (token: BackendUserMeta) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch, getState) => {
  dispatch(action(ActionType.LOG_IN_BEGIN))

  const formdata = serialize({
    grant_type: 'refresh_token',
    client_id: '9b300a0d-5d88-480b-a51c-4aa6d4e71e54',
    client_secret: 'k3JYKua2pjGZVyRF',
    refresh_token: getState().user.meta?.refreshToken || '',
  })

  try {
    const userInfo: AxiosResponse<BackendUserMeta> = await axios.post(
      'oauth/token',
      formdata,
      {
        baseURL: isDev
          ? 'https://ives-staging.leipzig.sup-sahlmann.com/backend/'
          : '/backend/',
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      },
    )
    dispatch(action(ActionType.REFRESH_TOKEN, userInfo.data))
    onSuccess?.(userInfo.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.LOG_OUT_SUCCESS, error))
    onError?.(error)
  }
}

export const logout = (onSuccess?: () => void): AppThunkAsync => async (
  dispatch,
) => {
  dispatch(action(ActionType.LOG_OUT_SUCCESS))
  onSuccess?.()
}

/**
 * @param user will be merged with redux state user
 */
export const updateUser = (
  user: Partial<User>,
  onSuccess?: () => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.UPDATE_USER, user))
  onSuccess?.()
}

export const getUserSettings = (): AppThunkAsync => async (
  dispatch,
  getState,
) => {
  const { data }: AxiosResponse<Settings> = await api.get(
    `/users/${getState().user.user?.id}/settings?_format=json`,
  )

  dispatch(action(ActionType.GET_USER_SETTINGS, data))
}

export const addWidget = (widgetData: AnyWidget): AppThunkAsync => async (
  dispatch,
  getState,
) => {
  try {
    const userState = getState().user
    const userId = userState.user?.id
    if (!userId) return

    const settings: Settings = {
      ...userState.settings,
      mySuP: {
        ...userState.settings.mySuP,
        widgets: [...userState.settings.mySuP.widgets, widgetData],
      },
    }
    await api.post(`users/${userId}/settings?_format=json`, settings)

    dispatch(action(ActionType.SET_USER_SETTINGS, settings))
  } catch (error) {
    notify(error)
  }
}

export const updateWidgets = (layouts: Layout[]): AppThunkAsync => async (
  dispatch,
  getState,
) => {
  try {
    const userState = getState().user
    const userId = userState.user?.id
    if (!userId) return

    const widgets = layouts.flatMap((layout) => {
      const widget = userState.settings.mySuP.widgets.find(
        (widget) => widget.layout.i === layout.i,
      )

      if (!widget) return []

      return {
        ...widget,
        layout: {
          ...widget.layout,
          ...layout,
        },
      }
    })

    const settings: Settings = {
      ...userState.settings,
      mySuP: {
        ...userState.settings.mySuP,
        widgets,
      },
    }
    await api.post(`users/${userId}/settings?_format=json`, settings)

    dispatch(action(ActionType.SET_USER_SETTINGS, settings))
  } catch (error) {
    notify(error)
  }
}

export const removeWidget = (id: string): AppThunkAsync => async (
  dispatch,
  getState,
) => {
  try {
    const userState = getState().user
    const userId = userState.user?.id
    if (!userId) return

    const widgets = [...userState.settings.mySuP.widgets]
    const indexToDelete = widgets.findIndex((widget) => widget.layout.i === id)

    indexToDelete !== -1 && widgets.splice(indexToDelete, 1)

    const settings: Settings = {
      ...userState.settings,
      mySuP: {
        ...userState.settings.mySuP,
        widgets,
      },
    }
    await api.post(`users/${userId}/settings?_format=json`, settings)

    dispatch(action(ActionType.SET_USER_SETTINGS, settings))
  } catch (error) {
    notify(error)
  }
}

export const getUser = (
  onSuccess?: (user: UserWithMeta) => void,
  onError?: (e: Error) => void,
): AppThunkAsync<UserWithMeta> => async (dispatch) => {
  dispatch(action(ActionType.GET_USER_BEGIN))
  try {
    const user: AxiosResponse<UserWithMeta> = await api.get(
      'users/current?_format=json',
    )
    dispatch(action(ActionType.GET_USER_SUCCESS, user.data))
    onSuccess?.(user.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_USER_ERROR, error))
    onError?.(error)
  }
}

export default {
  login,
  updateUser,
  logout,
  getUser,
}
