import { CompanyCluster } from 'constants/companyStructure'
import {
  DetailsPage,
  EmployeeGroupDetails,
  Error,
  ProjectGroup,
  ProjectGroupEmployee,
} from 'types/index'

enum ActionType {
  GET_COMPANY_STRUCTURE_BEGIN = '[company structure] GET_COMPANY_STRUCTURE_BEGIN',
  GET_COMPANY_STRUCTURE_SUCCESS = '[company structure] GET_COMPANY_STRUCTURE_SUCCESS',
  GET_COMPANY_STRUCTURE_ERROR = '[company structure] GET_COMPANY_STRUCTURE_ERROR',
  GET_EMPLOYEE_BEGIN = '[company structure] GET_EMPLOYEE_BEGIN',
  GET_EMPLOYEE_SUCCESS = '[company structure] GET_EMPLOYEE_SUCCESS',
  GET_EMPLOYEE_ERROR = '[company structure] GET_EMPLOYEE_ERROR',
  GET_DETAILS_BEGIN = '[company structure] GET_DETAILS_BEGIN',
  GET_DETAILS_SUCCESS = '[company structure] GET_DETAILS_SUCCESS',
  GET_DETAILS_ERROR = '[company structure] GET_DETAILS_ERROR',
  DETAILS_PAGE_ADDED = '[company structure] DETAILS_PAGE_ADDED',
  DETAILS_PAGE_DELETED = '[company structure] DETAILS_PAGE_DELETED',
  DETAILS_PAGES_REORDERED = '[company structure] DETAILS_PAGES_REORDERED',
}

interface GetCompanyStructureBeginAction {
  type: ActionType.GET_COMPANY_STRUCTURE_BEGIN
}

interface GetCompanyStructureSuccessAction {
  type: ActionType.GET_COMPANY_STRUCTURE_SUCCESS
  payload: {
    cluster: CompanyCluster
    data: ProjectGroup[]
  }
}

interface GetCompanyStructureErrorAction {
  type: ActionType.GET_COMPANY_STRUCTURE_ERROR
  payload: Error
}

interface GetEmployeeBeginAction {
  type: ActionType.GET_EMPLOYEE_BEGIN
}

interface GetEmployeeSuccessAction {
  type: ActionType.GET_EMPLOYEE_SUCCESS
  payload: ProjectGroupEmployee
}

interface GetEmployeeErrorAction {
  type: ActionType.GET_EMPLOYEE_ERROR
  payload: Error
}

interface GetDetailsBeginAction {
  type: ActionType.GET_DETAILS_BEGIN
}

interface GetDetailsSuccessAction {
  type: ActionType.GET_DETAILS_SUCCESS
  payload: EmployeeGroupDetails
}

interface GetDetailsErrorAction {
  type: ActionType.GET_DETAILS_ERROR
  payload: Error
}

interface DetailsPageAddedAction {
  type: ActionType.DETAILS_PAGE_ADDED
  payload: DetailsPage
}

interface DetailsPageDeletedAction {
  type: ActionType.DETAILS_PAGE_DELETED
  payload: number
}

interface DetailsPagesReorderedAction {
  type: ActionType.DETAILS_PAGES_REORDERED
  payload: number[]
}

export type Action =
  | GetCompanyStructureBeginAction
  | GetCompanyStructureSuccessAction
  | GetCompanyStructureErrorAction
  | GetEmployeeBeginAction
  | GetEmployeeSuccessAction
  | GetEmployeeErrorAction
  | GetDetailsBeginAction
  | GetDetailsSuccessAction
  | GetDetailsSuccessAction
  | GetDetailsErrorAction
  | DetailsPageAddedAction
  | DetailsPageDeletedAction
  | DetailsPagesReorderedAction

export default ActionType
