import { TextField, TextFieldProps } from '@mui/material'
import styled from '@mui/styles/styled'
import cn from 'classnames'
import koboldFields from 'constants/employeeFields'
import { FormikProps } from 'formik'

const CssTextField = styled(TextField)({
  '& label.Mui-focused': {
    color: '#EE5F00',
  },

  '& .MuiOutlinedInput-root': {
    marginBottom: 14,
    backgroundColor: 'white',
    lineHeight: '1.7rem',

    '& input': {
      height: '1.7em',
    },

    '& fieldset': {
      borderColor: 'rgba(208, 208, 208, 0.9)',
    },

    '&.Mui-focused fieldset': {
      borderColor: '#EE5F00',
      borderWidth: 1,
    },

    '&.Mui-disabled': {
      backgroundColor: '#f2f2f2',
    },

    '&.Mui-error': {
      marginBottom: 0,

      '& + .MuiFormHelperText-root.Mui-error': {
        marginBottom: 14,
      },
    },
  },
  '&.withValue': {
    '& .MuiOutlinedInput-root.Mui-disabled': {
      color: 'rgba(0, 0, 0, 0.6)',
      '& input': {
        '-webkit-text-fill-color': 'rgb(0 0 0 / 60%)',
      },
    },
  },
  '& .MuiFormLabel-filled + .MuiOutlinedInput-root fieldset': {
    borderColor: '#000000',
  },
})

interface CustomInputProps<T> {
  name: string
  // name: keyof T & string
  label: string
  variant?: 'filled' | 'outlined' | 'standard'
  disabled?: boolean
  formik?: FormikProps<T>
  className?: string
  typeOfInput?: string
}

type TextInputProps<T> = CustomInputProps<T> & TextFieldProps

const TextInput = <T extends Record<string, unknown>>({
  disabled,
  label,
  name,
  variant = 'outlined',
  id,
  formik,
  className,
  typeOfInput,
  ...props
}: TextInputProps<T>) => (
  <CssTextField
    type="text"
    id={id || name}
    name={name}
    label={label}
    variant={variant}
    className={cn(className, {
      withValue: formik?.values[name],
    })}
    disabled={disabled || koboldFields.includes(name)}
    onChange={(value) =>
      formik?.handleChange({
        target: {
          name,
          value: typeOfInput === 'date' ? value : value.target.value,
        },
      })
    }
    onBlur={formik?.handleBlur}
    value={formik?.values[name]}
    error={!!(formik?.touched[name] && formik.errors[name])}
    helperText={formik?.touched[name] && formik.errors[name]}
    size="small"
    fullWidth
    {...props}
  />
)

export default TextInput
