import FullscreenLoader from 'common/FullscreenLoader'
import { ACCESS_TOKEN_REFRESH_THRESHOLD } from 'constants/index'
import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getTimeSpan } from 'services/date'
import { getBackendSettings } from 'store/app/actions'
import { getUser, getUserSettings, refresh } from 'store/user/actions'
import { ReduxState } from 'types'

const ValidationWrapper: React.FC = ({ children }) => {
  const userMeta = useSelector(({ user }: ReduxState) => user.meta)

  const dispatch = useDispatch()

  const tokenExpirationTimeSpan = userMeta?.accessTokenExpirationDate
    ? getTimeSpan(new Date(), new Date(userMeta.accessTokenExpirationDate))
    : 0
  const shouldRefresh = !!(
    userMeta && tokenExpirationTimeSpan < ACCESS_TOKEN_REFRESH_THRESHOLD
  )

  useEffect(() => {
    if (shouldRefresh) {
      dispatch(refresh())
    } else if (userMeta) {
      dispatch(getUser())
      dispatch(getBackendSettings())
      dispatch(getUserSettings())

      const timeout = setTimeout(
        () => dispatch(refresh()),
        (tokenExpirationTimeSpan - ACCESS_TOKEN_REFRESH_THRESHOLD) * 1000,
      )

      return () => clearTimeout(timeout)
    }

    return () => undefined
  }, [userMeta, shouldRefresh, tokenExpirationTimeSpan])

  return (
    <>
      {!shouldRefresh && children}
      <FullscreenLoader isLoading={shouldRefresh} />
    </>
  )
}

export default ValidationWrapper
