import cn from 'classnames'
import { useCallback } from 'react'
import { Message } from 'types'

import styles from './styles.module.scss'

interface Props {
  showNotify?: boolean
  notificationsData: Message | null
  notifyTime: string
  notificationType?: 'message' | 'vacation'
  onClickNotify: () => void
}

const Notification: React.FC<Props> = ({
  showNotify,
  notifyTime,
  notificationsData,
  notificationType,
  onClickNotify = () => null,
}) => {
  const checkMessage = useCallback(() => {
    switch (notificationType) {
      case 'vacation':
        return (
          <p
            onClick={onClickNotify}
            className={cn(styles.notifyText, styles.label)}
          >
            {notificationsData?.message}
          </p>
        )
      default:
        return <p className={styles.notifyText}>{notificationsData?.message}</p>
    }
  }, [notificationType, notificationsData?.message])

  return (
    <div className={cn(styles.notifyWrap, showNotify && styles.active)}>
      <div className={styles.notifyTitleWrap}>
        <h4>{notificationsData?.title}</h4>
        <span>{notifyTime}</span>
      </div>
      {checkMessage()}
    </div>
  )
}

export default Notification
