import { format, fromUnixTime } from 'date-fns'
import { NationalHolidays } from 'types'

const isNationalHoliday = (
  date: Date,
  nationalHolidays: NationalHolidays[] | null,
): boolean => {
  let returnStatement = false
  nationalHolidays?.forEach((nationalHoliday) => {
    if (
      format(fromUnixTime(nationalHoliday.from), 'dd.MM.yyyy') ===
      format(date, 'dd.MM.yyyy')
    ) {
      returnStatement = true
    }
  })
  return returnStatement
}
export default isNationalHoliday
