import ZoomButton from 'components/Button/zoomButton'
import Carousel from 'components/Carousel'
import Widget, { WidgetProps } from 'components/Widget'
import WidgetCarouselItem from 'components/widgetItems/WidgetCarouselItem'
import Routes from 'constants/routes'
import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { getBlog } from 'store/blog/actions'
import Swiper from 'swiper'
import { ReduxState } from 'types'

interface Props extends WidgetProps {
  onInit?: (swiper: Swiper) => void
  id: string
  categoryId: number
  title: string
  withoutMenu?: boolean
}

const NewsPreviewWidget: React.FC<Props> = ({
  onInit,
  id,
  categoryId,
  title,
  withoutMenu,
  ...props
}) => {
  const { blog } = useSelector((state: ReduxState) => state.blog)
  const history = useHistory()
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getBlog(undefined, categoryId))
  }, [])

  return (
    <Widget title={title} withoutMenu={withoutMenu} id={id} {...props}>
      <Carousel onInit={onInit} id={id}>
        {blog?.rows?.map((item) => (
          <WidgetCarouselItem
            key={item.title}
            title={item.title}
            body={item.body}
            date={item.created}
          />
        )) || []}
      </Carousel>
      <ZoomButton
        onClick={() =>
          history.push(`${Routes.dashboard}?categoryId=${categoryId}`)
        }
      />
    </Widget>
  )
}

export default NewsPreviewWidget
