import cn from 'classnames'
import Table from 'common/Table'
import Button from 'components/Button'
import { useState } from 'react'
import { Column } from 'react-table'
import { EmployeeHolidays } from 'types'

import HolidayCounter from '../HolidayCounter'
import { TableData } from '.'
import styles from './styles.module.scss'

interface PersonalViewProps {
  userData: EmployeeHolidays[] | null
  columnsData: Column<TableData>[]
  tableData: TableData[]
  handleChange: (status: boolean) => void
  calculateHolidays: (employee: EmployeeHolidays[] | null) => number
}

const YearFilter: {
  value: boolean
  title: string
}[] = [
  { value: true, title: 'Aktuelles Jahr' },
  { value: false, title: 'Alle Urlaube' },
]

const ContentView: React.FC<PersonalViewProps> = ({
  userData,
  columnsData,
  tableData,
  handleChange,
  calculateHolidays,
}) => {
  const [isCurrentYear, setIsCurrentYear] = useState(true)
  const onChange = (state: boolean) => {
    setIsCurrentYear(state)
    handleChange(state)
  }
  return (
    <div className={styles.wrap}>
      <div className={styles.tableWidgets}>
        {YearFilter.map((item) => (
          <Button
            key={item.title}
            onClick={() => onChange(item.value)}
            className={cn(
              styles.filterItem,
              isCurrentYear === item.value && styles.selected,
            )}
          >
            {item.title}
          </Button>
        ))}
      </div>
      <Table columnsData={columnsData} tableData={tableData} />
      <HolidayCounter
        userData={userData}
        calculateHolidays={calculateHolidays}
      />
    </div>
  )
}

export default ContentView
