import { ReactComponent as Cancel } from 'assets/icons/cancel.svg'
// import menuLogo from 'assets/images/menuLogo.png'
import BigMenuItem from 'components/BigMenuItem'
import Button from 'components/Button'
import { MenuItem } from 'types'

import styles from './styles.module.scss'

interface BigMenuProps {
  menuItems?: MenuItem[]
  toggleMenu: () => void
}

const BigMenu = ({ menuItems, toggleMenu }: BigMenuProps) => (
  <div className={styles.bigMenu}>
    <Button className={styles.close} onClick={toggleMenu}>
      <Cancel width="17" height="17" />
    </Button>
    <div className={styles.logo}>{/* <img src={menuLogo} alt="logo" /> */}</div>
    <div className={styles.menuItemsWrapper}>
      {menuItems?.map((item) => (
        <BigMenuItem key={item.title} item={item} />
      ))}
    </div>
  </div>
)

export default BigMenu
