import { cancellablePromise, delay } from 'services/utils'

import useCancellablePromises from './useCancellablePromises'

const useClickPreventionOnDoubleClick = <
  ClArgs extends any[],
  DbClArgs extends any[]
>(
  onClick: (...args: ClArgs) => void,
  onDoubleClick: (...args: DbClArgs) => void,
): [(...args: ClArgs) => void, (...args: DbClArgs) => void] => {
  const api = useCancellablePromises()

  const handleClick = (...args: ClArgs) => {
    api.clearPendingPromises()
    const waitForClick = cancellablePromise(delay(300))
    api.appendPendingPromise(waitForClick)

    return waitForClick.promise
      .then(() => {
        api.removePendingPromise(waitForClick)
        onClick(...args)
      })
      .catch((errorInfo) => {
        api.removePendingPromise(waitForClick)
        if (!errorInfo.isCanceled) {
          throw errorInfo.error
        }
      })
  }

  const handleDoubleClick = (...args: DbClArgs) => {
    api.clearPendingPromises()
    onDoubleClick(...args)
  }

  return [handleClick, handleDoubleClick]
}

export default useClickPreventionOnDoubleClick
