import Button from 'components/Button'
import Checkbox from 'components/inputs/Checkbox'
import DatePicker from 'components/inputs/DatePicker'
import Select from 'components/inputs/Select'
import TextInput from 'components/inputs/TextInput'
import { FormikProps } from 'formik'
import { OptionsType } from 'react-select'
import { SelectOption } from 'types'

import { FormValues } from '.'
import styles from './styles.module.scss'

interface Props {
  formik: FormikProps<FormValues>
  probabilityOptions: OptionsType<SelectOption>
  liabilityOptions: OptionsType<SelectOption>
  projectManagerOptions: OptionsType<SelectOption>
}

const FormView: React.FC<Props> = ({
  formik,
  probabilityOptions,
  liabilityOptions,
  projectManagerOptions,
}) => (
  <form
    onSubmit={formik.handleSubmit}
    onReset={formik.handleReset}
    className={styles.form}
  >
    <div className={styles.inputWithCheckbox}>
      <TextInput
        name="amount"
        label="Angebotssumme (Brutto)"
        variant="outlined"
        formik={formik}
        className={styles.textInput}
      />
      <Checkbox name="isEstimate" className={styles.checkbox}>
        {'Summe ist nur Geschätzt'}
      </Checkbox>
    </div>
    <TextInput
      name="nan"
      label="NAN Summe (Brutto)"
      variant="outlined"
      formik={formik}
      className={styles.textInput}
    />
    <Select
      name="liabilityDamage"
      placeholder="Haftung Personen-/ Sachschäden"
      isSearchable={false}
      options={liabilityOptions}
      formik={formik}
    />
    <DatePicker
      name="bindingUntil"
      formik={formik}
      placeholder="Bindefrist bis zum"
    />
    <Select
      name="projectManager"
      placeholder="Projektleiter"
      isSearchable
      options={projectManagerOptions}
      formik={formik}
    />
    <Select
      name="probability"
      placeholder="Warscheinlichkeit der beauftragung"
      isSearchable={false}
      options={probabilityOptions}
      formik={formik}
    />
    <div className={styles.footerButtons}>
      <Button className={styles.button} type="reset">
        {'Abbrechen'}
      </Button>
      <Button className={styles.buttonPrimary} type="submit">
        {'OK'}
      </Button>
    </div>
  </form>
)

export default FormView
