import axios from 'axios'
import IconNavigationBar from 'common/IconNavigationBar'
import Layout from 'common/Layout'
import Modal from 'common/Modal'
import EmployeeInfo from 'common/widgets/CompanyCluster/EmployeeInfo'
import Group from 'common/widgets/CompanyCluster/ProjectGroup'
import NewsWidget from 'common/widgets/NewsWidget'
import Loader from 'components/Loader'
import { CompanyCluster, companyClustersMap } from 'constants/companyStructure'
import Routes from 'constants/routes'
import Widgets from 'constants/widgets'
import ProjectsInAcquisition from 'containers/Ives/ProjectsInAcquisition'
import useToggle from 'hooks/useToggle'
import { nanoid } from 'nanoid'
import { useEffect, useMemo, useState } from 'react'
import { Item, ItemParams, Menu, useContextMenu } from 'react-contexify'
import { useDispatch, useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import {
  deleteDetailsPage,
  getCompanyStructure,
  getDetails,
  reorderDetailsPage,
} from 'store/companyStructure/actions'
import { addWidget } from 'store/user/actions'
import { ReduxState } from 'types'

import DetailsPage from './DetailsPage'
import DetailsPageForm from './DetailsPage/detailsPageForm'
import styles from './styles.module.scss'

export enum NavigationItem {
  HOME = 'Startseite',
  TEAM = 'Team',
  PROJECTS = 'Projekte',
}

const nonDeletableItems = [
  NavigationItem.HOME,
  NavigationItem.TEAM,
  NavigationItem.PROJECTS,
]

interface ContextMenuProps {
  pageId: number
  pageTitle: string
}

const MENU_ID = 'details-pages-context-menu'

interface ParamsType {
  groupId?: string
}

const ProjectGroupContainer: React.FC = () => {
  const [selectedItemId, setSelectedItemId] = useState<number | null>(
    Number(new URLSearchParams(window.location.search).get('pageId')) || null,
  )
  const [openedEmployeeId, setOpenedEmployeeId] = useState<number | null>(null)
  const detailsPageFormToggle = useToggle()
  const { data, groupDetails } = useSelector(
    (state: ReduxState) => state.companyStructure,
  )
  const { user } = useSelector((state: ReduxState) => state.user)
  const { groupId } = useParams<ParamsType>()

  const { show } = useContextMenu({ id: MENU_ID })

  const dispatch = useDispatch()

  const projectGroups = useMemo(() => Object.values(data).flat(), [data])
  const projectGroup = projectGroups.find(
    (group) => group.tid === Number(groupId),
  )

  useEffect(() => {
    Object.keys(companyClustersMap).map(
      (cluster) =>
        !data[cluster as CompanyCluster].length &&
        dispatch(getCompanyStructure(cluster as CompanyCluster)),
    )
  }, [])

  useEffect(() => {
    groupId && dispatch(getDetails(Number(groupId)))
  }, [groupId])

  useEffect(() => {
    if (
      !selectedItemId ||
      !groupDetails?.detail_pages.find((page) => page.id === selectedItemId)
    )
      setSelectedItemId(groupDetails?.detail_pages[0]?.id || null)

    const linkElements = groupDetails?.styles.map((styleUrl) => {
      const link = document.createElement('link')
      link.rel = 'stylesheet'
      link.type = 'text/css'
      link.href = styleUrl

      document.head.appendChild(link)
      return link
    })

    return () =>
      linkElements?.forEach((element) => document.head.removeChild(element))
  }, [groupDetails])

  const page = groupDetails?.detail_pages.find(
    (page) => page.id === selectedItemId,
  )

  const getTabContent = () => {
    switch (page?.title) {
      case NavigationItem.HOME:
        return <NewsWidget groupId={groupId} menu={[]} />
      case NavigationItem.PROJECTS:
        return <ProjectsInAcquisition tableOnly />
      case NavigationItem.TEAM:
        return (
          <>
            <h2>{'Team'}</h2>
            {projectGroup && (
              <Group
                group={projectGroup}
                openEmployeeInfo={(id) => setOpenedEmployeeId(id)}
                withoutIcon
              />
            )}
          </>
        )
      default:
        return null
    }
  }

  const editing = window.location.hash === '#edit'
  const baseBEURL = groupDetails?.detail_pages[0].menu_icon?.substring(
    0,
    groupDetails?.detail_pages[0].menu_icon?.indexOf('/site'),
  )
  const name = localStorage.getItem('username')
  const pass = localStorage.getItem('password')

  const loginInBE = async () => {
    const formdata = new FormData()

    if (name && pass) {
      formdata.append('name', name)
      formdata.append('pass', pass)
      formdata.append('form_id', 'user_login_form')
    }
    await axios.post(`${baseBEURL}/node`, formdata, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    })
  }
  const logoutBE = async () => {
    await axios.get(`${baseBEURL}/user/logout`)
  }
  useEffect(() => {
    if (editing) {
      loginInBE()
    } else {
      logoutBE()
    }
  }, [editing])

  return (
    <Layout>
      {!projectGroup ? (
        <Loader className={styles.loader} />
      ) : (
        <>
          <h1 className={styles.title}>{projectGroup.name}</h1>
          <IconNavigationBar
            selectedItemId={selectedItemId}
            items={groupDetails?.detail_pages || []}
            nonDeletableItems={nonDeletableItems}
            onContextMenu={(event, item) =>
              show(event, { props: { pageId: item.id, pageTitle: item.title } })
            }
            setSelectedItemId={(id) => setSelectedItemId(id)}
            onAddItemClick={detailsPageFormToggle.on}
            onDeleteItemClick={(id) => dispatch(deleteDetailsPage(id))}
            onDragEnd={(result) => {
              if (!result.source || !result.destination) return

              const itemsCopy = [...(groupDetails?.detail_pages || [])]
              const [dragged] = itemsCopy.splice(result.source.index, 1)
              itemsCopy.splice(result.destination.index, 0, dragged)
              const itemIds = itemsCopy.map((item) => item.id)

              dispatch(reorderDetailsPage(Number(groupId), itemIds))
            }}
          />
          <div className={styles.content}>
            {getTabContent()}
            {page?.title !== NavigationItem.HOME && user && page && (
              <DetailsPage
                page={page}
                editing={editing}
                editingLink={`${Routes.projectGroups}/${groupId}?pageId=${selectedItemId}#edit`}
                user={user}
              />
            )}
          </div>
        </>
      )}
      <Modal
        isOpen={openedEmployeeId !== null}
        onRequestClose={() => setOpenedEmployeeId(null)}
        withCloseButton
      >
        <EmployeeInfo id={openedEmployeeId} />
      </Modal>
      <Modal
        isOpen={detailsPageFormToggle.value}
        onRequestClose={detailsPageFormToggle.off}
        withCloseButton
      >
        <DetailsPageForm
          groupId={Number(groupId)}
          onSubmit={detailsPageFormToggle.off}
          onCancel={detailsPageFormToggle.off}
        />
      </Modal>
      <Menu id={MENU_ID}>
        <Item
          onClick={({ props }: ItemParams<ContextMenuProps>) => {
            props &&
              projectGroup &&
              dispatch(
                addWidget({
                  data: {
                    groupId: projectGroup.tid,
                    pageId: props.pageId,
                    pageTitle: props.pageTitle,
                  },
                  title: projectGroup.name,
                  type: Widgets.detailsPage,
                  layout: {
                    i: nanoid(),
                    x: 0,
                    y: Infinity,
                    w: 6,
                    h: 4,
                    minW: 4,
                    minH: 4,
                  },
                }),
              )
          }}
        >
          {'Auf Mein S&P Hinzufügen'}
        </Item>
      </Menu>
    </Layout>
  )
}

export default ProjectGroupContainer
