import AllowencesModal from 'common/History/Templates/AllowencesModal'
import SalaryModal from 'common/History/Templates/SalaryModal'
import SickModal from 'common/History/Templates/SickModal'
import {
  Allowences,
  EmployeeHolidays,
  FinancialDetails,
  Notes,
  Sick,
} from 'types'

import styles from './styles.module.scss'
import HolidayModal from './Templates/HolidayModal'
import NotesModal from './Templates/NotesModal'

interface HistoryModalProps {
  holidayData?: EmployeeHolidays[] | undefined
  notesData?: Notes[] | undefined
  salaryData?: FinancialDetails[]
  sickData?: Sick[]
  allowencesData?: Allowences[]
  workingDays?: number[]
  modalType: string
  handleEditComment?: (id: number | string) => void
  handleEditAttachment?: (id: number | string) => void
  handleDeleteNote?: (id: number | string) => void
  isShowingDelete?: boolean
}

const HistoryModal = ({
  holidayData,
  workingDays,
  modalType,
  notesData,
  salaryData,
  sickData,
  allowencesData,
  handleEditComment,
  handleEditAttachment,
  handleDeleteNote,
  isShowingDelete,
}: HistoryModalProps) => {
  let modal
  switch (modalType) {
    case 'holiday':
      modal = (
        <HolidayModal holidayData={holidayData} workingDays={workingDays} />
      )
      break
    case 'notes':
      modal =
        (notesData?.length || 0) > 0 ? (
          <NotesModal
            notesData={notesData}
            handleEditComment={handleEditComment}
            handleEditAttachment={handleEditAttachment}
            handleDeleteNote={handleDeleteNote}
            isShowingDelete={isShowingDelete}
          />
        ) : (
          <div>{'Keine Notizen vorhanden'}</div>
        )
      break
    case 'salary':
      modal =
        (salaryData?.length || 0) > 0 ? (
          <SalaryModal salaryData={salaryData} />
        ) : (
          <div>{'Keine Gehalts Datei'}</div>
        )
      break
    case 'sick':
      modal =
        (sickData?.length || 0) > 0 ? (
          <SickModal sickData={sickData} />
        ) : (
          <div>{'Keine Gehalts Datei'}</div>
        )
      break
    case 'bonus':
      modal =
        (allowencesData?.length || 0) > 0 ? (
          <AllowencesModal allowencesData={allowencesData} />
        ) : (
          <div>{'Keine Bonuszahlungen Datei'}</div>
        )
      break
    default:
      modal = <div>{'Invalid'}</div>
      break
  }

  return (
    <div className={styles.historyContent}>
      <h2 className={styles.title}>{'Historie'}</h2>
      {modal}
    </div>
  )
}
export default HistoryModal
