import { ReactComponent as ArrowDown } from 'assets/icons/arrowDown.svg'
import { ReactComponent as ArrowNext } from 'assets/icons/arrowNextOrange.svg'
import { Link } from 'react-router-dom'
import strings from 'strings'

import styles from './styles.module.scss'

interface OpenLinkProps {
  text?: string
  arrowType?: 'down' | 'right'
}

const OpenLink = ({ text, arrowType = 'right' }: OpenLinkProps) => (
  <Link to="/" className={styles.openArticleContainer}>
    <div className={styles.linkText}>{text || strings.openArticle}</div>
    <div>{arrowType === 'down' ? <ArrowDown /> : <ArrowNext />}</div>
  </Link>
)

export default OpenLink
