import { ReactComponent as ArrowNext } from 'assets/icons/arrowNext.svg'
import { ReactComponent as ArrowPrev } from 'assets/icons/arrowPrev.svg'
import Button from 'components/Button'
import SwiperCore, { Navigation, Scrollbar } from 'swiper/core'
import { Swiper, SwiperSlide } from 'swiper/react'

import styles from './styles.module.scss'

SwiperCore.use([Navigation, Scrollbar])

interface CarouselProps {
  /**
   * @param id use different id for multiple swipers on same page
   */
  id: string
  nextElSelector?: string
  prevElSelector?: string
  children: React.ReactNode[]
  onInit?: (swiper: SwiperCore) => void
}

const Carousel = ({
  id,
  nextElSelector,
  prevElSelector,
  children,
  onInit,
}: CarouselProps) => (
  <div className={styles.swiperContainer}>
    <Swiper
      navigation={{
        prevEl: prevElSelector || `#custom-prev-arrow-${id}`,
        nextEl: nextElSelector || `#custom-next-arrow-${id}`,
      }}
      scrollbar={{ draggable: true }}
      spaceBetween={16}
      slidesPerView="auto"
      freeMode
      className={styles.swiper}
      onSwiper={(_swiper) => onInit?.(_swiper)}
    >
      {children.map((item, i) => (
        <SwiperSlide style={{ width: 150 }} key={i}>
          {item}
        </SwiperSlide>
      ))}
    </Swiper>
    <div className={styles.arrowsContainer}>
      <Button id={`custom-prev-arrow-${id}`} className={styles.arrowButton}>
        <ArrowPrev />
      </Button>
      <Button id={`custom-next-arrow-${id}`} className={styles.arrowButton}>
        <ArrowNext />
      </Button>
    </div>
  </div>
)

export default Carousel
