import Routes from 'constants/routes'
import notify from 'services/notify'
import {
  BackendMenuItem,
  BlogCategory,
  CancellablePromise,
  MenuItem,
  SingleSelectOption,
} from 'types'

const getRoute = (title: string) => {
  switch (title) {
    case 'dashboard':
      return Routes.dashboard
    case 'ives 2.0':
      return Routes.ives
    case 'news':
      return '/newsblog'
    default:
      return `/${title.replace(/([ /])/g, '-')}`
  }
}

export const mapLeftMenu = (
  menuItems: BackendMenuItem[],
  path = '',
): MenuItem[] =>
  menuItems.map((item) => {
    const currentPath = item.uri || path + getRoute(item.title.toLowerCase())
    const below = item.below && mapLeftMenu(item.below, currentPath)

    return {
      below,
      visible: item.enabled,
      selected: item.expanded,
      icon: item.options?.menu_icon?.url,
      key: item.key,
      title: item.title,
      order: item.weight,
      link: currentPath,
      external: /^(https?:\/\/|mailto:)/g.test(currentPath),
      attributes: {
        class: item.options?.attributes?.class?.[0],
      },
    }
  })

export const mapDashboardMenu = (
  menuItems: BackendMenuItem[],
): BlogCategory[] =>
  menuItems.map((item) => ({
    tid: item.options?.tid || 0,
    title: item.title,
    link: item.uri,
    below: item.below && mapDashboardMenu(item.below),
  }))

export const findMenuItem = (
  item: MenuItem,
  menuItem: MenuItem,
  collapseOthers: boolean,
): MenuItem => {
  if (item.title === menuItem.title) {
    return { ...item, selected: !item.selected }
  }
  if (item.below?.length) {
    // TODO refactor
    // eslint-disable-next-line no-param-reassign
    item.below = item.below.map((belowItem) =>
      findMenuItem(belowItem, menuItem, collapseOthers),
    )
  }
  if (collapseOthers) {
    return { ...item, selected: false }
  }
  return { ...item }
}

export const cancellablePromise = <T>(
  promise: Promise<T>,
): CancellablePromise => {
  let isCanceled = false

  const wrappedPromise = new Promise((resolve, reject) => {
    promise.then(
      // eslint-disable-next-line prefer-promise-reject-errors
      (value) => (isCanceled ? reject({ isCanceled, value }) : resolve(value)),
      // eslint-disable-next-line prefer-promise-reject-errors
      (error) => reject({ isCanceled, error }),
    )
  })

  return {
    promise: wrappedPromise,
    cancel: () => {
      isCanceled = true
    },
  }
}

export const delay = (n: number) =>
  new Promise((resolve) => setTimeout(resolve, n))

export const percent = (perc: number, from: number) => (from * perc) / 100

export const getSelectValue = (option: SingleSelectOption | string) =>
  typeof option === 'string' ? option : option?.value || ''

export const getBase64 = (file: File, cb: (val: any) => void) => {
  const reader = new FileReader()
  reader.readAsDataURL(file)
  reader.onload = () => {
    cb(reader.result)
  }
  reader.onerror = (error) => {
    notify(`Error: ${error}`)
  }
}
