import ImageContainer from 'common/ImageContainer'
import { format } from 'date-fns'
import { VersioningContent } from 'types'

import styles from './styles.module.scss'

interface Props {
  versions: VersioningContent
}
const Versioning = ({ versions }: Props) => (
  <>
    {versions.items.map((item, index) => (
      <div className={styles.version_card} key={index}>
        <div className={styles.version_details}>
          <h2 className={styles.version}>{item.fields.version}</h2>
          <p className={styles.release_date}>
            {'Veröffentlicht am: '}
            {format(new Date(item.fields.releaseDate), 'dd.MM.yyyy')}
          </p>
        </div>
        <div className={styles.overview}>
          <h3>{'Übersicht'}</h3>
          <p>{item.fields.overview}</p>
        </div>
        <div className={styles.whats_new}>
          <h3>{"What's New"}</h3>
          <p>{item.fields.whatsNew}</p>
        </div>
        <div className={styles.improvements}>
          <h3>{"Was gibt's Neues?"}</h3>
          <p>{item.fields.improvements}</p>
        </div>
        <div className={styles.bug_fixes}>
          <h3>{'Fehlerbehebungen'}</h3>
          <p>{item.fields.bugFixes}</p>
        </div>
        <div className={styles.previews}>
          <h3>{'Vorschau'}</h3>
          <div className={styles.images}>
            <ImageContainer
              images={item.fields.preview.map((item) => ({
                element: (
                  <img
                    key={item.sys.id}
                    src={item.asset?.fields.file.url}
                    alt={item.asset?.fields.description}
                  />
                ),
                src: item.asset?.fields.file.url || (item as any),
              }))}
            />
          </div>
        </div>
      </div>
    ))}
  </>
)
export default Versioning
