import 'react-calendar-timeline/lib/Timeline.css'
import 'moment/locale/de'

import { Menu, MenuItem } from '@mui/material'
import FullscreenLoader from 'common/FullscreenLoader'
import HolidayModal from 'common/History/Templates/HolidayModal'
import Modal from 'common/Modal'
import { PopupTypes } from 'constants/holidaysPage'
import { UserAccess } from 'constants/index'
import HolidayPopup from 'containers/Ives/Holidays/HolidayPopup'
import { addMonths, endOfMonth, format, startOfMonth } from 'date-fns'
import useToggle from 'hooks/useToggle'
import { useCallback, useMemo, useState } from 'react'
import Timeline, { DateHeader, TimelineHeaders } from 'react-calendar-timeline'
import { CSVLink } from 'react-csv'
import { useDispatch, useSelector } from 'react-redux'
import api from 'services/api'
import { getEmployeeRecord } from 'store/ives/employees/actions'
import { getHolidayEmployees } from 'store/ives/holidays/actions'
import {
  Employee,
  EmployeeHolidays,
  ReduxState,
  TableProps,
  UserRole,
} from 'types'

import styles from './styles.module.scss'

const userStates = {
  planned: 'planned',
}

const Timetable = ({
  timeTableGroups,
  timeTableItems,
  contextMenuAvailability,
  applicationToggle,
  popupType,
  setPopupType,
  currentUser,
  setCurrentUser,
  csvHeader,
  csvData,
}: TableProps) => {
  const [searchValue] = useState<string>()
  const [shouldRefresh, setShouldRefresh] = useState<boolean>(false)
  const defaultTimeStart = useMemo(() => Number(startOfMonth(new Date())), [])
  const defaultTimeEnd = useMemo(() => Number(endOfMonth(new Date())), [])
  const { user } = useSelector((state: ReduxState) => state.user)

  const minTime = useMemo(
    () => Number(addMonths(startOfMonth(new Date()), -12)),
    [],
  )
  const maxTime = useMemo(
    () => Number(addMonths(endOfMonth(new Date()), 12)),
    [],
  )
  const [contextMenu, setContextMenu] = useState<{
    mouseX: number
    mouseY: number
  } | null>(null)
  const [contextMenuCsv, setContextMenuCsv] = useState<{
    mouseX: number
    mouseY: number
  } | null>(null)
  const { employees } = useSelector(({ ives }: ReduxState) => ives.holidays)
  const [csvDataForEmp, setCsvDataForEmp] = useState(csvData)
  // const [csvHeader, setCsvHeader] = useState([])
  const [currentSelectedEmployee, setCurrentSelectedEmployee] = useState<
    EmployeeHolidays[] | undefined
  >([] as EmployeeHolidays[])
  const [showPopup, setShowPopup] = useState(false)
  const dispatch = useDispatch()
  const { holidays } = useSelector((state: ReduxState) => state.ives)
  const toggleHolidayModal = useToggle()
  const [holidayData, setHolidayData] = useState<
    EmployeeHolidays[] | undefined
  >()
  const [workingDays, setWorkingDays] = useState<number[] | undefined>()
  const formatDate = (time: number | string) => {
    // Check if the time is in Unix format (number)
    if (
      typeof time === 'string' &&
      !Number.isNaN(Number(time)) &&
      !Number.isNaN(parseFloat(time))
    ) {
      // Convert Unix time to JavaScript Date object
      // Assuming Unix time is in seconds (multiply by 1000 to convert to milliseconds)
      return format(new Date(Number(time)), 'dd.MM.yyyy')
    }
    // Handle non-Unix time formats (e.g., ISO string)
    return format(new Date(time), 'dd.MM.yyyy')
  }

  // Example usage:
  /** Open/close context menu */
  const handleContextMenu = useCallback(
    (event: React.MouseEvent, itemId: string) => {
      event.preventDefault()
      event.stopPropagation()

      const userDataIds = contextMenuAvailability?.find(
        (item) => String(item.holidayId) === itemId,
      )

      if (!userDataIds?.showContextMenu) {
        return null
      }

      const userData = timeTableItems.filter((item) => item.id === itemId)[0]
      const groupData = timeTableGroups.filter(
        (item) => item.id === String(userDataIds?.employeeId),
      )[0]
      const setUserData = () => {
        setCurrentUser({
          ...userData,
          id: String(userDataIds?.holidayId),
          title: groupData.title,
          start_time: formatDate(userData.start_time),
          end_time: formatDate(userData.end_time),
        })
      }

      /** If holiday admin */
      if (user?.access?.[UserAccess.deleteAnyEmployeeHolidayContent]) {
        setUserData()
        setContextMenu(
          contextMenu === null
            ? {
                mouseX: event.clientX + 2,
                mouseY: event.clientY - 6,
              }
            : null,
        )
        return null
      }

      if (Number(userDataIds?.employeeId) === Number(user?.employee)) {
        setUserData()
        setContextMenu(
          contextMenu === null
            ? {
                mouseX: event.clientX + 2,
                mouseY: event.clientY - 6,
              }
            : null,
        )
        return null
      }
      return null
    },
    [user, contextMenu, currentUser, timeTableItems, timeTableGroups, holidays],
  )

  const handleClose = useCallback(() => {
    setContextMenu(null)
  }, [contextMenu])

  /** Delete vacation, update store */
  const onDeleteVacation = useCallback(async () => {
    if (popupType === PopupTypes.update) {
      applicationToggle.on()
    }
    await api.delete(`employees/holidays/${currentUser?.id}?_format=json`)
    setShowPopup(false)
    // dispatch(getHolidays())
    dispatch(getHolidayEmployees({ query: searchValue }))
  }, [currentUser, user, popupType, timeTableGroups, timeTableItems, holidays])

  /** Update substitution, update store */
  const onUpdateSubstitution = useCallback(async () => {
    await api.delete(`employees/holidays/${currentUser?.id}?_format=json`)

    setShowPopup(false)
    // dispatch(getHolidays())
    dispatch(getHolidayEmployees({ query: searchValue }))
    if (popupType === PopupTypes.change) {
      applicationToggle.on()
    }
  }, [currentUser, user, popupType, timeTableGroups, timeTableItems, holidays])

  const getPopup = useCallback(() => {
    switch (popupType) {
      case PopupTypes.delete:
        return (
          <HolidayPopup
            title="Urlaubseintrag löschen"
            subtitle="Möchten Sie den folgenden Urlaubsantrag löschen? Der Vorgang kann noch abgebrochen werden."
            onClose={() => setShowPopup(false)}
            onDelete={onDeleteVacation}
            userData={currentUser}
            buttonText="Eintrag löschen"
          />
        )
      case PopupTypes.update:
        return (
          <HolidayPopup
            title="Urlaubseintrag ändern"
            subtitle="Möchten Sie den folgenden Urlaubsantrag ändern?
            Nach Bestätigung wird der aktuelle Eintrag gelöscht. Der Vorgang kann noch abgebrochen werden."
            onClose={() => setShowPopup(false)}
            onDelete={onDeleteVacation}
            userData={currentUser}
            buttonText="Eintrag ändern"
          />
        )
      case PopupTypes.change:
        return (
          <HolidayPopup
            title="Urlaubvetretung verschieben"
            subtitle="Möchten Sie die folgende Urlaubsvetretung ändern? Nach Bestätigung wird der aktuelle Eintrag gelöscht.
            Der Vorgang kann noch abgebrochen werden."
            onClose={() => setShowPopup(false)}
            onDelete={onUpdateSubstitution}
            userData={currentUser}
            buttonText="Eintrag verschieben"
          />
        )
      default:
        return null
    }
  }, [popupType, currentUser, user])
  const filterData = (event?: React.MouseEvent) => {
    const employee = employees?.filter(
      (emp) => emp.name === event?.target.innerText,
    )
    setCurrentSelectedEmployee(employee)
    if (employee && employee.length > 0) {
      csvData.forEach((data: any) => {
        if (data.Vollname === employee[0].name) {
          setCsvDataForEmp([data])
        }
      })
    }
  }
  const handleRightClick = (event: React.MouseEvent) => {
    event.preventDefault()
    event.stopPropagation()
    if (
      event.target.className.includes('rct-sidebar-row') &&
      user?.roles.includes(UserRole.Administrator)
    ) {
      filterData(event)
      setContextMenuCsv({
        mouseX: event.clientX + 2,
        mouseY: event.clientY - 6,
      })
    } else {
      setContextMenuCsv(null)
    }
  }

  const handleCsvClose = () => {
    setContextMenuCsv(null)
  }
  const handleModalOpen = () => {
    setShouldRefresh(true)
    setContextMenuCsv(null)
    dispatch(
      getHolidayEmployees({ query: undefined }, (res) => {
        if (currentSelectedEmployee) {
          const response = res.filter(
            (holiday) => holiday.name === currentSelectedEmployee[0].name,
          )
          setHolidayData(response)
          dispatch(
            getEmployeeRecord(
              currentSelectedEmployee[0].id,
              (record: Employee) => setWorkingDays(record.working_days),
            ),
          )
          toggleHolidayModal.on()
          setShouldRefresh(false)
        }
      }),
    )
  }

  return (
    <>
      <Modal
        isOpen={toggleHolidayModal.value}
        onRequestClose={toggleHolidayModal.off}
        withCloseButton
        maxWidth={1400}
      >
        <HolidayModal holidayData={holidayData} workingDays={workingDays} />
      </Modal>
      <FullscreenLoader isLoading={shouldRefresh} />
      <div className={styles.container} onContextMenu={handleRightClick}>
        <Timeline
          groups={timeTableGroups}
          items={timeTableItems}
          canMove={false}
          canResize={false}
          itemsSorted
          onTimeChange={(
            visibleTimeStart: number,
            visibleTimeEnd: number,
            updateScrollCanvas: (minTime: number, maxTime: number) => null,
          ) => {
            if (visibleTimeStart < minTime && visibleTimeEnd > maxTime) {
              updateScrollCanvas(minTime, maxTime)
            } else if (visibleTimeStart < minTime) {
              updateScrollCanvas(
                minTime,
                minTime + (visibleTimeEnd - visibleTimeStart),
              )
            } else if (visibleTimeEnd > maxTime) {
              updateScrollCanvas(
                maxTime - (visibleTimeEnd - visibleTimeStart),
                maxTime,
              )
            } else {
              updateScrollCanvas(visibleTimeStart, visibleTimeEnd)
            }
          }}
          defaultTimeStart={defaultTimeStart}
          defaultTimeEnd={defaultTimeEnd}
          itemHeightRatio={1}
          minZoom={1000 * 60 * 60 * 24 * 30}
          maxZoom={1000 * 60 * 60 * 24 * 31 * 3}
          onItemContextMenu={(itemId: string, event: React.MouseEvent) => {
            handleContextMenu(event, itemId)
          }}
        >
          <TimelineHeaders className={styles.timelineHeader}>
            <DateHeader unit="primaryHeader" />
            <DateHeader />
            <Menu
              open={contextMenu !== null}
              className={styles.contextMenu}
              onClose={handleClose}
              anchorReference="anchorPosition"
              anchorPosition={
                contextMenu !== null
                  ? { top: contextMenu.mouseY, left: contextMenu.mouseX }
                  : undefined
              }
            >
              {/* update option */}
              <MenuItem
                className={styles.contextMenuWrap}
                onClick={() => {
                  setPopupType(PopupTypes.update)
                  handleClose()
                  setShowPopup(true)
                }}
              >
                {'Urlaub Verschieben'}
              </MenuItem>

              {/* delete option */}
              <MenuItem
                className={styles.contextMenuWrap}
                onClick={() => {
                  setPopupType(PopupTypes.delete)
                  handleClose()
                  setShowPopup(true)
                }}
              >
                {'Urlaub löschen'}
              </MenuItem>

              {/* change substitution option */}
              {currentUser?.state !== userStates.planned && (
                <MenuItem
                  className={styles.contextMenuWrap}
                  onClick={() => {
                    setPopupType(PopupTypes.change)
                    handleClose()
                    setShowPopup(true)
                  }}
                >
                  {'Vertretung  bearbeiten'}
                </MenuItem>
              )}
            </Menu>
            <Menu
              open={contextMenuCsv !== null}
              className={styles.contextMenu}
              onClose={handleCsvClose}
              anchorReference="anchorPosition"
              anchorPosition={
                contextMenuCsv !== null
                  ? { top: contextMenuCsv.mouseY, left: contextMenuCsv.mouseX }
                  : undefined
              }
            >
              {/* update option */}
              <MenuItem
                className={styles.contextMenuWrap}
                onClick={() => {
                  handleCsvClose()
                }}
              >
                <CSVLink
                  data={csvDataForEmp}
                  headers={csvHeader}
                  filename={`UrlaubsHistorien für ${
                    currentSelectedEmployee &&
                    currentSelectedEmployee?.length > 0
                      ? currentSelectedEmployee[0].name
                      : ''
                  } ${new Date()}.csv`}
                >
                  {`Historie für ${
                    currentSelectedEmployee &&
                    currentSelectedEmployee?.length > 0
                      ? currentSelectedEmployee[0].name
                      : ''
                  } exportieren`}
                </CSVLink>
              </MenuItem>
              <MenuItem onClick={handleModalOpen}>{`Historie für ${
                currentSelectedEmployee && currentSelectedEmployee?.length > 0
                  ? currentSelectedEmployee[0].name
                  : ''
              } ansehen`}</MenuItem>
            </Menu>
          </TimelineHeaders>
        </Timeline>
      </div>
      {showPopup && getPopup()}
    </>
  )
}

export default Timetable
