import { AxiosResponse } from 'axios'
import api from 'services/api'
import notify from 'services/notify'
import { action } from 'store/action'
import {
  AppThunkAsync,
  Blog,
  BlogCategories,
  BlogItem,
  Error,
  FileWithPreview,
} from 'types/index'

import ActionType from './types'

export const getBlog = (
  page = 0,
  categoryId?: string | number,
  projectGroupId?: string,
  onSuccess?: (blog: Blog) => void,
  onError?: (e: Error) => void,
): AppThunkAsync<Blog> => async (dispatch) => {
  dispatch(
    action(
      page
        ? ActionType.GET_BLOG_NEXT_PAGE_BEGIN
        : ActionType.GET_BLOG_FIRST_PAGE_BEGIN,
    ),
  )

  try {
    const blog: AxiosResponse<Blog> = await api.get(
      `pages/newsblog${categoryId ? `/${categoryId}` : '/all'}/${
        projectGroupId ? `${projectGroupId}` : 'all'
      }?_format=json&page=${page}`,
    )
    dispatch(
      action(
        page
          ? ActionType.GET_BLOG_NEXT_PAGE_SUCCESS
          : ActionType.GET_BLOG_FIRST_PAGE_SUCCESS,
        blog.data,
      ),
    )
    onSuccess?.(blog.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_BLOG_ERROR, error))
    onError?.(error)
  }
}

export const getCategories = (
  onSuccess?: (categories: BlogCategories) => void,
  onError?: (e: Error) => void,
): AppThunkAsync<BlogCategories> => async (dispatch) => {
  dispatch(action(ActionType.GET_CATEGORIES_BEGIN))
  try {
    const categories: AxiosResponse<BlogCategories> = await api.get(
      'categories/newsblog?_format=json',
    )
    dispatch(action(ActionType.GET_CATEGORIES_SUCCESS, categories.data))
    onSuccess?.(categories.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_CATEGORIES_ERROR, error))
    onError?.(error)
  }
}

export const getFullItem = (
  id: string | number,
  onSuccess?: (fullArticle: BlogItem | undefined) => void,
  onError?: (e: Error) => void,
): AppThunkAsync<BlogItem | undefined> => async (dispatch) => {
  dispatch(action(ActionType.GET_FULL_ITEM_BEGIN))
  try {
    const fullItem: AxiosResponse<BlogItem[]> = await api.get(
      `content/newsblog/${id}?_format=json`,
    )
    dispatch(action(ActionType.GET_FULL_ITEM_SUCCESS, fullItem.data?.[0]))
    onSuccess?.(fullItem.data?.[0])
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_FULL_ITEM_ERROR, error))
    onError?.(error)
  }
}

export const createArticle = (
  article: {
    title: string
    summary: string | number | null
    body: string
    category?: number[]
    projectGroup?: number[]
    images: FileWithPreview[]
  },
  onSuccess?: () => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.CREATE_ARTICLE_BEGIN))
  try {
    const response: AxiosResponse<{
      id: string
    }> = await api.post('newsblog?_format=json', {
      ...article,
      images: article.images.map((image) => image.id),
      projectGroup: undefined,
      project_group: article.projectGroup,
    })
    dispatch(
      action(ActionType.CREATE_ARTICLE_SUCCESS, {
        ...article,
        nid: response.data.id,
        images: article.images.map((image) => image.preview),
        categories: article.category,
        project_group: article.projectGroup,
      }),
    )
    onSuccess?.()
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.CREATE_ARTICLE_ERROR, error))
    onError?.(error)
  }
}

export const updateArticle = (
  article: {
    title: string
    summary: string | number | null
    body: string
    category?: number[]
    projectGroup?: number[]
    images: FileWithPreview[]
    nid?: number
  },
  onSuccess?: () => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  try {
    await api.patch(`newsblog/${article.nid || ''}?_format=json`, {
      ...article,
      images: article.images.map((image) => (image.id ? image.id : image.fid)),
      projectGroup: undefined,
      project_group: article.projectGroup,
    })
    onSuccess?.()
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.CREATE_ARTICLE_ERROR, error))
    onError?.(error)
  }
}

export const deleteBlogItem = (
  nid: number,
  onSuccess?: () => void,
  onSuccessNotify?: () => void,
): AppThunkAsync => async () => {
  try {
    await api.delete(`newsblog/${nid}?_format=json`)
    onSuccess?.()
    onSuccessNotify?.()
  } catch (error) {
    notify(error)
  }
}

export default {
  getBlog,
  getCategories,
  getFullItem,
  createArticle,
  deleteBlogItem,
}
