import Group from 'common/widgets/CompanyCluster/ProjectGroup'
import ZoomButton from 'components/Button/zoomButton'
import Widget, { WidgetProps } from 'components/Widget'
import { CompanyCluster, companyClustersMap } from 'constants/companyStructure'
import Routes from 'constants/routes'
import { useEffect, useMemo } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { getCompanyStructure } from 'store/companyStructure/actions'
import Swiper from 'swiper'
import { ReduxState } from 'types'

interface Props extends WidgetProps {
  onInit?: (swiper: Swiper) => void
  groupId: number
  title: string
  withoutMenu?: boolean
}

const GroupPreviewWidget: React.FC<Props> = ({
  groupId,
  title,
  withoutMenu,
  ...props
}) => {
  const { data } = useSelector((state: ReduxState) => state.companyStructure)
  const history = useHistory()
  const dispatch = useDispatch()

  useEffect(() => {
    Object.keys(companyClustersMap).map(
      (cluster) =>
        !data[cluster as CompanyCluster].length &&
        dispatch(getCompanyStructure(cluster as CompanyCluster)),
    )
  }, [])

  const projectGroups = useMemo(() => Object.values(data).flat(), [data])
  const projectGroup = projectGroups.find(
    (group) => group.tid === Number(groupId),
  )

  return (
    <Widget title={title} withoutMenu={withoutMenu} {...props}>
      {projectGroup && <Group group={projectGroup} withoutIcon big />}
      <ZoomButton
        onClick={() => history.push(`${Routes.projectGroups}/${groupId}`)}
      />
    </Widget>
  )
}

export default GroupPreviewWidget
