import { ReactComponent as ArrowNext } from 'assets/icons/arrowNextOrange.svg'
import { ReactComponent as Chat } from 'assets/icons/chat.svg'
import { ReactComponent as Edit } from 'assets/icons/edit.svg'
import { ReactComponent as Profile } from 'assets/icons/profile.svg'
import cn from 'classnames'
import Button from 'components/Button'
import useToggle from 'hooks/useToggle'
import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { formatUnixDistanceToNow } from 'services/date'
import {
  AppThunkAsync,
  Comment as IComment,
  Comments,
  Error,
} from 'types/index'

import styles from './styles.module.scss'

interface CommentProps {
  acquisitionId: string
  comment: IComment
  onReplyClick: (comment: IComment) => void
  isReply?: boolean
  editedCommentId?: string | number
  loadComments: (
    acquisitionId: string | number,
    commentId?: string | number | null,
    onSuccess?: ((comments: Comments) => void) | undefined,
    onError?: ((e: Error) => void) | undefined,
  ) => AppThunkAsync
}

const Comment = ({
  acquisitionId,
  onReplyClick,
  isReply,
  editedCommentId,
  comment,
  loadComments,
}: CommentProps) => {
  const {
    author,
    author_picture: authorPicture,
    created,
    comment_body: commentBody,
    replies_count: repliesCount,
    cid,
  } = comment
  const [comments, setComments] = useState<Comments>()
  const arrowToggle = useToggle()
  const dispatch = useDispatch()

  const onExpandClick = () => {
    arrowToggle.toggle()
    !comments && dispatch(loadComments(acquisitionId, cid, setComments))
  }

  const onReply = () => {
    onReplyClick(comment)
  }

  return (
    <div className={cn(styles.commentsContainer, isReply && styles.reply)}>
      <div className={styles.commentsHeader}>
        <div className={styles.userTitle}>
          <div className={styles.avatar}>
            {authorPicture ? <img src={authorPicture} alt="" /> : <Profile />}
          </div>
          <div className={styles.textContainer}>
            <div className={styles.userName}>{author}</div>
            <div className={styles.dot} />
            <div>{formatUnixDistanceToNow(+created)}</div>
          </div>
        </div>
        {repliesCount > 0 && (
          <Button
            className={cn(
              styles.arrowContainer,
              arrowToggle.value && styles.expanded,
            )}
            onClick={onExpandClick}
          >
            <ArrowNext />
          </Button>
        )}
      </div>
      <div className={styles.commentBody}>
        <div className={styles.commentTitle}>{'Änderungskommentar:'}</div>
        <div dangerouslySetInnerHTML={{ __html: commentBody }} />
      </div>
      <div className={styles.commentsFooter}>
        <Button
          className={cn(
            styles.editWrapper,
            editedCommentId === cid && styles.active,
          )}
          onClick={onReply}
        >
          <div>
            <Edit />
          </div>
          <div>{'Antworten'}</div>
        </Button>
        <div className={styles.chatIconWrapper}>
          <div>
            <Chat />
          </div>
          <div>{repliesCount}</div>
        </div>
      </div>
      {arrowToggle.value && (
        <div>
          {comments?.rows.map((_comment) => (
            <Comment
              key={_comment.cid}
              acquisitionId={acquisitionId}
              comment={_comment}
              isReply
              onReplyClick={onReplyClick}
              editedCommentId={editedCommentId}
              loadComments={loadComments}
            />
          ))}
        </div>
      )}
    </div>
  )
}
export default Comment
