import dateTransform from 'common/pipes/dateTime.pipe'
import Table from 'common/Table'
import { baseUrl } from 'constants/index'
import { parse } from 'date-fns'
import { useMemo } from 'react'
import { Item, ItemParams, Menu, useContextMenu } from 'react-contexify'
import { CSVLink } from 'react-csv'
import { Column, Row } from 'react-table'
import { Notes } from 'types'

interface NotesModalProps {
  notesData: Notes[] | undefined
  handleEditComment?: (id: number | string) => void
  handleEditAttachment?: (id: number | string) => void
  handleDeleteNote?: (id: number | string) => void
  isShowingDelete?: boolean
}

type TableData = {
  id: number
  date: string
  content: string
  attachment: string
}

const NotesModal = ({
  notesData,
  handleEditComment,
  handleEditAttachment,
  handleDeleteNote,
  isShowingDelete,
}: NotesModalProps) => {
  const tableData = (): TableData[] => [
    ...(notesData?.flatMap((note) => ({
      id: note.id || 0,
      date:
        (note.created &&
          note.created.substr(
            note.created.indexOf(', ') + 1,
            note.created.indexOf(' - ') - 3,
          )) ||
        '',
      content: note.note || '',
      attachment:
        note.attachments && note?.attachments.length > 0
          ? note.attachments
              .map((attachment) => `${attachment.name}`)
              .join('\\n')
          : 'Keine Anhänge',
    })) || []),
  ]
  const columnsData = useMemo<Column<TableData>[]>(
    () => [
      {
        Header: 'Datum',
        accessor: 'date',
        isSortedDesc: true,
        sortType: (a, b) => dateTransform(a.values.from, b.values.from),
      },
      {
        Header: 'Inhalt',
        accessor: 'content',
      },
      {
        Header: 'Anhang',
        accessor: 'attachment',
      },
    ],
    [],
  )

  const MENU_ID = 'notes-context-menu'
  const { show } = useContextMenu({
    id: MENU_ID,
  })
  const onContextEditClick = ({
    props,
  }: ItemParams<{ row: Row<TableData> }>) => {
    if (handleEditComment) {
      handleEditComment(props?.row.original.id as number | string)
    }
  }
  const onContextDeleteNoteClick = ({
    props,
  }: ItemParams<{ row: Row<TableData> }>) => {
    if (handleDeleteNote) {
      handleDeleteNote(props?.row.original.id as number | string)
    }
  }
  const onContextEditAttachmentClick = ({
    props,
  }: ItemParams<{ row: Row<TableData> }>) => {
    if (handleEditAttachment) {
      handleEditAttachment(props?.row.original.id as number | string)
    }
  }
  const displayMenu = (row: Row<any>, e: React.MouseEvent) =>
    show(e, { props: { row } })
  const headers = columnsData.map((el) => ({
    label: el.Header,
    key: el.accessor,
  }))
  const handleAttachmentView = (row: Row<any>) => {
    if (row.original.attachment !== 'Keine Anhänge') {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      window
        .open(
          `${baseUrl}/sites/default/files/employees/notes/${row.original.attachment}`,
          '_blank',
        )
        .focus()
    }
  }

  return (
    <>
      <Table
        columnsData={columnsData as any}
        onContextMenu={displayMenu}
        onRowDbClick={handleAttachmentView}
        isExpandable
        tableData={
          tableData().sort(
            (a, b) =>
              parse(b.date, 'dd.MM.yyyy', new Date()).valueOf() -
              parse(a.date, 'dd.MM.yyyy', new Date()).valueOf(),
          ) as any
        }
      />
      <Menu id={MENU_ID}>
        <Item onClick={onContextEditClick}>{'Kommentar bearbeiten'}</Item>
        <Item onClick={onContextEditAttachmentClick}>{'Anhang ersetzen'}</Item>
        {isShowingDelete && (
          <Item onClick={onContextDeleteNoteClick}>{'Eintrag löschen'}</Item>
        )}
        <Item>
          <CSVLink
            filename={`NotizenHistorien${new Date()}.csv`}
            data={tableData()}
            headers={[...headers] as any}
          >
            {'Als excel Tabelle exportieren'}
          </CSVLink>
        </Item>
      </Menu>
    </>
  )
}

export default NotesModal
