import Modal from 'common/Modal'
import Table from 'common/Table'
import GlobalSearch from 'components/tableComponents/GlobalSearch'
import { EncodedCompareOperators } from 'constants/index'
import Routes from 'constants/routes'
import Form, { FormValues } from 'containers/Ives/Offers/OffersForm'
import { fromUnixTime, getUnixTime } from 'date-fns'
import useDebounce from 'hooks/useDebounce'
import useToggle from 'hooks/useToggle'
import { useEffect, useMemo, useRef, useState } from 'react'
import {
  Item,
  ItemParams,
  Menu,
  Separator,
  useContextMenu,
} from 'react-contexify'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { Column, Row } from 'react-table'
import { formatDate, zonedDate } from 'services/date'
import notify from 'services/notify'
import { getSelectValue } from 'services/utils'
import { getOffers, updateOffer } from 'store/ives/offers/actions'
import { Offers as IOffers, ReduxState } from 'types'

import { FormValues as FilterFormValues } from '../Filter/index'
import styles from './styles.module.scss'

const MENU_ID = 'offer-menu-id'

// ? we need use type instead of interface because of https://github.com/microsoft/TypeScript/issues/15300
export type TableData = {
  id: string
  number: string
  title: string
  amount: string
  index: string
  bindingUntil: string
}

interface TableWidgetProps {
  filters: FilterFormValues
}

const TableWidget = ({ filters }: TableWidgetProps): JSX.Element => {
  const editModalToggle = useToggle()
  const [globalFilter, setGlobalFilter] = useState<string>()
  const debouncedQuery = useDebounce(globalFilter, 500)
  const [selectedRow, setSelectedRow] = useState<Row<TableData>>()
  const [offers, setOffers] = useState<IOffers>()
  const dispatch = useDispatch()
  const { isLoading } = useSelector(({ ives }: ReduxState) => ives.offers)
  const history = useHistory()
  const parentRef = useRef<HTMLDivElement>(null)

  const { show } = useContextMenu({
    id: MENU_ID,
  })

  useEffect(() => {
    dispatch(
      getOffers(
        {
          page: 0,
          query: globalFilter,
          amount: filters.amount ? +filters.amount.slice(1) : undefined,
          bindingUntil: filters.bindingUntil
            ? getUnixTime(zonedDate(filters.bindingUntil))
            : undefined,
          amountCompareOperator: filters.amount
            ? EncodedCompareOperators[
                // TODO  <= and >=
                filters.amount[0] as keyof typeof EncodedCompareOperators
              ]
            : undefined,
        },
        setOffers,
      ),
    )
  }, [filters, debouncedQuery])

  const tableData = useMemo<TableData[]>(
    () =>
      offers?.rows.map(
        (row) =>
          ({
            id: `${row.nid}`,
            amount: `${row.amount}`,
            index: row.index,
            number: `${row.number}`,
            title: row.title,
            bindingUntil: row.binding_until
              ? formatDate(fromUnixTime(+row.binding_until))
              : '',
          } as TableData),
      ) || [],
    [offers],
  )

  const columnsData = useMemo<Column<TableData>[]>(
    () => [
      {
        Header: 'Angebotsnummer',
        accessor: 'number', // accessor is the "key" in the data
      },
      {
        Header: 'Angebotsinhalt',
        accessor: 'title',
      },
      {
        Header: 'Angebotssumme',
        accessor: 'amount',
      },
      {
        Header: 'Index',
        accessor: 'index',
      },
      {
        Header: 'Bindefrist',
        accessor: 'bindingUntil',
      },
    ],
    [],
  )

  const displayMenu = (row: Row<TableData>, e: React.MouseEvent) =>
    show(e, { props: { row } })

  const onEditClick = (row?: Row<TableData>) => {
    setSelectedRow(row)
    editModalToggle.on()
  }

  const onContextEditClick = ({ props }: ItemParams<{ row: Row<TableData> }>) =>
    history.push({
      pathname: Routes.offerDetails,
      hash: props?.row.original.id,
    })

  const onContextPrintClick = () => window.print()

  const hasNextPage = useMemo(
    () =>
      !!(
        offers &&
        (offers.pager.current_page || 0) + 1 < offers.pager.total_pages
      ),
    [offers?.pager],
  )

  const loadNextPage = (startIndex: number, stopIndex: number) => {
    dispatch(
      getOffers(
        { page: stopIndex / (offers?.pager.items_per_page || 50) },
        setOffers,
      ),
    )
    return null
  }

  const onEditOfferSubmit = (values: FormValues) => {
    dispatch(
      updateOffer(
        {
          nid: selectedRow?.original.id,
          amount: '',
          amount_estimated: false,
          binding_until: '',
          comment: values.comment,
          created: getUnixTime(zonedDate(values.created)),
          customer: { id: values.customer || '' },
          liability_damage: getSelectValue(values.liabilityDamage),
          number: values.number,
          probability: getSelectValue(values.probability),
          title: values.title,
        },
        () => notify('Angebote wurde erfolgreich bearbeitet', 'success'),
      ),
    )
    editModalToggle.off()
  }

  return (
    <>
      <div className={styles.searchAndTableWrapper} ref={parentRef}>
        <GlobalSearch
          globalFilter={globalFilter}
          setGlobalFilter={setGlobalFilter}
          containerClassName={styles.search}
        />
        <Table
          columnsData={columnsData}
          tableData={tableData}
          onContextMenu={displayMenu}
          onRowDbClick={onEditClick}
          notSelectable
          hasNextPage={hasNextPage}
          isNextPageLoading={isLoading}
          loadNextPage={loadNextPage}
          tableWidth={parentRef.current?.getBoundingClientRect().width}
        />
      </div>
      <Modal
        isOpen={editModalToggle.value}
        onRequestClose={editModalToggle.off}
      >
        <Form
          onCancel={editModalToggle.off}
          onSubmit={onEditOfferSubmit}
          // TODO
          // initialData={}
        />
      </Modal>
      <Menu id={MENU_ID}>
        <Item onClick={onContextEditClick}>{'Angebot Bearbeiten'}</Item>
        <Separator />
        <Item onClick={onContextPrintClick}>{'Ausdrucken'}</Item>
      </Menu>
    </>
  )
}

export default TableWidget
