import FieldArray from 'components/inputs/FieldArray'
import HtmlEditor from 'components/inputs/HtmlEditor'
import Select from 'components/inputs/Select'
import { FormikProps } from 'formik'

import { FormOptions, Step5 as Props } from '../formData'
import styles from '../styles.module.scss'

interface FormProps {
  formik: FormikProps<Props>
  options: FormOptions
}

const Step5 = ({ formik, options }: FormProps) => (
  <>
    <h2 className={styles.stepTitle}>{'Unternehmensstruktur'}</h2>
    <FieldArray
      isGroup
      name="working_groups"
      label="Arbeitsgruppe"
      formik={formik}
      renderField={(defaultValue, index) => (
        <div>
          <Select
            name={`working_groups[${index}].working_group`}
            placeholder="Auswahl der AG"
            isSearchable={false}
            value={defaultValue.working_group}
            options={options.workingGroupOptions}
            formik={formik}
            label="Arbeitsgruppe"
          />
          <Select
            name={`working_groups[${index}].working_group_function`}
            placeholder="Funktion in der AG"
            isSearchable={false}
            value={defaultValue.working_group_function}
            options={options.workingFunctionOptions}
            formik={formik}
            label="Rolle"
          />
          <HtmlEditor
            name={`working_groups[${index}].working_group_spec`}
            label="Spezialisierung innerhalb der AG (Fachgruppe)"
            defaultValue={defaultValue.working_group_spec}
            formik={formik}
          />
        </div>
      )}
    />
    <FieldArray
      isGroup
      name="competence_areas"
      label="Kompetenzbereich"
      formik={formik}
      renderField={(defaultValue, index) => (
        <div>
          <Select
            name={`competence_areas[${index}].competence_area`}
            placeholder="Auswahl der KB"
            isSearchable={false}
            options={options.caOptions}
            value={defaultValue.competence_area}
            formik={formik}
            label="Kompetenzbereich"
          />
          <Select
            name={`competence_areas[${index}].ca_function`}
            placeholder="Funktion in der KB"
            isSearchable={false}
            options={options.competenceFunctionOptions}
            value={defaultValue.ca_function}
            formik={formik}
            label="Rolle"
          />
          <HtmlEditor
            name={`competence_areas[${index}].ca_spec`}
            label="Spezialisierung innerhalb der KB (Fachgruppe)"
            defaultValue={defaultValue.ca_spec}
            formik={formik}
          />
        </div>
      )}
    />
    <FieldArray
      isGroup
      name="competence_centers"
      label="Kompetenzzentrum"
      formik={formik}
      renderField={(defaultValue, index) => (
        <div>
          <Select
            name={`competence_centers[${index}].competence_center`}
            placeholder="Kompetenzzentrum auswählen"
            isSearchable={false}
            options={options.competenceCenterOptions}
            value={defaultValue.competence_center}
            formik={formik}
            label="Kompetenzzentrum"
          />
          <Select
            name={`competence_centers[${index}].function_in_compete`}
            placeholder="Funktion in der KB"
            isSearchable={false}
            options={options.functionInCompetenceOptions}
            value={defaultValue.function_in_compete}
            formik={formik}
            label="Rolle"
          />
        </div>
      )}
    />
  </>
)

export default Step5
