import Timetable from 'common/Timeline'
import { TableProps } from 'types'

const ContentView = ({
  timeTableGroups,
  timeTableItems,
  contextMenuAvailability,
  applicationToggle,
  popupType,
  setPopupType,
  currentUser,
  setCurrentUser,
  csvData,
  csvHeader,
}: TableProps) => (
  <Timetable
    timeTableGroups={timeTableGroups}
    timeTableItems={timeTableItems}
    contextMenuAvailability={contextMenuAvailability}
    applicationToggle={applicationToggle}
    popupType={popupType}
    setPopupType={setPopupType}
    currentUser={currentUser}
    setCurrentUser={setCurrentUser}
    csvHeader={csvHeader}
    csvData={csvData}
  />
)

export default ContentView
