import { mapDashboardMenu } from 'services/utils'
import { Blog, BlogCategory, CompanyInfoItem, Error } from 'types/index'

import ActionType, { Action } from './types'

export interface IState {
  isLoading: boolean
  error: Error | null
  blogWidget?: Blog
  companyInfo: CompanyInfoItem[]
  menu: BlogCategory[]
}

const initialState: IState = {
  isLoading: false,
  error: null,
  blogWidget: undefined,
  companyInfo: [],
  menu: [],
}

const reducer = (state = initialState, action: Action): IState => {
  switch (action.type) {
    case ActionType.GET_BLOG_WIDGET_BEGIN:
    case ActionType.GET_COMPANY_INFORMATION_BEGIN:
    case ActionType.GET_MENU_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.GET_BLOG_WIDGET_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: null,
        blogWidget: action.payload,
      }
    }
    case ActionType.GET_COMPANY_INFORMATION_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: null,
        companyInfo: action.payload,
      }
    }
    case ActionType.COMPANY_INFORMATION_READ: {
      const index = state.companyInfo.findIndex(
        (item) => item.nid === action.payload.nid,
      )

      if (index < 0) return state

      return {
        ...state,
        companyInfo: [
          ...state.companyInfo.slice(0, index),
          {
            ...state.companyInfo[index],
            read: String(action.payload.userId),
          },
          ...state.companyInfo.slice(index + 1),
        ],
      }
    }
    case ActionType.COMPANY_INFORMATION_CREATED: {
      return {
        ...state,
        companyInfo: [action.payload, ...state.companyInfo],
      }
    }
    case ActionType.GET_MENU_SUCCESS: {
      return {
        ...state,
        menu: mapDashboardMenu(action.payload),
      }
    }
    case ActionType.GET_COMPANY_INFORMATION_ERROR:
    case ActionType.GET_BLOG_WIDGET_ERROR:
    case ActionType.GET_MENU_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    default:
      return state
  }
}

export default reducer
