import { Acquisition, Acquisitions, Comments, Error } from 'types/index'

enum ActionType {
  GET_ACQUISITION_RECORD_BEGIN = '[ives] [acquisitions] GET_ACQUISITION_RECORD_BEGIN',
  GET_ACQUISITION_RECORD_SUCCESS = '[ives] [acquisitions] GET_ACQUISITION_RECORD_SUCCESS',
  GET_ACQUISITION_RECORD_ERROR = '[ives] [acquisitions] GET_ACQUISITION_RECORD_ERROR',
  GET_ACQUISITIONS_BEGIN = '[ives] [acquisitions] GET_ACQUISITIONS_BEGIN',
  GET_ACQUISITIONS_SUCCESS = '[ives] [acquisitions] GET_ACQUISITIONS_SUCCESS',
  GET_ACQUISITIONS_ERROR = '[ives] [acquisitions] GET_ACQUISITIONS_ERROR',
  UPDATE_ACQUISITION_BEGIN = '[ives] [acquisitions] UPDATE_ACQUISITION_BEGIN',
  UPDATE_ACQUISITION_SUCCESS = '[ives] [acquisitions] UPDATE_ACQUISITION_SUCCESS',
  UPDATE_ACQUISITION_ERROR = '[ives] [acquisitions] UPDATE_ACQUISITION_ERROR',
  CREATE_ACQUISITION_BEGIN = '[ives] [acquisitions] CREATE_ACQUISITION_BEGIN',
  CREATE_ACQUISITION_SUCCESS = '[ives] [acquisitions] CREATE_ACQUISITION_SUCCESS',
  CREATE_ACQUISITION_ERROR = '[ives] [acquisitions] CREATE_ACQUISITION_ERROR',
  ACQUISITION_RECORD_DELETED = '[ives] [acquisitions] ACQUISITION_RECORD_DELETED',
  GET_COMMENTS_BEGIN = '[ives] [acquisitions] GET_COMMENTS_BEGIN',
  GET_COMMENTS_SUCCESS = '[ives] [acquisitions] GET_COMMENTS_SUCCESS',
  GET_COMMENTS_ERROR = '[ives] [acquisitions] GET_COMMENTS_ERROR',
  UPDATE_COMMENT_BEGIN = '[ives] [acquisitions] UPDATE_COMMENT_BEGIN',
  UPDATE_COMMENT_SUCCESS = '[ives] [acquisitions] UPDATE_COMMENT_SUCCESS',
  UPDATE_COMMENT_ERROR = '[ives] [acquisitions] UPDATE_COMMENT_ERROR',
  CREATE_COMMENT_BEGIN = '[ives] [acquisitions] CREATE_COMMENT_BEGIN',
  CREATE_COMMENT_SUCCESS = '[ives] [acquisitions] CREATE_COMMENT_SUCCESS',
  CREATE_COMMENT_ERROR = '[ives] [acquisitions] CREATE_COMMENT_ERROR',
}

export interface GetAcquisitionRecordBeginAction {
  type: ActionType.GET_ACQUISITION_RECORD_BEGIN
}

export interface GetAcquisitionRecordSuccessAction {
  type: ActionType.GET_ACQUISITION_RECORD_SUCCESS
  payload: Acquisition
}

export interface GetAcquisitionRecordErrorAction {
  type: ActionType.GET_ACQUISITION_RECORD_ERROR
  payload: Error
}

export interface GetAcquisitionsBeginAction {
  type: ActionType.GET_ACQUISITIONS_BEGIN
}

export interface GetAcquisitionsSuccessAction {
  type: ActionType.GET_ACQUISITIONS_SUCCESS
  payload: Acquisitions
}

export interface GetAcquisitionsErrorAction {
  type: ActionType.GET_ACQUISITIONS_ERROR
  payload: Error
}

export interface UpdateAcquisitionBeginAction {
  type: ActionType.UPDATE_ACQUISITION_BEGIN
}

export interface UpdateAcquisitionSuccessAction {
  type: ActionType.UPDATE_ACQUISITION_SUCCESS
}

export interface UpdateAcquisitionErrorAction {
  type: ActionType.UPDATE_ACQUISITION_ERROR
  payload: Error
}

export interface CreateAcquisitionBeginAction {
  type: ActionType.CREATE_ACQUISITION_BEGIN
}

export interface CreateAcquisitionSuccessAction {
  type: ActionType.CREATE_ACQUISITION_SUCCESS
}

export interface CreateAcquisitionErrorAction {
  type: ActionType.CREATE_ACQUISITION_ERROR
  payload: Error
}
interface AcquisitionRecordDeletedAction {
  type: ActionType.ACQUISITION_RECORD_DELETED
  payload: number
}
export interface GetCommentsBeginAction {
  type: ActionType.GET_COMMENTS_BEGIN
}

export interface GetCommentsSuccessAction {
  type: ActionType.GET_COMMENTS_SUCCESS
  payload: Comments
}

export interface GetCommentsErrorAction {
  type: ActionType.GET_COMMENTS_ERROR
  payload: Error
}

export interface UpdateCommentBeginAction {
  type: ActionType.UPDATE_COMMENT_BEGIN
}

export interface UpdateCommentSuccessAction {
  type: ActionType.UPDATE_COMMENT_SUCCESS
}

export interface UpdateCommentErrorAction {
  type: ActionType.UPDATE_COMMENT_ERROR
  payload: Error
}

export interface CreateCommentBeginAction {
  type: ActionType.CREATE_COMMENT_BEGIN
}

export interface CreateCommentSuccessAction {
  type: ActionType.CREATE_COMMENT_SUCCESS
}

export interface CreateCommentErrorAction {
  type: ActionType.CREATE_COMMENT_ERROR
  payload: Error
}

export type Action =
  | GetAcquisitionRecordBeginAction
  | GetAcquisitionRecordSuccessAction
  | GetAcquisitionRecordErrorAction
  | GetAcquisitionsBeginAction
  | GetAcquisitionsSuccessAction
  | GetAcquisitionsErrorAction
  | UpdateAcquisitionBeginAction
  | UpdateAcquisitionSuccessAction
  | UpdateAcquisitionErrorAction
  | CreateAcquisitionBeginAction
  | CreateAcquisitionSuccessAction
  | CreateAcquisitionErrorAction
  | AcquisitionRecordDeletedAction
  | GetCommentsBeginAction
  | GetCommentsSuccessAction
  | GetCommentsErrorAction
  | UpdateCommentBeginAction
  | UpdateCommentSuccessAction
  | UpdateCommentErrorAction
  | CreateCommentBeginAction
  | CreateCommentSuccessAction
  | CreateCommentErrorAction

export default ActionType
