import { ReactComponent as Base } from 'assets/icons/employeeDataBaseWhite.svg'
import Placeholder from 'assets/icons/profile.svg'
import cn from 'classnames'
import AccordionComponent from 'common/Accordion'
import HistoryModal from 'common/History'
import Modal from 'common/Modal'
import Button from 'components/Button'
import TextInput from 'components/inputs/TextInput'
import { baseUrl } from 'constants/index'
import { format } from 'date-fns'
import { useFormik } from 'formik'
import useToggle from 'hooks/useToggle'
import { useEffect, useRef, useState } from 'react'
import { useDropzone } from 'react-dropzone'
import { useDispatch, useSelector } from 'react-redux'
import api from 'services/api'
import notify from 'services/notify'
import {
  deleteNote,
  editComment,
  getEmployeeRecord,
} from 'store/ives/employees/actions'
import {
  Attachment,
  Employee,
  FileWithPreview,
  ReduxState,
  UserRole,
} from 'types'

import { FormOptions, getFormSelectOptions } from '../../EmployeeForm/formData'
import { FormSchema, initialNotesValues } from '..'
import styles from '../styles.module.scss'

interface EmployeeData {
  employeeTitle: Employee | undefined
  currentRecord: Employee
  allAccordions: boolean
}
export const isUTF8 = (string: string): boolean => {
  // eslint-disable-next-line no-plusplus
  for (let i = 0; i < string.length; i++) {
    if (string.charCodeAt(i) > 127) {
      return false
    }
  }
  return true
}
const BaseDataView = ({
  currentRecord,
  employeeTitle,
  allAccordions,
}: EmployeeData) => {
  const [slideBase, setSlideBase] = useState(false)
  const [commentSection, setCommentSection] = useState(false)
  const [noteID, setNoteID] = useState<string | null>(null)
  const inputRef = useRef<any>()
  const dispatch = useDispatch()
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [currentEmployee, setCurrentEmployee] = useState<Employee>(
    currentRecord,
  )
  const currentUser = useSelector(({ user }: ReduxState) => user.user)

  const [attachments, setAttachments] = useState<
    Attachment[] | { id: string; name: string }
  >([] as Attachment[])
  // const currentProjectGroup = useSelector(({ user }: ReduxState) => user.user)
  //   ?.roles
  const { user } = useSelector(({ user }: ReduxState) => user)
  const [addedDocuments, setAddedDocuments] = useState<FileWithPreview>()
  const IDquery = new URLSearchParams(window.location.search)
  const [options, setOptions] = useState<FormOptions>({
    projectGroups: [],
    familyOptions: [],
    leaveOptions: [],
    companyFunctionOptions: [],
    locationOptions: [],
    academicOptions: [],
    hourlyModelOptions: [],
    competenceFunctionOptions: [],
    caOptions: [],
    crossCompetenceOptions: [],
    crossGroupOptions: [],
    workingGroupOptions: [],
    workingFunctionOptions: [],
    workingDaysOptions: [],
  })
  const ID = IDquery.get('id')
  // eslint-disable-next-line consistent-return
  useEffect(() => {
    getFormSelectOptions().then((res: FormOptions) => {
      setOptions(res)
    })
    // getCompanyFunction()
    const onPageLoad = () => {
      setTimeout(() => {
        setSlideBase(true)
      }, 1000)
    }
    if (document.readyState === 'complete') {
      onPageLoad()
    } else {
      window.addEventListener('load', onPageLoad)
      return () => window.removeEventListener('load', onPageLoad)
    }
    // setEmployeeRecordsFromDispatch()
  }, [])

  const filterData = (id: string | number | undefined, object: any) => {
    const data = object.filter((option: any) => option.value === id)[0]
    return data ? data.label : ''
  }
  const toggleNotesModal = useToggle()
  const academicDegree = currentRecord.academic_degrees?.map((item) => {
    const academicDegree = options.academicOptions.find(
      (option) => option.value === item,
    )
    return [academicDegree?.label]
  })

  const formik = useFormik({
    initialValues: initialNotesValues,
    validationSchema: FormSchema,
    onSubmit: async (values) => {
      try {
        if (noteID) {
          dispatch(
            editComment(ID as string, {
              notes_main: {
                id: noteID,
                attachments,
                note: values.notes_main,
              },
            }),
          )
          setNoteID(null)
        } else {
          await api.post(`/employees?_format=json`, {
            id: ID,
            notes_main: {
              attachments,
              note: values.notes_main,
            },
          })
        }
        dispatch(
          getEmployeeRecord(ID as string, (record: Employee) =>
            setCurrentEmployee(record),
          ),
        )
        formik.resetForm()
      } catch (error) {
        notify(error)
      }
    },
  })
  const handleEditComment = (id: any) => {
    setNoteID(id)
    toggleNotesModal.off()
    setCommentSection(true)
    formik.setFieldValue(
      'notes_main',
      currentEmployee?.notes_main?.find(
        (note) => note.id.toString() === id?.toString(),
      )?.note,
    )
    const attachmentArray = currentEmployee?.notes_contract?.find(
      (note) => note.id.toString() === id?.toString(),
    )?.attachments
    const attachment = currentEmployee?.notes_contract
      ?.find((note) => note.id.toString() === id?.toString())
      ?.attachments.map((attachment) => attachment.name)
      .join('')
    setAddedDocuments({ id: 0, name: attachment })
    setAttachments(attachmentArray || [])
  }
  const handleDeleteNote = async (id: number | string) => {
    toggleNotesModal.off()
    await dispatch(deleteNote(id as string))
    dispatch(
      getEmployeeRecord(ID as string, (record: Employee) =>
        setCurrentEmployee(record),
      ),
    )
  }

  const { getRootProps, getInputProps, isDragActive, open } = useDropzone({
    accept: 'application/msword, text/plain, application/pdf',
    maxSize: 6000000,
    maxFiles: 1,
    onDrop: async (acceptedFiles) => {
      const file = acceptedFiles[0]
      if (isUTF8(file.name)) {
        if (file.size < 2097152) {
          const { data } = await api.post(
            '/file/upload/node/employee_note/field_attachments?_format=json',
            file,
            {
              headers: {
                'Content-Type': 'application/octet-stream',
                'Content-Disposition': `file; filename="${file.name.replace(
                  /^a-zA-Z0-9 ]/g,
                  '',
                )}"`,
              },
            },
          )
          setAddedDocuments(
            Object.assign(file, {
              // id: data.fid[0].value,
              preview: URL.createObjectURL(file),
            }),
          )
          setAttachments({ id: data.fid[0].value, name: file.name })
        } else {
          notify(
            'Die Größe der Datei sollte nicht mehr als 2Mb betragen.',
            'info',
          )
        }
      } else {
        notify(
          'Das aktuelle Dokument enthält ein oder mehrere Sonderzeichen in seinem Namen. Bitte benennen Sie das Dokument um und laden Sie es erneut hoch',
          'error',
        )
      }
    },
  })

  const handleEditAttachment = (id: any) => {
    handleEditComment(id)
    open()
  }

  return (
    <>
      <div className={styles.detailMainWrap}>
        <div className={cn(styles.detailMain, slideBase && styles.open)}>
          <div className={cn(styles.detailHeader, styles.base)}>
            <h2>{'Stammdaten'}</h2>
          </div>
          {/* Basic data overview */}
          <div className={cn(styles.detailMainInnerOverview, styles.base)}>
            <img
              src={currentEmployee.picture_url || Placeholder}
              alt="avatar"
            />
            <div className={styles.detailBody}>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'Titel, Name'}</span>
                <br />
                <span className={styles.dataValue}>
                  {`${currentEmployee.first_name} ${currentEmployee.last_name}` ||
                    'Keine Angabe'}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>{'Einstiegsdatum'}</span>
                <br />
                {(currentEmployee?.entry_date && (
                  <span className={styles.dataValue}>
                    {`${format(
                      new Date(currentEmployee?.entry_date as string),
                      'dd.MM.yyyy',
                    )}` || 'Keine Angabe'}
                  </span>
                )) ||
                  'Keine Angabe'}
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'Position/Projektgruppe'}
                </span>
                <br />
                <span className={styles.dataValue}>
                  {`${employeeTitle?.company_function} bei ${employeeTitle?.project_group}` ||
                    'Keine Angabe'}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'AG/KB/Rolle in der AG'}
                </span>
                <br />
                <span className={styles.dataArray}>
                  {currentEmployee?.working_groups?.length > 0
                    ? currentEmployee.working_groups?.map((item, index) => (
                        <p key={index}>
                          {`${filterData(
                            item.working_group,
                            options.workingGroupOptions,
                          )} - ${filterData(
                            item.working_group_function,
                            options.workingFunctionOptions,
                          )}`}
                        </p>
                      ))
                    : 'Keine Angabe'}
                </span>
              </div>
              <div className={styles.dataWrap}>
                <span className={styles.dataLabel}>
                  {'KPZ und Rolle in der KPZ'}
                </span>
                <br />
                <span className={styles.dataArray}>
                  {currentEmployee?.competence_centers?.length > 0
                    ? currentEmployee.competence_centers?.map((item, index) => (
                        <p key={index}>
                          {`${filterData(
                            item.competence_center,
                            options.competenceCenterOptions,
                          )} - ${filterData(
                            item.function_in_compete as any,
                            options.competenceFunctionOptions,
                          )}\`}`}
                        </p>
                      ))
                    : 'Keine Angabe'}
                </span>
              </div>
            </div>
          </div>
          {/* Basic data detail */}
          <div className={styles.detailMainInner}>
            <div className={styles.detailBody}>
              <AccordionComponent
                expanded={allAccordions}
                title="Weitere Stammdaten"
                currentTitleStyle={styles.base}
              >
                <div className={styles.dataWrap}>
                  <span className={styles.dataLabel}>
                    {'Geburtstag, Anschrift'}
                  </span>
                  <br />
                  {currentEmployee?.birth_date ? (
                    <span className={styles.dataValue}>
                      {`${format(
                        new Date(currentEmployee?.birth_date as string),
                        'dd.MM.yyyy',
                      )}` || 0}
                    </span>
                  ) : (
                    'Keine Angabe'
                  )}
                </div>

                <div className={styles.dataWrap}>
                  <span className={styles.dataLabel}>
                    {'Kammer, bes. Abschlüsse'}
                  </span>
                  <br />
                  {academicDegree?.length ? (
                    <span className={styles.dataValue}>
                      {academicDegree?.map(
                        (item, index) =>
                          `${item} ${
                            academicDegree?.length - 1 === index ? '' : ', '
                          }`,
                      )}
                    </span>
                  ) : (
                    <span className={styles.dataValue}>{'Keine Angabe'}</span>
                  )}
                </div>
                <div className={styles.dataWrap}>
                  <span className={styles.dataLabel}>{'Austrittsdatum'}</span>
                  <br />
                  <span className={styles.dataValue}>{' - '}</span>
                </div>
                <div className={styles.dataWrap}>
                  <span className={styles.dataLabel}>
                    {'Telefon (Privat) '}
                  </span>
                  <br />
                  <span className={styles.dataValue}>
                    {currentEmployee.contact_phone || 'Keine Angabe'}
                  </span>
                </div>
                <div className={styles.dataWrap}>
                  <span className={styles.dataLabel}>{'Handy (Privat) '}</span>
                  <br />
                  <span className={styles.dataValue}>
                    {currentEmployee.contact_mobile || 'Keine Angabe'}
                  </span>
                </div>
                <div className={styles.dataWrap}>
                  <span className={styles.dataLabel}>{'Standort'}</span>
                  <br />
                  <span className={styles.dataValue}>
                    {filterData(
                      currentEmployee.location,
                      options.locationOptions,
                    ) || 'Keine Angabe'}
                  </span>
                </div>
                <div className={styles.dataWrap}>
                  <span className={styles.dataLabel}>{'E-Mail (Arbeit)'}</span>
                  <br />
                  <span className={styles.dataValue}>
                    {currentEmployee.corporate_email || 'Keine Angabe'}
                  </span>
                </div>
              </AccordionComponent>
              <AccordionComponent
                expanded={allAccordions}
                title="Kinder"
                currentTitleStyle={styles.base}
              >
                {currentEmployee.children?.map((child, index) => (
                  <div key={index}>
                    <span className={styles.dataValue}>
                      <p>{child.name || 'Keine Angabe'}</p>
                      {child.date ? (
                        <p>{format(new Date(child.date), 'dd.MM.yyyy')}</p>
                      ) : null}
                    </span>
                    <br />
                  </div>
                )) || 'Keine Angabe'}
              </AccordionComponent>
              <AccordionComponent
                expanded={allAccordions}
                title="Verlauf Position PG"
                currentTitleStyle={styles.base}
              >
                <div className={styles.dateIncludedField}>
                  <span className={styles.dateLabel}>
                    {new Date().getFullYear()}
                  </span>
                  <span className={styles.dataValue}>
                    {`${employeeTitle?.company_function} bei ${employeeTitle?.project_group}` ||
                      'Keine Angabe'}
                  </span>
                </div>

                {/* <Button
                  className={cn(styles.historyBtn, styles.base)}
                  variant="fulfilled"
                  onClick={() => undefined}
                >
                  {'Historienansicht'}
                </Button> */}
              </AccordionComponent>
              <AccordionComponent
                expanded={allAccordions}
                title="Verlauf Position AG/KB"
                currentTitleStyle={styles.base}
              >
                <div className={styles.dateIncludedField}>
                  <span className={styles.dateLabel}>
                    {new Date().getFullYear()}
                  </span>
                  <span className={styles.dataValue}>
                    {currentEmployee.working_groups?.map((item, index) => (
                      <p key={index}>
                        {`
                      ${filterData(
                        item.working_group_function,
                        options.workingFunctionOptions,
                      )}
                      in
                      ${filterData(
                        item.working_group,
                        options.workingGroupOptions,
                      )}
                      `}
                      </p>
                    )) || 'Keine Angabe'}
                  </span>
                </div>
                {/* <Button
                  className={cn(styles.historyBtn, styles.base)}
                  variant="fulfilled"
                  onClick={() => undefined}
                >
                  {'Historienansicht'}
                </Button> */}
              </AccordionComponent>
              {currentUser?.roles.includes(UserRole.HumanResource) && (
                <AccordionComponent
                  expanded={allAccordions}
                  title="Letzte Gesprächsnotizen"
                  currentTitleStyle={styles.base}
                >
                  <span className={styles.dataValue}>
                    {currentEmployee.notes_main
                      ?.slice(-3)
                      .reverse()
                      .flatMap((note, index) => (
                        <div className={styles.dateIncludedField} key={index}>
                          <span className={styles.dateLabel}>
                            {note.created &&
                              note.created.substr(
                                note.created.indexOf(', ') + 1,
                                note.created.indexOf(' - ') - 3,
                              )}
                          </span>
                          <span className={styles.dataValue}>{note.note}</span>
                        </div>
                      ))}
                  </span>
                  <div className={styles.historyButton}>
                    <Button
                      className={cn(styles.base)}
                      variant="fulfilled"
                      onClick={toggleNotesModal.on}
                    >
                      {'Historienansicht'}
                    </Button>
                  </div>
                </AccordionComponent>
              )}
              {currentUser?.roles.includes(UserRole.HumanResource) && (
                <AccordionComponent
                  expanded={allAccordions || commentSection}
                  title="Aktuelle Gesprächsnotizen"
                  currentTitleStyle={styles.base}
                >
                  <form onSubmit={formik.handleSubmit} ref={inputRef}>
                    <p className={styles.dataLabelAttachment}>
                      {addedDocuments && (
                        <a
                          href={`${baseUrl}/sites/default/files/employees/notes/${addedDocuments.name}`}
                          target="_blank"
                          className={styles.dataLabelAttachment}
                          rel="noreferrer"
                        >
                          {addedDocuments.name}
                        </a>
                      )}
                    </p>
                    <TextInput
                      name="notes_main"
                      id="notes_main"
                      label={`${format(new Date(), 'dd.MM.yyyy')}`}
                      multiline
                      rows={5}
                      formik={formik}
                      onChange={(e) => {
                        formik.handleChange(e)
                      }}
                      value={formik.values.notes_main}
                    />
                    <div className={styles.notesButtonWrap}>
                      <div
                        {...getRootProps({
                          className: cn(styles.dndContainer, {
                            [styles.active]: isDragActive,
                          }),
                        })}
                      >
                        <input
                          {...getInputProps()}
                          accept=".png,.jpg,.jpeg,.svg,.pdf"
                        />
                        <Button
                          variant="fulfilled"
                          className={cn(styles.base, styles.upload)}
                        >
                          {'Hochladen'}
                        </Button>
                      </div>
                      <Button
                        variant="fulfilled"
                        color="orange"
                        className={styles.notesButton}
                        type="submit"
                      >
                        {'Notiz speichern'}
                      </Button>
                    </div>
                  </form>
                </AccordionComponent>
              )}
            </div>
          </div>
        </div>
        <Button
          className={cn(styles.detailBtn, slideBase && styles.toggleBase)}
          onClick={() => setSlideBase(!slideBase)}
        >
          {(!slideBase && <Base className={styles.baseIcon} />) || <Base />}
          <h4 className={styles.sideTitle}>{'Stammdaten'}</h4>
        </Button>
      </div>
      <Modal
        isOpen={toggleNotesModal.value}
        onRequestClose={toggleNotesModal.off}
        withCloseButton
        maxWidth={1000}
      >
        <HistoryModal
          modalType="notes"
          notesData={currentEmployee.notes_main}
          handleEditComment={handleEditComment}
          handleEditAttachment={handleEditAttachment}
          handleDeleteNote={handleDeleteNote}
          isShowingDelete={
            user?.project_group.name ===
            'Controlling / Sekretariat / Administration'
          }
        />
      </Modal>
    </>
  )
}
export default BaseDataView
