import { ReactComponent as ArrowNextOrange } from 'assets/icons/arrowNextOrange.svg'
import Button from 'components/Button'

import styles from './styles.module.scss'

interface FormProps {
  title?: string
  footer?: JSX.Element
  header?: JSX.Element
  isNextStep?: boolean
  prevButton?: JSX.Element
  onCancel?: () => void
  /**
   * fired when form is submitted
   */
  handleSubmit: (e?: React.FormEvent<HTMLFormElement> | undefined) => void
  /**
   * on Ok button click
   */
  onSubmit?: () => void
}

const Form: React.FC<FormProps> = ({
  handleSubmit,
  title,
  footer,
  header,
  onCancel,
  onSubmit,
  children,
  isNextStep,
  prevButton,
}) => (
  <div className={styles.formContainer}>
    {title && <div className={styles.formTitle}>{title}</div>}
    {header}
    <form onSubmit={handleSubmit}>
      <div className={styles.form}>{children}</div>
      <div className={styles.formFooter}>
        {footer || (
          <div className={styles.footerButtons}>
            {prevButton}
            <Button
              variant="outlined"
              className={styles.button}
              onClick={onCancel}
              type="reset"
            >
              {'Abbrechen'}
            </Button>
            <Button
              variant="fulfilled"
              color="orange"
              className={styles.buttonPrimary}
              type="submit"
              onClick={onSubmit}
            >
              {isNextStep ? (
                <>
                  {'Weiter'} <ArrowNextOrange />
                </>
              ) : (
                'OK'
              )}
            </Button>
          </div>
        )}
      </div>
    </form>
  </div>
)

export default Form
