import { Employees, Error } from 'types/index'

import ActionType, { Action } from './types'

export interface IState {
  isLoading: boolean
  error: Error | null
  employees: Employees | null
  employeeRecords: any
}

const initialState: IState = {
  isLoading: false,
  error: null,
  employees: null,
  employeeRecords: null,
}

const reducer = (state = initialState, action: Action): IState => {
  switch (action.type) {
    case ActionType.GET_EMPLOYEES_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.GET_EMPLOYEES_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
        employees: action.payload,
      }
    }
    case ActionType.EMPLOYEES_PAGE_LOADED: {
      return {
        ...state,
        isLoading: false,
        error: '',
        employees: {
          pager: action.payload.pager,
          rows: [...(state.employees?.rows || []), ...action.payload.rows],
        },
      }
    }
    case ActionType.GET_EMPLOYEES_RECORD_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
        employeeRecords: action.payload,
      }
    }
    case ActionType.GET_EMPLOYEES_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    case ActionType.EDIT_COMMENT_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.EDIT_COMMENT_SUCCESS:
    case ActionType.DELETE_NOTE_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
      }
    }
    case ActionType.EDIT_COMMENT_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    default:
      return state
  }
}

export default reducer
