/* eslint-disable camelcase */
/* eslint-disable no-console */
import axios, { AxiosResponse } from 'axios'
import { apiUrl, isDev } from 'constants/index'
import { dispatch, getState } from 'services/store'
// import { refresh } from 'store/user/actions'
import ActionType from 'store/user/types'
import { BackendUserMeta } from 'types'

export const refresh = async (onError?: () => void) => {
  const formdata = new FormData()
  formdata.append('grant_type', 'refresh_token')
  formdata.append('client_id', '9b300a0d-5d88-480b-a51c-4aa6d4e71e54')
  formdata.append('client_secret', 'k3JYKua2pjGZVyRF')
  formdata.append('refresh_token', getState()?.user.meta?.refreshToken || '')

  try {
    const userInfo: AxiosResponse<BackendUserMeta> = await axios.post(
      'oauth/token',
      formdata,
      {
        baseURL: isDev
          ? 'https://ives-staging.leipzig.sup-sahlmann.com/backend/'
          : '/backend/',
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      },
    )
    return userInfo.data
  } catch (error) {
    onError?.()
  }
}

const instance = axios.create({
  baseURL: apiUrl,
})

instance.defaults.withCredentials = true

instance.interceptors.request.use(
  async (config) => {
    const newConfigs = { ...config }
    const token = getState()?.user.meta?.accessToken
    if (!newConfigs.headers) {
      newConfigs.headers = {}
    }

    if (token) {
      newConfigs.headers.Authorization = `Bearer ${token}`
    }

    return newConfigs
  },
  (error) => Promise.reject(error),
)

instance.interceptors.response.use(
  (response) => response,
  async (error) => {
    const originalRequest = error.config
    if (
      error.response.status === 401 &&
      !originalRequest._retry &&
      originalRequest.url === 'users/current?_format=json'
    ) {
      originalRequest._retry = true
      const res = await refresh(() => {
        dispatch({
          type: ActionType.LOG_OUT_SUCCESS,
        })
      })

      if (res) {
        dispatch({
          type: ActionType.REFRESH_TOKEN,
          payload: res,
        })

        originalRequest.headers.Authorization = `Bearer ${res?.access_token}`
        axios.defaults.headers.common.Authorization = `Bearer ${res?.access_token}`

        return instance(originalRequest)
      }
    }

    return Promise.reject(error)
  },
)

if (isDev) {
  axios.interceptors.request.use(
    (config) => {
      console.log(`Sending request to ${config.baseURL}${config.url}`)
      console.log('config => ', config)
      return config
    },
    (error) => {
      console.log('Request error => ', error)
      return Promise.reject(error)
    },
  )
  axios.interceptors.response.use(
    (response) => {
      console.log(
        'Received response from ',
        response.config.baseURL,
        response.config.url,
      )
      console.log('response => ', response)
      return response
    },
    (error) => {
      console.log('Response error => ', error)
      return Promise.reject(error)
    },
  )
}

export default instance
