import { useFormik } from 'formik'
import { useEffect, useMemo, useState } from 'react'
import { useDispatch } from 'react-redux'
import { validateDateInput } from 'services/date'
import notify from 'services/notify'
import {
  getFieldProbability,
  getLiability,
  getProjectManagers,
} from 'store/ives/offers/actions'
import {
  BackEndSelectOptions,
  Offer,
  SelectOption,
  SingleSelectOption,
} from 'types'
import * as Yup from 'yup'

import Form from './form'

const FormSchema: Yup.SchemaOf<FormValues> = Yup.object().shape({
  amount: Yup.number(),
  nan: Yup.number(),
  liabilityDamage: Yup.mixed<SingleSelectOption>(),
  probability: Yup.mixed<SingleSelectOption>(),
  projectManager: Yup.mixed<SingleSelectOption>(),
  bindingUntil: Yup.string()
    .transform(validateDateInput)
    .typeError('Bitte korrektes Datum eingeben')
    .default(''),
})

export type FormValues = Partial<Pick<Offer, 'amount' | 'nan'>> & {
  liabilityDamage?: SingleSelectOption
  bindingUntil?: string
  probability?: SingleSelectOption
  projectManager?: SingleSelectOption
}

const initialValues: FormValues = {
  liabilityDamage: undefined as SingleSelectOption,
  bindingUntil: '',
  probability: undefined as SingleSelectOption,
  projectManager: undefined as SingleSelectOption,
}

interface Props {
  onSubmit: (values: FormValues) => void
  initialData: Partial<{ [key in keyof FormValues]: string }>
}

const OfferProcessingForm: React.FC<Props> = ({ onSubmit, initialData }) => {
  const [projectManagers, setProjectManagers] = useState<BackEndSelectOptions>()
  const [liability, setLiability] = useState<Record<string, string>>()
  const [probability, setProbability] = useState<Record<string, string>>()
  const dispatch = useDispatch()

  const formik = useFormik({
    initialValues: {
      ...initialValues,
    },
    onSubmit: (values, helpers) => {
      try {
        helpers.resetForm()
        onSubmit({ ...values })
      } catch (error) {
        notify(error)
      }
    },
    validationSchema: FormSchema,
  })

  // TODO refactor
  useEffect(() => {
    probability &&
      liability &&
      projectManagers &&
      initialData &&
      formik.setValues({
        bindingUntil: initialData.bindingUntil,
        amount:
          initialData.amount && !Number.isNaN(+initialData.amount)
            ? +initialData.amount
            : undefined,
        nan:
          initialData.nan && !Number.isNaN(+initialData.nan)
            ? +initialData.nan
            : undefined,
        probability: initialData?.probability
          ? ({
              value: initialData.probability,
              label: probability?.[initialData.probability],
            } as SingleSelectOption)
          : undefined,
        liabilityDamage: initialData?.liabilityDamage
          ? ({
              value: initialData.liabilityDamage,
              label: liability?.[initialData.liabilityDamage],
            } as SingleSelectOption)
          : undefined,
        projectManager: initialData?.projectManager
          ? ({
              value: initialData.projectManager,
              label: projectManagers?.rows.find(
                (item) => `${item.id}` === initialData?.projectManager,
              )?.name,
            } as SingleSelectOption)
          : undefined,
      })
  }, [probability, liability, projectManagers, initialData])

  useEffect(() => {
    dispatch(getFieldProbability(setProbability))
    dispatch(getLiability(setLiability))
    dispatch(getProjectManagers('', setProjectManagers))
  }, [])

  const probabilityOptions = useMemo<SelectOption[]>(
    () =>
      probability
        ? Object.entries(probability).map<SelectOption>((pair) => ({
            value: pair[0],
            label: pair[1],
          }))
        : [],
    [probability],
  )

  const liabilityOptions = useMemo<SelectOption[]>(
    () =>
      liability
        ? Object.entries(liability)
            .sort((a, b) => a[0].localeCompare(b[0]))
            .map<SelectOption>((pair) => ({
              value: pair[0],
              label: pair[1],
            }))
        : [],
    [liability],
  )

  const projectManagerOptions = useMemo<SelectOption[]>(
    () =>
      projectManagers?.rows.map((item) => ({
        label: item.name,
        value: `${item.id}`,
      })) || [],
    [projectManagers],
  )

  return (
    <Form
      formik={formik}
      probabilityOptions={probabilityOptions}
      liabilityOptions={liabilityOptions}
      projectManagerOptions={projectManagerOptions}
    />
  )
}

export default OfferProcessingForm
