import {
  DatePicker as DatePickerComponent,
  LocalizationProvider,
} from '@mui/x-date-pickers'
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns'
import deLocale from 'date-fns/locale/de'
import { FormikProps } from 'formik'
import { DateUtils } from 'react-day-picker'
import { formatDate } from 'services/date'

import TextInput from '../TextInput'

interface DateInputProps<T> {
  placeholder?: string
  name: string
  value?: keyof T & string
  formik?: FormikProps<T>
  disabled?: boolean
  onChange?: (value: string) => void
  shouldDisableDate?: (date: Date) => boolean
}

const DatePicker = <T extends Record<string, unknown>>({
  placeholder = '',
  name,
  formik,
  value,
  onChange,
  disabled,
  shouldDisableDate,
}: DateInputProps<T>) => (
  <div className="customDatepicker">
    <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={deLocale}>
      <DatePickerComponent
        shouldDisableDate={shouldDisableDate}
        // disableCloseOnSelect
        disabled={disabled}
        label={placeholder}
        inputFormat={name === 'created' ? 'dd.MM.yyyy' : 'yyyy-MM-dd'}
        mask={name === 'created' ? '__.__.____' : '____-__-__'}
        value={`${formik?.values[name] || value}`}
        onChange={(newValue) => {
          if (newValue) {
            const formatType = name === 'created' ? 'dd.MM.yyyy' : 'yyyy-MM-dd'
            formik?.setFieldValue(name, formatDate(newValue, formatType))
            onChange && onChange(newValue.toString())
          }
        }}
        renderInput={(props) => (
          <TextInput
            {...props}
            name={name}
            typeOfInput="date"
            label={placeholder}
            disabled
            formik={formik}
            error={
              !!(
                formik?.touched[name] &&
                !DateUtils.isDate(formik?.values[name] as Date)
              )
            }
          />
        )}
      />
    </LocalizationProvider>
  </div>
)

export default DatePicker
