import { Error } from 'types/index'

import ActionType, { Action } from './types'

export interface IState {
  isLoading: boolean
  error: Error | null
  offerDocumentUrl: string
}

const initialState: IState = {
  isLoading: false,
  error: null,
  offerDocumentUrl: '',
}

const reducer = (state = initialState, action: Action): IState => {
  switch (action.type) {
    case ActionType.GET_PROJECTS_BEGIN:
    case ActionType.GET_PM_BEGIN:
    case ActionType.GET_PGL_BEGIN:
    case ActionType.GET_LIABILITY_BEGIN:
    case ActionType.GET_FIELD_PROBABILITY_BEGIN:
    case ActionType.GET_FIELD_DEPARTMENTS_BEGIN:
    case ActionType.GET_RESPONSIBLES_BEGIN:
    case ActionType.CREATE_OFFER_BEGIN:
    case ActionType.UPDATE_OFFER_BEGIN:
    case ActionType.GET_OFFER_RECORD_BEGIN:
    case ActionType.GET_OFFERS_BEGIN:
    case ActionType.GET_OFFER_COMMENTS_BEGIN:
    case ActionType.CONVERT_FILE_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.CONVERT_FILE_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: null,
        offerDocumentUrl: action.payload.uri,
      }
    }
    case ActionType.GET_PROJECTS_SUCCESS:
    case ActionType.GET_PM_SUCCESS:
    case ActionType.GET_PGL_SUCCESS:
    case ActionType.GET_LIABILITY_SUCCESS:
    case ActionType.GET_FIELD_PROBABILITY_SUCCESS:
    case ActionType.GET_FIELD_DEPARTMENTS_SUCCESS:
    case ActionType.GET_RESPONSIBLES_SUCCESS:
    case ActionType.GET_OFFER_RECORD_SUCCESS:
    case ActionType.GET_OFFERS_SUCCESS:
    case ActionType.CREATE_OFFER_SUCCESS:
    case ActionType.UPDATE_OFFER_SUCCESS:
    case ActionType.GET_OFFER_COMMENTS_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
      }
    }
    case ActionType.GET_PROJECTS_ERROR:
    case ActionType.GET_PM_ERROR:
    case ActionType.GET_PGL_ERROR:
    case ActionType.GET_LIABILITY_ERROR:
    case ActionType.GET_FIELD_PROBABILITY_ERROR:
    case ActionType.GET_FIELD_DEPARTMENTS_ERROR:
    case ActionType.GET_RESPONSIBLES_ERROR:
    case ActionType.GET_OFFER_RECORD_ERROR:
    case ActionType.GET_OFFERS_ERROR:
    case ActionType.CREATE_OFFER_ERROR:
    case ActionType.UPDATE_OFFER_ERROR:
    case ActionType.GET_OFFER_COMMENTS_ERROR:
    case ActionType.CONVERT_FILE_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    default:
      return state
  }
}

export default reducer
