import Customers, { TableData } from 'common/Customers'
import Modal from 'common/Modal'
import { DEFAULT_TABLE_WIDTH } from 'constants/index'
import { useFormik } from 'formik'
import useToggle from 'hooks/useToggle'
import useWindowSize from 'hooks/useWindowSize'
import { useEffect, useMemo, useState } from 'react'
import { useDispatch } from 'react-redux'
import { Row } from 'react-table'
import { validateDateInput } from 'services/date'
import notify from 'services/notify'
import { percent } from 'services/utils'
import { getResponsible } from 'store/ives/offers/actions'
import { BackEndSelectOptions, SelectOption } from 'types'
import * as Yup from 'yup'

import Form from './form'

const FormSchema: Yup.SchemaOf<FormValues> = Yup.object().shape({
  title: Yup.string().default(''),
  created: Yup.string()
    .transform(validateDateInput)
    .typeError('Bitte korrektes Datum eingeben')
    .default(''),
  responsible: Yup.array()
    .of(
      Yup.object().shape({
        label: Yup.string(),
        value: Yup.string(),
      }),
    )
    .default([]),
  customer: Yup.string().default(''),
  customerResponsible: Yup.string().default(''),
  comment: Yup.string().required('Erforderlich'),
})

export const initialValues = {
  title: '',
  created: '',
  responsible: [] as SelectOption[],
  customer: '',
  customerResponsible: '',
  comment: '',
}

export type FormValues = typeof initialValues

export type InitialData = Partial<Omit<FormValues, 'customer'>> & {
  customer?: { id: string; title: string }
}

interface AcquisitionFormProps {
  initialData?: InitialData
  onCancel: () => void
  onSubmit: (values: FormValues) => void
}

const AcquisitionForm = ({
  initialData,
  onCancel,
  onSubmit,
}: AcquisitionFormProps) => {
  const { width = DEFAULT_TABLE_WIDTH, height = 300 } = useWindowSize()
  const customers = useToggle()
  const [selectedCustomerId, setSelectedCustomerId] = useState(
    initialData?.customer?.id,
  )
  const [responsibles, setResponsibles] = useState<BackEndSelectOptions>()
  const dispatch = useDispatch()

  useEffect(() => {
    // TODO backend search
    dispatch(getResponsible('', setResponsibles))
  }, [])

  const responsibleOptions = useMemo<SelectOption[]>(
    () =>
      responsibles?.rows.map((row) => ({
        label: row.name,
        value: `${row.id}`,
      })) || [],
    [responsibles],
  )

  const formik = useFormik({
    initialValues: {
      ...initialValues,
      ...initialData,
      customer: initialData?.customer?.title || '',
    },
    onSubmit: (values, helpers) => {
      try {
        helpers.resetForm()
        onSubmit({
          ...values,
          customer: (values.customer && selectedCustomerId) || '',
        })
      } catch (error) {
        notify(error)
      }
    },
    validationSchema: FormSchema,
  })

  const onCustomerSubmitClick = (row?: Row<TableData>) => {
    formik.setFieldValue('customer', row?.original.name || '')
    setSelectedCustomerId(row?.id)
    customers.off()
  }

  return (
    <>
      <Form
        formik={formik}
        responsibleOptions={responsibleOptions}
        onCancel={onCancel}
        onUserSelectClick={customers.toggle}
      />
      <Modal
        isOpen={customers.value}
        onRequestClose={customers.off}
        maxWidth={percent(46.2, width)}
      >
        <Customers
          tableWidth={percent(45.8, width)}
          tableHeight={percent(50, height)}
          onCancel={customers.off}
          onSubmit={onCustomerSubmitClick}
        />
      </Modal>
    </>
  )
}

export default AcquisitionForm
