import Table from 'common/Table'
import { format, parse } from 'date-fns'
import { useMemo } from 'react'
import { Item, Menu, useContextMenu } from 'react-contexify'
import { CSVLink } from 'react-csv'
import { Column, Row } from 'react-table'
import { Allowences } from 'types'

type TableData = {
  id: string
  type: string
  date: string
  amount: string
}
interface AllowencesModalProps {
  allowencesData?: Allowences[]
}
const MENU_ID = 'allowences-menu-id'

const AllowencesModal = ({ allowencesData }: AllowencesModalProps) => {
  const tableData = useMemo<TableData[]>(
    () =>
      allowencesData?.map((allowence: any) => ({
        id: allowence.id,
        date: format(new Date(allowence.date), 'dd.MM.yyyy'),
        amount: `${allowence.amount} €`,
        type: allowence.type,
      })) || [],
    [allowencesData],
  )
  const columnsData = useMemo<Column<TableData>[]>(
    () => [
      {
        Header: 'Art',
        accessor: 'type',
      },
      {
        Header: 'Datum',
        accessor: 'date',
      },
      {
        Header: 'Betrag',
        accessor: 'amount',
      },
    ],
    [],
  )
  const { show } = useContextMenu({
    id: MENU_ID,
  })
  const displayMenu = (row: Row<any>, e: React.MouseEvent) =>
    show(e, { props: { row } })
  const headers = columnsData.map((el) => ({
    label: el.Header,
    key: el.accessor,
  }))

  return (
    <>
      <Table
        columnsData={columnsData}
        tableData={
          tableData.sort(
            (a, b) =>
              parse(b.date, 'dd.MM.yyyy', new Date()).valueOf() -
              parse(a.date, 'dd.MM.yyyy', new Date()).valueOf(),
          ) as any
        }
        onContextMenu={displayMenu}
      />
      <Menu id={MENU_ID}>
        <Item>
          <CSVLink
            filename={`BonuszahlungenHistorien${new Date()}.csv`}
            data={tableData}
            headers={[...headers] as any}
          >
            {'Als excel Tabelle exportieren'}
          </CSVLink>
        </Item>
      </Menu>
    </>
  )
}
export default AllowencesModal
