import Avatar from 'assets/images/default-avatar.svg'
import cn from 'classnames'
import Routes from 'constants/routes'
import { MouseEvent, useEffect, useRef, useState } from 'react'
import { BaseEmployee, ProjectGroup } from 'types'

import styles from './styles.module.scss'

const WIDTH_IMAGE = 34

export enum ColorSchema {
  gray = 'gray',
  green = 'green',
  orange = 'orange',
  blue = 'blue',
}

interface Props {
  active?: ProjectGroup
  group: ProjectGroup
  withoutIcon?: boolean
  big?: boolean
  onContextMenu?(event: MouseEvent<HTMLDivElement>): void
  openEmployeeInfo?(id: number | null): void
  setActive?(): void
}

const ProjectGroupComponent: React.FC<Props> = ({
  active,
  group,
  withoutIcon,
  big,
  onContextMenu,
  setActive,
  openEmployeeInfo,
}) => {
  const rowRef = useRef<HTMLHeadingElement>(null)
  const titleRef = useRef<HTMLHeadingElement>(null)
  const [arrayRows, setArrayRows] = useState<{
    firstRow: BaseEmployee[]
    secondRow: BaseEmployee[][]
  }>({
    firstRow: [],
    secondRow: [],
  })

  useEffect(() => {
    if (rowRef.current?.clientWidth && titleRef.current?.clientWidth) {
      const showImageCount = Math.floor(
        titleRef.current?.clientWidth / WIDTH_IMAGE,
      )
      const arraySize = Math.ceil(
        (group.employees.length -
          group.employees.slice(0, showImageCount).length) /
          showImageCount,
      )
      setArrayRows({
        firstRow: group.employees.slice(0, showImageCount),
        secondRow: Array(arraySize)
          .fill(null)
          .map((el, i) =>
            group.employees.slice(
              showImageCount * (i + 1),
              (i + 2) * showImageCount,
            ),
          ),
      })
    }
  }, [rowRef.current?.clientWidth, titleRef.current?.clientWidth])

  const memberIds = active?.employees?.map((m) => m.nid) || []
  active?.manager?.[0] && memberIds.push(active?.manager[0].nid)
  return (
    <>
      <div
        className={cn(styles.row, {
          [styles.active]: active?.tid === group.tid,
        })}
        onContextMenu={onContextMenu}
        ref={rowRef}
      >
        {!withoutIcon && (
          // eslint-disable-next-line jsx-a11y/control-has-associated-label
          <button type="button" className={styles.icon} onClick={setActive} />
        )}
        <a
          href={`${Routes.projectGroups}/${group.tid}`}
          className={cn(styles.groupTitle)}
          title={group.name}
        >
          {group.name}
        </a>
        {(group.manager[0] || !big) && (
          <div className={styles.lead}>
            <div
              role="button"
              className={cn(styles.imgWrap, {
                [styles.active]:
                  group.manager[0]?.nid &&
                  memberIds?.includes(group.manager[0].nid),
              })}
              onClick={() => openEmployeeInfo?.(group.manager[0]?.nid || null)}
              tabIndex={0}
            >
              <img
                className={cn({
                  [styles.defaultImg]: !group.manager[0]?.picture,
                })}
                src={group.manager[0]?.picture || undefined}
                alt={group.manager[0]?.name}
              />
            </div>
          </div>
        )}
        <div className={styles.members} ref={titleRef}>
          {arrayRows.firstRow?.map((employee) => (
            <div
              role="button"
              key={group.name + employee.name}
              className={cn(styles.imgWrap, {
                [styles.active]: memberIds?.includes(employee.nid),
              })}
              onClick={() => openEmployeeInfo?.(employee.nid)}
              tabIndex={0}
            >
              <img src={employee.picture || Avatar} alt={employee.name} />
            </div>
          ))}
        </div>
      </div>
      {arrayRows.secondRow.length !== 0 &&
        arrayRows.secondRow.map((el, index) => (
          <div
            className={cn(styles.row, styles.detailRow)}
            onContextMenu={onContextMenu}
            key={`row-${index}`}
          >
            {el.map((employee) => (
              <div
                role="button"
                key={group.name + employee.name}
                className={cn(styles.imgWrap, styles.photo, {
                  [styles.active]: memberIds?.includes(employee.nid),
                })}
                onClick={() => openEmployeeInfo?.(employee.nid)}
                tabIndex={0}
              >
                <img src={employee.picture || Avatar} alt={employee.name} />
              </div>
            ))}
          </div>
        ))}
    </>
  )
}

export default ProjectGroupComponent
