import isNationalHoliday from 'common/pipes/isNationalHoliday.pipe'
import isPartOfWorkingDays from 'common/pipes/isPartOfWorkingDays.pipe'
import { eachDayOfInterval, fromUnixTime, isWeekend } from 'date-fns'
import { NationalHolidays } from 'types'

const returnNumberOfWorkDays = (
  from: number,
  to: number,
  nationalHolidays: NationalHolidays[] | null,
  workingDays?: number[],
) => {
  let counter = 0
  const intervalArray = eachDayOfInterval({
    start: new Date(fromUnixTime(from)),
    end: new Date(fromUnixTime(to)),
  })
  intervalArray.forEach((day) => {
    if (
      !isWeekend(day) &&
      !isNationalHoliday(day, nationalHolidays) &&
      !isPartOfWorkingDays(day, workingDays)
    ) {
      counter += 1
    }
  })
  return counter
}
export default returnNumberOfWorkDays
