import { ReactComponent as EditIcon } from 'assets/icons/menuEdit.svg'
import cn from 'classnames'
import Button from 'components/Button'
import { UserAccess } from 'constants/index'
import { MouseEvent, useState } from 'react'
import {
  DragDropContext,
  Draggable,
  Droppable,
  DropResult,
} from 'react-beautiful-dnd'
import { useSelector } from 'react-redux'
import { NavMenuItem, ReduxState } from 'types'

import styles from './styles.module.scss'

interface Props {
  items: NavMenuItem[]
  selectedItemId: number | null
  nonDeletableItems?: string[]
  className?: string
  onContextMenu(event: MouseEvent<HTMLButtonElement>, item: NavMenuItem): void
  onAddItemClick(): void
  onDeleteItemClick(it: number): void
  onDragEnd(result: DropResult): void
  setSelectedItemId(id: number): void
}

const IconNavigationBar: React.FC<Props> = ({
  items,
  selectedItemId,
  className,
  nonDeletableItems,
  onContextMenu,
  onAddItemClick,
  onDeleteItemClick,
  onDragEnd,
  setSelectedItemId,
}) => {
  const [editing, setEditing] = useState(false)
  const { user } = useSelector((state: ReduxState) => state.user)

  const userRights = (user?.access || {}) as Record<UserAccess, boolean>

  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Droppable direction="horizontal" droppableId="navigation-bar-droppable">
        {(provided) => (
          <nav
            className={cn(styles.navigation, className)}
            {...provided.droppableProps}
            ref={provided.innerRef}
          >
            {items.map((item, index) => (
              <Draggable
                key={item.id}
                draggableId={String(item.id)}
                index={index}
                isDragDisabled={!editing}
                disableInteractiveElementBlocking
              >
                {(draggableProvided) => (
                  <div
                    className={cn(styles.navigationItem, {
                      [styles.selected]: selectedItemId === item.id,
                    })}
                    {...draggableProvided.draggableProps}
                    {...draggableProvided.dragHandleProps}
                    ref={draggableProvided.innerRef}
                  >
                    <button
                      className={styles.button}
                      type="button"
                      onClick={() => setSelectedItemId(item.id)}
                      onContextMenu={(event) => onContextMenu(event, item)}
                    >
                      {item.menu_icon ? (
                        <img src={item.menu_icon} alt="" />
                      ) : (
                        <div className={styles.initials}>
                          {item.title
                            .split(/[ /-]/g)
                            .map((word) => word[0])
                            .join('')}
                        </div>
                      )}
                      <div className={styles.itemTitle} title={item.title}>
                        {item.title}
                      </div>
                    </button>
                    {userRights[UserAccess.deleteDetailPageContent] &&
                      editing &&
                      !nonDeletableItems?.includes(item.title) && (
                        <Button
                          className={styles.deleteItem}
                          onClick={() => onDeleteItemClick(item.id)}
                        >
                          {'×'}
                        </Button>
                      )}
                  </div>
                )}
              </Draggable>
            ))}
            {provided.placeholder}
            {(userRights[UserAccess.createDetailPageContent] ||
              userRights[UserAccess.deleteDetailPageContent]) && (
              <>
                <div className={styles.addItem}>
                  {userRights[UserAccess.createDetailPageContent] && editing && (
                    <Button
                      variant="text"
                      className={styles.addItemButton}
                      onClick={onAddItemClick}
                    >
                      {'+ Element hinzufügen'}
                    </Button>
                  )}
                </div>
                {editing ? (
                  <Button
                    variant="fulfilled"
                    color="orange"
                    onClick={() => setEditing(false)}
                    className={styles.saveButton}
                  >
                    {'Änderung speichern'}
                  </Button>
                ) : (
                  <Button
                    onClick={() => setEditing(true)}
                    className={styles.editButton}
                  >
                    <EditIcon />
                  </Button>
                )}
              </>
            )}
          </nav>
        )}
      </Droppable>
    </DragDropContext>
  )
}

export default IconNavigationBar
