import '@progress/kendo-theme-default/dist/all.css'

import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Collapse,
} from '@mui/material'
import {
  Editor,
  EditorChangeEvent,
  EditorTools,
} from '@progress/kendo-react-editor'
import { ReactComponent as Arrow } from 'assets/icons/arrowDown.svg'
import { ReactComponent as Upload } from 'assets/icons/upload.svg'
import cn from 'classnames'
import Button from 'components/Button'
import Checkbox from 'components/inputs/Checkbox'
import TextInput from 'components/inputs/TextInput'
import DOMPurify from 'dompurify'
import { FormikProps } from 'formik'
import { useEffect, useMemo } from 'react'
import ReactHtmlParser from 'react-html-parser'
import { CheckboxGroup, CheckboxOption, FileWithPreview } from 'types'

import {
  ArticleFormValues,
  ArticleTab,
  BaseArticleFormValues,
  CheckboxTypes,
} from '.'
import styles from './styles.module.scss'

const {
  Bold,
  Italic,
  Underline,
  Strikethrough,
  AlignLeft,
  AlignCenter,
  AlignRight,
  AlignJustify,
  Indent,
  Outdent,
  OrderedList,
  UnorderedList,
  Undo,
  Redo,
  Link,
  Unlink,
  ViewHtml,
} = EditorTools

export interface CategoryItem {
  label: string
  subitems: CheckboxOption[]
}

export interface CategoryCheckboxes {
  title: string
  name: string
  items: CategoryItem[]
}

interface FormProps<T> {
  formik: FormikProps<T>
  groupsCheckboxes: CheckboxGroup[]
  categoryCheckboxes: CategoryCheckboxes
  getRootProps: any
  getInputProps: any
  isDragActive: boolean
  multipleProps: any
  multipleInputProps: any
  multipleDragActive: boolean
  images?: FileWithPreview[]
  expanded: string[]
  expandedCategory: CategoryItem | null
  activeTab: string
  openCropping(): void
  setExpandedCategory(val: CategoryItem | null): void
  addExpanded(val: string): void
  removeExpanded(val: string): void
}

const FormView = <T extends BaseArticleFormValues>({
  formik,
  groupsCheckboxes,
  categoryCheckboxes,
  getRootProps,
  getInputProps,
  isDragActive,
  multipleProps,
  multipleInputProps,
  multipleDragActive,
  images,
  expanded,
  activeTab,
  expandedCategory,
  openCropping,
  setExpandedCategory,
  addExpanded,
  removeExpanded,
}: FormProps<T>) => {
  const articleFormikValues = (formik.values as unknown) as ArticleFormValues
  const { category, projectGroup } = articleFormikValues
  const allCheckboxValues = useMemo(() => projectGroup?.concat(category), [
    projectGroup,
    category,
  ])
  const setChecked = (value: string) => allCheckboxValues.includes(value)
  const categoryCheckboxesValues = useMemo(
    () =>
      categoryCheckboxes.items.flatMap((item) =>
        item.subitems.map((subItem) => ({
          value: subItem.value,
          name: categoryCheckboxes.name,
        })),
      ),
    [categoryCheckboxes],
  )
  /** Get all checkboxes and connect with categoryCheckboxesValues */
  const activeFormikCheckboxes = useMemo(
    () =>
      groupsCheckboxes
        .flatMap((item) =>
          item.options.map((option) => ({
            name: item.name,
            value: option.value,
          })),
        )
        .concat(categoryCheckboxesValues),
    [groupsCheckboxes, categoryCheckboxesValues],
  )

  const htmlFrom = (htmlString: string) =>
    ReactHtmlParser(DOMPurify.sanitize(ReactHtmlParser(htmlString)))

  const compareCheckboxesLength =
    activeFormikCheckboxes?.length > 0 &&
    allCheckboxValues?.length > 0 &&
    activeFormikCheckboxes?.length === allCheckboxValues?.length

  /** Set all active checkbox */
  const setAllCategoryProjects = () => {
    if (activeFormikCheckboxes.length === allCheckboxValues.length) {
      formik.setFieldValue(CheckboxTypes.category, [])
      formik.setFieldValue(CheckboxTypes.projectGroup, [])

      return
    }
    /** Set checkboxes for category */
    formik.setFieldValue(
      CheckboxTypes.category,
      activeFormikCheckboxes
        .filter((item) => item.name === CheckboxTypes.category)
        .map((item) => String(item.value)),
    )

    /** Set checkboxes for projectGroup */
    formik.setFieldValue(
      CheckboxTypes.projectGroup,
      activeFormikCheckboxes
        .filter((item) => item.name === CheckboxTypes.projectGroup)
        .map((item) => String(item.value)),
    )
  }

  useEffect(() => {
    if (compareCheckboxesLength) {
      formik.setFieldValue('selectAll', ['on'])
    } else {
      formik.setFieldValue('selectAll', [])
    }
  }, [allCheckboxValues])

  return (
    <>
      <div className={styles.mainImageInputWrapper}>
        <div
          {...getRootProps({
            className: cn(styles.dndContainer, {
              [styles.active]: isDragActive,
            }),
          })}
        >
          {formik.values.mainImage.preview && (
            <img
              src={formik.values.mainImage.preview}
              alt="main article cover"
            />
          )}
          <input {...getInputProps()} accept=".png,.jpg,.jpeg" />
          {'Titelbild reinziehen'} <br />
          {'oder'} <br />
          <button type="button" className={styles.upload}>
            {'Hochladen'}
            <Upload />
          </button>
        </div>
        {formik.values.mainImage.preview && (
          <Button
            type="button"
            variant="fulfilled"
            color="orange"
            className={styles.cropButton}
            onClick={openCropping}
          >
            {'Bild zurechtschneiden'}
          </Button>
        )}
      </div>
      <TextInput
        name="title"
        label="Titel"
        formik={formik}
        value={htmlFrom(formik.values.title)}
      />
      <TextInput
        name="summary"
        label="Vorschautext"
        value={htmlFrom(formik.values.summary)}
        multiline
        rows={5}
        formik={formik}
      />
      <section>
        <h2 className={styles.title}>{'Fließtext'}</h2>
        <Editor
          tools={[
            [Bold, Italic, Underline, Strikethrough],
            [AlignLeft, AlignCenter, AlignRight, AlignJustify],
            [Indent, Outdent],
            [OrderedList, UnorderedList],
            [Link, Unlink],
            [Undo, Redo],
            ViewHtml,
          ]}
          contentStyle={{
            height: 300,
            fontFamily: 'sans-serif',
          }}
          value={formik.values.body}
          onBlur={() => formik.setTouched({ ...formik.touched, body: true })}
          onChange={(event: EditorChangeEvent) => {
            formik.setFieldValue('body', event.html)
          }}
          defaultEditMode="div"
          className={cn(styles.editor, {
            [styles.error]: formik.touched.body && formik.errors.body,
          })}
        />
        {formik.touched.body && formik.errors.body && (
          <div className={styles.bodyError}>{formik.errors.body}</div>
        )}
      </section>
      <section>
        <h2 className={styles.title}>{'Weitere Bilder auswählen'}</h2>
        <div
          {...multipleProps({
            className: cn(styles.dndContainer, styles.dndContainerMultiple, {
              [styles.active]: multipleDragActive,
            }),
          })}
        >
          {images && (
            <div className={styles.imageWrap}>
              {images.map((image, index) => (
                <img
                  key={`${image.preview}_${index}`}
                  src={image.preview}
                  alt="article"
                />
              ))}
            </div>
          )}
          <input {...multipleInputProps()} accept=".png,.jpg,.jpeg" />
          <button type="button" className={styles.upload}>
            {'+'}
            {/* {'Hochladen'}
        <Upload /> */}
          </button>
        </div>
      </section>
      {activeTab === ArticleTab.ARTICLE && (
        <>
          <section>
            <h2 className={styles.title}>{'Freigeben für'}</h2>
            <Checkbox
              id="selectAll"
              name="selectAll"
              theme="orange"
              onChange={formik.handleChange}
              onClick={setAllCategoryProjects}
              checked={
                articleFormikValues.selectAll.includes('on') ||
                compareCheckboxesLength
              }
            >
              {'Alle'}
            </Checkbox>
          </section>
          {groupsCheckboxes.map((group) => (
            <Accordion
              key={group.title}
              expanded={expanded.includes(group.title)}
              onChange={() =>
                expanded.includes(group.title)
                  ? removeExpanded(group.title)
                  : addExpanded(group.title)
              }
            >
              <AccordionSummary
                expandIcon={<Arrow />}
                className={styles.collapse}
              >
                <h3 className={styles.title}>{group.title}</h3>
              </AccordionSummary>
              <AccordionDetails className={styles.checkboxes}>
                {group.options.map((option) => (
                  <Checkbox
                    theme="orange"
                    key={`${group.title}-${option.label}`}
                    id={`${group.title}-${option.label}`}
                    name={group.name}
                    value={option.value}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    checked={setChecked(String(option.value))}
                  >
                    {option.label}
                  </Checkbox>
                ))}
                {formik.touched[group.name as keyof T] &&
                  formik.errors[group.name as keyof T] && (
                    <div className={styles.bodyError}>
                      {formik.errors[group.name as keyof T]}
                    </div>
                  )}
              </AccordionDetails>
            </Accordion>
          ))}
          <Accordion
            key={categoryCheckboxes.title}
            expanded={expanded.includes(categoryCheckboxes.title)}
            onChange={() =>
              expanded.includes(categoryCheckboxes.title)
                ? removeExpanded(categoryCheckboxes.title)
                : addExpanded(categoryCheckboxes.title)
            }
          >
            <AccordionSummary
              expandIcon={<Arrow />}
              className={styles.collapse}
            >
              <h3 className={styles.title}>{categoryCheckboxes.title}</h3>
            </AccordionSummary>
            <AccordionDetails
              className={cn(styles.checkboxes, styles.categoryItems)}
            >
              {categoryCheckboxes.items.map((item) => (
                <div key={item.label} className={styles.categoryCheckboxWrap}>
                  <Checkbox
                    theme="orange"
                    id={`${categoryCheckboxes.title}-${item.label}`}
                    name={categoryCheckboxes.name}
                    checked={
                      !!item.subitems.find((subitem) =>
                        ((formik.values as unknown) as ArticleFormValues).category.includes(
                          subitem.value.toString(),
                        ),
                      )
                    }
                    onChange={() =>
                      setExpandedCategory(
                        expandedCategory?.label === item.label ? null : item,
                      )
                    }
                  >
                    {item.label}
                  </Checkbox>
                  <Button
                    variant="custom"
                    onClick={() =>
                      setExpandedCategory(
                        expandedCategory?.label === item.label ? null : item,
                      )
                    }
                    className={cn(styles.arrow, {
                      [styles.rotated]: expandedCategory?.label === item.label,
                    })}
                  >
                    <Arrow />
                  </Button>
                </div>
              ))}
              {categoryCheckboxes.items.map((item) => (
                <Collapse
                  key={item.label}
                  in={expandedCategory?.label === item.label}
                >
                  <div
                    className={cn(styles.checkboxes, styles.categorySubitems)}
                  >
                    {item.subitems.map((subitem) => (
                      <Checkbox
                        key={subitem.label}
                        theme="orange"
                        id={`${categoryCheckboxes.title}-${item.label}-${subitem.label}`}
                        name={categoryCheckboxes.name}
                        value={subitem.value}
                        checked={setChecked(String(subitem.value))}
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                      >
                        {subitem.label}
                      </Checkbox>
                    ))}
                  </div>
                </Collapse>
              ))}
              {formik.touched[categoryCheckboxes.name as keyof T] &&
                formik.errors[categoryCheckboxes.name as keyof T] && (
                  <div className={styles.bodyError}>
                    {formik.errors[categoryCheckboxes.name as keyof T]}
                  </div>
                )}
            </AccordionDetails>
          </Accordion>
        </>
      )}
    </>
  )
}

export default FormView
