import { FormikProps } from 'formik'

import DatePicker from '../DatePicker'

interface DateInputProps<T> {
  name: string
  // name: keyof T & string
  formik?: FormikProps<T>
  values?: any
  shouldDisableDate?: (date: Date) => boolean
}

const DateRange = <T extends Record<string, unknown>>({
  name,
  formik,
  values,
  shouldDisableDate,
}: DateInputProps<T>) => (
  <div>
    <DatePicker
      name={`${name}.from`}
      placeholder="Von"
      formik={formik}
      value={values?.from || ''}
      shouldDisableDate={shouldDisableDate}
    />
    <DatePicker
      name={`${name}.to`}
      placeholder="Bis"
      formik={formik}
      value={values?.to || ''}
      shouldDisableDate={shouldDisableDate}
    />
  </div>
)

export default DateRange
