// import { ReactComponent as ArrowNext } from 'assets/icons/arrowNextOrange.svg'
// import Form from 'common/Form'
// import Button from 'components/Button'
import Button from 'components/Button'
import DatePicker from 'components/inputs/DatePicker'
import FieldArray from 'components/inputs/FieldArray'
import HtmlEditor from 'components/inputs/HtmlEditor'
import { FormikProps } from 'formik'

// import { FileWithPreview } from 'types'
import DocUploader from '../DocumentUploader'
import { Step4 as Props } from '../formData'
import styles from '../styles.module.scss'

interface FormProps {
  formik: FormikProps<Props>
  // onUploadDoc: (field: string, id: string) => void
}

const Step4 = ({ formik }: FormProps) => (
  <>
    <FieldArray
      isGroup
      name="appraisal_interviews"
      label="Personalgespräch"
      initialEmptyAllowed
      keepLastEntry
      formik={formik}
      renderField={(defaultValue, index) => (
        <div>
          <DatePicker
            name={`appraisal_interviews[${index}].date`}
            placeholder="Datum"
            formik={formik}
            value={defaultValue.date}
          />
          <HtmlEditor
            name={`appraisal_interviews[${index}].description`}
            label="Besprechungspunkt"
            formik={formik}
            defaultValue={defaultValue.description}
            onChange={(val) => {
              formik?.setFieldValue(
                `appraisal_interviews[${index}].description`,
                val,
              )
            }}
          />
          {formik.values.appraisal_interviews[index]?.appraisal_files ? (
            <div className={styles.fileName}>
              {formik.values.appraisal_interviews[index].appraisal_files?.map(
                (el) => (
                  <span key={el.name}>{el.name}</span>
                ),
              )}
              <Button
                variant="text"
                type="button"
                className={styles.removeButton}
                onClick={() => {
                  formik.setFieldValue(
                    `appraisal_interviews[${index}].appraisal_files`,
                    null,
                  )
                }}
              >
                &times;
              </Button>
            </div>
          ) : (
            <DocUploader
              onUpload={(fid, name) =>
                formik.setFieldValue(
                  `appraisal_interviews[${index}].appraisal_files`,
                  [{ fid, name }],
                )
              }
            />
          )}
        </div>
      )}
    />
  </>
)

export default Step4
