import Layout from 'common/Layout'

import styles from './styles.module.scss'

const WikiLive: React.FC = () => (
  <Layout>
    <div className={styles.wrapper}>
      <h1 className={styles.title}>{'S&P Leipzig Wiki'}</h1>
      <iframe
        src="https://intranetleipzig.leipzig.sup-sahlmann.com/dokuwiki/doku.php"
        title="Intranet Wiki"
        className={styles.iframe}
      />
    </div>
  </Layout>
)

export default WikiLive
