import {
  addSeconds,
  format as formatFns,
  formatDistanceToNow,
  fromUnixTime,
  isDate,
  isValid,
  Locale as FnsLocale,
  parse,
  toDate,
} from 'date-fns'
import { de, enUS } from 'date-fns/locale'
import { zonedTimeToUtc } from 'date-fns-tz'
import { DateUtils, LocaleUtils } from 'react-day-picker'

export type Locales = 'en' | 'de'

export type Months = [
  string,
  string,
  string,
  string,
  string,
  string,
  string,
  string,
  string,
  string,
  string,
  string,
]

export const DEFAULT_LOCALE = 'de'

export const getFnsLocale = (localeString: Locales | string): FnsLocale => {
  switch (localeString) {
    case 'de':
      return de
    case 'en':
      return enUS
    default:
      return de
  }
}

export const FORMAT = 'dd.MM.yyyy'
export const FULL_DATE_FORMAT = 'd. MMM yyy'

export const parseDate = (str: string, format = FORMAT, locale = 'de') => {
  const parsed = parse(str, format, new Date(), {
    locale: getFnsLocale(locale),
  })
  if (DateUtils.isDate(parsed)) {
    return parsed
  }
  return new Date()
}

export const formatDate = (
  date: number | Date,
  format = FORMAT,
  locale = 'de',
) => {
  const originalDate = toDate(date)

  if (isValid(originalDate)) {
    return formatFns(originalDate, format, { locale: getFnsLocale(locale) })
  }

  return ''
}

export const stringToFullDate = (date: string | Date) =>
  formatDate(new Date(date), FULL_DATE_FORMAT)

export const WEEKDAYS_LONG = {
  en: [
    'Sunday',
    'Monday',
    'Tuesday',
    'Wednesday',
    'Thursday',
    'Friday',
    'Saturday',
  ],
  de: [
    'Sonntag',
    'Montag',
    'Dienstag',
    'Mittwoch',
    'Donnerstag',
    'Freitag',
    'Samstag',
  ],
}

export const WEEKDAYS_SHORT = {
  en: ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'],
  de: ['Mo', 'Di', 'Mi', 'Do', 'Fr', 'Sa', 'So'],
}

export const MONTHS = {
  en: [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ] as Months,
  de: [
    'Januar',
    'Februar',
    'März',
    'April',
    'Mai',
    'Juni',
    'Juli',
    'August',
    'September',
    'Oktober',
    'November',
    'Dezember',
  ] as Months,
}

export const FIRST_DAY = {
  en: 0,
  de: 1,
}

export const formatDay = (d: Date, locale: Locales = 'en') =>
  `${WEEKDAYS_LONG[locale][d.getDay()]}, ${d.getDate()} ${
    MONTHS[locale][d.getMonth()]
  } ${d.getFullYear()}`

export const formatMonthTitle = (d: Date, locale: Locales = 'en') =>
  `${MONTHS[locale][d.getMonth()]}, ${d.getFullYear()}`

export const formatWeekdayShort = (i: number, locale: Locales) =>
  WEEKDAYS_SHORT[locale][i]

export const formatWeekdayLong = (i: number, locale: Locales) =>
  WEEKDAYS_SHORT[locale][i]

export const getFirstDayOfWeek = (locale: Locales) => FIRST_DAY[locale]

export const getMonths = (locale: Locales) => MONTHS[locale]

export const localeUtils: LocaleUtils = {
  formatDay,
  formatMonthTitle,
  formatWeekdayShort,
  formatWeekdayLong,
  getFirstDayOfWeek,
  getMonths,
  formatDate,
  parseDate,
}

export const validateDateInput = (
  value: string,
  originalValue: string | Date,
) => {
  if (typeof originalValue !== 'string' && isDate(originalValue)) {
    return formatDate(originalValue)
  }
  if (
    typeof originalValue === 'string' &&
    DateUtils.isDate(parse(originalValue, 'dd.MM.yyyy', new Date()))
  ) {
    return originalValue
  }
  return false
}

export const zonedDate = (
  date: string,
  zone = 'Europe/Berlin',
  format = FORMAT,
) => zonedTimeToUtc(parseDate(date, format), zone)

export const formatUnixDistanceToNow = (date: number) =>
  formatDistanceToNow(fromUnixTime(date), {
    locale: getFnsLocale(DEFAULT_LOCALE),
  })

export const getExpirationDateString = (expiresIn: number, date = new Date()) =>
  addSeconds(date, expiresIn).toISOString()

/**
 * @description Returns time span between dates in seconds
 */
export const getTimeSpan = (start: Date = new Date(), end = new Date()) =>
  (Number(end) - Number(start)) / 1000
