import FullscreenLoader from 'common/FullscreenLoader'
import { PopupTypes } from 'constants/holidaysPage'
import { UseToggle } from 'hooks/useToggle'
import { useEffect, useMemo, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getHolidayEmployeeView } from 'store/ives/holidays/actions'
import { ReduxState, TimeTableEmployeeData, TimeTableItemData } from 'types'

import ContentView from './view'

interface Props {
  globalFilter?: string
  applicationToggle: UseToggle
  popupType: PopupTypes
  setPopupType: React.Dispatch<React.SetStateAction<PopupTypes>>
  currentUser: TimeTableItemData | null
  setCurrentUser: React.Dispatch<React.SetStateAction<TimeTableItemData | null>>
  selectedGroup: string
  csvData?: any
  csvHeader?: any
}

const HolidayTable: React.FC<Props> = ({
  globalFilter,
  applicationToggle,
  popupType,
  setPopupType,
  currentUser,
  setCurrentUser,
  selectedGroup,
  csvData,
  csvHeader,
}) => {
  const { user } = useSelector((state: ReduxState) => state.user)
  const [loading, setLoading] = useState(false)
  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(getHolidayEmployeeView(user?.project_group.id))
  }, [])
  const { projectGroups, employees, nationalHolidays, isLoading } = useSelector(
    ({ ives }: ReduxState) => ives.holidays,
  )
  useEffect(() => {
    setLoading(true)
    setTimeout(() => {
      setLoading(false)
    }, 1200)
  }, [])
  const userGroups = useMemo(() => {
    console.log(
      'sadjgaskfhyldausbyvu;dakubfijewuofhiuewkgfylkdsu v',
      selectedGroup,
      projectGroups,
    )
    if (selectedGroup && selectedGroup !== 'Alle') {
      return projectGroups?.filter(
        (group) => group.project_group.id === selectedGroup,
      )
    }
    return projectGroups
  }, [selectedGroup])

  const contextMenuAvailability = useMemo(
    () =>
      userGroups?.flatMap((item) =>
        item.project_group.employees?.flatMap((employee) => [
          ...(employee.holidays?.length > 0
            ? employee.holidays?.flatMap((holiday) => ({
                holidayId: holiday.id,
                employeeId: employee.id,
                showContextMenu: true,
              }))
            : [] || []),
          ...(employee.maternity?.length > 0
            ? employee.maternity?.flatMap((holiday) => ({
                holidayId: holiday.id,
                employeeId: employee.id,
                showContextMenu: false,
              }))
            : [] || []),
          ...(employee.holidays?.length > 0
            ? employee.parental?.flatMap((holiday) => ({
                holidayId: holiday.id,
                employeeId: employee.id,
                showContextMenu: false,
              }))
            : [] || []),
        ]),
      ) || [],
    [projectGroups, selectedGroup, userGroups],
  )

  const loadedEmployes = useMemo(
    () =>
      selectedGroup && userGroups
        ? userGroups.flatMap((item) => item.project_group.employees)
        : employees,
    [selectedGroup],
  )

  const employeeData = useMemo<TimeTableEmployeeData[]>(
    () =>
      (employees &&
        loadedEmployes?.flatMap((employee) => ({
          id: `${employee.id}`,
          title: employee.name || '',
        }))) ||
      [],
    [employees, selectedGroup],
  )

  const employeeHolidayData = useMemo<TimeTableItemData[]>(
    () =>
      userGroups?.flatMap(
        (item) =>
          item.project_group.employees.flatMap((employee) =>
            employee.holidays?.length > 0
              ? employee.holidays?.map((holiday) => ({
                  id: String(holiday.id) || '',
                  group: String(employee.id) || '',
                  title: holiday.description || '',
                  start_time: String(holiday.from * 1000),
                  end_time: String(holiday.to * 1000),
                  className: holiday.state || '',
                  substitutes: holiday.substitutes,
                  state: holiday.state,
                }))
              : [],
          ) || [],
      ) || [],
    [selectedGroup],
  )

  const maternityData = useMemo<TimeTableItemData[]>(
    () =>
      userGroups?.flatMap(
        (item) =>
          item.project_group.employees.flatMap((employee) =>
            employee.maternity?.map((holiday) => ({
              id: String(holiday.id) || '',
              group: String(employee.id) || '',
              start_time: String(holiday.from * 1000),
              end_time: String(holiday.to * 1000),
              className: 'maternity',
            })),
          ) || [],
      ) || [],
    [projectGroups, selectedGroup, userGroups],
  )

  const parentalData = useMemo<TimeTableItemData[]>(
    () =>
      userGroups?.flatMap(
        (item) =>
          item.project_group.employees.flatMap((employee) =>
            employee.parental?.map((holiday) => ({
              id: String(holiday.id) || '',
              group: String(employee.id) || '',
              start_time: String(holiday.from * 1000),
              end_time: String(holiday.to * 1000),
              className: 'parental',
            })),
          ) || [],
      ) || [],
    [projectGroups],
  )

  const nationalHolidayData = useMemo<TimeTableItemData[]>(
    () =>
      nationalHolidays?.flatMap(
        (row) =>
          employees?.map((employee) => ({
            id:
              String(
                employee.id * row.id * row.from * row.to * Math.random(),
              ) || '',
            group: String(employee.id) || '',
            type: String(row.type) || '',
            title: String(row.title) || '',
            start_time: String(row.from * 1000),
            end_time: String(row.to * 1000),
            className: 'national-holiday',
          })) || [],
      ) || [],
    [employees, nationalHolidays],
  )

  const itemTableData = useMemo(
    () => [
      ...nationalHolidayData,
      ...employeeHolidayData,
      ...maternityData,
      ...parentalData,
    ],
    [userGroups],
  )

  /**  Search employee */
  const searchedEmployee = useMemo(
    () =>
      employeeData.filter((item) =>
        item.title.toLowerCase().includes(globalFilter?.toLowerCase() || ''),
      ),
    [employeeData, globalFilter, employees, userGroups],
  )

  return loading ? (
    <FullscreenLoader isLoading={loading || isLoading} />
  ) : (
    <>
      <ContentView
        timeTableGroups={searchedEmployee}
        timeTableItems={itemTableData}
        contextMenuAvailability={contextMenuAvailability}
        applicationToggle={applicationToggle}
        popupType={popupType}
        setPopupType={setPopupType}
        currentUser={currentUser}
        setCurrentUser={setCurrentUser}
        csvHeader={csvHeader}
        csvData={csvData}
      />
    </>
  )
}

export default HolidayTable
