import Backdrop from '@mui/material/Backdrop'
import { ReactComponent as ArrowNext } from 'assets/icons/arrowNextOrange.svg'
import { ReactComponent as BurgerMenu } from 'assets/icons/burgerMenu2.svg'
import { ReactComponent as Cancel } from 'assets/icons/cancelThin.svg'
import cn from 'classnames'
import Button from 'components/Button'
import useToggle from 'hooks/useToggle'
import { useEffect, useMemo } from 'react'
import { ContextMenuParams, TriggerEvent } from 'react-contexify'

import { ItemWithMenu } from '../rows'
import styles from './styles.module.scss'

interface RowItemProps {
  item: string | ItemWithMenu
  index?: number
  color: 'blue' | 'gray' | 'orange'
  searchValue: string
  showContextMenu(
    event: TriggerEvent,
    params?: Pick<ContextMenuParams, 'id' | 'props' | 'position'>,
  ): void
  onClick?(): void
}

const RowItem = ({
  item,
  index,
  color,
  searchValue,
  onClick,
  showContextMenu,
}: RowItemProps) => {
  const zoomed = useToggle()

  const onZoom = () => {
    onClick?.()
    zoomed.toggle()
  }

  const isSearchTitleMatched = useMemo(() => {
    if (typeof item === 'string' && item === searchValue) {
      return true
    }

    if (typeof item !== 'string' && item.title === searchValue) {
      return true
    }

    return false
  }, [searchValue])

  const isMenuItemMatched = useMemo(() => {
    if (
      typeof item !== 'string' &&
      item.menu?.find((menuItem) => menuItem.title === searchValue)
    ) {
      return true
    }

    return false
  }, [searchValue])

  useEffect(() => {
    isMenuItemMatched && onZoom()
  }, [isMenuItemMatched])

  return (
    <div
      style={{ width: typeof item !== 'string' ? item.width : undefined }}
      className={cn(
        styles.rowItem,
        styles[color],
        isSearchTitleMatched && styles.searched,
      )}
    >
      <a
        href={item.link as string}
        className={styles.clickableArea}
        rel="noreferrer"
        target="_blank"
        onContextMenu={(event) =>
          typeof item !== 'string' &&
          !item.menu &&
          showContextMenu(event, {
            props: {
              url: item.link,
            },
          })
        }
      >
        {typeof index === 'number' && (
          <div className={styles.number}>{index + 1}</div>
        )}
        {typeof item === 'string' ? item : item.title}
      </a>
      {typeof item !== 'string'
        ? item.menu && (
            <>
              <Button className={styles.menuIcon} onClick={onZoom}>
                <BurgerMenu />
              </Button>
              {zoomed.value && (
                <>
                  <div className={styles.menu}>
                    <Button onClick={onZoom} className={styles.cancel}>
                      <Cancel />
                    </Button>
                    <div className={styles.menuItemsList}>
                      {item.menu.map((menuItem) => (
                        <Button
                          key={menuItem.title}
                          className={cn(
                            styles.menuItem,
                            isMenuItemMatched &&
                              searchValue === menuItem.title &&
                              styles.searched,
                          )}
                        >
                          <a
                            href={menuItem.link}
                            className={styles.text}
                            rel="noopener noreferrer"
                            target="_blank"
                            onContextMenu={(event) =>
                              showContextMenu(event, {
                                props: {
                                  url: menuItem.link,
                                },
                              })
                            }
                          >
                            {menuItem.title}
                          </a>
                          <div className={styles.arrow}>
                            <ArrowNext />
                          </div>
                        </Button>
                      ))}
                    </div>
                  </div>
                  <Backdrop
                    open={zoomed.value}
                    onClick={onZoom}
                    sx={{ zIndex: 1 }}
                  />
                </>
              )}
            </>
          )
        : null}
    </div>
  )
}

export default RowItem
