import cn from 'classnames'
import Widget from 'components/Widget'
import { ColorSchema } from 'constants/companyStructureLayout'
import { ContextMenuProps } from 'containers/CompanyStructure/GridLayout'
import { MouseEvent } from 'react'
import { ProjectGroup } from 'types'

import Group from './ProjectGroup'
import styles from './styles.module.scss'

interface Props {
  active: ProjectGroup
  groups: ProjectGroup[]
  color: keyof typeof ColorSchema
  title: string
  withoutMenu?: boolean
  onContextMenu?: (
    event: MouseEvent<HTMLDivElement>,
    params: { props: ContextMenuProps },
  ) => void
  setActive(group: ProjectGroup): void
  openEmployeeInfo(id: number | null): void
}

const CompanyClusterWidget: React.FC<Props> = ({
  active,
  groups,
  color,
  title = 'Projektgruppen',
  withoutMenu,
  onContextMenu,
  setActive,
  openEmployeeInfo,
}) => (
  <Widget
    title={title}
    containerClassName={cn(styles.projectGroups, color)}
    withoutMenu={withoutMenu}
  >
    {groups.map((group: ProjectGroup) => (
      <Group
        key={title + group.name + group.tid}
        group={group}
        active={active}
        setActive={() =>
          active.tid === group.tid
            ? setActive({} as ProjectGroup)
            : setActive(group)
        }
        openEmployeeInfo={openEmployeeInfo}
        onContextMenu={(event) => onContextMenu?.(event, { props: { group } })}
      />
    ))}
  </Widget>
)

export default CompanyClusterWidget
