import Layout from 'common/Layout'
import Widgets from 'constants/widgets'
import useToggle from 'hooks/useToggle'
import useWindowSize from 'hooks/useWindowSize'
import { nanoid } from 'nanoid'
import { useEffect, useMemo, useState } from 'react'
import { useContextMenu } from 'react-contexify'
import { useDispatch } from 'react-redux'
import { addWidget } from 'store/user/actions'
import { Breakpoint, SelectOption } from 'types'

import { ROWS } from './rows'
import ContentView from './view'

export const MENU_ID = 'process-landscape-context-menu'

export interface ContextMenuProps {
  label: string
  url: string
}

export interface Offset {
  x: number | string
  y: number | string
}

const ProcessLandscape: React.FC = () => {
  const zoomToggle = useToggle()
  const [zoomOffset, setZoomOffset] = useState<Offset>({ x: 0, y: 0 })
  const [currentBreakpoint, setCurrentBreakpoint] = useState<Breakpoint>('lg')
  const [searchValue, setSearchValue] = useState<string>()
  const { width } = useWindowSize()

  const { show } = useContextMenu({ id: MENU_ID })

  const dispatch = useDispatch()

  const searchDropdownOptions = useMemo<SelectOption[]>(
    () =>
      ROWS.flat()
        .map((item) =>
          item.menu
            ? [...item.menu.map((el) => el.title), item.title]
            : [item.title],
        )
        .flat()
        .map((item) => ({ label: item, value: item })),
    [],
  )
  const matched = useMemo(
    () => searchDropdownOptions.find((item) => item.label === searchValue),
    [searchValue],
  )

  useEffect(() => {
    if (width) {
      switch (true) {
        case width > 1660:
          setCurrentBreakpoint('lg')
          break
        case width > 1100:
          setCurrentBreakpoint('md')
          break
        default:
          setCurrentBreakpoint('sm')
          break
      }
    }
  }, [width])

  /**
   * @returns true == zoomedIn, false == zoomOut
   */
  const zoomInOut = (offset: Offset) => () => {
    if (zoomToggle.value) {
      zoomToggle.off()
      setZoomOffset({ x: 0, y: 0 })
      return false
    }
    zoomToggle.on()
    setZoomOffset(offset)
    return true
  }

  return (
    <Layout
      searchValue={searchValue}
      onSearchChange={setSearchValue}
      searchDropdownOptions={searchDropdownOptions}
    >
      <ContentView
        zoomed={zoomToggle.value}
        zoomOffset={zoomOffset}
        currentBreakpoint={currentBreakpoint}
        rows={ROWS}
        searchValue={matched?.label || ''}
        zoom={zoomInOut}
        showContextMenu={show}
        onContextItemClick={({ props }) =>
          props &&
          dispatch(
            addWidget({
              title: 'Prozesslandkarte',
              data: { url: props.url },
              type: Widgets.processLink,
              layout: {
                i: nanoid(),
                x: 0,
                y: Infinity,
                w: 6,
                h: 4,
                minW: 4,
                minH: 4,
              },
            }),
          )
        }
      />
    </Layout>
  )
}

export default ProcessLandscape
