import { ProcessWidgetData } from 'constants/widgets'
import { getExpirationDateString } from 'services/date'
import { Error, User, UserMeta } from 'types/index'
import { Settings } from 'types/settings'

import ActionType, { Action } from './types'

export interface IState {
  isLoading: boolean
  error: Error | null
  user: User | null
  settings: Settings
  meta: UserMeta | null
}

const initialState: IState = {
  isLoading: false,
  error: null,
  user: null,
  settings: {
    mySuP: {
      widgets: [ProcessWidgetData],
    },
  },
  meta: null,
}

const reducer = (state = initialState, action: Action): IState => {
  switch (action.type) {
    case ActionType.LOG_OUT_BEGIN:
    case ActionType.LOG_IN_BEGIN:
    case ActionType.GET_USER_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.GET_USER_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
        user: action.payload.current_user,
      }
    }
    case ActionType.LOG_IN_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
        user: action.payload.current_user,
        meta: {
          accessToken: action.payload.access_token,
          refreshToken: action.payload.refresh_token,
          accessTokenExpirationDate: getExpirationDateString(
            action.payload.expires_in,
          ),
        },
      }
    }
    case ActionType.REFRESH_TOKEN: {
      return {
        ...state,
        meta: {
          accessToken: action.payload.access_token,
          refreshToken: action.payload.refresh_token,
          accessTokenExpirationDate: getExpirationDateString(
            action.payload.expires_in,
          ),
        },
      }
    }
    case ActionType.UPDATE_USER: {
      return {
        ...state,
        isLoading: false,
        error: '',
        user: {
          ...state.user,
          ...action.payload,
        } as User,
      }
    }
    case ActionType.GET_USER_SETTINGS: {
      return {
        ...state,
        isLoading: false,
        error: '',
        settings: {
          ...state.settings,
          ...action.payload,
        },
      }
    }
    case ActionType.SET_USER_SETTINGS: {
      return {
        ...state,
        settings: action.payload,
      }
    }
    case ActionType.LOG_OUT_SUCCESS: {
      return {
        ...initialState,
      }
    }
    case ActionType.LOG_OUT_ERROR:
    case ActionType.LOG_IN_ERROR:
    case ActionType.GET_USER_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    default:
      return state
  }
}

export default reducer
