/* eslint-disable jsx-a11y/anchor-is-valid */
import Tippy from '@tippyjs/react'
import standardIcon from 'assets/icons/chart.svg'
import cn from 'classnames'
import SubItem from 'components/SmallMenuSubItem'
import { Link } from 'react-router-dom'
import { MenuItem as IMenuItem } from 'types'

import styles from './styles.module.scss'

interface Props {
  item: IMenuItem
  onClick: () => void
  activeMenuItem?: string
  setActiveMenuItem: (val: string) => void
}

const MenuItem: React.FC<Props> = ({
  item: { title, icon, selected, below, link, attributes, external },
  activeMenuItem,
  setActiveMenuItem,
  onClick,
}) => {
  const linkContent = (
    <>
      <div>
        <img src={icon || standardIcon} alt="menu icon" />
      </div>
      <span>{title}</span>
    </>
  )

  const linkProps = {
    id: title,
    onClick,
    className: cn(styles.menuSectionWrapper, {
      [styles.selected]: selected,
      [styles.withDropdown]: selected && below,
      [styles.highlighted]: attributes?.class?.includes('highlight'),
    }),
  }

  return (
    <Tippy
      content={
        <div className={styles.dropdownContent}>
          {below?.map((subItem) => (
            <SubItem
              key={subItem.title}
              item={subItem}
              activeMenuItem={activeMenuItem}
              setActiveMenuItem={setActiveMenuItem}
            />
          ))}
        </div>
      }
      placement="right"
      arrow={false}
      interactive
      visible={!!(selected && below)}
    >
      {external ? (
        <a href={below ? '#' : link} {...linkProps}>
          {linkContent}
        </a>
      ) : (
        <Link to={below ? '#' : link} {...linkProps}>
          {linkContent}
        </Link>
      )}
    </Tippy>
  )
}

export default MenuItem
