import { ReactComponent as Chat } from 'assets/icons/chat.svg'
import Button from 'components/Button'
import { CellProps, Renderer, Row } from 'react-table'

import styles from './styles.module.scss'

interface CommentsCellProps {
  commentCount: string
}

const CommentsCell = <D extends Record<string, string> & CommentsCellProps>(
  onClick: (row: Row<D>) => void,
) => {
  const InnerCell: Renderer<CellProps<D>> = ({ row }) => (
    <Button
      className={styles.cell}
      onClick={(e) => {
        e.stopPropagation()
        onClick(row)
      }}
    >
      <Chat />
      {row.original.commentCount}
    </Button>
  )

  return InnerCell
}

export default CommentsCell
