// import { useCalendarState } from '@mui/x-date-pickers/internals'
import Form from 'common/Form'
import DatePicker from 'components/inputs/DatePicker'
import Select from 'components/inputs/Select'
import TextInput from 'components/inputs/TextInput'
import { useFormik } from 'formik'
import { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
// import { Option } from 'react-select/src/filters'
import api from 'services/api'
import notify from 'services/notify'
import { createHoliday } from 'store/ives/holidays/actions'
import {
  FreeEmployee,
  ReduxState,
  SelectOption,
  TimeTableItemData,
} from 'types'

import styles from './styles.module.scss'

interface Props {
  title?: string
  currentUser: TimeTableItemData | null
  inputTextLabel?: string
  onCancel: () => void
  onSubmit: () => void
}

const ChangeSubstitutionForm: React.FC<Props> = ({
  title,
  currentUser,
  inputTextLabel,
  onCancel,
  onSubmit,
}) => {
  const { employee } = useSelector(({ user }: ReduxState) => user.user) || {}
  const [substituteOptions, setSubstituteOptions] = useState<SelectOption[]>([])
  const [substitutes, setSubstitutes] = useState(currentUser?.substitutes)
  const dispatch = useDispatch()

  const initialValues = {
    fromDate: currentUser?.start_time,
    toDate: currentUser?.end_time,
    substitutes,
  }

  const formik = useFormik({
    initialValues,
    onSubmit: async (values) => {
      try {
        dispatch(
          createHoliday(currentUser?.group, {
            period: {
              from: values.fromDate,
              to: values.toDate,
            },
            substitutes:
              substitutes &&
              substitutes.flatMap((item: any) => ({
                period: {
                  from: item.period.from,
                  to: item.period.to,
                },
                employee: item.employee.toString(),
              })),
            state: 'requested',
            auto_approval: false,
          }),
        )
        onSubmit()
      } catch (error) {
        notify(error)
      }
    },
  })

  const setSubstitution = useCallback(
    (option: SelectOption<string> | null, index: number) => {
      const newSubstitutes = substitutes ? [...substitutes] : []
      if (newSubstitutes && option) {
        newSubstitutes[index].employee = Number(option.value)
        newSubstitutes[index].employee_name = option.label
      }
      setSubstitutes(newSubstitutes)
    },
    [substitutes],
  )

  /** Get free substitutions */
  useEffect(() => {
    const fetchEmployees = async () => {
      try {
        const response = await api.get<FreeEmployee[]>(
          `/employees/${currentUser?.group}/holidays/substitutes/${formik.values.fromDate}/${formik.values.toDate}?_format=json`,
        )

        setSubstituteOptions(
          response.data.map((employee) => ({
            label: employee.name || '',
            value: String(employee.id),
          })),
        )
      } catch (error) {
        notify(error)
      }
    }

    fetchEmployees()
  }, [employee, formik.values.fromDate, formik.values.toDate])

  return (
    <Form
      handleSubmit={formik.handleSubmit}
      title={title}
      header={<div />}
      onCancel={onCancel}
    >
      {inputTextLabel && (
        <TextInput name="vacation_remaining" label={inputTextLabel} disabled />
      )}
      {substitutes?.map((item, index) => (
        <div key={index}>
          <p>{`Vertretung: ${item.employee_name}`}</p>
          <div className={styles.datePickersWrap}>
            <DatePicker
              disabled
              name="fromDate"
              placeholder={String(item.period.from)}
            />
            <DatePicker
              disabled
              name="toDate"
              placeholder={String(item.period.to)}
            />
          </div>
          <Select
            name="substitutes"
            isClearable={false}
            options={substituteOptions}
            placeholder="Vertretung"
            value={{ label: item.employee_name, value: String(item.employee) }}
            label="Vertretung"
            isCreatable={false}
            onChange={(option) => setSubstitution(option, index)}
          />
        </div>
      ))}
    </Form>
  )
}

export default ChangeSubstitutionForm
