import Setup from 'boot/setup'
import Navigation from 'navigation'
import { ToastContainer } from 'react-toastify'

const App = () => (
  <Setup>
    <ToastContainer
      position="bottom-center"
      autoClose={5000}
      hideProgressBar
      newestOnTop={false}
      closeOnClick
      rtl={false}
      draggable
      pauseOnHover
    />
    <Navigation />
  </Setup>
)

export default App
