import { AxiosResponse } from 'axios'
import { EncodedCompareOperators } from 'constants/index'
import api from 'services/api'
import notify from 'services/notify'
import { action } from 'store/action'
import {
  AppThunkAsync,
  BackendPatchResponse,
  BackendPostResponse,
  BackEndSelectOptions,
  Comments,
  ConvertedFile,
  CreateOfferRequest,
  Error,
  Offer,
  Offers,
  Projects,
} from 'types/index'

import ActionType from './types'

export const getOfferComments = (
  offerId: string | number,
  commentId: string | number | null = null,
  onSuccess?: (comments: Comments) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_OFFER_COMMENTS_BEGIN))
  try {
    const comments: AxiosResponse<Comments> = await api.get(
      `processes/offers/${offerId}/comments/${commentId}?_format=json`,
    )
    dispatch(action(ActionType.GET_OFFER_COMMENTS_SUCCESS, comments.data))
    onSuccess?.(comments.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_OFFER_COMMENTS_ERROR, error))
    onError?.(error)
  }
}

