export const getOffers = (
  data: {
    page?: number
    query?: string
    bindingUntil?: number
    amount?: number
    amountCompareOperator?: EncodedCompareOperators
  },
  onSuccess?: (offers: Offers) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  const { page = 0, bindingUntil, amount, amountCompareOperator, query } = data
  const queryFilter = query ? `/${query}` : ''

  let filterQuery = ''
  if (amountCompareOperator) {
    filterQuery = filterQuery.concat(`&amount_op=${amountCompareOperator}`)
  }
  if (amount) {
    filterQuery = filterQuery.concat(`&amount%5Bvalue%5D=${amount}`)
  }
  if (bindingUntil) {
    filterQuery = filterQuery.concat(`&binding_until=${bindingUntil}`)
  }

  dispatch(action(ActionType.GET_OFFERS_BEGIN))
  try {
    const offers: AxiosResponse<Offers> = await api.get(
      `processes/offers${queryFilter}?_format=json&page=${page}${filterQuery}`,
    )
    dispatch(action(ActionType.GET_OFFERS_SUCCESS, offers.data))
    onSuccess?.(offers.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_OFFERS_ERROR, error))
    onError?.(error)
  }
}

export const createOffer = (
  offer: CreateOfferRequest & { comment: string },
  onSuccess?: (response: BackendPostResponse) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.CREATE_OFFER_BEGIN))
  try {
    const response: AxiosResponse<BackendPostResponse> = await api.post(
      `processes/offers?_format=json`,
      {
        ...offer,
        customer:
          typeof offer.customer !== 'string'
            ? offer.customer?.id
            : offer.customer,
      },
    )
    dispatch(action(ActionType.CREATE_OFFER_SUCCESS))
    onSuccess?.(response.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.CREATE_OFFER_ERROR, error))
    onError?.(error)
  }
}

export const updateOffer = (
  offer: Partial<Omit<Offer, 'comment_count'> & { comment: string }>,
  onSuccess?: (response: BackendPatchResponse) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.UPDATE_OFFER_BEGIN))
  try {
    const response = await api.patch(
      `processes/offers/${offer.nid}?_format=json`,
      {
        ...offer,
        customer:
          typeof offer.customer !== 'string'
            ? offer.customer?.id
            : offer.customer,
      },
    )
    dispatch(action(ActionType.UPDATE_OFFER_SUCCESS))
    onSuccess?.(response.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.UPDATE_OFFER_ERROR, error))
    onError?.(error)
  }
}

