export const convertFileToPDF = (
  fileName: string,
  onSuccess?: (file: ConvertedFile) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.CONVERT_FILE_BEGIN))
  try {
    const convertFile: AxiosResponse<ConvertedFile> = await api.post(
      '/external_files/convert?_format=json',
      {
        file: fileName,
        directory: '',
      },
    )
    dispatch(action(ActionType.CONVERT_FILE_SUCCESS, convertFile.data))
    onSuccess?.(convertFile.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.CONVERT_FILE_ERROR, error))
    onError?.(error)
  }
}
