import { Blog, BlogCategories, BlogItem, Error } from 'types/index'

import ActionType, { Action } from './types'

export interface IState {
  isLoading: boolean
  selectedItemLoading: boolean
  error: Error | null
  blog: Blog | null
  categories: BlogCategories | null
  selectedItem?: BlogItem
}

const initialState: IState = {
  isLoading: false,
  selectedItemLoading: false,
  error: null,
  blog: null,
  categories: null,
}

const reducer = (state = initialState, action: Action): IState => {
  switch (action.type) {
    case ActionType.GET_CATEGORIES_BEGIN:
    case ActionType.GET_BLOG_NEXT_PAGE_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.GET_BLOG_FIRST_PAGE_BEGIN: {
      return {
        ...state,
        isLoading: true,
        blog: null,
      }
    }
    case ActionType.GET_FULL_ITEM_BEGIN: {
      return {
        ...state,
        selectedItemLoading: true,
      }
    }
    case ActionType.GET_CATEGORIES_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: null,
        categories: action.payload,
      }
    }
    case ActionType.GET_BLOG_FIRST_PAGE_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: null,
        blog: action.payload,
      }
    }
    case ActionType.GET_BLOG_NEXT_PAGE_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: null,
        blog: {
          pager: action.payload.pager,
          rows: [...(state.blog?.rows || []), ...action.payload.rows],
        },
      }
    }
    case ActionType.GET_FULL_ITEM_SUCCESS: {
      return {
        ...state,
        selectedItemLoading: false,
        error: null,
        selectedItem: action.payload,
      }
    }
    case ActionType.CREATE_ARTICLE_SUCCESS:
    case ActionType.UPDATE_ARTICLE_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: null,
        blog: state.blog
          ? {
              pager: state.blog.pager,
              rows: [action.payload, ...state.blog.rows],
            }
          : null,
      }
    }
    case ActionType.GET_FULL_ITEM_ERROR:
    case ActionType.GET_CATEGORIES_ERROR:
    case ActionType.GET_BLOG_ERROR:
    case ActionType.CREATE_ARTICLE_ERROR:
    case ActionType.UPDATE_ARTICLE_ERROR: {
      return {
        ...state,
        isLoading: false,
        selectedItemLoading: false,
        error: action.payload,
      }
    }
    default:
      return state
  }
}

export default reducer
