import { ReactComponent as ArrowNext } from 'assets/icons/arrowNextOrange.svg'
import Form from 'common/Form'
import Button from 'components/Button'
import DatePicker from 'components/inputs/DatePicker'
import Select from 'components/inputs/Select'
import TextInput from 'components/inputs/TextInput'
import { FormikProps } from 'formik'
import { OptionsType } from 'react-select'
import { SelectOption } from 'types'

import { FormValues } from '.'
import styles from './styles.module.scss'

interface FormProps {
  formik: FormikProps<FormValues>
  responsibleOptions: OptionsType<SelectOption>
  onUserSelectClick: () => void
  onCancel: () => void
}

const FormView = ({
  formik,
  responsibleOptions,
  onCancel,
  onUserSelectClick,
}: FormProps) => (
  <Form
    handleSubmit={formik.handleSubmit}
    title="Akquise Auftrag erstellen"
    onCancel={onCancel}
  >
    <TextInput name="title" label="Bezeichnung" formik={formik} />
    <DatePicker name="created" formik={formik} placeholder="Erstellungsdatum" />
    <Select
      name="responsible"
      placeholder="Verantwortliche"
      isMulti
      options={responsibleOptions}
      formik={formik}
    />
    <div className={styles.userInputContainer}>
      <TextInput
        name="customer"
        label="Kunde"
        formik={formik}
        autoComplete="off"
      />
      <Button className={styles.userInputArrow} onClick={onUserSelectClick}>
        <ArrowNext />
      </Button>
    </div>
    <TextInput
      name="customerResponsible"
      label="Verantwortliche beim Kunde"
      formik={formik}
    />
    <TextInput
      name="comment"
      label="Kommentar erstellen"
      multiline
      rows={15}
      maxRows={25}
      formik={formik}
    />
  </Form>
)

export default FormView
