import { Button } from '@mui/material'
import cn from 'classnames'
import React, { useCallback } from 'react'
import { Message, RejectProps } from 'types'

import { messagesTypes } from '..'
import CancelForm from './cancelForm'
import styles from './styles.module.scss'

interface Props {
  userData: Message | null
  showReasonField?: boolean
  onAccept?: (userData: Message | null) => void
  onCancel?: () => void
  onLater?: () => void
  onReject?: ({ values, userData }: RejectProps) => void
}

const EmployeePopup: React.FC<Props> = ({
  userData,
  showReasonField = false,
  onAccept = () => null,
  onLater = () => null,
  onCancel = () => null,
  onReject,
}) => {
  const buttons = useCallback(() => {
    if (userData) {
      switch (userData.data.type) {
        case messagesTypes.mdConfirm:
        case messagesTypes.pglConfirm:
        case messagesTypes.subsConfirm:
          return (
            <>
              <Button
                className={styles.buttonLater}
                variant="outlined"
                type="button"
                onClick={onLater}
              >
                {'Später erinnern'}
              </Button>
              <Button
                className={styles.buttonLater}
                variant="outlined"
                type="button"
                onClick={onCancel}
              >
                {'Ablehnen'}
              </Button>
              <Button
                onClick={() => {
                  onAccept(userData)
                }}
                className={styles.buttonAccept}
                variant="contained"
                type="button"
              >
                {'Annehmen'}
              </Button>
            </>
          )
        default:
          return (
            <Button
              onClick={onLater}
              className={cn(styles.buttonAccept, styles.buttonRight)}
              variant="contained"
              type="button"
            >
              {'Ok'}
            </Button>
          )
      }
    }

    return null
  }, [userData])

  const showAnswer = useCallback((answer: boolean | null) => {
    switch (answer) {
      case true:
        return 'Bestätigt'
      case false:
        return 'Nicht angekommen'
      case null:
        return 'Nicht Bestätigt'
      default:
        return null
    }
  }, [])

  const acceptedPersons = useCallback(() => {
    if (userData) {
      const subsBlock = (
        <div className={styles.rangeWrap}>
          <span className={styles.greyText}>{'Urlaubsvertretung:'}</span>
          {userData.data.substitutes.map((item, index) => (
            <span
              key={`${item.employee_name}_${index}`}
              className={styles.numberText}
            >
              {item.employee_name}
              {' : '}
              {showAnswer(item.subs_answer)}
            </span>
          ))}
        </div>
      )

      const pglBlock = (
        <div className={styles.rangeWrap}>
          <span className={styles.greyText}>{'Genehmigt von:'}</span>
          <span className={styles.numberText}>{userData.data.pglName}</span>
        </div>
      )
      const mdBlock = (
        <div className={styles.rangeWrap}>
          <span className={styles.greyText}>{'Genehmigt HGF:'}</span>
          <span className={styles.numberText}>{userData.data.mdName}</span>
        </div>
      )

      switch (userData.data.type) {
        // If substitution accept show him
        case messagesTypes.pglConfirm:
        case messagesTypes.subsConfirmed:
          return subsBlock

        // If project leader accept show him and substitution
        case messagesTypes.mdConfirm:
        case messagesTypes.pglConfirmed:
          return (
            <>
              {subsBlock}
              {pglBlock}
            </>
          )

        // If MD accept show him and project leader and substitution
        case messagesTypes.mdConfirmed:
          return (
            <>
              {subsBlock}
              {pglBlock}
              {mdBlock}
            </>
          )
        default:
          return null
      }
    }
    return null
  }, [userData])

  if (userData) {
    const { data } = userData

    /** show reject message */
    // const subsReject = Number(data.subsAnswer) === 0 && data.subsReason
    // const pglReject = Number(data.pglAnswer) === 0 && data.pglReason
    // const mdReject = Number(data.mdAnswer) === 0 && data.mdReason

    return (
      <div className={styles.popupBackground}>
        <div className={styles.popupWrap}>
          {!showReasonField ? (
            <div className={styles.popup}>
              {data.holidayOwner && (
                <>
                  <h5>{'Vertretungsanfrage'}</h5>
                  <div className={styles.workerInfo}>
                    <span className={styles.workerTitle}>{'Mitarbeiter:'}</span>
                    <img src={data.holidayOwner.picture} alt="Worker" />
                    <p className={styles.womenName}>{data.holidayOwner.name}</p>
                  </div>
                </>
              )}

              <div className={styles.rangeWrap}>
                <span className={styles.greyText}>{'Von:'}</span>
                <span className={styles.numberText}>{data.period.from}</span>
              </div>

              <div className={styles.rangeWrap}>
                <span className={styles.greyText}>{'Bis:'}</span>
                <span className={styles.numberText}>{data.period.to}</span>
              </div>
              {/* Show accept persons */}
              {acceptedPersons()}
              {/* If cancel vacation show reason */}
              <div className={styles.rangeWrap}>
                <span className={styles.numberText}>
                  {data.subsReason || data.pglReason || data.mdReason}
                </span>
              </div>
              <div className={styles.buttonsWrap}>{buttons()}</div>
            </div>
          ) : (
            <CancelForm
              userData={userData}
              onCancel={onLater}
              onReject={onReject}
            />
          )}
        </div>
      </div>
    )
  }
  return null
}

export default EmployeePopup
