import { Blog, BlogCategories, BlogItem, Error } from 'types/index'

enum ActionType {
  GET_BLOG_FIRST_PAGE_BEGIN = '[blog] GET_BLOG_FIRST_PAGE_BEGIN',
  GET_BLOG_NEXT_PAGE_BEGIN = '[blog] GET_BLOG_NEXT_PAGE_BEGIN',
  GET_BLOG_FIRST_PAGE_SUCCESS = '[blog] GET_BLOG_FIRST_PAGE_SUCCESS',
  GET_BLOG_NEXT_PAGE_SUCCESS = '[blog] GET_BLOG_NEXT_PAGE_SUCCESS',
  GET_BLOG_ERROR = '[blog] GET_BLOG_ERROR',
  GET_CATEGORIES_BEGIN = '[blog] GET_CATEGORIES_BEGIN',
  GET_CATEGORIES_SUCCESS = '[blog] GET_CATEGORIES_SUCCESS',
  GET_CATEGORIES_ERROR = '[blog] GET_CATEGORIES_ERROR',
  GET_FULL_ITEM_BEGIN = '[blog] GET_FULL_ITEM_BEGIN',
  GET_FULL_ITEM_SUCCESS = '[blog] GET_FULL_ITEM_SUCCESS',
  GET_FULL_ITEM_ERROR = '[blog] GET_FULL_ITEM_ERROR',
  CREATE_ARTICLE_BEGIN = '[blog] CREATE_ARTICLE_BEGIN',
  CREATE_ARTICLE_SUCCESS = '[blog] CREATE_ARTICLE_SUCCESS',
  CREATE_ARTICLE_ERROR = '[blog] CREATE_ARTICLE_ERROR',
  UPDATE_ARTICLE_SUCCESS = '[blog] UPDATE_ARTICLE_SUCCESS',
  UPDATE_ARTICLE_ERROR = '[blog] UPDATE_ARTICLE_ERROR',
  BLOG_RECORD_DELETED = '[blog] BLOG_RECORD_DELETED',
}

export interface GetFullItemBeginAction {
  type: ActionType.GET_FULL_ITEM_BEGIN
}

export interface GetFullItemSuccessAction {
  type: ActionType.GET_FULL_ITEM_SUCCESS
  payload: BlogItem | undefined
}

export interface GetFullItemErrorAction {
  type: ActionType.GET_FULL_ITEM_ERROR
  payload: Error
}

export interface CreateArticleBeginAction {
  type: ActionType.CREATE_ARTICLE_BEGIN
}

export interface CreateArticleSuccessAction {
  type: ActionType.CREATE_ARTICLE_SUCCESS
  payload: BlogItem
}

export interface CreateArticleErrorAction {
  type: ActionType.CREATE_ARTICLE_ERROR
  payload: Error
}

export interface UpdateArticleSuccessAction {
  type: ActionType.UPDATE_ARTICLE_SUCCESS
  payload: BlogItem
}

export interface UpdateArticleErrorAction {
  type: ActionType.UPDATE_ARTICLE_ERROR
  payload: Error
}

export interface GetBlogFirstPageBeginAction {
  type: ActionType.GET_BLOG_FIRST_PAGE_BEGIN
}

export interface GetBlogNextPageBeginAction {
  type: ActionType.GET_BLOG_NEXT_PAGE_BEGIN
}

export interface GetBlogFirstPageSuccessAction {
  type: ActionType.GET_BLOG_FIRST_PAGE_SUCCESS
  payload: Blog
}

export interface GetBlogNextPageSuccessAction {
  type: ActionType.GET_BLOG_NEXT_PAGE_SUCCESS
  payload: Blog
}

export interface GetBlogErrorAction {
  type: ActionType.GET_BLOG_ERROR
  payload: Error
}

export interface GetCategoriesBeginAction {
  type: ActionType.GET_CATEGORIES_BEGIN
}

export interface GetCategoriesSuccessAction {
  type: ActionType.GET_CATEGORIES_SUCCESS
  payload: BlogCategories
}

export interface GetCategoriesErrorAction {
  type: ActionType.GET_CATEGORIES_ERROR
  payload: Error
}

export interface BlogRecordDeleteAction {
  type: ActionType.BLOG_RECORD_DELETED
  payload: number
}

export type Action =
  | GetBlogFirstPageBeginAction
  | GetBlogNextPageBeginAction
  | GetBlogFirstPageSuccessAction
  | GetBlogNextPageSuccessAction
  | GetBlogErrorAction
  | GetCategoriesBeginAction
  | GetCategoriesSuccessAction
  | GetCategoriesErrorAction
  | GetFullItemBeginAction
  | GetFullItemSuccessAction
  | GetFullItemErrorAction
  | CreateArticleBeginAction
  | CreateArticleSuccessAction
  | CreateArticleErrorAction
  | UpdateArticleSuccessAction
  | UpdateArticleErrorAction
  | BlogRecordDeleteAction

export default ActionType
