import { CompanyCluster } from 'constants/companyStructure'
import {
  EmployeeGroupDetails,
  Error,
  ProjectGroup,
  ProjectGroupEmployee,
} from 'types/index'

import ActionType, { Action } from './types'

export interface State {
  isLoading: boolean
  error: Error | null
  data: Record<CompanyCluster, ProjectGroup[]>
  employee: ProjectGroupEmployee | null
  groupDetails: EmployeeGroupDetails | null
}

const initialState: State = {
  isLoading: false,
  error: null,
  data: {
    verwaltung: [],
    arbeitsgruppen: [],
    projektgruppen: [],
    kompetenzbereiche: [],
    standorte: [],
    kompetenzzentrum: [],
  },
  employee: null,
  groupDetails: null,
}

const reducer = (state = initialState, action: Action): State => {
  switch (action.type) {
    case ActionType.GET_COMPANY_STRUCTURE_BEGIN:
    case ActionType.GET_EMPLOYEE_BEGIN:
    case ActionType.GET_DETAILS_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.GET_COMPANY_STRUCTURE_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        data: {
          ...state.data,
          [action.payload.cluster]: action.payload.data,
        },
      }
    }
    case ActionType.GET_EMPLOYEE_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        employee: action.payload,
      }
    }
    case ActionType.GET_DETAILS_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        groupDetails: action.payload,
      }
    }
    case ActionType.DETAILS_PAGE_ADDED: {
      if (!state.groupDetails) return state

      return {
        ...state,
        groupDetails: {
          ...state.groupDetails,
          detail_pages: [...state.groupDetails.detail_pages, action.payload],
        },
      }
    }
    case ActionType.DETAILS_PAGE_DELETED: {
      const pageIndex = state.groupDetails?.detail_pages.findIndex(
        (page) => page.id === action.payload,
      )

      if (!state.groupDetails || pageIndex === undefined) return state

      return {
        ...state,
        groupDetails: {
          ...state.groupDetails,
          detail_pages: [
            ...state.groupDetails?.detail_pages.slice(0, pageIndex),
            ...state.groupDetails?.detail_pages.slice(pageIndex + 1),
          ],
        },
      }
    }
    case ActionType.DETAILS_PAGES_REORDERED: {
      const orderedPages = action.payload
        .map((pageId) =>
          state.groupDetails?.detail_pages.find((page) => page.id === pageId),
        )
        .filter(Boolean)

      if (!state.groupDetails) return state

      return {
        ...state,
        groupDetails: {
          ...state.groupDetails,
          detail_pages: orderedPages,
        },
      }
    }
    case ActionType.GET_COMPANY_STRUCTURE_ERROR:
    case ActionType.GET_EMPLOYEE_ERROR:
    case ActionType.GET_DETAILS_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    default:
      return state
  }
}

export default reducer
