import { Accordion, AccordionDetails, AccordionSummary } from '@mui/material'
import { ReactComponent as Arrow } from 'assets/icons/arrowDown.svg'
import { ReactComponent as Remove } from 'assets/icons/trashbin.svg'
import Button from 'components/Button'
import DateRange from 'components/inputs/DateRange'
import Select from 'components/inputs/Select'
import { Step3 as Props } from 'containers/Ives/Employees/EmployeeForm/formData'
import styles from 'containers/Ives/Employees/EmployeeForm/styles.module.scss'
import { isValid, parse } from 'date-fns'
import { FormikProps } from 'formik'
import React, { useCallback, useEffect, useState } from 'react'
import api from 'services/api'
import { formatDate } from 'services/date'
import notify from 'services/notify'
import { DateRange as DateRangeType, FreeEmployee, SelectOption } from 'types'

interface FormProps {
  formik: FormikProps<Props>
  employeeId: string | number
  index: number
  indexEmployee: number
  period: DateRangeType
  removeSubstitution: () => void
  addSubstitution: () => void
}
const EmployeeSickFormSubstitution = ({
  formik,
  employeeId,
  index,
  indexEmployee,
  period,
  removeSubstitution,
  addSubstitution,
}: FormProps) => {
  const [substituteOptions, setSubstituteOptions] = useState<SelectOption[]>([])
  const setCorrectSubstitutes = async (fromDate: string, toDate: string) => {
    const response = await api.get<FreeEmployee[]>(
      `/employees/${employeeId}/holidays/substitutes/${fromDate}/${toDate}?_format=json`,
    )
    setSubstituteOptions(
      response.data.map((employee) => ({
        label: employee.name,
        value: String(employee.id),
      })),
    )
  }
  const { from, to } = period
  useEffect(() => {
    const fetchEmployees = () => {
      const timeoutId = setTimeout(async () => {
        const fromParsed = parse(from, 'yyyy-MM-dd', new Date())
        const toParsed = parse(to, 'yyyy-MM-dd', new Date())
        if (isValid(fromParsed) && isValid(toParsed)) {
          await setCorrectSubstitutes(from, to)
        }
      }, 200)
      return () => clearTimeout(timeoutId)
    }
    try {
      fetchEmployees()
    } catch (error) {
      notify(error)
    }
  }, [from, to, period])
  const shouldDisableDate = useCallback(
    (date: Date) => {
      const correctDate = formatDate(date, 'yyyy-MM-dd')
      return correctDate < period.from || correctDate > period.to
    },
    [formik],
  )
  return (
    <div>
      <div>
        <Accordion className={styles.accordionWrap}>
          <AccordionSummary expandIcon={<Arrow />} className={styles.dropdown}>
            <h3 className={styles.fieldTitle}>
              {'Vertretung '}
              {index + 1}
              {' auswählen'}
            </h3>
          </AccordionSummary>
          <AccordionDetails>
            <div className={styles.fieldWrap}>
              <Select
                name={`employee_sick[${indexEmployee}].substitutes[${index}].employee`}
                key={`employee_sick-${indexEmployee}.substitutes-${index}.employee`}
                options={substituteOptions}
                label="Vertretung auswählen"
                formik={formik}
                isCreatable={false}
                value={
                  formik.values.employee_sick[indexEmployee].substitutes[index]
                    .employee
                }
              />
              <DateRange
                name={`employee_sick[${indexEmployee}].substitutes[${index}].period`}
                key={`employee_sick-${indexEmployee}.substitutes-${index}.period`}
                formik={formik}
                values={
                  formik.values.employee_sick[indexEmployee].substitutes[index]
                    .period
                }
                shouldDisableDate={shouldDisableDate}
              />
              <Button
                variant="text"
                type="button"
                className={styles.removeButton}
                onClick={removeSubstitution}
              >
                <Remove />
              </Button>
            </div>
          </AccordionDetails>
        </Accordion>
        <Button
          variant="text"
          type="button"
          className={styles.button}
          onClick={addSubstitution}
        >
          {'+ Hinzufügen'}
        </Button>
      </div>
    </div>
  )
}
export default EmployeeSickFormSubstitution
