import { Accordion, AccordionSummary } from '@mui/material'
import { ReactComponent as Arrow } from 'assets/icons/arrowDown.svg'
import cn from 'classnames'
import React, { useEffect, useState } from 'react'

import styles from '../../containers/Ives/Employees/EmployeeDetails/styles.module.scss'

export interface AccordionComponentProps {
  expanded?: boolean
  title: string
  children?: NonNullable<React.ReactNode>
  currentTitleStyle: React.CSSProperties | string
}

const AccordionComponent = ({
  expanded,
  title,
  currentTitleStyle,
  children,
}: AccordionComponentProps) => {
  const [expandedDropdown, setExpandedDropdown] = useState(expanded)
  useEffect(() => {
    setExpandedDropdown(expanded)
  }, [expanded])
  const handleChange = () => {
    setExpandedDropdown(!expandedDropdown)
  }
  return (
    <>
      <Accordion
        expanded={expandedDropdown}
        className={cn(styles.accordionWrap, styles.base)}
      >
        <AccordionSummary
          expandIcon={<Arrow />}
          className={styles.dropdown}
          onClick={() => handleChange()}
        >
          <h3 className={currentTitleStyle}>{title}</h3>
        </AccordionSummary>
        {children}
      </Accordion>
    </>
  )
}
export default AccordionComponent
