import { AxiosResponse } from 'axios'
import { CompanyCluster, companyClustersMap } from 'constants/companyStructure'
import api from 'services/api'
import notify from 'services/notify'
import { action } from 'store/action'
import {
  AppThunkAsync,
  EditDetailPage,
  EmployeeGroupDetails,
  Error,
  ProjectGroup,
  ProjectGroupEmployee,
} from 'types/index'

import ActionType from './types'

export const getCompanyStructure = (
  cluster: CompanyCluster,
  onSuccess?: (projectGroups: ProjectGroup[]) => void,
  onError?: (error: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_COMPANY_STRUCTURE_BEGIN))

  try {
    const { data } = await api.get(
      `widgets/cs/${companyClustersMap[cluster]}?_format=json`,
    )
    dispatch(
      action(ActionType.GET_COMPANY_STRUCTURE_SUCCESS, { cluster, data }),
    )
    onSuccess?.(data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_COMPANY_STRUCTURE_ERROR, error))
    onError?.(error)
  }
}

export const getEmployee = (
  id: number,
  onSuccess?: (employee: ProjectGroupEmployee) => void,
  onError?: (error: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_EMPLOYEE_BEGIN))

  try {
    const { data } = await api.get(`/employees/cs/${id}?_format=json`)
    dispatch(action(ActionType.GET_EMPLOYEE_SUCCESS, data[0]))
    onSuccess?.(data[0])
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_EMPLOYEE_ERROR, error))
    onError?.(error)
  }
}

export const getDetails = (id: number): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_DETAILS_BEGIN))
  try {
    const { data }: AxiosResponse<EmployeeGroupDetails> = await api.get(
      `/pages/employee/group/${id}?_format=json`,
    )
    dispatch(action(ActionType.GET_DETAILS_SUCCESS, data))
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_DETAILS_ERROR, error))
  }
}

export const addDetailsPage = (
  id: number,
  page: EditDetailPage,
): AppThunkAsync => async (dispatch) => {
  try {
    const { data } = await api.post(`/detail_page/${id}?_format=json`, {
      title: page.title,
      menu_icon: page.icon.id && [page.icon.id],
    })
    dispatch(
      action(ActionType.DETAILS_PAGE_ADDED, {
        id: data.id,
        title: page.title,
        menu_icon: page.icon.preview,
        content_link: null,
        edit_link: null,
        email: null,
        body: null,
      }),
    )
  } catch (error) {
    notify(error)
  }
}

export const deleteDetailsPage = (pageId: number): AppThunkAsync => async (
  dispatch,
) => {
  try {
    await api.delete(`/pages/detail/${pageId}?_format=json`)
    dispatch(action(ActionType.DETAILS_PAGE_DELETED, pageId))
  } catch (error) {
    notify(error)
  }
}

export const reorderDetailsPage = (
  groupId: number,
  pageIds: number[],
): AppThunkAsync => async (dispatch, getState) => {
  const oldPageIds =
    getState().companyStructure.groupDetails?.detail_pages.map(
      (page) => page.id,
    ) || []

  api
    .patch(`/pages/employee/group/${groupId}?_format=json`, {
      detail_pages: pageIds,
    })
    .catch((error) => {
      notify(error)
      dispatch(action(ActionType.DETAILS_PAGES_REORDERED, oldPageIds))
    })

  dispatch(action(ActionType.DETAILS_PAGES_REORDERED, pageIds))
}
