import AccordionComponent from 'common/Accordion'
import Button from 'components/Button'
import Checkbox from 'components/inputs/Checkbox'
import { FormSchema } from 'containers/Ives/Employees/EmployeeDetails'
import { useFormik } from 'formik'

import styles from './styles.module.scss'

const PrintFilterModal = () => {
  const printTemplate = useFormik({
    initialValues: {
      further_data: false,
      children: false,
      position_in_func: false,
      position_in_company: false,
      allowences: false,
      financial_details: false,
      notes_main: false,
      notes_contract: false,
      notes_absence: false,
    },
    validationSchema: FormSchema,
    onSubmit: async (values) => {
      console.log(values)
    },
  })
  return (
    <div className={styles.wrapper}>
      <form onSubmit={printTemplate.handleSubmit}>
        <AccordionComponent title="Stammdaten" currentTitleStyle={styles.base}>
          <Checkbox
            checked={!!printTemplate.values.further_data}
            theme="default"
            variant="round"
            {...printTemplate.getFieldProps('further_data')}
          >
            {'Weitere Stammadaten'}
          </Checkbox>{' '}
          <Checkbox
            checked={!!printTemplate.values.children}
            theme="default"
            variant="round"
            {...printTemplate.getFieldProps('children')}
          >
            {'Kinder'}
          </Checkbox>{' '}
          <Checkbox
            checked={!!printTemplate.values.position_in_func}
            theme="default"
            variant="round"
            {...printTemplate.getFieldProps('position_in_func')}
          >
            {'Verlauf Position PG'}
          </Checkbox>{' '}
          <Checkbox
            checked={!!printTemplate.values.position_in_company}
            theme="default"
            variant="round"
            {...printTemplate.getFieldProps('position_in_company')}
          >
            {'Verlauf Position AG/KB'}
          </Checkbox>{' '}
          <Checkbox
            checked={!!printTemplate.values.notes_main}
            theme="default"
            variant="round"
            {...printTemplate.getFieldProps('notes_main')}
          >
            {'Letzes Gespäch'}
          </Checkbox>
        </AccordionComponent>
        {/* <AccordionComponent
          title="Gehaltsdaten"
          currentTitleStyle={styles.base}
        >
          <Checkbox
            checked={!!printTemplate.values.allowences}
            theme="default"
            variant="round"
            {...printTemplate.getFieldProps('allowences')}
          >
            {'Detail Bonuszahlungen'}
          </Checkbox>{' '}
          <Checkbox
            checked={!!printTemplate.values.financial_details}
            theme="default"
            variant="round"
            {...printTemplate.getFieldProps('financial_details')}
          >
            {'Gehalts Entwicklungen'}
          </Checkbox>{' '}
          <Checkbox
            checked={!!printTemplate.values.notes_contract}
            theme="default"
            variant="round"
            {...printTemplate.getFieldProps('notes_contract')}
          >
            {'Letzte Vertragsnotizen'}
          </Checkbox>
        </AccordionComponent> */}
        <AccordionComponent
          title="Zeitmanagement"
          currentTitleStyle={styles.base}
        />
        <Button
          variant="fulfilled"
          color="orange"
          // className={styles.notesButton}
          type="submit"
        >
          {'Speichern'}
        </Button>
      </form>
    </div>
  )
}

export default PrintFilterModal
