import './styles.scss'

import { Area, AreaChart, CartesianGrid, Tooltip, XAxis, YAxis } from 'recharts'

import styles from './styles.module.scss'

const data = [
  {
    name: '0',
    min: 4000,
    max: 2400,
  },
  {
    name: '10',
    min: 3000,
    max: 1398,
  },
  {
    name: '20',
    min: 2000,
    max: 9800,
  },
  {
    name: '30',
    min: 2780,
    max: 3908,
  },
  {
    name: '40',
    min: 1890,
    max: 4800,
  },
  {
    name: '50',
    min: 2390,
    max: 3800,
  },
  {
    name: '60',
    min: 3490,
    max: 4300,
  },
]

const Graph = () => (
  <div>
    <div className={styles.header}>
      <div>
        <div className={styles.title}>{'Zeitraum'}</div>
        <div className={styles.period}>{'21.02.2020 - 25.06.2021'}</div>
      </div>
      <div className={styles.legend}>
        <div className={styles.legendTextWrapper}>
          <div style={{ background: '#203A88' }} className={styles.colorBar} />
          <div>{'Min'}</div>
        </div>
        <div className={styles.legendTextWrapper}>
          <div style={{ background: '#DFE0EB' }} className={styles.colorBar} />
          <div>{'Max'}</div>
        </div>
      </div>
    </div>
    <AreaChart
      width={730}
      height={250}
      data={data}
      margin={{ top: 10, right: 0, left: 0, bottom: 0 }}
      className="evaluationsGraph"
    >
      <defs>
        <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
          <stop offset="5%" stopColor="rgba(244, 147, 60)" stopOpacity={0.8} />
          <stop offset="95%" stopColor="rgba(244, 147, 60)" stopOpacity={0} />
        </linearGradient>
        <linearGradient id="colorPv" x1="0" y1="0" x2="0" y2="1">
          <stop offset="5%" stopColor="#18469E" stopOpacity={0.8} />
          <stop offset="95%" stopColor="#18469E" stopOpacity={0} />
        </linearGradient>
      </defs>
      <XAxis dataKey="name" tickLine={false} />
      <YAxis orientation="right" tickLine={false} />
      <CartesianGrid vertical={false} />
      <Tooltip />
      <Area
        type="monotone"
        dataKey="min"
        stroke="#18469E"
        fillOpacity={1}
        fill="url(#colorUv)"
      />
      <Area
        type="monotone"
        dataKey="max"
        stroke="#DFE0EB"
        fillOpacity={1}
        fill="url(#colorPv)"
      />
    </AreaChart>
    <div className={styles.statistic}>
      <div>
        <div className={styles.statisticTitle}>
          {'Projekte von 5000$ bis 20000$'}
        </div>
        <div className={styles.statisticData}>{'449'}</div>
      </div>
      <div>
        <div className={styles.statisticTitle}>{'Gesamt einnahmen'}</div>
        <div className={styles.statisticData}>{'1,2m'}</div>
      </div>
      <div>
        <div className={styles.statisticTitle}>
          {'Durchschnittliche Projektdauer'}
        </div>
        <div className={styles.statisticData}>{'30Monate'}</div>
      </div>
    </div>
  </div>
)

export default Graph
