import { ReactComponent as Upload } from 'assets/icons/upload.svg'
import cn from 'classnames'
import Form from 'common/Form'
import Button from 'components/Button'
import TextInput from 'components/inputs/TextInput'
import { useFormik } from 'formik'
import { useDropzone } from 'react-dropzone'
import { useDispatch } from 'react-redux'
import { uploadImage } from 'services/image'
import notify from 'services/notify'
import { addDetailsPage } from 'store/companyStructure/actions'
import { FileWithPreview } from 'types'
import * as Yup from 'yup'

import sharedStyles from '../styles.module.scss'

const initialValues = {
  title: '',
  icon: {} as FileWithPreview,
}

const validationSchema = Yup.object().shape({
  title: Yup.string().required('Erforderlich'),
})

type FormValues = typeof initialValues

interface Props {
  groupId: number
  onSubmit(): void
  onCancel(): void
}

const DetailsPageForm: React.FC<Props> = ({
  groupId,
  onSubmit: onSubmitProp,
  onCancel,
}) => {
  const dispatch = useDispatch()

  const onSubmit = async ({ icon, title }: FormValues) => {
    try {
      const iconId = icon.name
        ? await uploadImage(
            icon,
            '/file/upload/node/cs_detail_page/field_image',
          )
        : undefined
      const preview = icon.name ? URL.createObjectURL(icon) : undefined

      dispatch(
        addDetailsPage(groupId, {
          title,
          icon: { ...icon, id: iconId, preview },
        }),
      )
      onSubmitProp()
    } catch (error) {
      notify(error)
    }
  }

  const formik = useFormik({ initialValues, validationSchema, onSubmit })

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    maxFiles: 1,
    onDrop: async (acceptedFiles) => {
      const file = acceptedFiles[0]
      if (!file) return

      const preview = URL.createObjectURL(file)

      formik.setFieldValue('icon', Object.assign(file, { preview }))
    },
  })

  return (
    <Form
      handleSubmit={formik.handleSubmit}
      title="Neue Detailseite"
      onCancel={onCancel}
    >
      <div
        {...getRootProps({
          className: cn(sharedStyles.dndContainer, {
            [sharedStyles.active]: isDragActive,
          }),
        })}
      >
        {formik.values.icon.preview && (
          <img src={formik.values.icon.preview} alt="main article cover" />
        )}
        <input {...getInputProps()} accept=".png,.jpg,.jpeg,.svg" />
        {'Icon reinziehen'} <br />
        {'oder'} <br />
        <Button
          variant="fulfilled"
          color="orange"
          className={sharedStyles.upload}
        >
          {'Hochladen'}
          <Upload />
        </Button>
      </div>
      <TextInput name="title" label="Titel" formik={formik} />
    </Form>
  )
}

export default DetailsPageForm
