import { AxiosResponse } from 'axios'
import { CompanyInfoFormValues } from 'containers/Dashboard/ArticleForm'
import api from 'services/api'
import notify from 'services/notify'
import { getState } from 'services/store'
import { action } from 'store/action'
import {
  AppThunkAsync,
  BackendMenuItem,
  Blog,
  BlogItem,
  CompanyInfoItem,
  Error,
} from 'types/index'

import ActionType from './types'

export const getBlogWidget = (
  onSuccess?: (blog: Blog) => void,
  onError?: (e: Error) => void,
): AppThunkAsync<Blog | undefined> => async (dispatch) => {
  dispatch(action(ActionType.GET_BLOG_WIDGET_BEGIN))
  try {
    const blog: AxiosResponse<Blog> = await api.get('widgets/blog?_format=json')
    dispatch(action(ActionType.GET_BLOG_WIDGET_SUCCESS, blog.data))
    onSuccess?.(blog.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_BLOG_WIDGET_ERROR, error))
    onError?.(error)
  }
}

export const getCompanyInformation = (
  onSuccess?: (blog: CompanyInfoItem[]) => void,
  onError?: (error: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_COMPANY_INFORMATION_BEGIN))
  try {
    const items: AxiosResponse<Blog> = await api.get(
      '/widgets/company_info?_format=json',
    )
    dispatch(
      action(ActionType.GET_COMPANY_INFORMATION_SUCCESS, items.data.rows),
    )
    onSuccess?.(items.data.rows)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_COMPANY_INFORMATION_ERROR, error))
    onError?.(error)
  }
}

export const readCompanyInformation = (
  companyInfo: BlogItem,
): AppThunkAsync => async (dispatch) => {
  try {
    const userId = getState()?.user.user?.id
    await api.patch(`/company_info/${companyInfo.nid}?_format=json`, {
      read: userId,
    })
    dispatch(
      action(ActionType.COMPANY_INFORMATION_READ, {
        nid: companyInfo.nid,
        userId,
      }),
    )
  } catch (error) {
    notify(error)
  }
}

export const createCompanyInformation = (
  companyInfo: CompanyInfoFormValues,
  onSuccess?: () => void,
  onError?: (error: Error) => void,
): AppThunkAsync => async (dispatch) => {
  try {
    const response: AxiosResponse<{
      id: string
      message: string
    }> = await api.post('/company_info?_format=json', {
      ...companyInfo,
      images: companyInfo.images.map((image) => image.id),
    })
    dispatch(
      action(ActionType.COMPANY_INFORMATION_CREATED, {
        ...companyInfo,
        images: companyInfo.images.map((i) => i.preview),
        nid: response.data.id,
        created: new Date(),
        read: '0',
      }),
    )
    onSuccess?.()
  } catch (error) {
    notify(error)
    onError?.(error)
  }
}

export const getDashboardMenu = (
  onSuccess?: (menuItems: BackendMenuItem[]) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_MENU_BEGIN))
  try {
    const menuItems: AxiosResponse<BackendMenuItem[]> = await api.get(
      'menu_items/dashboard-menu?_format=json',
    )
    dispatch(action(ActionType.GET_MENU_SUCCESS, menuItems.data))
    onSuccess?.(menuItems.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_MENU_ERROR, error))
    onError?.(error)
  }
}

export default {
  getBlogWidget,
  getCompanyInformation,
  createCompanyInformation,
  getDashboardMenu,
}
