import Button from 'components/Button'
import GlobalSearch from 'components/tableComponents/GlobalSearch'

import EmployeeTable from './EmployeeTable'
import styles from './styles.module.scss'

interface ContentViewProps {
  globalFilter: string | undefined
  setGlobalFilter: (filterValue: string | undefined) => void
  handleSynchronizeData: () => void
  isActiveButton?: boolean
}

const ContentView: React.FC<ContentViewProps> = ({
  globalFilter,
  setGlobalFilter,
  handleSynchronizeData,
  isActiveButton,
}) => (
  <div>
    <div className={styles.header}>
      <h1 className={styles.title}>{'Stammdaten'}</h1>
      {isActiveButton && (
        <Button
          variant="fulfilled"
          color="orange"
          onClick={handleSynchronizeData}
        >
          {'KOBOLD Daten synchronisieren'}
        </Button>
      )}
    </div>
    <div className={styles.searchAndTableWrapper}>
      <GlobalSearch
        globalFilter={globalFilter}
        setGlobalFilter={setGlobalFilter}
        containerClassName={styles.search}
      />
      <EmployeeTable globalFilter={globalFilter} />
    </div>
  </div>
)

export default ContentView
