import cn from 'classnames'
import Comment from 'components/Comment'
import CommentEditor from 'components/CommentEditor'
import Loader from 'components/Loader'
import useToggle from 'hooks/useToggle'
import { useEffect, useRef, useState } from 'react'
import { useDispatch } from 'react-redux'
import notify from 'services/notify'
import { createComment } from 'store/ives/acquisitions/actions'
import { setTimeout } from 'timers'
import {
  AppThunkAsync,
  Comment as IComment,
  Comments as IComments,
  Error,
} from 'types/index'

import styles from './styles.module.scss'

interface CommentsProps {
  acquisitionName: string
  acquisitionId: string
  commentParentId?: string
  withoutHeader?: boolean
  loadComments: (
    acquisitionId: string | number,
    commentId?: string | number | null,
    onSuccess?: ((comments: IComments) => void) | undefined,
    onError?: ((e: Error) => void) | undefined,
  ) => AppThunkAsync
}

const Comments = ({
  acquisitionName,
  acquisitionId,
  commentParentId,
  withoutHeader,
  loadComments,
}: CommentsProps) => {
  const dispatch = useDispatch()
  const [editedComment, setEditedComment] = useState<IComment>()
  const [isLoading, setIsLoading] = useState(true)
  const [comments, setComments] = useState<IComments>()
  const editorToggle = useToggle()
  const commentsRef = useRef<HTMLDivElement>(null)
  const [maxHeight, setMaxHeight] = useState(0)

  useEffect(() => {
    dispatch(loadComments(acquisitionId, commentParentId, setComments))
  }, [])

  useEffect(() => {
    setTimeout(() => {
      setIsLoading(false)
    }, 3000)
  }, [isLoading])

  const onReplyClick = (comment: IComment) => {
    setEditedComment(comment)
    setMaxHeight(commentsRef.current?.offsetHeight || 50)
    editorToggle.on()
  }

  const onReplyCancel = () => {
    setEditedComment(undefined)
    editorToggle.off()
  }

  const onReplySubmit = (comment: string) => {
    editedComment &&
      dispatch(
        createComment(
          {
            acquisitionId,
            commentRootId: editedComment.cid,
            comment,
          },
          () => notify('Kommentar wurde erfolgreich erstellt', 'success'),
        ),
      )
    onReplyCancel()
  }

  const showComments = () => (
    <>
      <div
        style={{ maxHeight: editorToggle.value ? maxHeight : undefined }}
        ref={commentsRef}
        className={cn(
          styles.comments,
          editorToggle.value && editedComment && styles.editing,
        )}
      >
        {!withoutHeader && (
          <div
            className={styles.title}
          >{`Kommentare für ${acquisitionName}`}</div>
        )}
        <div>
          {comments?.rows.map((comment) => (
            <Comment
              key={comment.cid}
              comment={comment}
              acquisitionId={acquisitionId}
              onReplyClick={onReplyClick}
              editedCommentId={editedComment?.cid}
              loadComments={loadComments}
            />
          ))}
        </div>
        <CommentEditor
          editedComment={editedComment}
          className={cn(
            styles.commentEditor,
            editorToggle.value && editedComment && styles.editing,
          )}
          onSubmit={onReplySubmit}
          onCancel={onReplyCancel}
        />
      </div>
    </>
  )

  const showNoComments = () => (
    <>
      {isLoading ? (
        <div className={styles.loader}>
          <Loader />
        </div>
      ) : (
        <div className={styles.noComments}>
          <div className={styles.noCommentsText}>
            {`Keine Kommentare für ${acquisitionName}`}
          </div>
        </div>
      )}
    </>
  )

  return <>{comments?.rows.length ? showComments() : showNoComments()}</>
}

export default Comments
