import {
  EmployeeHolidays,
  Error,
  NationalHolidays,
  ProjectGroupHolidays,
} from 'types/index'

import ActionType, { Action } from './types'

export interface IState {
  isLoading: boolean
  error: Error | null
  projectGroups: ProjectGroupHolidays[] | null
  employees: EmployeeHolidays[] | null
  showEmployeeView: boolean
  nationalHolidays: NationalHolidays[] | null
}

const initialState: IState = {
  isLoading: false,
  error: null,
  projectGroups: null,
  employees: null,
  showEmployeeView: false,
  nationalHolidays: null,
}

const reducer = (state = initialState, action: Action): IState => {
  switch (action.type) {
    case ActionType.GET_HOLIDAYS_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.GET_HOLIDAYS_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
        projectGroups: action.payload,
      }
    }
    case ActionType.GET_HOLIDAYS_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    case ActionType.GET_HOLIDAY_EMPLOYEES_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.GET_HOLIDAY_EMPLOYEES_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
        employees: action.payload,
        showEmployeeView: false,
      }
    }
    case ActionType.GET_HOLIDAY_EMPLOYEE_VIEW_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
        employees: action.payload,
      }
    }
    case ActionType.GET_HOLIDAY_EMPLOYEES_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    case ActionType.SET_EMPLOYEE_VIEW: {
      return {
        ...state,
        showEmployeeView: action.payload,
      }
    }
    case ActionType.GET_NATIONAL_HOLIDAYS_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.GET_NATIONAL_HOLIDAYS_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
        nationalHolidays: action.payload,
      }
    }
    case ActionType.GET_NATIONAL_HOLIDAYS_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    case ActionType.CREATE_HOLIDAY_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.CREATE_HOLIDAY_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
      }
    }
    case ActionType.CREATE_HOLIDAY_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    default:
      return state
  }
}

export default reducer
