// import { ReactComponent as ArrowNext } from 'assets/icons/arrowNextOrange.svg'
// import Form from 'common/Form'
// import Button from 'components/Button'
import DatePicker from 'components/inputs/DatePicker'
import FieldArray from 'components/inputs/FieldArray'
import NumberInput from 'components/inputs/NumberInput'
import Select from 'components/inputs/Select'
import TextInput from 'components/inputs/TextInput'
import { FormikProps } from 'formik'
import { useEffect } from 'react'

import { FormOptions, Step2 as Props } from '../formData'
import styles from '../styles.module.scss'

interface FormProps {
  formik: FormikProps<Props>
  options: FormOptions
}

const Step2 = ({ formik, options }: FormProps) => {
  useEffect(() => {
    formik.values.bonuses?.length === 0 && formik.setFieldValue('bonuses', [''])
  }, [])

  const parseBonuses = (bonus: string) => {
    const parsed = bonus.split(' ')
    if (parsed.length > 1) {
      return {
        date: parsed[0],
        value: parsed[1],
      }
    }

    return {
      date: '',
      value: parsed[0],
    }
  }

  return (
    <>
      <h2 className={styles.stepTitle}>{'Vertragsdaten'}</h2>
      <NumberInput
        name="salary"
        label="Gehalt"
        suffix=" €"
        formik={formik}
        thousandSeparator
        disabled
      />
      <Select
        name="working_days"
        options={options.workingDaysOptions}
        placeholder="Arbeitstage auswählen"
        label="Arbeitstage auswählen"
        formik={formik}
        isMulti
      />
      <section>
        <FieldArray
          isGroup
          name="allowences"
          label="Detail Bonuszahlungen"
          formik={formik}
          renderField={(defaultValue, index) => (
            <div>
              <Select
                name={`allowences[${index}].type`}
                placeholder="Grund Auswählen"
                isCreatable
                options={[
                  { value: 'Jobticket', label: 'Jobticket' },
                  { value: 'Kita', label: 'Kita' },
                  { value: 'Hochzeit', label: 'Hochzeit' },
                ]}
                value={defaultValue.type}
                formik={formik}
              />
              <DatePicker
                name={`allowences[${index}].date`}
                value={defaultValue.date}
                placeholder="Beginn"
                formik={formik}
              />
              <NumberInput
                label="Betrag"
                suffix=" €"
                name={`allowences[${index}].amount`}
                value={defaultValue.amount}
                formik={formik}
              />
            </div>
          )}
        />
      </section>
      <FieldArray
        disable
        name="bonuses"
        label="Prämien"
        formik={formik}
        renderField={(val, index) => (
          <NumberInput
            key={`bonuses-${index}`}
            name={`bonuses[${index}]`}
            label={`Prämien / Sonderzahlungen ${parseBonuses(val).date}`}
            suffix=" €"
            value={parseBonuses(val).value}
            onChange={({ value }) => {
              const values = [...formik.values.bonuses]
              values[index] = value
              formik.setFieldValue('bonuses', values)
            }}
            thousandSeparator
            disabled
          />
        )}
      />
      <Select
        name="hourly_model"
        options={[]}
        placeholder="Stundenmodell"
        formik={formik}
        isDisabled
      />
      <TextInput name="vbe" label="VBE" formik={formik} />
    </>
  )
}

export default Step2
