import { Editor, EditorTools, ProseMirror } from '@progress/kendo-react-editor'
import cn from 'classnames'
import { useEffect, useState } from 'react'

import { renderPlaceholder, styles as plStyles } from './placeholder'
// import { FormikProps } from 'formik'
import styles from './styles.module.scss'

const {
  Bold,
  Italic,
  Underline,
  Strikethrough,
  // AlignLeft,
  // AlignCenter,
  // AlignRight,
  // AlignJustify,
  // Indent,
  // Outdent,
  OrderedList,
  UnorderedList,
  Undo,
  Redo,
  // Link,
  // Unlink,
} = EditorTools

interface HtmlEditorProps {
  name: string
  label: string
  defaultValue?: string
  placeholder?: string
  disabled?: boolean
  formik?: any // TODO: FIX THIS!
  onChange?: (value: string) => void
  // className?: string
}

// type HtmlEditorProps<T> = CustomInputProps<T> & Editor

const HtmlEditor = ({
  disabled,
  label,
  name,
  formik,
  defaultValue,
  placeholder = '',
  onChange,
}: HtmlEditorProps) => {
  const [value, setValue] = useState<string>()

  useEffect(() => {
    value && setValue(undefined)
  })
  useEffect(() => {
    defaultValue && setValue(defaultValue)
  }, [defaultValue])

  return (
    <section>
      <h3 className={cn(styles.title, { [styles.disabled]: disabled })}>
        {label}
      </h3>
      <Editor
        tools={[
          [Bold, Italic, Underline, Strikethrough],
          // [AlignLeft, AlignCenter, AlignRight, AlignJustify],
          // [Indent, Outdent],
          [OrderedList, UnorderedList],
          // [Link, Unlink],
          [Undo, Redo],
        ]}
        onMount={(event) => {
          if (placeholder !== '') {
            const { state } = event.viewProps
            const plugins = [...state.plugins, renderPlaceholder(placeholder)]

            const editorDocument = event.dom.ownerDocument
            const styleElement =
              editorDocument && editorDocument.querySelector('style')
            if (styleElement) {
              styleElement.appendChild(editorDocument.createTextNode(plStyles))
            }

            return new ProseMirror.EditorView(
              { mount: event.dom },
              {
                ...event.viewProps,
                state: ProseMirror.EditorState.create({
                  doc: state.doc,
                  plugins,
                }),
              },
            )
          }
          return undefined
        }}
        contentStyle={{
          height: 80,
          fontFamily: 'sans-serif',
        }}
        value={value}
        onBlur={({ nativeEvent }) => {
          const value = (nativeEvent.target as EventTarget & HTMLElement)
            ?.innerHTML
          formik?.setFieldValue(name, value)
          onChange && onChange(value)
        }}
        defaultEditMode="div"
        className={cn(styles.editor, {
          [styles.error]: formik?.errors[name],
        })}
      />
      {formik?.errors[name] && (
        <div className="MuiFormHelperText-root MuiFormHelperText-contained Mui-error">
          {formik?.errors[name]}
        </div>
      )}
    </section>
  )
}

export default HtmlEditor
