import { useFormik } from 'formik'
import notify from 'services/notify'
import * as Yup from 'yup'

import Form from './form'

const FormSchema: Yup.SchemaOf<FormValues> = Yup.object().shape({
  name: Yup.string().required('Erforderlich'),
  street: Yup.string().required('Erforderlich'),
  postcode: Yup.string()
    .matches(/[0-9]/g, 'Nur Zahlen verwenden')
    .required('Erforderlich'),
  city: Yup.string().required('Erforderlich'),
  homepage: Yup.string().url('Homepage muss eine gültige URL sein').default(''),
})

export const initialValues = {
  name: '',
  street: '',
  postcode: '',
  city: '',
  homepage: '',
}

export type FormValues = typeof initialValues

interface UserFormProps {
  initialData?: Partial<FormValues>
  title: string
  onCancel: () => void
  onSubmit: (values: FormValues) => void
}

const CustomerForm = ({
  initialData,
  title,
  onCancel,
  onSubmit,
}: UserFormProps) => {
  const formik = useFormik({
    initialValues: { ...initialValues, ...initialData },
    onSubmit: (values, helpers) => {
      try {
        helpers.resetForm()
        onSubmit(values)
      } catch (error) {
        notify(error)
      }
    },
    validationSchema: FormSchema,
  })

  return <Form title={title} formik={formik} onCancel={onCancel} />
}
export default CustomerForm
