/* eslint-disable react/jsx-key */

import cn from 'classnames'
import IndeterminateCheckbox from 'components/inputs/IndeterminateCheckbox'
import InfinityScroll from 'components/tableComponents/InfinityScroll'
import IvesTableItem from 'components/tableComponents/IvesTableItem'
import { DEFAULT_TABLE_WIDTH } from 'constants/index'
import useClickPreventionOnDoubleClick from 'hooks/useClickPreventionOnDoubleClick'
import useWindowSize from 'hooks/useWindowSize'
import { useCallback, useEffect, useMemo, useState } from 'react'
import {
  Column,
  ColumnInstance,
  FilterValue,
  // IdType,
  Row,
  useBlockLayout,
  useFilters,
  useGlobalFilter,
  useResizeColumns,
  useRowSelect,
  useRowState,
  useSortBy,
  useTable,
} from 'react-table'
import { FixedSizeList } from 'react-window'

import styles from './styles.module.scss'

interface RowData extends Record<string, string | undefined> {
  id: string
  commentCount?: string
}

interface TableProps<D extends RowData> {
  columnsData: Column<D>[]
  tableData?: D[]
  tableWidth?: number
  tableHeight?: number
  appendedColumn?: Partial<ColumnInstance<D>>
  notSelectable?: boolean

  /**
   * required for infinity scroll
   */
  hasNextPage?: boolean
  /**
   * required for infinity scroll
   */
  loadNextPage?: (
    startIndex: number,
    stopIndex: number,
  ) => Promise<unknown> | null
  /**
   * required for infinity scroll
   */
  isNextPageLoading?: boolean
  withInitialSort?: boolean

  setGlobalFilterCallback?: (filterValue: FilterValue) => void
  globalFilterCallback?: (globalFilter: string) => void

  onRowSelect?: (row?: Row<D>) => void
  onRowDbClick?: (row?: Row<D>) => void
  onContextMenu?: (
    row: Row<D>,
    e: React.MouseEvent<HTMLLabelElement, MouseEvent>,
  ) => void

  tableContainerClassName?: string
  sortField?: string
  isExpandable?: boolean
}

const Table = <D extends RowData>({
  tableContainerClassName,
  columnsData,
  tableData,
  hasNextPage,
  loadNextPage,
  isNextPageLoading,
  tableWidth,
  tableHeight,
  appendedColumn,
  notSelectable,
  onRowSelect,
  onRowDbClick,
  onContextMenu,
  setGlobalFilterCallback,
  globalFilterCallback,
  isExpandable,
}: TableProps<D>) => {
  const [rowsLength, setRowsLength] = useState(0)
  const [rowExpanded, setRowExpanded] = useState(false)
  const { width: windowWidth = DEFAULT_TABLE_WIDTH } = useWindowSize()

  const width = useMemo(() => tableWidth || windowWidth - 250, [
    tableWidth,
    windowWidth,
  ])

  const data = useMemo<D[]>(() => tableData || [], [tableData])
  const columns = useMemo<Column<D>[]>(
    () =>
      columnsData.map((column) => ({
        ...column,
        width: width / columnsData.length,
      })),
    [width],
  )

  const defaultColumn = useMemo(
    () => ({
      minWidth: 30,
    }),
    [],
  )

  const getRowId = (row: D, relativeIndex: number, parent?: Row<D>): string =>
    parent ? [parent.id, row.id].join('.') : `${row.id}`

  const {
    rows,
    selectedFlatRows,
    prepareRow,
    setGlobalFilter,
    getTableProps,
    headerGroups,
    getTableBodyProps,
    state: { globalFilter, columnResizing, sortBy },
  } = useTable<D>(
    {
      columns,
      data,
      defaultColumn,
      disableSortRemove: true,
      getRowId,
      autoResetSortBy: false,
      sortTypes: {
        // Override default sorting function, just for alphanumeric characters
        // to sort the strings independently if the value is a number or an uppercase/lowercase
        // string text. Add further custom cases here for dates etc.
        alphanumeric: (row1, row2, columnName) => {
          const rowOneColumn = row1.values[columnName]
          const rowTwoColumn = row2.values[columnName]
          return rowOneColumn
            .toLocaleLowerCase()
            .localeCompare(rowTwoColumn.toLocaleLowerCase(), undefined, {
              numeric: true,
              sensitivity: 'base',
            })
        },
      },
    },

    useFilters,
    useGlobalFilter,
    useSortBy,
    useBlockLayout,
    useResizeColumns,
    useRowSelect,
    useRowState,
    (hooks) => {
      appendedColumn &&
        hooks.visibleColumns.push((visibleColumns) => [
          ...visibleColumns,
          { ...appendedColumn } as ColumnInstance<D>,
        ])
    },
  )

  useEffect(() => {
    setRowsLength(rows.length)
  }, [rows.length])

  useEffect(() => {
    setGlobalFilterCallback?.(setGlobalFilter)
  }, [setGlobalFilter])

  useEffect(() => {
    globalFilterCallback?.(globalFilter)
  }, [globalFilter])

  const [handleClick, handleDoubleClick] = useClickPreventionOnDoubleClick(
    (row: Row<D>) => {
      row.toggleRowSelected()
      if (isExpandable) {
        row.isSelected ? setRowExpanded(false) : setRowExpanded(true)
      }
      selectedFlatRows?.[0]?.toggleRowSelected(false)
      row.toggleRowExpanded(true)
      row.isSelected ? onRowSelect?.(undefined) : onRowSelect?.(row)
    },
    (row: Row<D>) => {
      onRowDbClick?.(row)
    },
  )

  const RenderRow = useCallback(
    ({ index, style }) => {
      const row = rows[index] as Row<D> | undefined

      if (!row) {
        return <div>{'Loading...'}</div>
      }
      prepareRow(row)
      return (
        <label
          htmlFor={row.id}
          {...row.getRowProps({
            style,
          })}
          onClick={(e) => {
            e.preventDefault()
            notSelectable || handleClick(row)
          }}
          onDoubleClick={() => handleDoubleClick(row)}
          onContextMenu={(e) => {
            onContextMenu?.(row, e)
          }}
          className={cn(
            styles.tr,
            row.isSelected && styles.selected,
            isExpandable && row.isSelected && styles.expanded,
          )}
        >
          <IndeterminateCheckbox
            {...row.getToggleRowSelectedProps?.()}
            id={row.id}
          />
          {row.cells.map((cell) => (
            <div
              {...cell.getCellProps()}
              className={cn(
                styles.td,
                isExpandable && row.isSelected && styles.expanded,
              )}
            >
              {cell.render('Cell')}
            </div>
          ))}
        </label>
      )
    },
    [
      prepareRow,
      rows.length,
      selectedFlatRows?.[0],
      columnResizing.isResizingColumn,
      sortBy,
    ],
  )
  const tableHeightValue = () =>
    rowExpanded ? rowsLength * 31 + 90 : rowsLength * 32
  return (
    <div className={styles.container}>
      <div className={cn(styles.tableContainer, tableContainerClassName)}>
        <div {...getTableProps()} className={styles.table}>
          <div className={styles.thead}>
            {headerGroups.map((headerGroup) => (
              <div {...headerGroup.getHeaderGroupProps()} className={styles.tr}>
                {headerGroup.headers.map((column) => (
                  <div {...column.getHeaderProps()} className={styles.th}>
                    <div {...column.getSortByToggleProps()}>
                      <IvesTableItem
                        order={column.isSortedDesc ? 'down' : 'up'}
                        hideArrow={!column?.canSort}
                      >
                        {column.render('Header')}
                      </IvesTableItem>
                    </div>
                    <div
                      {...column.getResizerProps()}
                      className={cn(
                        styles.resizer,
                        column.isResizing && styles.isResizing,
                      )}
                    />
                  </div>
                ))}
              </div>
            ))}
          </div>
          <div {...getTableBodyProps()} className={styles.tbody}>
            {typeof hasNextPage !== 'undefined' &&
            typeof loadNextPage !== 'undefined' &&
            typeof isNextPageLoading !== 'undefined' ? (
              <InfinityScroll
                hasNextPage={hasNextPage}
                isNextPageLoading={isNextPageLoading}
                itemsLength={rows.length}
                loadNextPage={loadNextPage}
                renderItem={RenderRow}
                height={tableHeight}
              />
            ) : (
              <FixedSizeList
                height={tableHeight || tableHeightValue()}
                itemCount={rows.length}
                itemSize={32}
                width="100%"
              >
                {RenderRow}
              </FixedSizeList>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}

export default Table
