import { ReactComponent as WidgetBurgerMenu } from 'assets/icons/burgerMenu2.svg'
import cn from 'classnames'
import { ContextMenuParams, TriggerEvent } from 'react-contexify'

import styles from './styles.module.scss'

export interface WidgetProps {
  title: string
  children?: React.ReactNode
  withoutMenu?: boolean
  containerClassName?: string
  bodyClassName?: string
  headerClassName?: string
  id?: string
  showContextMenu?(
    event: TriggerEvent,
    params?: Pick<ContextMenuParams, 'id' | 'props' | 'position'>,
  ): void
}

const Widget = ({
  title,
  children,
  containerClassName,
  bodyClassName,
  headerClassName,
  withoutMenu,
  id,
  showContextMenu,
}: WidgetProps) => (
  <div
    className={cn(styles.widget, containerClassName)}
    onContextMenu={(event) => showContextMenu?.(event, { props: { id } })}
  >
    <div className={cn(styles.header, headerClassName)}>
      <div className={styles.title}>{title}</div>
      {withoutMenu || (
        <div className="draggable">
          <WidgetBurgerMenu />
        </div>
      )}
    </div>
    <div className={cn(styles.body, bodyClassName)}>{children}</div>
  </div>
)

export default Widget
