import { OfferProcessingField } from 'containers/Ives/OfferDetails/view'
import { fromUnixTime, getUnixTime } from 'date-fns'
import { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { formatDate, zonedDate } from 'services/date'
import { getPgl, updateOffer } from 'store/ives/offers/actions'
import { BackEndSelectOptions, Offer } from 'types'

import shared from '../shared.module.scss'
import Form, { FormValues } from './form'
import styles from './styles.module.scss'

const FIELDS: OfferProcessingField[] = [
  { name: 'Angebots Nr:', value: 'number' },
  { name: 'Angebots Inhalt:', value: 'title' },
  { name: 'Auftraggerber:', value: 'customer' },
  { name: 'Zuordnung Fachbereich:', value: 'department' },
  { name: 'Angedachter PGL:', value: 'pgl' },
  { name: 'Angebotsersteller:', value: 'author' },
  { name: 'Datum:', value: 'created' },
]

interface OfferProcessingProps {
  offer: Offer
}

const OfferProcessing = ({ offer }: OfferProcessingProps) => {
  const [pgl, setPgl] = useState<BackEndSelectOptions>()
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getPgl('', setPgl))
  }, [])

  const renderField = (value: keyof Offer) => {
    if (value === 'customer') {
      return typeof offer.customer === 'string'
        ? offer.customer
        : offer.customer?.title
    }
    if (value === 'pgl') {
      return pgl?.rows.find((item) => `${item.id}` === offer.pgl)?.name
    }
    if (value === 'created') {
      return formatDate(fromUnixTime(+offer.created))
    }

    return offer[value]
  }

  const onSubmit = (values: FormValues) =>
    dispatch(
      updateOffer({
        nid: offer.nid,
        amount: values.amount,
        nan: values.nan,
        liability_damage: values.liabilityDamage?.value,
        probability: values.probability?.value,
        project_manager: values.projectManager?.value,
        binding_until: values.bindingUntil
          ? getUnixTime(zonedDate(values.bindingUntil))
          : undefined,
      }),
    )

  return (
    <>
      <div className={shared.subtitle}>{'Angebotsbearbeitung'}</div>
      <div className={styles.fieldsWrapper}>
        {FIELDS.map(({ name, value }) => (
          <div key={name} className={styles.fieldWrapper}>
            <div className={styles.fieldName}>{name}</div>
            <div className={styles.fieldValue}>{renderField(value)}</div>
          </div>
        ))}
        <Form
          onSubmit={onSubmit}
          initialData={{
            amount: `${offer.amount}`,
            bindingUntil: offer.binding_until
              ? formatDate(fromUnixTime(+offer.binding_until))
              : '',
            liabilityDamage: offer.liability_damage
              ? `${offer.liability_damage}`
              : '',
            nan: `${offer.nan}`,
            probability: offer.probability ? `${offer.probability}` : '',
            projectManager: offer.project_manager,
          }}
        />
      </div>
    </>
  )
}
export default OfferProcessing
