import { SelectChangeEvent } from '@mui/material'
import CompanyStructureImg from 'assets/images/company_structure.jpg'
import ProjectStructureImg from 'assets/images/project_structure.svg'
import Dresden from 'assets/images/SP-Dresdenpdf.png'
import Geb from 'assets/images/SP-Gebäudetechnik.png'
import Potsdam from 'assets/images/SP-Potsdam.png'
import SCS from 'assets/images/SP-SCS.png'
import cn from 'classnames'
import Layout from 'common/Layout'
import Modal from 'common/Modal'
import EmployeeInfo from 'common/widgets/CompanyCluster/EmployeeInfo'
import Dropdown from 'components/inputs/Dropdown'
import { CompanyCluster, companyClustersMap } from 'constants/companyStructure'
import GridLayout from 'containers/CompanyStructure/GridLayout'
import useToggle from 'hooks/useToggle'
import React, { useEffect, useMemo, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getCompanyStructure } from 'store/companyStructure/actions'
import strings from 'strings'
import { BaseEmployee, ReduxState } from 'types'

import styles from './styles.module.scss'

const navigationItems = [
  {
    id: 1,
    name: 'S&P Leipzig ',
    link: '',
  },
  {
    id: 2,
    name: 'S&P BuM ',
    title: 'Unternehmensstruktur kommt demnächst',
    content: ProjectStructureImg,
    link: '',
  },
  {
    id: 3,
    name: 'S&P Potsdam ',
    content: Potsdam,
    link: '',
  },
  {
    id: 4,
    name: 'S&P Dresden ',
    content: Dresden,
    link: '',
  },
  {
    id: 5,
    name: 'S&P Zwickau ',
    title: 'Unternehmensstruktur kommt demnächst',
    content: ProjectStructureImg,
    link: '',
  },
  {
    id: 6,
    name: 'S&P GbR ',
    title: '',
    content: CompanyStructureImg,
    link: '',
  },
  {
    id: 7,
    name: 'S&P Gebäudetechnik ',
    content: Geb,
    link: '',
  },
  {
    id: 8,
    name: 'S&P IT ',
    title: 'Unternehmensstruktur kommt demnächst',
    content: ProjectStructureImg,
    link: '',
  },
  {
    id: 9,
    name: 'S&P ScS ',
    content: SCS,
    link: '',
  },
]

const CompanyStructure: React.FC = () => {
  const [employeeId, setEmployeeId] = useState<number | null>(null)
  const [selectedItem, setSelectedItem] = useState<number>(
    navigationItems[1].id,
  )
  const [searchValue, setSearchValue] = useState<string>()
  const employeeInfoToggle = useToggle()
  const [company, setCompany] = React.useState('')
  const handleChange = (event: SelectChangeEvent) => {
    setCompany(event.target.value)
    setSelectedItem(event.target.value as any)
  }
  const { data: companyStructure, isLoading } = useSelector(
    (state: ReduxState) => state.companyStructure,
  )
  const [structueredCompany, setStructuredCompany] = useState(companyStructure)
  const removeFromVerwaltung = (objName: string) => {
    const strCmp = structueredCompany
    strCmp.verwaltung = strCmp.verwaltung.filter(
      (obj: any) => obj.name !== objName,
    )
    setStructuredCompany(strCmp)
  }

  const moveObjectToVerwaltung = (objName: string) => {
    const structure = structueredCompany
    let movedObj: any | null = null

    // Iterating over the object's keys using Object.keys
    Object.keys(structure).forEach((key) => {
      const index = structure[key].findIndex((obj: any) => obj.name === objName)
      if (index !== -1) {
        ;[movedObj] = structure[key].splice(index, 1)
      }
    })

    // If found, add the item to verwaltung
    if (movedObj) {
      structure.verwaltung.push(movedObj)
    }
    setStructuredCompany(structure)
  }
  const dispatch = useDispatch()
  const getGroups = async () => {
    Object.keys(companyClustersMap).forEach((cluster) => {
      dispatch(getCompanyStructure(cluster as CompanyCluster))
    })
  }
  useEffect(() => {
    getGroups()
  }, [])
  useEffect(() => {
    removeFromVerwaltung('Kaufmännische und Technische Mitarbeiter')
    moveObjectToVerwaltung('Controlling / Sekretariat / Administration')
  }, [companyStructure, isLoading])

  const options = useMemo(
    () =>
      Object.entries(
        Object.values(companyStructure)
          .flat()
          .map((group) => [group.manager[0], ...group.employees])
          .flat()
          .filter((employee): employee is BaseEmployee => Boolean(employee))
          .reduce(
            (prev, curr) => ({ ...prev, [curr.nid]: curr }),
            {} as Record<number, BaseEmployee>,
          ),
      ).map(([value, employee]) => ({ label: employee.name, value })),
    [companyStructure],
  )

  const activeImg = navigationItems.find((i) => i.id === selectedItem)?.content
  const title = navigationItems.find((i) => i.id === selectedItem)?.title

  return (
    <Layout
      searchDropdownOptions={options}
      searchValue={searchValue}
      onSearchChange={(id) => {
        setSearchValue(id)
        setEmployeeId(Number(id))
        id && employeeInfoToggle.on()
      }}
    >
      <h1 className={styles.title}>{strings.companyStructure}</h1>
      <div className={styles.dropdownWrap}>
        <Dropdown
          dropdownValue={company}
          label="Alle Unternehmen"
          menuItems={navigationItems}
          onItemChange={handleChange}
        />
      </div>
      {selectedItem !== 2 && activeImg ? (
        <div
          className={cn(styles.imgContent, {
            [styles.withTitle]: title,
          })}
        >
          {title && <h2>{title}</h2>}
          <img
            className={cn({
              [styles.table]: selectedItem === 6,
            })}
            src={activeImg}
            alt=""
          />
        </div>
      ) : (
        <GridLayout
          selectedCompany={selectedItem}
          companyStructure={structueredCompany}
          openEmployeeInfo={(id) => {
            setEmployeeId(id)
            employeeInfoToggle.on()
          }}
        />
      )}
      <Modal
        isOpen={employeeInfoToggle.value}
        onRequestClose={() => {
          employeeInfoToggle.off()
          setSearchValue(undefined)
        }}
        withCloseButton
      >
        <EmployeeInfo id={employeeId} />
      </Modal>
    </Layout>
  )
}

export default CompanyStructure
