import { Customer, Customers, Error } from 'types/index'

enum ActionType {
  GET_CUSTOMER_RECORD_BEGIN = '[ives] GET_CUSTOMER_RECORD_BEGIN',
  GET_CUSTOMER_RECORD_SUCCESS = '[ives] GET_CUSTOMER_RECORD_SUCCESS',
  GET_CUSTOMER_RECORD_ERROR = '[ives] GET_CUSTOMER_RECORD_ERROR',
  GET_CUSTOMERS_BEGIN = '[ives] GET_CUSTOMERS_BEGIN',
  GET_CUSTOMERS_SUCCESS = '[ives] GET_CUSTOMERS_SUCCESS',
  GET_CUSTOMERS_ERROR = '[ives] GET_CUSTOMERS_ERROR',
  UPDATE_CUSTOMER_BEGIN = '[ives] UPDATE_CUSTOMER_BEGIN',
  UPDATE_CUSTOMER_SUCCESS = '[ives] UPDATE_CUSTOMER_SUCCESS',
  UPDATE_CUSTOMER_ERROR = '[ives] UPDATE_CUSTOMER_ERROR',
  CREATE_CUSTOMER_BEGIN = '[ives] CREATE_CUSTOMER_BEGIN',
  CREATE_CUSTOMER_SUCCESS = '[ives] CREATE_CUSTOMER_SUCCESS',
  CREATE_CUSTOMER_ERROR = '[ives] CREATE_CUSTOMER_ERROR',
}

export interface GetCustomerRecordBeginAction {
  type: ActionType.GET_CUSTOMER_RECORD_BEGIN
}

export interface GetCustomerRecordSuccessAction {
  type: ActionType.GET_CUSTOMER_RECORD_SUCCESS
  payload: Customer
}

export interface GetCustomerRecordErrorAction {
  type: ActionType.GET_CUSTOMER_RECORD_ERROR
  payload: Error
}

export interface GetCustomersBeginAction {
  type: ActionType.GET_CUSTOMERS_BEGIN
}

export interface GetCustomersSuccessAction {
  type: ActionType.GET_CUSTOMERS_SUCCESS
  payload: Customers
}

export interface GetCustomersErrorAction {
  type: ActionType.GET_CUSTOMERS_ERROR
  payload: Error
}

export interface UpdateCustomerBeginAction {
  type: ActionType.UPDATE_CUSTOMER_BEGIN
}

export interface UpdateCustomerSuccessAction {
  type: ActionType.UPDATE_CUSTOMER_SUCCESS
}

export interface UpdateCustomerErrorAction {
  type: ActionType.UPDATE_CUSTOMER_ERROR
  payload: Error
}

export interface CreateCustomerBeginAction {
  type: ActionType.CREATE_CUSTOMER_BEGIN
}

export interface CreateCustomerSuccessAction {
  type: ActionType.CREATE_CUSTOMER_SUCCESS
}

export interface CreateCustomerErrorAction {
  type: ActionType.CREATE_CUSTOMER_ERROR
  payload: Error
}

export type Action =
  | GetCustomerRecordBeginAction
  | GetCustomerRecordSuccessAction
  | GetCustomerRecordErrorAction
  | GetCustomersBeginAction
  | GetCustomersSuccessAction
  | GetCustomersErrorAction
  | UpdateCustomerBeginAction
  | UpdateCustomerSuccessAction
  | UpdateCustomerErrorAction
  | CreateCustomerBeginAction
  | CreateCustomerSuccessAction
  | CreateCustomerErrorAction

export default ActionType
