import { useFormik } from 'formik'
import { validateDateInput } from 'services/date'
import notify from 'services/notify'
import * as Yup from 'yup'

import ContentView from './view'

export const initialValues = {
  bindingUntil: '',
  amount: '',
  // TODO when back-end will be ready
  // totalFee: '',
}

export type FormValues = typeof initialValues

const FormSchema: Yup.SchemaOf<FormValues> = Yup.object().shape({
  bindingUntil: Yup.string()
    .transform(validateDateInput)
    .typeError('Bitte korrektes Datum eingeben')
    .default(''),
  amount: Yup.string().default(''),
  // TODO when back-end will be ready
  // totalFee: Yup.string().default(''),
})

interface FilterProps {
  setFilters: (values: FormValues) => void
}

const Filter = ({ setFilters }: FilterProps) => {
  const formik = useFormik({
    initialValues: { ...initialValues },
    onSubmit: (values) => {
      try {
        setFilters({ ...values })
      } catch (error) {
        notify(error)
      }
    },
    onReset: (values) => {
      setFilters({ ...values })
    },
    validationSchema: FormSchema,
  })

  return <ContentView formik={formik} />
}

export default Filter
