import { AxiosResponse } from 'axios'
import Form from 'common/Form'
import ImageCropModal from 'common/ImageCropModal'
import NavigationBar from 'common/NavigationBar'
import { CompanyCluster, companyClustersMap } from 'constants/companyStructure'
import { vocabularies } from 'constants/dashboard'
import { isUTF8 } from 'containers/Ives/Employees/EmployeeDetails/Slides/baseData'
import { useFormik } from 'formik'
import useToggle from 'hooks/useToggle'
import { useEffect, useMemo, useState } from 'react'
import { useDropzone } from 'react-dropzone'
import { useDispatch, useSelector } from 'react-redux'
import api from 'services/api'
import { cropImage } from 'services/image'
import notify from 'services/notify'
import { getCompanyStructure } from 'store/companyStructure/actions'
import {
  BlogCategory,
  BlogItem,
  CheckboxGroup,
  FileWithPreview,
  GlossaryItem,
  ReduxState,
} from 'types'
import * as Yup from 'yup'

import FormView, { CategoryCheckboxes, CategoryItem } from './form'
import styles from './styles.module.scss'

export enum ArticleTab {
  ARTICLE = 'Neue Artikel',
  COMPANY_INFORMATION = 'Neue Unternehmensinformation',
}

// FIXME: required fields when back-end will be ready
const ArticleFormSchema = Yup.object().shape({
  title: Yup.string()
    .max(50, 'Titel darf höchstens 50 Zeichen lang sein')
    .required('Erforderlich'),
  summary: Yup.string()
    .max(100, 'Vorschautext darf höchstens 100 Zeichen lang sein')
    .required('Erforderlich'),
  body: Yup.string().required('Erforderlich').not(['<p></p>'], 'Erforderlich'),
  category: Yup.array()
    .of(Yup.number())
    .min(
      1,
      'Um ein Artikel zu erstellen müssen Sie mindestens eine Kategorie auswählen',
    )
    .default([]),
  images: Yup.array().of(Yup.object()).default([]),
  projectGroup: Yup.array().of(Yup.string()).default([]),
})

const CompanyInfoFormSchema = Yup.object().shape({
  title: Yup.string()
    .max(50, 'Titel darf höchstens 50 Zeichen lang sein')
    .required('Erforderlich'),
  summary: Yup.string()
    .max(100, 'Vorschautext darf höchstens 100 Zeichen lang sein')
    .default(''),
  body: Yup.string().required('Erforderlich'),
  images: Yup.array().of(Yup.object()).default([]),
})

export enum CheckboxTypes {
  category = 'category',
  projectGroup = 'projectGroup',
}

const baseArticleInitialValues = {
  title: '',
  summary: '',
  body: '',
  mainImage: {} as FileWithPreview,
  images: [] as FileWithPreview[],
}

export const articleInitialValues = {
  ...baseArticleInitialValues,
  selectAll: [] as string[],
  category: [] as string[],
  projectGroup: [] as string[],
}

export const companyInfoInitialValues = {
  ...baseArticleInitialValues,
}

export type BaseArticleFormValues = typeof baseArticleInitialValues
export type ArticleFormValues = typeof articleInitialValues
export type EditArticle = Omit<
  ArticleFormValues,
  'category' | 'projectGroup'
> & {
  category: number[]
  projectGroup: number[]
}
export type CompanyInfoFormValues = typeof companyInfoInitialValues

type Vocabulary = keyof typeof vocabularies

interface Glossary {
  categories: GlossaryItem<Vocabulary>[]
}

const validateImage = (file: File) => {
  if (!['image/png', 'image/jpeg'].includes(file.type)) {
    notify(
      'Format wird nicht unterstützt, bitte laden Sie nur PNG oder JPG/JPEG Bilder hoch',
    )
    return false
  }

  if (file.size > 10_000_000) {
    notify('Bitte laden Sie ein Bild hoch, was kleiner als 10MB ist')
    return false
  }

  return true
}

const uploadImage = async (file: File) => {
  const { data } = await api.post(
    '/file/upload/node/blog_post/field_images',
    file,
    {
      headers: {
        'Content-Type': 'application/octet-stream',
        'Content-Disposition': `file; filename="${file.name}"`,
      },
    },
  )

  return data.fid[0].value
}

const enhanceFiles = async (files: (FileWithPreview | undefined)[]) =>
  (
    await Promise.all(
      files.filter(Boolean).map(async (image) => {
        if (image.fid) {
          return image
        }
        try {
          const id = await uploadImage(image)
          return Object.assign(image, {
            id,
          }) as FileWithPreview
        } catch (error) {
          notify(error)
          return undefined
        }
      }),
    )
  ).filter((image): image is FileWithPreview =>
    image?.id ? Boolean(image?.id) : Boolean(image?.fid),
  )

interface FormProps {
  initialData?: ArticleFormValues | CompanyInfoFormValues
  categories?: BlogCategory[]
  tabs: string[]
  post?: BlogItem | null
  onCancel(): void
  onArticleSubmit(values: EditArticle): void
  onCompanyInfoSubmit(values: CompanyInfoFormValues): void
}

const ArticleForm: React.FC<FormProps> = ({
  initialData,
  tabs,
  post,
  onArticleSubmit,
  onCompanyInfoSubmit,
  onCancel,
}) => {
  const [expanded, setExpanded] = useState<string[]>([])
  const [expandedCategory, setExpandedCategory] = useState<CategoryItem | null>(
    null,
  )
  const [mainImage, setMainImage] = useState<FileWithPreview>() // Original Image
  const [images, setImages] = useState<FileWithPreview[]>([])
  const [activeTab, setActiveTab] = useState(tabs[0])
  const [glossaryItems, setGlossaryItems] = useState<
    GlossaryItem<Vocabulary>[]
  >([])
  const imageCropModalToggle = useToggle()

  const { data: companyStructure } = useSelector(
    (state: ReduxState) => state.companyStructure,
  )

  const postValues = useMemo(() => {
    if (post) {
      const postImages = (post.images.map((item) => ({
        ...item,
        preview: item.url,
      })) as unknown) as FileWithPreview[]
      setImages(postImages.slice(1))
      return {
        selectAll: [],
        title: post.title,
        summary: post.summary as string,
        body: post.body,
        nid: post.nid,
        mainImage: ({
          ...(post.images?.[0] || ['/']),
          preview: post.images[0]?.url || '',
        } as unknown) as FileWithPreview,
        images: postImages,
        category: post.category.map((item) => String(item)),
        projectGroup: post.project_group.map((item) => String(item)),
      }
    }
    return articleInitialValues
  }, [post, articleInitialValues])

  const dispatch = useDispatch()

  const articleFormik = useFormik({
    initialValues: postValues,
    onSubmit: async (values, helpers) => {
      try {
        helpers.resetForm()
        const allImages = () => [mainImage, ...images]
        const imageData = await enhanceFiles(allImages().filter(Boolean))
        onArticleSubmit({
          ...(values as ArticleFormValues),
          category: values.category.map((c) => Number(c)),
          projectGroup: values.projectGroup.map((c) => Number(c)),
          images: imageData,
        })
      } catch (error) {
        notify(error)
      }
    },
    validationSchema: ArticleFormSchema,
  })

  const companyFormik = useFormik({
    initialValues: companyInfoInitialValues,
    onSubmit: async (values, helpers) => {
      try {
        helpers.resetForm()
        const allImages = [values.mainImage, ...images]
        const imageData = await enhanceFiles(allImages.filter(Boolean))

        onCompanyInfoSubmit({
          ...values,
          images: imageData,
        })
      } catch (error) {
        notify(error)
      }
    },
    validationSchema: CompanyInfoFormSchema,
  })

  const selectedFormik =
    activeTab === ArticleTab.ARTICLE ? articleFormik : companyFormik

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    maxFiles: 1,
    onDrop: async (acceptedFiles) => {
      const file = acceptedFiles[0]
      if (isUTF8(file.name)) {
        if (file.size < 2097152) {
          try {
            if (!file || !validateImage(file)) return
            const image = Object.assign(file, {
              preview: URL.createObjectURL(file),
            })

            const cropped = await cropImage(image)

            selectedFormik.setFieldValue('mainImage', cropped)
            setMainImage(image)
          } catch (error) {
            notify(error)
          }
        } else {
          notify(
            'Die Größe der Datei sollte nicht mehr als 2Mb betragen.',
            'info',
          )
        }
      } else {
        notify(
          'Der Titel von dem aktuellen Anhang kann keine Umlaute und Sonderzeichen enthalten',
          'error',
        )
      }
    },
  })

  const {
    getRootProps: multipleProps,
    getInputProps: multipleInputProps,
    isDragActive: multipleDragActive,
  } = useDropzone({
    maxFiles: 10,
    onDrop: (acceptedFiles) =>
      acceptedFiles.forEach(async (file) => {
        if (isUTF8(file.name)) {
          if (file.size < 2097152) {
            try {
              if (!file || !validateImage(file) || images.length > 10) return
              const multiImage = Object.assign(file, {
                preview: URL.createObjectURL(file),
              })
              setImages((prev) => [...prev, multiImage])
            } catch (error) {
              notify(error)
            }
          } else {
            notify(
              'Die Größe der Datei sollte nicht mehr als 2Mb betragen.',
              'info',
            )
          }
        } else {
          notify(
            `Der Titel von dem aktuellen Anhang: ${file.name} kann keine Umlaute und Sonderzeichen enthalten `,
            'error',
          )
        }
      }),
  })

  useEffect(() => {
    const fetchGlossary = async () => {
      const {
        data: { categories },
      }: AxiosResponse<Glossary> = await api.get(
        '/glossary/common/node/blog_post?_format=json',
      )
      setGlossaryItems(
        categories.filter(
          (category) => category.vid && category.vid in vocabularies,
        ),
      )
    }
    fetchGlossary()
    Object.keys(companyClustersMap).map((cluster) =>
      dispatch(getCompanyStructure(cluster as CompanyCluster)),
    )
  }, [])

  // TODO refactor
  useEffect(() => {
    initialData &&
      articleFormik.setValues({
        ...articleFormik.values,
        ...initialData,
        // mainImageUrl: initialData?.mainImageUrl || '',
      })
  }, [initialData])

  const groupsCheckboxes: CheckboxGroup[] = [
    {
      title: 'Verwaltung',
      name: 'category',
      options: companyStructure.verwaltung.map((g) => ({
        label: g.name,
        value: g.tid,
      })),
    },
    {
      title: 'Projektgruppen',
      name: 'projectGroup',
      options: companyStructure.projektgruppen.map((g) => ({
        label: g.name,
        value: g.tid,
      })),
    },
    {
      title: 'Arbeitsgruppen',
      name: 'category',
      options: companyStructure.arbeitsgruppen.map((g) => ({
        label: g.name,
        value: g.tid,
      })),
    },
    {
      title: 'Kompetenzbereiche',
      name: 'category',
      options: companyStructure.kompetenzbereiche.map((g) => ({
        label: g.name,
        value: g.tid,
      })),
    },
    {
      title: 'Standorte',
      name: 'category',
      options: companyStructure.standorte.map((g) => ({
        label: g.name,
        value: g.tid,
      })),
    },
    {
      title: 'Kompetenzzentrum',
      name: 'category',
      options: companyStructure.kompetenzzentrum.map((g) => ({
        label: g.name,
        value: g.tid,
      })),
    },
  ]

  const categoryCheckboxes: CategoryCheckboxes = useMemo(
    () => ({
      title: 'Kategorien',
      name: 'category',
      items: (Object.keys(vocabularies) as Vocabulary[]).map((key) => ({
        label: vocabularies[key],
        subitems: glossaryItems
          .filter((item) => item.vid === key)
          .map((item) => ({
            label: item.name,
            value: item.tid,
          })),
      })),
    }),
    [vocabularies, glossaryItems],
  )

  const formProps = {
    groupsCheckboxes,
    categoryCheckboxes,
    getRootProps,
    getInputProps,
    isDragActive,
    multipleProps,
    multipleInputProps,
    multipleDragActive,
    images,
    expanded,
    activeTab,
    expandedCategory,
    openCropping: imageCropModalToggle.on,
    setActiveTab,
    setExpandedCategory,
    removeExpanded: (str: string) => {
      const index = expanded.indexOf(str)
      index >= 0 &&
        setExpanded([...expanded.slice(0, index), ...expanded.slice(index + 1)])
    },
    addExpanded: (str: string) =>
      expanded.includes(str) || setExpanded([...expanded, str]),
  }

  return (
    <Form
      handleSubmit={(e) => {
        selectedFormik.validateForm().then((errors) =>
          Object.keys(errors)
            .map((key) => groupsCheckboxes.find((group) => group.name === key))
            .map((group) => group && formProps.addExpanded(group.title)),
        )
        selectedFormik.handleSubmit(e)
      }}
      title={activeTab}
      onCancel={onCancel}
      header={
        <NavigationBar
          items={tabs}
          selectedItem={activeTab}
          setSelectedItem={(tab) => setActiveTab(tab)}
          className={styles.formTabs}
        />
      }
    >
      {activeTab === ArticleTab.ARTICLE ? (
        <FormView formik={articleFormik} {...formProps} />
      ) : (
        <FormView formik={companyFormik} {...formProps} />
      )}
      {mainImage && (
        <ImageCropModal
          isOpen={imageCropModalToggle.value}
          onClose={imageCropModalToggle.off}
          originalImage={mainImage}
          aspect={16 / 9}
          setCroppedImage={(image) =>
            selectedFormik.setFieldValue('mainImage', image)
          }
        />
      )}
    </Form>
  )
}

export default ArticleForm
