import { Employees, Error } from 'types/index'

enum ActionType {
  GET_EMPLOYEES_BEGIN = '[ives] GET_EMPLOYEES_BEGIN',
  GET_EMPLOYEES_SUCCESS = '[ives] GET_EMPLOYEES_SUCCESS',
  GET_EMPLOYEES_ERROR = '[ives] GET_EMPLOYEES_ERROR',
  GET_EMPLOYEES_RECORD_BEGIN = '[ives] GET_EMPLOYEES_RECORD_BEGIN',
  GET_EMPLOYEES_RECORD_SUCCESS = '[ives] GET_EMPLOYEES_RECORD_SUCCESS',
  GET_EMPLOYEES_RECORD_ERROR = '[ives] GET_EMPLOYEES_RECORD_ERROR',
  UPDATE_EMPLOYEES_RECORD_BEGIN = '[ives] UPDATE_EMPLOYEES_RECORD_BEGIN',
  UPDATE_EMPLOYEES_RECORD_SUCCESS = '[ives] UPDATE_EMPLOYEES_RECORD_SUCCESS',
  UPDATE_EMPLOYEES_RECORD_ERROR = '[ives] UPDATE_EMPLOYEES_RECORD_ERROR',
  EMPLOYEES_PAGE_LOADED = '[ives] EMPLOYEES_PAGE_LOADED',
  EDIT_COMMENT_BEGIN = '[ives] EDIT_COMMENT_BEGIN',
  EDIT_COMMENT_SUCCESS = '[ives] EDIT_COMMENT_SUCCESS',
  EDIT_COMMENT_ERROR = '[ives] EDIT_COMMENT_ERROR',
  DELETE_NOTE_SUCCESS = '[ives] DELETE_NOTE_ERROR',
}

export interface GetEmployeesBeginAction {
  type: ActionType.GET_EMPLOYEES_BEGIN
}

export interface GetEmployeesSuccessAction {
  type: ActionType.GET_EMPLOYEES_SUCCESS
  payload: Employees
}

export interface GetEmployeesRecord {
  type: ActionType.GET_EMPLOYEES_RECORD_SUCCESS
  payload: Employees[]
}

export interface GetEmployeesErrorAction {
  type: ActionType.GET_EMPLOYEES_ERROR
  payload: Error
}

export interface EmployeesAddedAction {
  type: ActionType.EMPLOYEES_PAGE_LOADED
  payload: Employees
}

export interface EditCommentBeginAction {
  type: ActionType.EDIT_COMMENT_BEGIN
}

export interface EditCommentSuccessAction {
  type: ActionType.EDIT_COMMENT_SUCCESS
}

export interface EditCommentErrorAction {
  type: ActionType.EDIT_COMMENT_ERROR
  payload: Error
}
interface NoteDeletedAction {
  type: ActionType.DELETE_NOTE_SUCCESS
  payload: number
}
export type Action =
  | GetEmployeesBeginAction
  | GetEmployeesSuccessAction
  | GetEmployeesRecord
  | GetEmployeesErrorAction
  | EmployeesAddedAction
  | EditCommentBeginAction
  | EditCommentSuccessAction
  | EditCommentErrorAction
  | NoteDeletedAction

export default ActionType
