import { BackendMenuItem, BackendSettings, Error, MenuItem } from 'types/index'

enum ActionType {
  GET_BACKEND_SETTINGS_BEGIN = '[app] GET_BACKEND_SETTINGS_BEGIN',
  GET_BACKEND_SETTINGS_SUCCESS = '[app] GET_BACKEND_SETTINGS_SUCCESS',
  GET_BACKEND_SETTINGS_ERROR = '[app] GET_BACKEND_SETTINGS_ERROR',
  GET_LEFT_MENU_ITEMS_BEGIN = '[app] GET_LEFT_MENU_ITEMS_BEGIN',
  GET_LEFT_MENU_ITEMS_SUCCESS = '[app] GET_LEFT_MENU_ITEMS_SUCCESS',
  GET_LEFT_MENU_ITEMS_ERROR = '[app] GET_LEFT_MENU_ITEMS_ERROR',
  SELECT_LEFT_MENU_ITEM = '[dashboard] SELECT_LEFT_MENU_ITEM',
}

export interface GetBackendSettingsBeginAction {
  type: ActionType.GET_BACKEND_SETTINGS_BEGIN
}

export interface GetBackendSettingsSuccessAction {
  type: ActionType.GET_BACKEND_SETTINGS_SUCCESS
  payload: BackendSettings
}

export interface GetBackendSettingsErrorAction {
  type: ActionType.GET_BACKEND_SETTINGS_ERROR
  payload: Error
}

export interface GetLeftMenuItemsBeginAction {
  type: ActionType.GET_LEFT_MENU_ITEMS_BEGIN
}

export interface GetLeftMenuItemsSuccessAction {
  type: ActionType.GET_LEFT_MENU_ITEMS_SUCCESS
  payload: BackendMenuItem[]
}

export interface GetLeftMenuItemsErrorAction {
  type: ActionType.GET_LEFT_MENU_ITEMS_ERROR
  payload: Error
}

export interface SelectLeftMenuItemAction {
  type: ActionType.SELECT_LEFT_MENU_ITEM
  payload: MenuItem[]
}

export type Action =
  | GetBackendSettingsBeginAction
  | GetBackendSettingsSuccessAction
  | GetBackendSettingsErrorAction
  | GetLeftMenuItemsBeginAction
  | GetLeftMenuItemsSuccessAction
  | GetLeftMenuItemsErrorAction
  | SelectLeftMenuItemAction

export default ActionType
