import cn from 'classnames'
// import Footer from 'common/Footer'
import Header from 'common/Header'
import LeftMenu from 'common/LeftMenu'
import RightMenu from 'common/RightMenu'
import Backdrop from 'components/Backdrop'
import useToggle from 'hooks/useToggle'
import { OptionsType } from 'react-select'
import { HeaderOptions, SelectOption } from 'types'

import styles from './styles.module.scss'

interface LayoutProps {
  children: React.ReactNode
  contentClassName?: string
  hasTabs?: boolean
  headerProps?: HeaderOptions
  searchValue?: string
  scrollableRef?: React.RefObject<HTMLDivElement>
  onSearchChange?: (value: string) => void
  searchDropdownOptions?: OptionsType<SelectOption>
}

const Layout = ({
  children,
  contentClassName,
  hasTabs,
  headerProps,
  searchValue,
  scrollableRef,
  onSearchChange,
  searchDropdownOptions,
}: LayoutProps) => {
  const leftMenuToggle = useToggle()

  return (
    <div className={styles.layout}>
      <Header
        options={headerProps}
        searchValue={searchValue}
        onSearchChange={onSearchChange}
        searchDropdownOptions={searchDropdownOptions}
      />
      <LeftMenu
        toggleMenu={leftMenuToggle.toggle}
        expanded={leftMenuToggle.value}
      />
      <div className={cn(styles.content, contentClassName)} ref={scrollableRef}>
        {children}
        <Backdrop onClick={leftMenuToggle.off} show={leftMenuToggle.value} />
      </div>
      <RightMenu hasTabs={hasTabs} />
      {/* temporary commented */}
      {/* <Footer dragWidgetAllow={hasTabs} /> */}
    </div>
  )
}

export default Layout
