import { ReactComponent as Upload } from 'assets/icons/upload.svg'
import cn from 'classnames'
import DatePicker from 'components/inputs/DatePicker'
import FieldArray from 'components/inputs/FieldArray'
import HtmlEditor from 'components/inputs/HtmlEditor'
import NumberInput from 'components/inputs/NumberInput'
import Select from 'components/inputs/Select'
import TextInput from 'components/inputs/TextInput'
import { FormikProps } from 'formik'
import { DropzoneInputProps } from 'react-dropzone'
import { FileWithPreview } from 'types'

import { FormOptions, Step1 as Props } from '../formData'
import styles from '../styles.module.scss'

interface FormProps {
  formik: FormikProps<Props>
  photo?: FileWithPreview
  pictureUrl?: string
  isDragActive: boolean
  options: FormOptions
  getRootProps(props?: DropzoneInputProps): DropzoneInputProps
  getInputProps(props?: DropzoneInputProps): DropzoneInputProps
}

const Step1 = ({
  formik,
  photo,
  pictureUrl,
  getRootProps,
  getInputProps,
  isDragActive,
  options,
}: FormProps) => (
  <>
    <h3 className={styles.stepTitle}>{'Mitarbeiterfoto'}</h3>
    <div
      {...getRootProps({
        className: cn(styles.dndContainer, { [styles.active]: isDragActive }),
      })}
    >
      {pictureUrl && !photo && <img src={pictureUrl} alt="avatar" />}
      {photo && <img src={photo.preview} alt="avatar" />}
      <input {...getInputProps()} />
      {'Zieh das Foto rein'} <br />
      {'oder'} <br />
      <button type="button" className={styles.upload}>
        {'Hochladen'}
        <Upload />
      </button>
    </div>
    <h4 className={styles.stepTitle}>{'Vorname'}</h4>
    <TextInput name="first_name" label="Vorname" formik={formik} />
    <TextInput name="last_name" label="Nachname" formik={formik} />
    <DatePicker
      name="birth_date"
      placeholder="Geburtsdatum"
      formik={formik}
      disabled
    />
    <TextInput
      name="address_address"
      label="Straße und Hausnummer"
      formik={formik}
    />
    <TextInput name="address_postalcode" label="PLZ" formik={formik} />
    <TextInput name="address_city" label="Stadt" formik={formik} />
    <Select
      name="project_group"
      options={options.projectGroups}
      placeholder="Projektgruppe"
      label="Projektgruppe"
      formik={formik}
      isDisabled
    />
    <DatePicker
      name="entry_date"
      placeholder="Einstiegsdatum"
      formik={formik}
      disabled
    />
    <DatePicker
      name="exit_date"
      placeholder="Austrittsdatum"
      formik={formik}
      disabled
    />
    <TextInput
      name="contact_email"
      label="E-mail (privat)"
      disabled
      formik={formik}
    />
    <NumberInput
      name="contact_phone"
      label="Telefon (privat)"
      disabled
      prefix="+49 "
      formik={formik}
      format="+49 ##############"
    />
    <NumberInput
      name="contact_mobile"
      label="Handy (privat)"
      prefix="+49 "
      disabled
      formik={formik}
      format="+49 ##############"
    />

    <Select
      name="location"
      options={options.locationOptions}
      label="Standort"
      formik={formik}
      isDisabled
    />
    <Select
      name="academic_degrees"
      options={options.academicOptions}
      label="Abschluss"
      formik={formik}
      isMulti
    />
    <Select
      name="company_function"
      options={options.companyFunctionOptions}
      label="Funktion im Unternehmen"
      formik={formik}
      isMulti
    />
    <HtmlEditor
      name="special_degrees"
      label="Kammereintragungen / besondere Abschlüsse"
      formik={formik}
      defaultValue={formik.values.special_degrees}
    />
    <TextInput
      name="corporate_email"
      label="E-mail Arbeit"
      disabled
      formik={formik}
    />
    <TextInput name="corporate_phone" label="Telefon Arbeit" formik={formik} />
    <TextInput name="corporate_mobile" label="Handy Arbeit" formik={formik} />
    <FieldArray
      name="children"
      formik={formik}
      label="Kind"
      isGroup
      renderField={(defaultValue, index) => (
        <div key={`kid-${index}`}>
          <TextInput
            name={`children[${index}].name`}
            label={`Name Kind ${index + 1}`}
            value={defaultValue.name || ''}
            formik={formik}
          />
          <DatePicker
            name={`children[${index}].date`}
            placeholder={`Geburtstag kind ${index + 1}`}
            value={defaultValue.date || ''}
            formik={formik}
          />
        </div>
      )}
    />
  </>
)

export default Step1
