import FullscreenLoader from 'common/FullscreenLoader'
import Button from 'components/Button'
import { EmployeeHolidaysInner } from 'types'

import styles from './styles.module.scss'

export type HolidayInfoClickMethod = 'delete' | 'mutate' | 'edit'
interface Props {
  holiday: EmployeeHolidaysInner
  onButtonClick(arg: HolidayInfoClickMethod): void
  isLoading: boolean
}
const HolidayInfo: React.FC<Props> = ({
  holiday,
  onButtonClick,
  isLoading,
}) => (
  <div className={styles.holidaysInfoWrapper}>
    <h2 className={styles.holidaysInfoHeading}>{'Angedachter Urlaub'}</h2>
    <p className={styles.holidaysInfoHeadingSub}>
      {'würde in weniger als 7 Tage beginnen'}
    </p>
    <div className={styles.contentContainer}>
      <div className={styles.contentLabelsContainer}>
        <p className={styles.contentLabels}>{'Von: '}</p>
        {holiday.from_date}
      </div>
      <div className={styles.contentLabelsContainer}>
        <p className={styles.contentLabels}>{'Bis: '}</p>
        {holiday.to_date}
      </div>
      {holiday.substitutes.length > 0 && (
        <div className={styles.contentLabelsContainer}>
          <p className={styles.contentLabels}>{'Vertretung'}</p>
          {holiday.substitutes.map((sub) => sub.employee_name).join(', ')}
        </div>
      )}
    </div>
    <div className={styles.buttonContainer}>
      <Button
        variant="fulfilled"
        className={styles.button}
        onClick={() => onButtonClick('edit')}
      >
        {'Angedachter Urlaub verschieben'}
      </Button>
      <Button
        variant="fulfilled"
        className={styles.button}
        onClick={() => onButtonClick('mutate')}
      >
        {'Urlaubsantrag starten'}
      </Button>
      <Button
        variant="fulfilled"
        color="orange"
        className={styles.button}
        type="reset"
        onClick={() => onButtonClick('delete')}
      >
        {'Angedachter Urlaub löschen'}
      </Button>
    </div>
    <FullscreenLoader isLoading={isLoading} />
  </div>
)

export default HolidayInfo
