import { Customers, Error } from 'types/index'

import ActionType, { Action } from './types'

export interface IState {
  isLoading: boolean
  error: Error | null
  customers: Customers | null
}

const initialState: IState = {
  customers: null,
  isLoading: false,
  error: null,
}

const reducer = (state = initialState, action: Action): IState => {
  switch (action.type) {
    case ActionType.CREATE_CUSTOMER_BEGIN:
    case ActionType.UPDATE_CUSTOMER_BEGIN:
    case ActionType.GET_CUSTOMER_RECORD_BEGIN:
    case ActionType.GET_CUSTOMERS_BEGIN: {
      return {
        ...state,
        isLoading: true,
      }
    }
    case ActionType.GET_CUSTOMERS_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
        customers: action.payload,
      }
    }
    case ActionType.CREATE_CUSTOMER_SUCCESS:
    case ActionType.UPDATE_CUSTOMER_SUCCESS:
    case ActionType.GET_CUSTOMER_RECORD_SUCCESS: {
      return {
        ...state,
        isLoading: false,
        error: '',
      }
    }
    case ActionType.CREATE_CUSTOMER_ERROR:
    case ActionType.UPDATE_CUSTOMER_ERROR:
    case ActionType.GET_CUSTOMER_RECORD_ERROR:
    case ActionType.GET_CUSTOMERS_ERROR: {
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      }
    }
    default:
      return state
  }
}

export default reducer
