import FullscreenLoader from 'common/FullscreenLoader'
import Layout from 'common/Layout'
import Modal from 'common/Modal'
import LinkItem from 'components/LinkItem'
import { CompanyCluster, companyClustersMap } from 'constants/companyStructure'
import { UserAccess } from 'constants/index'
import Routes from 'constants/routes'
import useToggle from 'hooks/useToggle'
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import notify from 'services/notify'
import { getCompanyStructure } from 'store/companyStructure/actions'
import {
  getEmployeeRecord,
  // getAllEmployees,
  getEmployees,
  updateEmployee,
} from 'store/ives/employees/actions'
import { getHolidayEmployees } from 'store/ives/holidays/actions'
import { Employee, EmployeeHolidays, ReduxState } from 'types'
import * as yup from 'yup'

import Form from '../EmployeeForm'
import {
  FormOptions,
  getFormSelectOptions,
  Step1,
  Step2,
  Step3,
  Step4,
  Step5,
} from '../EmployeeForm/formData'
import styles from './styles.module.scss'
import ContentView from './view'

interface EmployeeDetailsProps {
  globalFilter?: string
}

export const initialNotesValues = {
  notes_main: '',
  notes_contract: '',
  notes_absence: '',
}

export type NotesValues = typeof initialNotesValues

export const FormSchema: yup.SchemaOf<NotesValues> = yup.object().shape({
  notes_main: yup.string().notRequired().default(''),
  notes_contract: yup.string().notRequired().default(''),
  notes_absence: yup.string().notRequired().default(''),
})

const EmployeeDetails = ({ globalFilter }: EmployeeDetailsProps) => {
  const [isLoading] = useState(false)
  const editModalToggle = useToggle()
  const dispatch = useDispatch()
  const access = useSelector(({ user }: ReduxState) => user.user?.access)
  const [employeeRecord, setEmployeeRecord] = useState<Employee>()
  const [holidayData, setHolidayData] = useState<
    EmployeeHolidays[] | undefined
  >()
  const { employees } = useSelector(({ ives }: ReduxState) => ives.employees)
  const [options, setOptions] = useState<FormOptions>({
    projectGroups: [],
    familyOptions: [],
    companyFunctionOptions: [],
    locationOptions: [],
    academicOptions: [],
    hourlyModelOptions: [],
    competenceFunctionOptions: [],
    caOptions: [],
    crossCompetenceOptions: [],
    crossGroupOptions: [],
    workingGroupOptions: [],
    workingFunctionOptions: [],
    workingDaysOptions: [],
    leaveOptions: [],
    competenceCenterOptions: [],
  })

  const IDquery = new URLSearchParams(window.location.search)
  const ID: string | null = IDquery.get('id')
  const { data } = useSelector((state: ReduxState) => state.companyStructure)
  const groupMember = data
  const [initialPage, setInitialPage] = useState(0)
  const currentEmployee = employees?.rows?.find(
    (employee) => employee.nid === ID,
  )
  useEffect(() => {
    dispatch(
      getEmployeeRecord(ID as string, (record: Employee) =>
        setEmployeeRecord(record),
      ),
    )
    if (!currentEmployee && !employeeRecord) {
      setInitialPage(initialPage + 1)
      dispatch(getEmployees({ page: initialPage, query: globalFilter }))
    } else {
      setInitialPage(0)
    }
  }, [initialPage])
  useEffect(() => {
    getFormSelectOptions().then((res: FormOptions) => {
      setOptions(res)
    })

    Object.keys(companyClustersMap).map(
      (cluster) =>
        !data[cluster as CompanyCluster].length &&
        dispatch(getCompanyStructure(cluster as CompanyCluster)),
    )
    dispatch(getHolidayEmployees({ query: globalFilter }, setHolidayData))
  }, [])

  // find the current employee in the holiday data (can't match the ID since the types have some differences)
  const getCurrentHolidayData = holidayData?.filter(
    (holiday) => holiday.name === currentEmployee?.full_name,
  )

  const onEditClick = () => {
    if (access && access[UserAccess.editEmployeeEntities]) {
      if (employeeRecord?.nid) {
        dispatch(
          getEmployeeRecord(employeeRecord?.nid, (record: Employee) => {
            setEmployeeRecord(record)
            editModalToggle.on()
          }),
        )
      }
    }
  }
  const onEditEmployeeSubmit = (
    values: Step1 | Step2 | Step3 | Step4 | Step5,
    finished: boolean,
  ) => {
    dispatch(
      updateEmployee({
        // allowences: [],
        // leave: [],
        // working_days: [],
        // leave: [],
        // working_days: [],
        // employee_sick: [],
        nid: employeeRecord?.nid || '',
        ...values,
      }),
    )
    if (finished) {
      dispatch(getEmployees({ query: globalFilter }))
      dispatch(
        getEmployeeRecord(ID as string, (record: Employee) =>
          setEmployeeRecord(record),
        ),
      )
      notify('Mitarbeiterdaten wurden erfolgreich aktualisiert', 'success')
      editModalToggle.off()
    } else {
      notify('Einträge wurden erfolgreich aktualisiert', 'success')
    }
  }

  const onCancel = () => {
    editModalToggle.off()
    dispatch(getEmployees({ query: globalFilter }))
    dispatch(
      getEmployeeRecord(ID as string, (record: Employee) =>
        setEmployeeRecord(record),
      ),
    )
  }
  const onEditEmployeeClose = () => {
    editModalToggle.off()
  }

  return (
    <Layout headerProps={{ withoutSearch: true }}>
      <div className={styles.header}>
        <h2 className={styles.title}>
          {employeeRecord?.full_name || 'Mitarbeiter'}
        </h2>
        <div className={styles.linkWrap}>
          <LinkItem onClick={() => window.open(Routes.employees, '_self')}>
            {'Mitarbeiterübersicht'}
          </LinkItem>
          <LinkItem onClick={() => onEditClick()}>
            {'Stammdaten bearbeiten'}
          </LinkItem>
        </div>
      </div>
      {employeeRecord && (
        <ContentView
          currentRecord={employeeRecord}
          employeeTitle={currentEmployee}
          groupMember={groupMember}
          holidayData={getCurrentHolidayData}
        />
      )}
      <Modal
        isOpen={editModalToggle.value}
        onRequestClose={onEditEmployeeClose}
      >
        <Form
          options={options}
          onSubmit={onEditEmployeeSubmit}
          initialData={employeeRecord}
          onCancel={onCancel}
        />
      </Modal>
      <FullscreenLoader isLoading={isLoading} />
    </Layout>
  )
}

export default EmployeeDetails
