import Table from 'common/Table'
import Button from 'components/Button'
import GlobalSearch from 'components/tableComponents/GlobalSearch'
import { Column, Row } from 'react-table'

import { TableData } from '.'
import styles from './styles.module.scss'

interface ContentViewProps {
  columnsData: Column<TableData>[]
  tableData?: TableData[]
  tableWidth: number
  tableHeight: number
  globalFilter: string | undefined
  setGlobalFilter: (filterValue: string | undefined) => void
  onCancel: () => void
  onSubmit: () => void
  onRowSelect: (row?: Row<TableData>) => void
  onRowDbClick: (row?: Row<TableData>) => void
  onNewCustomerClick: () => void
  onEditCustomerClick: () => void
  hasNextPage?: boolean
  isNextPageLoading: boolean
  loadNextPage: (startIndex: number, stopIndex: number) => Promise<any> | null
}

const ContentView = ({
  columnsData,
  tableData,
  tableWidth,
  tableHeight,
  globalFilter,
  setGlobalFilter,
  onCancel,
  onSubmit,
  onRowSelect,
  onRowDbClick,
  onNewCustomerClick,
  onEditCustomerClick,
  hasNextPage,
  isNextPageLoading,
  loadNextPage,
}: ContentViewProps) => (
  <div>
    <div className={styles.title}>{'Geschäftspartnerauswahl'}</div>
    <div className={styles.filterAndTableWrapper}>
      <div className={styles.filterWrapper}>
        <GlobalSearch
          globalFilter={globalFilter}
          setGlobalFilter={setGlobalFilter}
          containerClassName={styles.search}
        />
      </div>
      <Table
        tableContainerClassName={styles.table}
        columnsData={columnsData}
        tableData={tableData}
        tableWidth={tableWidth}
        tableHeight={tableHeight}
        onRowSelect={onRowSelect}
        onRowDbClick={onRowDbClick}
        hasNextPage={hasNextPage}
        isNextPageLoading={isNextPageLoading}
        loadNextPage={loadNextPage}
      />
    </div>
    <div className={styles.buttonsContainer}>
      <div>
        <Button className={styles.blueButton} onClick={onNewCustomerClick}>
          {'Neu'}
        </Button>
        <Button className={styles.blueButton} onClick={onEditCustomerClick}>
          {'Bearbeiten'}
        </Button>
      </div>
      <div>
        <Button className={styles.blueButton} onClick={onCancel}>
          {'Abbrechen'}
        </Button>
        <Button className={styles.orangeButton} onClick={onSubmit}>
          {'OK'}
        </Button>
      </div>
    </div>
  </div>
)

export default ContentView
