import { ReactComponent as ArrowOrange } from 'assets/icons/arrowNextOrange.svg'
import { ReactComponent as ReadMark } from 'assets/icons/read.svg'
import OpenLink from 'components/OpenLink'
import { stringToFullDate } from 'services/date'

import shared from '../shared.module.scss'

export interface WidgetCarouselProps {
  title: string
  date: string | Date
  body?: string
  className?: string
  read?: string
  onClick?: () => void
}

/**
 * Default Horizontal widget carousel item
 */
const WidgetCarouselItem = ({
  title,
  body,
  date,
  read,
  className,
  onClick,
}: WidgetCarouselProps) => (
  <div className={`${shared.contentItem} ${className}`}>
    <div className={shared.content}>
      <div>
        {read && read !== '0' && (
          <div className={shared.read}>
            <ReadMark />
          </div>
        )}
        <div className={shared.date}>{stringToFullDate(date)}</div>
        <div className={shared.title}>{title}</div>
        <div className={`${shared.bodyText} ${shared.small}`}>{body}</div>
      </div>
      <div className={shared.bottomControls}>
        {onClick ? (
          <button type="button" className={shared.more} onClick={onClick}>
            {'Info lesen'}
            <ArrowOrange />
          </button>
        ) : (
          <OpenLink />
        )}
      </div>
    </div>
  </div>
)

export default WidgetCarouselItem
