export const getHolidayEmployees = (
  { query, groupId }: { query?: string; groupId?: string },
  onSuccess?: (holidays: EmployeeHolidays[]) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_HOLIDAY_EMPLOYEES_BEGIN))
  const queryParam = query ? `&name=${query}` : ''
  const groupParam = groupId ? `&pg=${groupId}` : ''
  try {
    const holiday: AxiosResponse<ProjectGroupHolidays[]> = await api.get(
      `pages/employee/holiday?_format=json${queryParam}${groupParam}`,
    )
    const employees = holiday.data.flatMap(
      (item) => item.project_group.employees,
    )

    dispatch(action(ActionType.GET_HOLIDAY_EMPLOYEES_SUCCESS, employees))
    onSuccess?.(employees)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_HOLIDAY_EMPLOYEES_ERROR, error))
    onError?.(error)
  }
}

