import Checkbox from 'components/inputs/Checkbox'
import DateRange from 'components/inputs/DateRange'
import Select from 'components/inputs/Select'
import { Step3 as Props } from 'containers/Ives/Employees/EmployeeForm/formData'
import EmployeeSickFormSubstitution from 'containers/Ives/Employees/EmployeeForm/Steps/components/EmployeeSickFormSubstitution'
import { FormikProps } from 'formik'
import { useState } from 'react'
import { HolidaySubstitutes } from 'types'

import styles from '../../../styles.module.scss'

interface FormProps {
  formik: FormikProps<Props>
  // val: any
  employeeId: string | number
  index: number
}
const EmployeeSickForm = ({ formik, employeeId, index }: FormProps) => {
  const [checked, setChecked] = useState(
    formik.values.employee_sick[index].medical_certificate,
  )
  const removeSubstitution = (index: number, j: number) => {
    if (
      formik.values.employee_sick[index].substitutes &&
      formik.values.employee_sick[index].substitutes.length > 1
    ) {
      formik.setFieldValue(`employee_sick[${index}].substitutes`, [
        ...formik.values.employee_sick[index].substitutes.slice(0, j),
        ...formik.values.employee_sick[index].substitutes.slice(j + 1),
      ])
    }
  }
  const addSubstitution = (index: number) => {
    formik.setFieldValue(`employee_sick[${index}].substitutes`, [
      ...formik.values.employee_sick[index].substitutes,
      {} as HolidaySubstitutes,
    ])
  }
  return (
    <section>
      <Select
        name={`employee_sick[${index}].type`}
        key={`employee_sick-${index}.type`}
        placeholder="Art"
        isSearchable={false}
        options={[
          {
            label: `Mitarbeiterkrank`,
            value: `Mitarbeiterkrank`,
          },
          {
            label: `Kindkrank`,
            value: `Kindkrank`,
          },
        ]}
        value={formik.values.employee_sick[index].type}
        formik={formik}
      />
      <DateRange
        key={`employee_sick-${index}.period`}
        name={`employee_sick[${index}].period`}
        formik={formik}
        values={formik.values.employee_sick[index].period}
      />
      <p className={styles.label}>{'Nachweis erbracht ?'}</p>
      <Checkbox
        name={`employee_sick[${index}].medical_certificate`}
        checked={!!checked}
        theme="default"
        variant="round"
        onChange={(values) => {
          if (values.target.checked) {
            setChecked(true)
            formik.setFieldValue(
              `employee_sick[${index}].medical_certificate`,
              true,
            )
          }
        }}
      >
        {'Ja'}
      </Checkbox>
      <Checkbox
        name={`employee_sick[${index}].medical_certificate`}
        checked={!checked}
        theme="default"
        variant="round"
        onChange={(values) => {
          if (values.target.checked) {
            setChecked(false)
            formik.setFieldValue(
              `employee_sick[${index}].medical_certificate`,
              false,
            )
          }
        }}
      >
        {'Nein'}
      </Checkbox>
      {formik.values.employee_sick[index].substitutes?.map((item, indexJ) => (
        <EmployeeSickFormSubstitution
          key={`${index}-${indexJ}`}
          employeeId={employeeId}
          formik={formik}
          indexEmployee={index}
          index={indexJ}
          period={formik.values.employee_sick[index].period}
          removeSubstitution={() => removeSubstitution(index, indexJ)}
          addSubstitution={() => addSubstitution(index)}
        />
      ))}
    </section>
  )
}
export default EmployeeSickForm
