export interface FormOptions {
  projectGroups: SelectOption[]
  familyOptions: SelectOption[]
  leaveOptions: SelectOption[]
  locationOptions: SelectOption[]
  academicOptions: SelectOption[]
  hourlyModelOptions: SelectOption[]
  competenceFunctionOptions: SelectOption[]
  caOptions: SelectOption[]
  crossCompetenceOptions: SelectOption[]
  crossGroupOptions: SelectOption[]
  workingGroupOptions: SelectOption[]
  workingFunctionOptions: SelectOption[]
  companyFunctionOptions: GroupSelectOption[]
  workingDaysOptions: SelectOption[]
  competenceCenterOptions: SelectOption[]
  functionInCompetenceOptions: SelectOption[]
}

const selectOptionsFromArray = (arr: any[]): SelectOption[] =>
  arr.map((el: any) => ({
    value: el.tid,
    label: el.name,
  }))

const selectOptionsFromObject = (obj: {
  [key: string]: string
}): SelectOption[] => {
  const keys = Object.keys(obj)
  // return keys.map((el: any) => obj[el])
  return keys.map((el: any) => ({
    value: el,
    label: obj[el],
  }))
}
export const getFormSelectOptions = async (): Promise<FormOptions> => {
  const {
    data: {
      project_group,
      family_vacation: { family_vacation_type },
      company_function,
      location,
      academic_degrees,
      hourly_model,
      competence_areas: { ca_function, competence_area, cross_ca_function },
      competence_centers: { competence_center, function_in_the_competence },
      working_groups: {
        cross_group_function,
        working_group,
        working_group_function,
      },
    },
  } = await api.get('/glossary/common/employee/employee?_format=json')
  const projectGroups = selectOptionsFromArray(project_group)
  const competenceCenterOptions = selectOptionsFromArray(competence_center)
  const functionInCompetenceOptions = selectOptionsFromArray(
    function_in_the_competence,
  )
  const familyOptions = selectOptionsFromObject(family_vacation_type)
  const hourlyModelOptions = selectOptionsFromObject(hourly_model)
  const locationOptions = selectOptionsFromArray(location)
  const academicOptions = selectOptionsFromArray(academic_degrees)
  const competenceFunctionOptions = selectOptionsFromArray(ca_function)
  const caOptions = selectOptionsFromArray(competence_area)
  const crossCompetenceOptions = selectOptionsFromArray(cross_ca_function)
  const crossGroupOptions = selectOptionsFromArray(cross_group_function)
  const workingGroupOptions = selectOptionsFromArray(working_group)
  const workingFunctionOptions = selectOptionsFromArray(working_group_function)
  const companyFunctionOptions = company_function.map((el: any) => ({
    label: el.name,
    options: selectOptionsFromArray(el.children),
  }))
  const workingDaysOptions = [
    { value: 'Montag', label: 'Montag' },
    { value: 'Dienstag', label: 'Dienstag' },
    { value: 'Mittwoch', label: 'Mittwoch' },
    { value: 'Donnerstag', label: 'Donnerstag' },
    { value: 'Freitag', label: 'Freitag' },
  ]

  const leaveOptions = [
    {
      value: 'Mutterschutz',
      label: 'Mutterschutz',
    },
    {
      value: 'Elternzeit',
      label: 'Elternzeit',
    },
  ]

  return {
    projectGroups,
    familyOptions,
    leaveOptions,
    companyFunctionOptions,
    locationOptions,
    academicOptions,
    hourlyModelOptions,
    competenceFunctionOptions,
    caOptions,
    crossCompetenceOptions,
    crossGroupOptions,
    workingGroupOptions,
    workingFunctionOptions,
    workingDaysOptions,
    competenceCenterOptions,
    functionInCompetenceOptions,
  }
}

