type Keys = (keyof typeof inputLabels)[]
const getValue = (field: string, values: { [key: string]: any }): any[] => {
  const selects = [
    'project_group',
    'location',
    'academic_degrees',
    'hourly_model',
    'family_vacation_type',
    'working_group',
    'working_group_function',
    'cross_group_function',
    'competence_area',
    'ca_function',
    'cross_ca_function',
  ]

  if (Array.isArray(values[field]) && typeof values[field][0] !== 'string') {
    switch (field) {
      case 'company_function':
        return values[field].map((item: any) => item.label)
      case 'child_sick':
      case 'employee_sick':
      case 'leave':
        return values[field].map(
          (item: any) =>
            `${item.type.label}:\n${item.child.name}\nVon: ${item.period.from}, Bis: ${item.period.to}`,
        )
      case 'family_vacation':
        return values[field].map(
          (item: any) =>
            `${item.family_vacation_type.label}:\nVon: ${item.period.from}, Bis: ${item.period.to}`,
        )
      case 'appraisal_interviews':
        return values[field].map((item: any) =>
          item.date
            ? `${item.date}${item.description ? `\n${item.description}` : ''}`
            : '',
        )
      case 'working_groups':
        return (
          values[field]?.map(
            (element: any) =>
              `${inputLabels.working_group}: ${
                element.working_group?.label || ''
              }\n\n${inputLabels.working_group_function}: ${
                element.working_group_function?.label || ''
              }\n\n${inputLabels.cross_group_function}: ${
                element.cross_group_function?.label || ''
              }\n\n${inputLabels.working_group_spec}: ${
                element.working_group_spec
              }`,
          ) || ''
        )
      case 'allowences':
        return values[field].map(
          (item: any) =>
            `${item.allowence_type.label}:\nVon: ${item.period.from}, Bis: ${item.period.to}`,
        )
      case 'competence_areas':
        return (
          values[field]?.map(
            (element: any) =>
              `${inputLabels.competence_area}: ${
                element.competence_area?.label || ''
              }\n\n${inputLabels.ca_function}: ${
                element.ca_function?.label || ''
              }\n\n${inputLabels.cross_ca_function}: ${
                element.cross_ca_function?.label || ''
              }\n\n${inputLabels.ca_spec}: ${element.ca_spec}`,
          ) || ''
        )
      default:
        break
    }
  }
  if (selects.includes(field) && values[field]) return [values[field].label]
  if (['corporate_relocation'].includes(field) && values[field])
    return [`Von: ${values[field].from}, Bis: ${values[field].to}`]
  return [values[field] || '']
}

export const convertData = (values: { [key: string]: any }) => {
  const keys: Keys = Object.keys(values) as Keys
  const json = keys.map((item) => [
    inputLabels[item],
    ...getValue(item, values),
  ])

  return json
}
