import { AxiosResponse } from 'axios'
import api from 'services/api'
import notify from 'services/notify'
import { action } from 'store/action'
import {
  AppThunkAsync,
  CreateCustomerRequest,
  Customer,
  Customers,
  Error,
  UpdateCustomerRequest,
} from 'types/index'

import ActionType from './types'

export const getCustomerRecord = (
  id: string | number,
  onSuccess?: (customers: Customer[]) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_CUSTOMER_RECORD_BEGIN))
  try {
    const customers: AxiosResponse<Customer[]> = await api.get(
      `customers/${id}?_format=json`,
    )
    dispatch(action(ActionType.GET_CUSTOMER_RECORD_SUCCESS, customers.data))
    onSuccess?.(customers.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_CUSTOMER_RECORD_ERROR, error))
    onError?.(error)
  }
}

export const getCustomers = (
  page = 0,
  onSuccess?: (customers: Customers) => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.GET_CUSTOMERS_BEGIN))
  try {
    const customers: AxiosResponse<Customers> = await api.get(
      `pages/customers?_format=json&page=${page}`,
    )
    dispatch(action(ActionType.GET_CUSTOMERS_SUCCESS, customers.data))
    onSuccess?.(customers.data)
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.GET_CUSTOMERS_ERROR, error))
    onError?.(error)
  }
}

export const updateCustomer = (
  { customer, langcode = 'de', countryCode = 'DE' }: UpdateCustomerRequest,
  onSuccess?: () => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.UPDATE_CUSTOMER_BEGIN))
  try {
    await api.patch(`customers/${customer.nid}?_format=json`, {
      title: customer.title,
      address: {
        langcode,
        country_code: countryCode,
        locality: customer.address_city,
        postal_code: customer.address_postalcode,
        address_line1: customer.address_address,
      },
      homepage: customer.homepage?.text,
    })
    dispatch(action(ActionType.UPDATE_CUSTOMER_SUCCESS))
    onSuccess?.()
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.UPDATE_CUSTOMER_ERROR, error))
    onError?.(error)
  }
}

export const createCustomer = (
  { customer, langcode = 'de', countryCode = 'DE' }: CreateCustomerRequest,
  onSuccess?: () => void,
  onError?: (e: Error) => void,
): AppThunkAsync => async (dispatch) => {
  dispatch(action(ActionType.CREATE_CUSTOMER_BEGIN))
  try {
    await api.post('customers?_format=json', {
      title: customer.title,
      address: {
        langcode,
        country_code: countryCode,
        locality: customer.address_city,
        postal_code: customer.address_postalcode,
        address_line1: customer.address_address,
      },
      homepage: customer.homepage?.text,
    })
    dispatch(action(ActionType.CREATE_CUSTOMER_SUCCESS))
    onSuccess?.()
  } catch (error) {
    notify(error)
    dispatch(action(ActionType.CREATE_CUSTOMER_ERROR, error))
    onError?.(error)
  }
}
