import { ReactComponent as BurgerMenu } from 'assets/icons/burger-menu.svg'
import Button from 'components/Button'
import MenuItem from 'components/SmallMenuItem'
import { useState } from 'react'
import { MenuItem as IMenuItem } from 'types'

import styles from './styles.module.scss'

interface SmallMenuProps {
  menuItems?: IMenuItem[]
  toggleMenu: () => void
  onMenuItemClick: (item: IMenuItem) => void
}

const SmallMenu = ({
  menuItems,
  toggleMenu,
  onMenuItemClick,
}: SmallMenuProps) => {
  const [activeMenuItem, setActiveMenuItem] = useState<string>()

  return (
    <>
      <div className={styles.burgerMenuWrapper}>
        <Button id="burger-menu" onClick={toggleMenu}>
          <BurgerMenu />
        </Button>
      </div>
      <div className={styles.menuItemsWrapper}>
        {menuItems?.map((item) => (
          <MenuItem
            key={item.title}
            item={item}
            onClick={() => onMenuItemClick(item)}
            activeMenuItem={activeMenuItem}
            setActiveMenuItem={setActiveMenuItem}
          />
        ))}
      </div>
    </>
  )
}

export default SmallMenu
