import standardIcon from 'assets/icons/chart.svg'
import cn from 'classnames'
import MenuSubItem from 'components/BigMenuSubItem'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import { selectMenuItem } from 'store/app/actions'
import { MenuItem } from 'types'

import styles from './styles.module.scss'

interface BigMenuItemProps {
  item: MenuItem
}

const BigMenuItem = ({ item }: BigMenuItemProps) => {
  const { title, below, icon, selected, link } = item
  const dispatch = useDispatch()

  const onSubItemClick = (subItem: MenuItem) =>
    dispatch(selectMenuItem(subItem, false))
  const onClick = () => dispatch(selectMenuItem(item))

  return (
    <div
      id={title}
      className={cn(styles.bigMenuItem, {
        [styles.selected]: selected,
        [styles.withItems]: below?.length,
      })}
    >
      {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
      <Link
        to={below ? '#' : link}
        id={`menu-item-${title}`}
        onClick={onClick}
        className={styles.clickableWrapper}
      >
        <div className={cn(!below?.length && styles.iconWrapper)}>
          <img src={icon || standardIcon} alt="menu icon" />
        </div>
        <div className={styles.name}>{title}</div>
      </Link>
      {selected && below && (
        <div className={styles.subitemsContainer}>
          {below?.map((subItem) => (
            <MenuSubItem
              key={subItem.title}
              item={subItem}
              onClick={() => onSubItemClick(subItem)}
            />
          ))}
        </div>
      )}
    </div>
  )
}

export default BigMenuItem
