import Button from 'components/Button'
import DatePicker from 'components/inputs/DatePicker'
import RadioBox from 'components/inputs/RadioBox'
import TextInput from 'components/inputs/TextInput'
import { FormikProps } from 'formik'

import { FormValues } from '.'
import styles from './styles.module.scss'

const RADIO_BOXES: { name: string; value: string }[] = [
  { name: '<2500', value: '< 2.500€' },
  { name: '<5000', value: '< 5.000€' },
  { name: '<25000', value: '< 25.000€' },
  { name: '<50000', value: '< 50.000€' },
  { name: '<100000', value: '< 100.000€' },
  { name: '<500000', value: '< 500.000€' },
  { name: '>500000', value: '> 500.000€' },
]

interface ContentViewProps {
  formik: FormikProps<FormValues>
}

const ContentView = ({ formik }: ContentViewProps) => (
  <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
    <div className={styles.filterContainer}>
      <div className={styles.containerTitle}>{'Filterungen'}</div>
      <div>
        <div className={styles.sectionTitle}>{'Bindefrist'}</div>
        <DatePicker
          name="bindingUntil"
          formik={formik}
          placeholder="Bindefrist"
        />
      </div>
      <div>
        <div className={styles.sectionTitle}>{'Gesamthonorar'}</div>
        <TextInput
          name="Gesamthonorar"
          label="Gesamthonorar"
          variant="outlined"
        />
      </div>
      <div>
        <div className={styles.sectionTitle}>{'Angebotssumme'}</div>
        <div className={styles.checkboxesContainer}>
          {RADIO_BOXES.map((item) => (
            <RadioBox
              key={item.value}
              name={item.name}
              value={formik.values.amount}
              onChange={(val) => formik.setFieldValue('amount', val)}
              className={styles.radioBox}
            >
              {item.value}
            </RadioBox>
          ))}
        </div>
      </div>
      <div className={styles.buttonWrapper}>
        <Button className={styles.button} type="reset">
          {'Abbrechen'}
        </Button>
        <Button className={styles.buttonPrimary} type="submit">
          {'OK'}
        </Button>
      </div>
    </div>
  </form>
)

export default ContentView
