// import Logo from 'assets/images/logo.jpg'
import Logo from 'assets/images/Logo_3D.png'
import SearchInput from 'components/inputs/SearchInput'
import { OptionsType } from 'react-select'
import { HeaderOptions, SelectOption } from 'types'

import styles from './styles.module.scss'

interface HeaderProps {
  options?: HeaderOptions
  searchValue?: string
  onSearchChange?: (value: string) => void
  searchDropdownOptions?: OptionsType<SelectOption>
}

const Header = ({
  options = { withoutLogo: false, withoutSearch: false },
  searchValue,
  onSearchChange,
  searchDropdownOptions,
}: HeaderProps) => (
  <header className={styles.header}>
    <div className={styles.headerContent}>
      {!options.withoutLogo && (
        <div className={styles.logo}>
          <img src={Logo} alt="logo" />
        </div>
      )}
      {!options.withoutSearch && (
        <div className={styles.searchWrapper}>
          <SearchInput
            value={searchValue}
            onChange={onSearchChange}
            searchDropdownOptions={searchDropdownOptions}
          />
        </div>
      )}
    </div>
    <div className={styles.divider} />
  </header>
)

export default Header
