import Table from 'common/Table'
import { Column, ColumnInstance, Row } from 'react-table'

import { TableData } from '.'
import styles from './styles.module.scss'

interface ContentViewProps {
  columnsData: Column<TableData>[]
  tableData?: TableData[]
  displayMenu: (row: Row<TableData>, e: React.MouseEvent) => void
  onRowDbClick: (row?: Row<TableData>) => void
  hasNextPage: boolean
  isNextPageLoading: boolean
  loadNextPage: (startIndex: number, stopIndex: number) => Promise<any> | null
  appendedColumn: Partial<ColumnInstance<TableData>>
}

const ContentView = ({
  columnsData,
  tableData,
  displayMenu,
  onRowDbClick,
  hasNextPage,
  isNextPageLoading,
  loadNextPage,
  appendedColumn,
}: ContentViewProps) => (
  <Table
    tableContainerClassName={styles.table}
    columnsData={columnsData}
    tableData={tableData}
    onContextMenu={displayMenu}
    onRowDbClick={onRowDbClick}
    notSelectable
    hasNextPage={hasNextPage}
    isNextPageLoading={isNextPageLoading}
    loadNextPage={loadNextPage}
    appendedColumn={appendedColumn}
  />
)

export default ContentView
