import dateTransform from 'common/pipes/dateTime.pipe'
import returnNumberOfWorkDays from 'common/pipes/returnCorrectNumberOfDays.pipe'
import { format, fromUnixTime, parse } from 'date-fns'
import { useMemo } from 'react'
import { useSelector } from 'react-redux'
import { Column } from 'react-table'
import { EmployeeHolidays, ReduxState } from 'types'

import ContentView from './view'

export type TableData = {
  id: number
  from: string
  to: string
  state: string
  days: string
  substitute: string[] | unknown
}
export const translateState = (state: string) => {
  switch (state) {
    case 'approved':
      return 'Urlaub Genehmigt'
    case 'rejected':
      return 'Urlaub Abgelehnt'
    case 'pending':
      return 'Urlaub Ausstehend'
    case 'planned':
      return 'Urlaub Angedacht'
    case 'requested':
      return 'Urlaub Beantragt'
    default:
      return 'Urlaub Ausstehend'
  }
}
const PersonalView = ({
  employeeData,
  handleChange,
  workingDays,
}: {
  employeeData: EmployeeHolidays[] | null
  handleChange: (status: boolean) => void
  workingDays?: number[]
}): JSX.Element => {
  const { nationalHolidays } = useSelector(
    ({ ives }: ReduxState) => ives.holidays,
  )
  const { user } = useSelector((state: ReduxState) => state.user)
  const currentEmployee =
    employeeData?.filter((employee) => employee.id === user?.employee) || null

  // calculate the holidays left and approved holidays for each year to show the current years total holidays
  const calculateHolidays = (employee: EmployeeHolidays[] | null) => {
    if (employee) {
      const holidaysLeft = employee[0]?.holidays_left
        ? Number(employee[0]?.holidays_left)
        : 0
      const holidaysApproved = employee[0]?.holidays_count?.approved
        ? Number(employee[0]?.holidays_count?.approved)
        : 0
      return holidaysLeft + holidaysApproved
    }
    return 0
  }

  const tableData = (): TableData[] =>
    currentEmployee?.flatMap((holiday) => [
      ...(holiday.holidays?.flatMap((holiday) => ({
        id: holiday.id || 0,
        from: format(fromUnixTime(holiday.from), 'dd.MM.yyyy') || '',
        to: format(fromUnixTime(holiday.to), 'dd.MM.yyyy') || '',
        state: translateState(holiday.state) || '',
        days:
          returnNumberOfWorkDays(
            holiday.from,
            holiday.to,
            nationalHolidays,
            workingDays,
          ).toString() || '0',
        substitute:
          holiday.substitutes.length > 0
            ? holiday.substitutes
                .map((sub) => `${sub.employee_name}`)
                .join('\n')
            : 'Keine Vertretung',
      })) || []),
      ...(holiday.maternity?.flatMap((maternity) => ({
        id: maternity.id || 0,
        from: format(fromUnixTime(maternity.from), 'dd.MM.yyyy') || '',
        to: format(fromUnixTime(maternity.to), 'dd.MM.yyyy') || '',
        state: 'Mutterschaftsurlaub',
        days:
          returnNumberOfWorkDays(
            maternity.from,
            maternity.to,
            nationalHolidays,
            workingDays,
          ).toString() || '0',
        substitute: 'Mutterschaftsurlaub' || '',
      })) || []),
      ...(holiday.parental?.flatMap((parental) => ({
        id: parental.id || 0,
        from: format(fromUnixTime(parental.from), 'dd.MM.yyyy') || '',
        to: format(fromUnixTime(parental.to), 'dd.MM.yyyy') || '',
        state: 'Elternzeit',
        days:
          returnNumberOfWorkDays(
            parental.from,
            parental.to,
            nationalHolidays,
            workingDays,
          ).toString() || '0',
        substitute: 'Elternzeit',
      })) || []),
    ]) || []

  const columnsData = useMemo<Column<TableData>[]>(
    () => [
      {
        Header: 'Von',
        accessor: 'from',
        isSortedDesc: true,
        sortType: (a, b) => dateTransform(a.values.from, b.values.from),
      },
      {
        Header: 'Bis',
        accessor: 'to',
        sortType: (a, b) => dateTransform(a.values.to, b.values.to),
      },
      {
        Header: 'Status',
        accessor: 'state',
      },
      {
        Header: 'Tage',
        accessor: 'days',
      },
      {
        Header: 'Vertretung',
        accessor: 'substitute',
      },
    ],
    [],
  )
  // TODO Try to find in the reac-table the propper way to default sort columns

  return (
    <>
      <ContentView
        calculateHolidays={calculateHolidays}
        handleChange={handleChange}
        userData={currentEmployee}
        columnsData={columnsData}
        tableData={tableData().sort(
          (a, b) =>
            parse(b.from, 'dd.MM.yyyy', new Date()).valueOf() -
            parse(a.from, 'dd.MM.yyyy', new Date()).valueOf(),
        )}
      />
    </>
  )
}

export default PersonalView
