import { ReactComponent as ArrowNextOrange } from 'assets/icons/arrowNextOrange.svg'
import Button from 'components/Button'

import styles from './styles.module.scss'

interface LinkItemProps {
  children: React.ReactText
  onClick?: () => void
}

const LinkItem: React.FC<LinkItemProps> = ({ children, onClick }) => (
  <div className={styles.linkItem}>
    <Button className={styles.linkItemButton} onClick={onClick}>
      {children}
    </Button>
    <ArrowNextOrange />
  </div>
)

export default LinkItem
