import Widget from 'components/Widget'

import Filter, { FormValues } from './Filter'
import Graph from './Graph'
import styles from './styles.module.scss'
import Table from './Table'

interface ContentViewProps {
  filters: FormValues
  setFilters: (values: FormValues) => void
}

const ContentView = ({ filters, setFilters }: ContentViewProps) => (
  <div>
    <div className={styles.title}>{'Auswertungen'}</div>
    <div className={styles.content}>
      <Filter setFilters={setFilters} />
      <Widget
        title="Tabellenansicht"
        bodyClassName={styles.tableWidgetBody}
        containerClassName={styles.tableContainer}
      >
        <Table filters={filters} />
      </Widget>
      <Widget
        title="Graphen"
        bodyClassName={styles.graphWidgetBody}
        containerClassName={styles.graphContainer}
      >
        <Graph />
      </Widget>
    </div>
  </div>
)

export default ContentView
