import { ReactComponent as BurgerMenu } from 'assets/icons/burgerMenu2.svg'
import widget1 from 'assets/images/widget1.png'
import widget2 from 'assets/images/widget2.png'
import widget3 from 'assets/images/widget3.png'
import widget4 from 'assets/images/widget4.png'
import widget5 from 'assets/images/widget5.png'
import widget6 from 'assets/images/widget6.png'
import cn from 'classnames'
import Button from 'components/Button'

import styles from './styles.module.scss'

interface Module {
  image: string
}

const MODULES: Module[] = [
  { image: widget1 },
  { image: widget2 },
  { image: widget3 },
  { image: widget4 },
  { image: widget5 },
  { image: widget6 },
]

const Modules = () => (
  <div className={styles.modules}>
    <div className={styles.burgerIcon}>
      <Button variant="custom">
        <BurgerMenu />
      </Button>
    </div>
    <div className={styles.blocks}>
      {MODULES.map((item, i) => (
        <div
          key={i}
          className={cn('droppable-element', styles.block)}
          draggable
          // this is a hack for firefox
          // Firefox requires some kind of initialization
          // which we can do by adding this attribute
          // @see https://bugzilla.mozilla.org/show_bug.cgi?id=568313
          onDragStart={(e) => e.dataTransfer.setData('text/plain', '')}
        >
          <img src={item.image} alt="" />
        </div>
      ))}
    </div>
  </div>
)

export default Modules
