import cn from 'classnames'
import Table from 'common/Table'
import LinkItem from 'components/LinkItem'
import GlobalSearch from 'components/tableComponents/GlobalSearch'
import Routes from 'constants/routes'
import { forwardRef } from 'react'
import { Link } from 'react-router-dom'
import { Column, ColumnInstance, Row } from 'react-table'
import { AcquisitionState } from 'types'

import { TableData } from '.'
import styles from './styles.module.scss'

interface ContentViewProps {
  selectedCategory: AcquisitionState
  onRowDbClick: (row?: Row<TableData>) => void
  columnsData: Column<TableData>[]
  tableData: TableData[]
  hasNextPage: boolean
  isNextPageLoading: boolean
  loadNextPage: (startIndex: number, stopIndex: number) => Promise<any> | null
  appendedColumn: Partial<ColumnInstance<TableData>>
  toggleModal: (modalName: string) => void
  globalFilter: string | undefined
  setGlobalFilter: (filterValue: string | undefined) => void
  displayMenu: (row: Row<TableData>, e: React.MouseEvent) => void
  tableOnly?: boolean
}

const CATEGORIES: {
  link: AcquisitionState
  title: string
}[] = [
  { link: 'all', title: 'Alle' },
  { link: 'new', title: 'Offen' },
  { link: 'hold', title: 'Wiedervorlage' },
  { link: 'archived', title: 'Archiv' },
]

// eslint-disable-next-line react/display-name
const ContentView = forwardRef(
  (
    {
      selectedCategory,
      toggleModal,
      columnsData,
      tableData,
      hasNextPage,
      isNextPageLoading,
      loadNextPage,
      appendedColumn,
      onRowDbClick,
      globalFilter,
      setGlobalFilter,
      displayMenu,
      tableOnly,
    }: ContentViewProps,
    ref: React.Ref<HTMLDivElement>,
  ) => (
    <div ref={ref}>
      {!tableOnly && [
        <div key="title" className={styles.title}>
          {'Rechnungsverfolgung'}
        </div>,
        <div key="menu" className={styles.menuItemsRow}>
          <LinkItem onClick={() => toggleModal(Routes.new)}>
            {'Neuen Akquise Auftrag erstellen'}
          </LinkItem>
          <LinkItem onClick={() => toggleModal(Routes.customers)}>
            {'Kundenspezifische A.A. Historie'}
          </LinkItem>
        </div>,
      ]}
      <div
        className={cn(styles.filterAndTableWrapper, {
          [styles.tableOnly]: tableOnly,
        })}
      >
        <div className={styles.filterRow}>
          <div>
            {CATEGORIES.map((item) => (
              <Link
                key={item.title}
                to={`${Routes.acquisition}/${item.link}`}
                className={cn(
                  styles.filterItem,
                  selectedCategory === item.link && styles.selected,
                )}
              >
                {item.title}
              </Link>
            ))}
          </div>
          <GlobalSearch
            globalFilter={globalFilter}
            setGlobalFilter={setGlobalFilter}
            containerClassName={styles.search}
          />
        </div>
        <Table
          tableContainerClassName={styles.table}
          columnsData={columnsData}
          tableData={tableData}
          onRowDbClick={onRowDbClick}
          appendedColumn={appendedColumn}
          notSelectable
          onContextMenu={displayMenu}
          hasNextPage={hasNextPage}
          isNextPageLoading={isNextPageLoading}
          loadNextPage={loadNextPage}
        />
      </div>
    </div>
  ),
)
export default ContentView
