import { ReactComponent as ArrowOrange } from 'assets/icons/arrowNextOrange.svg'
import classNames from 'classnames'
import { MenuItem } from 'constants/dashboard'
import DOMPurify from 'dompurify'
import ReactHtmlParser from 'react-html-parser'
import { stringToFullDate } from 'services/date'
import { BlogCategory, BlogItem } from 'types'

import styles from '../styles.module.scss'

interface Props {
  item: BlogItem
  selectedItem: BlogItem
  categories: BlogCategory[]
  onClickDetails?: () => void
  setHighlighted: (val: BlogCategory[] | null) => void
}

const Card: React.FC<Props> = ({
  item,
  categories,
  onClickDetails,
  setHighlighted,
  selectedItem,
}) => {
  const parentCategories = item.categories?.length
    ? categories.filter(({ below }) =>
        below?.some(
          ({ tid }) =>
            item.categories?.includes(tid) || item.project_group?.includes(tid),
        ),
      )
    : null

  const firstSubCategory = parentCategories?.[0]?.below?.find((category) =>
    item.categories?.includes(category.tid),
  )
  const htmlFrom = (htmlString: string) =>
    ReactHtmlParser(DOMPurify.sanitize(ReactHtmlParser(htmlString)))

  return (
    <div
      onMouseOver={() => setHighlighted(parentCategories)}
      onFocus={() => setHighlighted(parentCategories)}
      onMouseOut={() => setHighlighted(null)}
      onBlur={() => setHighlighted(null)}
      className={classNames(styles.news)}
      style={{ backgroundImage: `url(${item.images[0]})` }}
      role="gridcell"
      tabIndex={0}
    >
      <div
        className={classNames(styles.animated, {
          [styles.orange]: selectedItem?.title === MenuItem.competenceAreas,
          [styles.green]: selectedItem?.title === MenuItem.workingGroups,
          [styles.blue]: selectedItem?.title === MenuItem.leipzigProjectGroups,
        })}
      >
        {item.summary && <span className={styles.plusButton}>{'+'}</span>}
        <div className={styles.date}>{stringToFullDate(item.created)}</div>
        <h2 className={styles.newsTitle}>
          {item.title && htmlFrom(item.title)}
        </h2>
        <div className={styles.description}>
          {item.title && htmlFrom(item.summary)}
        </div>
        <div className={styles.bottomControls}>
          {firstSubCategory && parentCategories?.[0] && (
            <div className={styles.sub}>
              {parentCategories[0].title}
              {' / '}
              {firstSubCategory.title}
            </div>
          )}
          <button
            className={styles.more}
            type="button"
            onClick={onClickDetails}
          >
            {'Artikel lesen'} <ArrowOrange />
          </button>
        </div>
      </div>
    </div>
  )
}

export default Card
