/* eslint-disable camelcase */
import { EmployeeHolidays } from 'types'

export interface AllSubstitutes {
  period: {
    from: string
    to: string
  }
  employee: number
  name: string
}

const getSubstitutes = (
  data?: EmployeeHolidays[] | null,
): SubstituteInterface => {
  const substitutesByEmployee: SubstituteInterface = {} as SubstituteInterface
  data?.forEach((employee) => {
    employee?.holidays?.forEach((holiday) => {
      holiday.substitutes.forEach((substitute) => {
        const employeeId = substitute.employee
        if (!substitutesByEmployee[employeeId]) {
          substitutesByEmployee[employeeId] = []
        }
        substitutesByEmployee[employeeId].push(substitute.period)
      })
    })
  })
  return substitutesByEmployee
}

interface SubstituteInterface {
  id: number
  period: [
    {
      from: string
      to: string
    },
  ]
  employee: number
  employee_name: string
  subs_answer: boolean | null
  subs_rejection_reason: string | null
}

export default getSubstitutes
