import Customers, { TableData } from 'common/Customers'
import Modal from 'common/Modal'
import { DEFAULT_TABLE_WIDTH } from 'constants/index'
import { useFormik } from 'formik'
import useToggle from 'hooks/useToggle'
import useWindowSize from 'hooks/useWindowSize'
import { useEffect, useMemo, useState } from 'react'
import { useDispatch } from 'react-redux'
import { Row } from 'react-table'
import { formatDate, validateDateInput } from 'services/date'
import notify from 'services/notify'
import { percent } from 'services/utils'
import {
  getFieldDepartments,
  getFieldProbability,
  getLiability,
  getPgl,
  getProjectManagers,
  getProjects,
} from 'store/ives/offers/actions'
import {
  BackEndSelectOptions,
  Projects,
  SelectOption,
  SingleSelectOption,
} from 'types'
import * as Yup from 'yup'

import Form from './form'

// FIXME: required fields when back-end will be ready
const FormSchema: Yup.SchemaOf<FormValues> = Yup.object().shape({
  number: Yup.string().default(''), // .required('Erforderlich'),
  project: Yup.mixed<SingleSelectOption>().notRequired(),
  title: Yup.string().default(''), // .required('Erforderlich'),
  customer: Yup.string().default(''), // .required('Erforderlich'),
  created: Yup.string()
    .transform(validateDateInput)
    .typeError('Bitte korrektes Datum eingeben')
    .default(''),
  probability: Yup.mixed<SingleSelectOption>().default(undefined),
  liabilityDamage: Yup.mixed<SingleSelectOption>().default(undefined),
  pgl: Yup.mixed<SingleSelectOption>().notRequired(), // .required('Erforderlich'),
  department: Yup.mixed<SingleSelectOption>().notRequired(), // .required('Erforderlich'),
  projectManager: Yup.mixed<SingleSelectOption>().notRequired(),
  comment: Yup.string().default(''),
})

export const initialValues = {
  number: formatDate(new Date()).split('.').join(''),
  project: undefined as SingleSelectOption | any,
  title: '',
  customer: '',
  created: formatDate(new Date()),
  probability: undefined as SingleSelectOption | any,
  liabilityDamage: undefined as SingleSelectOption | any,
  pgl: undefined as SingleSelectOption | any,
  department: undefined as SingleSelectOption | any,
  projectManager: undefined as SingleSelectOption | any,
  comment: '',
}

export type FormValues = typeof initialValues

export type InitialData = {
  [key in keyof FormValues]: string
}

interface OffersFormProps {
  initialData?: InitialData
  onCancel: () => void
  onSubmit: (values: FormValues) => void
}

const OffersForm = ({ initialData, onSubmit, onCancel }: OffersFormProps) => {
  const [departments, setDepartments] = useState<Record<string, string>>()
  const [fieldProbability, setFieldProbability] = useState<
    Record<string, string>
  >()
  const [liability, setLiability] = useState<Record<string, string>>()
  const [pgl, setPgl] = useState<BackEndSelectOptions>()
  const [projectManagers, setProjectManagers] = useState<BackEndSelectOptions>()
  const [projects, setProjects] = useState<Projects>()
  const [selectedCustomerId, setSelectedCustomerId] = useState<string>()
  const dispatch = useDispatch()
  const { width = DEFAULT_TABLE_WIDTH, height = 300 } = useWindowSize()
  const customers = useToggle()

  const formik = useFormik({
    initialValues: { ...initialValues },
    onSubmit: (values, helpers) => {
      try {
        helpers.resetForm()
        onSubmit({
          ...values,
          customer: selectedCustomerId || '',
        })
      } catch (error) {
        notify(error)
      }
    },
    validationSchema: FormSchema,
  })

  useEffect(() => {
    dispatch(getFieldDepartments(setDepartments))
    dispatch(getFieldProbability(setFieldProbability))
    dispatch(getLiability(setLiability))
    dispatch(getPgl('', setPgl))
    dispatch(getProjectManagers('', setProjectManagers))
    dispatch(getProjects(0, setProjects))
  }, [])

  // TODO refactor
  useEffect(() => {
    departments &&
      fieldProbability &&
      liability &&
      pgl &&
      projectManagers &&
      projects &&
      initialData &&
      formik.setValues({
        ...initialData,
        probability: initialData?.probability
          ? ({
              value: initialData.probability,
              label: fieldProbability?.[initialData.probability],
            } as SingleSelectOption)
          : undefined,
        liabilityDamage: initialData?.liabilityDamage
          ? ({
              value: initialData.liabilityDamage,
              label: liability?.[initialData.liabilityDamage],
            } as SingleSelectOption)
          : undefined,
        pgl: initialData?.pgl
          ? ({
              value: initialData.pgl,
              label: pgl?.rows.find((item) => `${item.id}` === initialData?.pgl)
                ?.name,
            } as SingleSelectOption)
          : undefined,
        department: initialData?.department
          ? ({
              value: initialData.department,
              label: departments?.[initialData.department],
            } as SingleSelectOption)
          : undefined,
        projectManager: initialData?.projectManager
          ? ({
              value: initialData.projectManager,
              label: projectManagers?.rows.find(
                (item) => `${item.id}` === initialData?.projectManager,
              )?.name,
            } as SingleSelectOption)
          : undefined,
        project: initialData?.project
          ? ({
              value: initialData.project,
              label: projects?.rows.find(
                (item) => `${item.nid}` === initialData?.project,
              )?.title,
            } as SingleSelectOption)
          : undefined,
      })
  }, [
    departments,
    fieldProbability,
    liability,
    pgl,
    projectManagers,
    projects,
    initialData,
  ])

  const departmentOptions = useMemo<SelectOption[]>(
    () =>
      departments
        ? Object.entries(departments).map<SelectOption>((pair) => ({
            value: pair[0],
            label: pair[1],
          }))
        : [],
    [departments],
  )

  const probabilityTranslation = (label: string) => {
    switch (label) {
      case 'Certain':
        return 'Sicher'
      case 'Very likely':
        return 'Sehr Wahrscheinlich'
      case 'Possible':
        return 'Wahrscheinlich'
      case 'Unlikely':
        return 'Unwahrscheinlich'
      default:
        return ''
    }
  }

  const probabilityOptions = useMemo<SelectOption[]>(
    () =>
      fieldProbability
        ? Object.entries(fieldProbability).map<SelectOption>((pair) => ({
            value: pair[0],
            label: probabilityTranslation(pair[1]),
          }))
        : [],
    [fieldProbability],
  )

  const liabilityOptions = useMemo<SelectOption[]>(
    () =>
      liability
        ? Object.entries(liability)
            .sort((a, b) => a[0].localeCompare(b[0]))
            .map<SelectOption>((pair) => ({
              value: pair[0],
              label: pair[1],
            }))
        : [],
    [liability],
  )

  const pglOptions = useMemo<SelectOption[]>(
    () =>
      pgl?.rows.map((item) => ({ label: item.name, value: `${item.id}` })) ||
      [],
    [pgl],
  )

  const projectManagerOptions = useMemo<SelectOption[]>(
    () =>
      projectManagers?.rows.map((item) => ({
        label: item.name,
        value: `${item.id}`,
      })) || [],
    [projectManagers],
  )

  const projectOptions = useMemo<SelectOption[]>(
    () =>
      projects?.rows.map((item) => ({
        label: item.title,
        value: `${item.nid}`,
      })) || [],
    [projects],
  )

  const onCustomerSubmitClick = (row?: Row<TableData>) => {
    formik.setFieldValue('customer' as keyof FormValues, row?.original.name)
    setSelectedCustomerId(row?.id)
    customers.off()
  }

  return (
    <>
      <Form
        formik={formik}
        probabilityOptions={probabilityOptions}
        departmentOptions={departmentOptions}
        liabilityOptions={liabilityOptions}
        pglOptions={pglOptions}
        projectManagerOptions={projectManagerOptions}
        projectOptions={projectOptions}
        onCancel={onCancel}
        onUserSelectClick={customers.toggle}
      />
      <Modal
        isOpen={customers.value}
        onRequestClose={customers.off}
        maxWidth={percent(46.2, width)}
      >
        <Customers
          tableWidth={percent(45.8, width)}
          tableHeight={percent(50, height)}
          onCancel={customers.off}
          onSubmit={onCustomerSubmitClick}
        />
      </Modal>
    </>
  )
}

export default OffersForm
