import { format } from 'date-fns'

const isPartOfWorkingDays = (day: Date, workingDays?: number[]): boolean => {
  const daysToExlude: string[] = []
  const daysOfTheWeek = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday']
  if (workingDays) {
    workingDays.forEach((value, i) => {
      if (value !== 1) {
        daysToExlude.push(daysOfTheWeek[i])
      }
    })
  }

  return daysToExlude.includes(format(day, 'EEEE'))
}
export default isPartOfWorkingDays
