/* eslint-disable no-case-declarations */
import { ReactComponent as Mail } from 'assets/icons/mail.svg'
import cn from 'classnames'
import Button from 'components/Button'
import { formatDistance } from 'date-fns'
import useWebSocket from 'hooks/useWebSocket'
import { useCallback, useEffect, useMemo, useState } from 'react'
import { useSelector } from 'react-redux'
import api from 'services/api'
import { Message, ReduxState, RejectProps, Substitutes } from 'types'

import BigMenu from './BigMenu'
import EmployeePopup from './employeePopup'
import Notification from './Notification'
import SmallMenu from './SmallMenu'
import styles from './styles.module.scss'
import TaskMenu from './TaskMenu'

interface RightMenuProps {
  hasTabs?: boolean
}

/** Messages types come from BackEnd in data.type */
export const messagesTypes = {
  /** Substitution */
  subsConfirm: 'employee_holiday_subs_confirm',
  subsConfirmed: 'employee_holiday_subs_confirmed',
  subsRejected: 'employee_holiday_subs_rejected',
  subsRejectedNotify: 'employee_holiday_subs_rejected_notify',
  /** Project leader */
  pglConfirm: 'employee_holiday_pgl_confirm',
  pglConfirmed: 'employee_holiday_pgl_confirmed',
  pglRejected: 'employee_holiday_pgl_rejected',
  pglRejectedNotify: 'employee_holiday_pgl_rejected_notify',
  /** MD */
  mdConfirm: 'employee_holiday_md_confirm',
  mdConfirmed: 'employee_holiday_md_confirmed',
  mdRejected: 'employee_holiday_md_rejected',
}

export const clickedMessagesTypes = [
  /** Substitution */
  messagesTypes.subsConfirm,
  messagesTypes.subsConfirmed,
  messagesTypes.subsRejected,
  messagesTypes.subsRejectedNotify,
  /** Project leader */
  messagesTypes.pglConfirm,
  messagesTypes.pglRejected,
  messagesTypes.pglConfirmed,
  messagesTypes.pglRejectedNotify,
  /** MD */
  messagesTypes.mdConfirm,
  messagesTypes.mdConfirmed,
  messagesTypes.mdRejected,
]

interface MessageAnswer {
  type: string
  answer: boolean
  text?: string
  substitutesData?: Substitutes[]
  sid?: number
}

const RightMenu = ({ hasTabs }: RightMenuProps) => {
  const [closeMenu, setCloseMenu] = useState(true)
  const [contentType, setContentType] = useState('')
  const [showNotify, setShowNotify] = useState(false)
  const { user } = useSelector((state: ReduxState) => state.user)

  const socketPath = '/websocket/notifications'
  const [notificationsData, setNotificationsData] = useState<Message | null>(
    null,
  )
  const [messages, setMessages] = useState<Message[]>([])
  const [currentPage, setCurrentPage] = useState(1)
  const [maxPages, setMaxPages] = useState(0)
  const [isLoad, setIsLoad] = useState(false)
  const [showPopup, setShowPopup] = useState(false)
  const [messageUser, setMessageUser] = useState<Message | null>(null)
  const [showReasonField, setShowReasonField] = useState(false)
  const messageCount = useMemo(
    () =>
      messages ? messages.filter((message) => message.read === 0).length : 0,
    [messages],
  )

  /** Load first 5 not read messages */
  const getMessageData = useCallback(async () => {
    const messageData = await api.get(
      'widgets/private_messages/0?_format=json&limit=5',
    )
    setMessages(messageData.data.rows)
  }, [])

  useEffect(() => {
    setIsLoad(false)
    getMessageData()
    setIsLoad(true)
  }, [notificationsData])

  /** Check on duplicate messages */
  const checkDuplicates = useCallback(
    (list: Message[], listForFilter: Message[]) => {
      const messagesIds = list.map((item) => item.mid)
      const mergedMessages = [
        ...list,
        ...listForFilter.filter(
          (item: Message) => !messagesIds.includes(item.mid),
        ),
      ]
      setMessages(mergedMessages)
    },
    [messages],
  )

  /** On scroll add messages */
  const getNextMessages = useCallback(async () => {
    if (currentPage <= maxPages) {
      setIsLoad(false)
      const allMessageData = await api.get(
        `widgets/private_messages/all?_format=json&limit=12&page=${currentPage}`,
      )
      setMessages([...messages, ...allMessageData.data.rows])
      setCurrentPage(currentPage + 1)
      setIsLoad(true)
    }
  }, [currentPage, maxPages])

  /** On button add messages */
  const getAllMessages = useCallback(
    async (messagesCount?: number) => {
      setIsLoad(false)
      const allMessageData = await api.get(
        `widgets/private_messages/all?_format=json&limit=${
          messagesCount || 12
        }`,
      )
      setMessages(allMessageData.data.rows ? allMessageData.data.rows : [])
      setMaxPages(allMessageData.data.pager.total_pages - 1)
      setCurrentPage(1)
      setIsLoad(true)
    },
    [messages],
  )

  /** Mark read or not message */
  const updateMessageStatus = useCallback(
    async ({ messageId, read }) => {
      await api.patch(`private_messages/${messageId}?_format=json`, {
        read,
      })
      /** Update own state on read message */
      const checkedReadMessages = messages.map((message) => {
        if (message.mid === messageId) {
          return { ...message, read }
        }
        return message
      })

      setMessages(checkedReadMessages)
    },
    [messages],
  )

  /** When click on message */
  const onClickMessage = useCallback(async (item?: Message | null) => {
    item && setMessageUser({ ...item })
    setShowPopup(true)
  }, [])

  /** Get notification data and show notification */
  const showNotifyMessage = useCallback(
    (event: MessageEvent) => {
      const data = JSON.parse(event.data)
      if (
        (data.to.uid && data.to.uid.includes(String(user?.id))) ||
        (data.to.employee && data.to.employee.includes(String(user?.employee)))
      ) {
        setShowNotify(true)
        setNotificationsData(data)
        setTimeout(() => {
          setShowNotify(false)
        }, 10000)
      }
      return null
    },
    [user],
  )

  useWebSocket(socketPath, showNotifyMessage)

  /** Delete message */
  const onDeleteMessage = useCallback(
    async (messageId?: number) => {
      setIsLoad(false)
      await api.delete(`private_messages/${messageId}?_format=json`)

      const allMessageData = await api.get(
        `widgets/private_messages/all?_format=json&limit=${messages.length}`,
      )
      const filteredMessages = messages.filter(
        (message) => message.mid !== messageId,
      )

      if (filteredMessages && allMessageData.data.rows) {
        checkDuplicates(filteredMessages, allMessageData.data.rows)
      }
      setIsLoad(true)
    },
    [messages],
  )

  /** * *Popup Functions *** */

  const messageAnswer = ({
    type,
    answer,
    text,
    substitutesData,
    sid,
  }: MessageAnswer) => {
    switch (type) {
      case messagesTypes.subsConfirm:
        const substitutes = substitutesData?.flatMap((item) => {
          if (String(item.id) === String(sid)) {
            return {
              ...item,
              subs_answer: answer,
              subs_rejection_reason: text || null,
            }
          }
          return item
        })
        // eslint-disable-next-line camelcase
        // eslint-disable-next-line camelcase

        return { substitutes }
      case messagesTypes.pglConfirm:
        return {
          pgl_answer: answer,
          pgl_rejection_reason: text || null,
        }
      case messagesTypes.mdConfirm:
        return {
          md_answer: answer,
          md_rejection_reason: text || null,
        }
      default:
        return null
    }
  }

  /** Accept vacation, delete message, reload messages */
  const onAccept = useCallback(async (userData: Message | null) => {
    if (userData) {
      const { data } = userData
      const body = messageAnswer({
        type: data.type,
        answer: true,
        substitutesData: data.substitutes,
        sid: data.sid,
      })

      await api.patch(
        `employees/holidays/${userData?.data.hid}?_format=json`,
        body,
      )
      setShowPopup(false)
      await onDeleteMessage(userData.mid)
      await getMessageData()
    }
  }, [])

  const onLater = useCallback(() => {
    setShowPopup(false)
    setShowReasonField(false)
  }, [])

  const onCancel = useCallback(() => {
    setShowReasonField(true)
  }, [])

  /** Reject application and send reason, delete message, reload messages */
  const onReject = useCallback(async ({ values, userData }: RejectProps) => {
    if (userData) {
      const { data } = userData
      const body = messageAnswer({
        type: data.type,
        answer: false,
        text: values.reason,
        substitutesData: data.substitutes,
        sid: data.sid,
      })

      await api.patch(
        `employees/holidays/${userData?.data.hid}?_format=json`,
        body,
      )
      setShowReasonField(false)
      setShowPopup(false)
      await onDeleteMessage(userData.mid)
      await api.delete(`employees/holidays/${userData?.data.hid}?_format=json`)
      await getMessageData()
    }
  }, [])

  const notifyTime = notificationsData?.time
    ? `${formatDistance(new Date(), new Date(notificationsData?.time))} ago`
    : ''

  const content = () => {
    switch (contentType) {
      case 'mail':
        return (
          <TaskMenu
            fetchItems={getNextMessages}
            messages={messages}
            onButton={getAllMessages}
            checkBoxCallback={updateMessageStatus}
            onRightClickMessage={onDeleteMessage}
            isLoad={isLoad}
            onClickMessage={onClickMessage}
          />
        )
      case 'plus':
        return <BigMenu hasTabs={hasTabs} />
      default:
        return <BigMenu hasTabs={hasTabs} />
    }
  }

  return (
    <div className={styles.rightMenuWrap}>
      <div className={cn(styles.rightMenu, !closeMenu && styles.big)}>
        <div className={styles.iconGroup}>
          <Button
            onClick={() => {
              closeMenu && setCloseMenu(!closeMenu)
              closeMenu && setContentType('mail')
            }}
            variant="custom"
            id="mail"
            className={styles.mailIcon}
          >
            <Mail />
            <div className={styles.mailNotification}>{messageCount}</div>
          </Button>
        </div>

        {/* EmployeePopup */}
        {showPopup && (
          <EmployeePopup
            showReasonField={showReasonField}
            userData={messageUser}
            onAccept={onAccept}
            onLater={onLater}
            onCancel={onCancel}
            onReject={onReject}
          />
        )}

        {closeMenu ? <SmallMenu /> : content()}
        <Button
          variant="custom"
          id="plus"
          onClick={() => {
            setCloseMenu(!closeMenu)
            closeMenu && setContentType('plus')
          }}
          className={cn(styles.animatedButton, !closeMenu && styles.toggled)}
        >
          <span />
          <span />
        </Button>
      </div>
      {/* Notification */}
      <Notification
        showNotify={showNotify}
        notificationsData={notificationsData}
        notifyTime={notifyTime}
        notificationType="vacation"
        onClickNotify={() => onClickMessage(notificationsData)}
      />
    </div>
  )
}

export default RightMenu
