import Table from 'common/Table'
import { eachDayOfInterval, format, isWeekend } from 'date-fns'
import { useMemo } from 'react'
import { Item, Menu, useContextMenu } from 'react-contexify'
import { CSVLink } from 'react-csv'
import { Column, Row } from 'react-table'
import { Sick } from 'types'

import styles from '../../../containers/Ives/Holidays/HolidayCounter/styles.module.scss'

interface SickModalProps {
  sickData?: Sick[]
}
const MENU_ID = 'sick-menu-id'

type TableData = {
  id: string
  from: string
  to: string
  type: string
  // eslint-disable-next-line camelcase
  number_of_days: string
}
const calculateSickDays = (item: Sick) => {
  let counter = 0
  const intervalArray = eachDayOfInterval({
    start: new Date(item.period.from),
    end: new Date(item.period.to),
  })
  intervalArray.forEach((day) => {
    if (!isWeekend(day)) {
      counter += 1
    }
  })
  return counter
}

const SickModal = ({ sickData }: SickModalProps) => {
  const sumSickDays = () => {
    let counter = 0
    sickData?.forEach((sick) => {
      counter += calculateSickDays(sick)
    })
    return counter
  }
  const everyYearSickDays = (): { previous: number; current: number } => {
    let previous = 0
    let current = 0
    const currentYear = new Date().getFullYear()
    sickData?.forEach((sick) => {
      if (new Date(sick.period.to).getFullYear() < currentYear) {
        previous += calculateSickDays(sick)
      } else {
        current += calculateSickDays(sick)
      }
    })
    return { previous, current }
  }
  const tableData = useMemo<TableData[]>(
    () =>
      sickData?.map((sick: any) => ({
        id: sick.id,
        from: format(new Date(sick.period.from), 'dd.MM.yyyy'),
        to: format(new Date(sick.period.to), 'dd.MM.yyyy'),
        type: sick.type,
        number_of_days: calculateSickDays(sick).toString(),
      })) || [],
    [sickData],
  )
  const columnsData = useMemo<Column<TableData>[]>(
    () => [
      {
        Header: 'Art der Krankheit',
        accessor: 'type',
      },
      {
        Header: 'Von',
        accessor: 'from',
      },
      {
        Header: 'Bis',
        accessor: 'to',
      },
      {
        Header: 'Anzahl der Tage',
        accessor: 'number_of_days',
      },
    ],
    [],
  )
  const headers = columnsData.map((el) => ({
    label: el.Header,
    key: el.accessor,
  }))
  const { show } = useContextMenu({
    id: MENU_ID,
  })
  const displayMenu = (row: Row<any>, e: React.MouseEvent) =>
    show(e, { props: { row } })
  return (
    <>
      <Table
        columnsData={columnsData}
        tableData={tableData}
        onContextMenu={displayMenu}
      />
      <div className={styles.counterWrap}>
        <div className={styles.counter}>
          <span className={styles.counterLabel}>{'Gesamt'}</span>
          <span className={styles.counterValue}>{`${
            sumSickDays() || '0'
          } Tage`}</span>
        </div>
        <div className={styles.counter}>
          <span className={styles.counterLabel}>
            {'Krankheitstage aktuelles Kalenderjahr'}
          </span>
          <span className={styles.counterValue}>
            {`${everyYearSickDays().current || '0'} Tage`}
          </span>
        </div>
        <div className={styles.counter}>
          <span className={styles.counterLabel}>
            {'Krankheitstage aus dem Vorjahr'}
          </span>
          <span className={styles.counterValue}>
            {`${everyYearSickDays().previous || '0'} Tage`}
          </span>
        </div>
      </div>
      <Menu id={MENU_ID}>
        <Item>
          <CSVLink
            filename={`KrankheitsHistorien${new Date()}.csv`}
            data={tableData}
            headers={[...headers] as any}
          >
            {'Als excel Tabelle exportieren'}
          </CSVLink>
        </Item>
      </Menu>
    </>
  )
}

export default SickModal
